					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.request.model') }}</span>
					</label>
					<div class="input-group" :title="requestRecordId">
						<input type="text" class="form-control view-disabled ellipsis" :value="requestRecordId" readonly="readonly" />
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('label.insert')"
							v-if="!readonly"
							@click="openRecordMngModal('I', 'create')"
						>
							<i class="icon-plus"></i>
						</button>
						<button
							class="btn btn-icon search-linkBtn"
							:title="readonly ? $t('label.open') : $t('label.update')"
							@click="openRecordMngModal('I', 'update')"
						>
							<i class="icon-link"></i>
						</button>
						<button class="btn btn-icon search-linkBtn" :title="$t('label.search')" v-if="!readonly" @click="openRecordSrchModal('I')">
							<i class="icon-srch"></i>
						</button>
						<button class="btn btn-icon search-linkBtn" :title="$t('label.initialize')" v-if="!readonly" @click="setRecordData('I')">
							<i class="icon-reset"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.response.model') }}</span>
					</label>
					<div class="input-group" :title="responseRecordId">
						<input type="text" class="form-control view-disabled ellipsis" :value="responseRecordId" readonly="readonly" />
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('label.insert')"
							v-if="!readonly"
							@click="openRecordMngModal('O', 'create')"
						>
							<i class="icon-plus"></i>
						</button>
						<button
							class="btn btn-icon search-linkBtn"
							:title="readonly ? $t('label.open') : $t('label.update')"
							@click="openRecordMngModal('O', 'update')"
						>
							<i class="icon-link"></i>
						</button>
						<button class="btn btn-icon search-linkBtn" :title="$t('label.search')" v-if="!readonly" @click="openRecordSrchModal('O')">
							<i class="icon-srch"></i>
						</button>
						<button class="btn btn-icon search-linkBtn" :title="$t('label.initialize')" v-if="!readonly" @click="setRecordData('O')">
							<i class="icon-reset"></i>
						</button>
					</div>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.useyn') }}</span>
					</label>
					<select v-model="object.useYn" class="form-control view-disabled" :readonly="true">
						<option v-for="option in ynList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.desc') }}</span>
						<span class="letterLength" v-if="!readonly">
							{{ `(${object.description ? object.description.length : 0}/${descMaxLength})` }}
						</span>
					</label>
					<textarea
						v-model="object.description"
						class="form-control view-disabled"
						:readonly="readonly"
						style="min-height: 150px"
						@input="inputEvt($event, `description`)"
						:maxlength="descMaxLength"
					/>
				</div>
			</div>
		</div>
	</div>
	<div v-else>
		<div class="row frm-row">
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<input type="text" v-model="object.id" class="form-control view-disabled dataKey" :readonly="'add' !== this.mode" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.name') }}</span>
						<span class="letterLength" v-if="!readonly">
							{{ `(${object.name ? object.name.length : 0}/${nameMaxLength})` }}
						</span>
					</label>
					<input
						type="text"
						v-model="object.name"
						class="form-control view-disabled"
						:readonly="readonly"
						:maxlength="nameMaxLength"
