				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.privilege') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privilegeId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in privilegeTypeList" :key="option.privilegeId" :value="option.privilegeId" :disabled="option.disabled">
							{{ option.privilegeId }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.privateYn') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privateYn" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in ynList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.biz.name') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.bizId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in bizList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.linked.server.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.serverId" class="form-control view-disabled" :readonly="true">
						<option v-for="serverInfo in linkedServerList" :key="serverInfo.id" :value="serverInfo.id">
							{{ serverInfo.name }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.interface.type') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.interfaceType" class="form-control view-disabled" :readonly="true">
						<option
							v-for="interfaceType in interfaceTypeList"
							:key="interfaceType.pk.propertyKey"
							:value="interfaceType.pk.propertyKey"
							v-text="interfaceType.propertyValue"
						></option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ `Target ${this.$t('label.system.id')}` }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.targetSystemId" class="form-control view-disabled" :readonly="true">
						<option
							v-for="targetSystemInfo in targetSystemInfoList"
							:key="targetSystemInfo.id"
							:value="targetSystemInfo.id"
							v-text="targetSystemInfo.id"
						></option>
					</select>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.metaDomain') }}</span>
					</label>
					<select v-model="object.metaDomain" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in metaDomainList" :key="option" :value="option">
							{{ option }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.request.model') }}</span>
					</label>
					<div class="input-group" :title="`${requestRecordId}${existRequestMapping ? `\n${$t(`msg.exist.interface.mapping`)}` : ``}`">
						<input type="text" class="form-control view-disabled ellipsis" :value="requestRecordId" readonly="readonly" />
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('label.insert')"
							v-if="!readonly"
							:disabled="existRequestMapping"
							@click="openRecordMngModal('I', 'create')"
						>
							<i class="icon-plus"></i>
						</button>
						<button
							class="btn btn-icon search-linkBtn"
							:title="readonly ? $t('label.open') : $t('label.update')"
							@click="openRecordMngModal('I', 'update')"
						>
							<i class="icon-link"></i>
						</button>
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('label.search')"
							v-if="!readonly"
							:disabled="existRequestMapping"
							@click="openRecordSrchModal('I')"
						>
							<i class="icon-srch"></i>
						</button>
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('label.initialize')"
							v-if="!readonly"
							:disabled="existRequestMapping"
							@click="setRecordData('I')"
						>
							<i class="icon-reset"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.response.model') }}</span>
					</label>
					<div class="input-group" :title="`${responseRecordId}${existResponseMapping ? `\n${$t(`msg.exist.interface.mapping`)}` : ``}`">
						<input type="text" class="form-control view-disabled ellipsis" :value="responseRecordId" readonly="readonly" />
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('label.insert')"
							v-if="!readonly"
							:disabled="existResponseMapping"
							@click="openRecordMngModal('O', 'create')"
						>
							<i class="icon-plus"></i>
						</button>
						<button
							class="btn btn-icon search-linkBtn"
							:title="readonly ? $t('label.open') : $t('label.update')"
							@click="openRecordMngModal('O', 'update')"
						>
							<i class="icon-link"></i>
						</button>
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('label.search')"
							v-if="!readonly"
							:disabled="existResponseMapping"
							@click="openRecordSrchModal('O')"
						>
							<i class="icon-srch"></i>
						</button>
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('label.initialize')"
							v-if="!readonly"
							:disabled="existResponseMapping"
							@click="setRecordData('O')"
						>
							<i class="icon-reset"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.useyn') }}</span>
					</label>
					<select v-model="object.useYn" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in ynList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.desc') }}</span>
						<span class="letterLength" v-if="!readonly">
							{{ `(${object.description ? object.description.length : 0}/${descMaxLength})` }}
						</span>
					</label>
					<textarea
						v-model="object.description"
						class="form-control view-disabled"
						:readonly="readonly"
						style="min-height: 150px"
						@input="inputEvt($event, `description`)"
						:maxlength="descMaxLength"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import merge from 'lodash/merge';
import { parseHierarchyObj, HttpReq, getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'ServiceBasicInfo',
	props: {
		detailData: Object,
		mode: String,
		existRequestMapping: Boolean,
		existResponseMapping: Boolean
	},
	data() {
		return {
			bizList: [],
			linkedServerList: [],
			ynList: [],
			privilegeTypeList: [],
			metaDomainList: [],
			interfaceTypeList: [],
			targetSystemInfoList: [],

			requestRecordObject: {},
			responseRecordObject: {},

			object: {
				id: null,
				name: null,
				description: null,
				serverId: null,
				interfaceType: null,
				targetSystemId: null,
				privateYn: `N`,
				privilegeId: null,
				metaDomain: null,
				requestRecordId: null,
				responseRecordId: null,
				publishTimestamp: null,
				useYn: `Y`
			},
			modalInfo: {
				title: this.$t('label.modelRecord'),
				search: {
					list: [
						{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId') },
						{
							type: 'dataList',
							vModel: 'pageSize',
							label: this.$t('label.listCnt'),
							placeholder: this.$t('msg.coditionList'),
							val: '10',
							optionInfo: {
								optionFor: 'option in [10, 100, 1000]',
								optionValue: 'option',
								optionText: 'option'
							}
						}
					]
				},
				button: {
					list: [{ id: 'initialize', isUse: true }]
				},
				grid: {
					url: `/api/entity/record/search`,
					totalCntUrl: `/api/entity/record/count`,
					paging: {
						isUse: true,
						side: 'server'
					},
					options: {
						columns: [
							{
								name: 'id',
								header: this.$t('label.id'),
								width: '50%'
							},
							{
								name: 'name',
								header: this.$t('label.name'),
								width: '50%'
							}
						]
					}
				}
			},

			httpReqList: [
				{
					url: `/api/entity/server/search`,
					param: {
						object: {},
						limit: null,
						next: null,
						reverseOrder: false
					},
					itemName: 'linkedServerList'
				},
				{
					url: `/api/entity/system/search`,
					param: {
						object: {},
						limit: null,
						next: null,
						reverseOrder: false
					},
					itemName: 'targetSystemInfoList'
				},
				{
					url: `/api/page/properties`,
					param: { pk: { propertyId: 'Interface.Type' }, orderByKey: true },
					itemName: 'interfaceTypeList'
				},
				{
					url: `/api/entity/fieldMeta/group/search`,
					itemName: 'metaDomainList'
				},
				{
					url: `/api/page/properties`,
					param: { pk: { propertyId: 'List.Biz' }, orderByKey: true },
					itemName: 'bizList'
				},
				{
					url: `/api/page/properties`,
					param: { pk: { propertyId: 'List.Yn' }, orderByKey: true },
					itemName: 'ynList'
				},
				{
					url: `/api/entity/privilege/search`,
					param: {
						object: {
							privilegeType: `b`
						},
						limit: null,
						next: null,
						reverseOrder: false
					},
					itemName: 'privilegeTypeList'
				}
			],

			curLengthCnt: {},
			maxLengthCnt: {},

			regExpInfo: {
				name: 'name',
				description: 'desc'
			},

			nameMaxLength: null,
			descMaxLength: null
		};
	},
	computed: {
		...mapState(['menuList', 'userInfo']),
		...mapGetters(['isUserAdmin']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		},
		requestRecordId() {
			const id = this.requestRecordObject && this.requestRecordObject.id ? this.requestRecordObject.id : '';
			return id;
		},
		responseRecordId() {
			const id = this.responseRecordObject && this.responseRecordObject.id ? this.responseRecordObject.id : '';
			return id;
		}
	},
	watch: {
		detailData() {
			this.initData();
		}
	},
	async created() {
		this.nameMaxLength = getRegExpInfo('name').maxLength;
		this.descMaxLength = getRegExpInfo('desc').maxLength;

		const promiseAllList = [];

		this.httpReqList.forEach(content => {
			promiseAllList.push(
				new Promise((resolve, reject) => {
					(async () => {
						const result = await new HttpReq(content.url).read(content.param ? content.param : null);

						if ('ok' === result.data.result) {
							if (`privilegeTypeList` === content.itemName) {
								const privilegeTypeList = result.data.object;

								const bTotalUserPrivileges = this.userInfo.totalUserPrivileges.filter(
									userPrivilege => `b` === userPrivilege.privilegeType
								);

								for (const info of privilegeTypeList) {
									if (this.isUserAdmin) {
										info.disabled = false;
									} else {
										info.disabled =
											`undefined` ===
											typeof bTotalUserPrivileges.find(userPrivilege => userPrivilege.privilegeId == info.privilegeId);
									}
								}

								this.privilegeTypeList = privilegeTypeList;
							} else {
								this[content.itemName] = result.data.object;
							}

							resolve(result);
						} else {
							reject(result);
						}
					})();
				})
			);
		});

		try {
			const resultList = await Promise.all(promiseAllList);

			for (const result of resultList) {
				if ('error' === result.data.result) throw result.data;
			}

			if (this.detailData.id) this.initData();
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('msg.load.data.warn') });
		}
	},
	methods: {
		initData() {
			//clear data
			for (const key in this.object) {
				this.object[key] = null;

				if (`useYn` === key) this.object[key] = `Y`;
				else if (`privateYn` === key) this.object[key] = `N`;
				else this.object[key] = null;
			}

			this.requestRecordObject = {};
			this.responseRecordObject = {};

			if ('add' === this.mode) return;

			//set data
			const isSameRecordObj = this.detailData.requestRecordId && this.detailData.requestRecordId === this.detailData.responseRecordId;

			for (const key in this.detailData) {
				const value = this.detailData[key];

				if ('serviceProperties' === key) {
					continue;
				} else if (key.startsWith('requestRecordObject')) {
					this.requestRecordObject[key.replace('requestRecordObject.', '')] = value;
					if (isSameRecordObj) this.object[key.replace('request', 'response')] = value;
				} else if (key.startsWith('responseRecordObject')) {
					this.responseRecordObject[key.replace('responseRecordObject.', '')] = value;
				}

				this.object[key] = value;
			}

			if (isSameRecordObj) this.responseRecordObject = cloneDeep(this.requestRecordObject);
		},
		getData() {
			return parseHierarchyObj(cloneDeep(this.object));
		},
		inputEvt(event, key) {
			this.object[key] = event.target.value;
			this.object[key] = this.object[key].replace(
				new RegExp(getRegExpInfo(this.regExpInfo[key] ? this.regExpInfo[key] : 'default').regExp, `g`),
				``
			);
		},
		setRecordData(type, info) {
			const typeKey = 'I' === type ? 'request' : 'response';
			const recordObjKey = `${typeKey}RecordObject`;

			this.object[`${typeKey}RecordId`] = info ? info.id : null;
			this[recordObjKey] = info ? cloneDeep(info) : {};

			if (info) {
				for (const key in info) {
					this.object[`${recordObjKey}.${key}`] = info[key];
				}
			} else {
				const obj = parseHierarchyObj(this.object);

				if (!obj[recordObjKey]) return;

				for (const key in obj[recordObjKey]) {
					delete this.object[`${recordObjKey}.${key}`];
				}
			}
		},
		async openRecordMngModal(type, mod) {
			const recordObj =
				'I' === type
					? { id: 'requestRecordObject', name: this.requestRecordId }
					: { id: 'responseRecordObject', name: this.responseRecordId };

			if ('update' === mod && !recordObj.name) {
				this.$alert({ type: 'warn', message: this.$t(this.readonly ? 'msg.no.data' : 'msg.no.update.record') });
				return;
			}

			const _this = this;
			const validateList = ['id', 'privateYn', 'metaDomain', 'privilegeId'];
			let validateDataInfo = parseHierarchyObj(cloneDeep(this.object));

			//set Modal Data
			if (`add` === this.mode) {
				const result = await this.$parent.checkValidate();

				delete result.isValidate;

				validateDataInfo = merge(validateDataInfo, result);
			}

			for (const validateKey in validateDataInfo) {
				if ('update' === mod) {
					if (recordObj.id === validateKey) validateDataInfo['recordObject'] = validateDataInfo[validateKey];
					if (!this[recordObj.id].mode) validateDataInfo[validateKey] = this[recordObj.id][validateKey];
				}

				if (-1 === validateList.indexOf(validateKey)) delete validateDataInfo[validateKey];
			}

			// modal open or not
			if ('create' === mod && validateList.some(key => isEmpty(validateDataInfo[key]))) {
				this.$alert({ type: 'warn', message: this.$t('msg.add.record.validation') });
				return;
			}

			const modalInstance = this.$openModal({
				title: this.$t('label.modelRecord'),
				fullscreen: true,
				contentList: [
					{
						template: `<RecordModal ref="RecordModal" :cmd="cmd" :mode="mode" :config="config"/>`,
						components: {
							RecordModal: require(`@/views/modal/modelRecord/RecordModal.vue`).default
						},
						data() {
							return {
								cmd: mod,
								mode: _this.mode,
								config: {
									modalType: `S${type}`,
									modalData: validateDataInfo
								}
							};
						}
					}
				],
				buttonList: (() => {
					if (this.readonly) return null;
					else
						return [
							{
								template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('label.ok') }}</button>`,
								methods: {
									async confirm() {
										const recordModalInstance = modalInstance.$refs.content[0].$refs.RecordModal;
										const recordModalData = recordModalInstance.getData();

										const result = await new HttpReq(`/api/entity/record/check`).read(recordModalData, true, result => {
											const errType = type => result.data.error.some(err => err.field.startsWith(type));

											if (errType(`fields`)) {
												recordModalInstance.changeTab(1);

												recordModalInstance.recordObjData.fields = cloneDeep(result.data.object.fields);
												recordModalInstance.recordObjData.options = cloneDeep(result.data.object.options);
												recordModalInstance.$refs.detailInfo.resetData();
											} else if (errType(`options`)) {
												recordModalInstance.changeTab(2);
											}

											this.$alert({
												type: `warn`,
												message: result.data.error
													.map(info => `${(info.className ? `${info.className}<hr>` : '') + info.message}`)
													.join(`,`)
											});
										});

										if (`error` === result.data.result) return;

										_this.setRecordData(type, merge('create' === mod ? { mode: mod } : {}, recordModalData));
										modalInstance.hideModal();
									}
								}
							}
						];
				})()
			});
		},
		openRecordSrchModal(type) {
			const _this = this;

			const modalInstane = this.$openModal({
				title: this.$t('label.modelRecord'),
				contentList: [
					{
						template: `
							<div>
								<Search :list="searchList" @search="search" ref="search"/>
							    <Main
							   	   :buttonList="buttonList"
								   :gridInfo="gridInfo"
								   :gridSearchObj="gridSearchObj"
								   :gridType="'modalBasic'"
								   :viewer="true"
								   @rowClicked="rowClicked"
								   @initialize="initialize"
							   />
						   </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								searchList: [],
								buttonList: [],
								gridInfo: {},
								gridSearchObj: {}
							};
						},
						created() {
							const { search, button, grid } = _this.modalInfo;
							this.searchList = search.list;
							this.buttonList = button.list;
							this.gridInfo = grid;
						},
						methods: {
							search(searchObj) {
								searchObj.recordType = 'I';
								this.gridSearchObj = searchObj;
							},
							async rowClicked(info) {
								const result = await new HttpReq(`/api/entity/record/object`).read({ id: info.id });

								if (`ok` === result.data.result) {
									_this.setRecordData(type, cloneDeep(result.data.object));
								}

								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
