<script>
import { mapState } from 'vuex';
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/modelRecord/Detail.vue';
import { getPrivilege, HttpReq, escapeHtml, changeDateFormat, openNewTab } from '@/utils/common.js';

export default {
	name: 'ModelRecord',
	extends: Basic,
	components: {
		Detail
	},
	computed: {
		...mapState(['constantsInfo'])
	},
	async created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;

		let httpReq = new HttpReq(`/api/page/properties`);

		let result = await httpReq.read({ pk: { propertyId: 'List.Record.Type' }, orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.recordTypeList = result.data.object;

		const isSpecification = this.constantsInfo.isSpecification;

		if (isSpecification && this.editor) {
			this.buttonList.push({ id: 'importExcel', isUse: true, importUrl: `/api/entity/record/document` });

			this.detailButtonList.push({
				id: 'exportExcel',
				isUse: true,
				exportUrl: `/api/entity/record/document`,
				fileName: `RecordTemplate`
			});
		}
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}

		if (localStorage.getItem('selectedModelRecordInfo')) {
			const selectedModelRecordInfo = JSON.parse(localStorage.getItem('selectedModelRecordInfo'));
			localStorage.removeItem('selectedModelRecordInfo');

			this.initDetail('detail', {
				id: selectedModelRecordInfo.recordId,
				isNewTab: true
			});
		}
	},
	data() {
		return {
			recordTypeList: null,

			cudUrl: `/api/entity/record/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId'), regExpType: 'searchId' },
				{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName'), regExpType: 'name' },
				{
					type: 'select',
					vModel: 'recordType',
					label: this.$t(`label.type`),
					val: `c`,
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'List.Record.Type'
							},
							orderByKey: false
						},
						optionListName: 'recordType',
						optionFor: 'option in recordType',
						optionValue: 'option.pk.propertyKey',
						optionText: `'c' === option.pk.propertyKey? '${this.$t(`label.all`)}' : option.propertyValue`,
						optionIf: `option.pk.propertyKey != 'E' && option.pk.propertyKey != 'I'`,
						isViewAll: false
					}
				},
				{
					type: 'select',
					vModel: 'useYn',
					label: `${this.$t('label.useyn')}`,
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'List.Yn'
							},
							orderByKey: true
						},
						optionListName: 'info',
						optionFor: 'option in info',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'select',
					vModel: 'publishStatus',
					label: this.$t('label.publishStatus'),
					optionInfo: {
						optionFor:
							'option in [ {value: `M`, name: `MAKE`}, {value: `R`, name: `REQUEST`}, {value: `C`, name: `CANCEL`}, {value: `A`, name: `APPROVE`}, {value: `D`, name: `DONE`} ]',
						optionValue: 'option.value',
						optionText: 'option.name'
					}
				},
				{
					type: 'text',
					vModel: 'description',
					label: this.$t(`label.desc`),
					placeholder: this.$t('msg.enterDesc'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'download', isUse: true, downloadUrl: `/api/entity/record/download` }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/record/search`,
				totalCntUrl: `/api/entity/record/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('label.id'),
							name: 'id',
							width: '15%'
						},
						{
							header: this.$t('label.name'),
							name: 'name',
							width: '15%'
						},
						{
							header: this.$t(`label.type`),
							name: `recordType`,
							align: `center`,
							formatter: ({ value }) =>
								this.recordTypeList
									? escapeHtml(this.recordTypeList.find(recordType => recordType.pk.propertyKey === value).propertyValue)
									: null,
							width: '5%'
						},
						{
							header: this.$t('label.privilege.id'),
							name: 'privilegeId',
							width: '5%'
						},
						{
							header: this.$t('label.publishStatus'),
							name: 'publishStatus',
							align: 'center',
							width: '10%',
							formatter: ({ value }) => {
								return `M` === value
									? 'MAKE'
									: 'R' === value
									? 'REQUEST'
									: 'C' === value
									? 'CANCEL'
									: 'A' === value
									? 'APPROVE'
									: 'D' === value
									? 'DONE'
									: '';
							}
						},
						{
							header: this.$t('label.publishTimestamp.request'),
							name: 'publishDateTime',
							align: 'center',
							width: '10%',
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							header: this.$t('label.publishTimestamp.complete'),
							name: 'publishTimestamp',
							align: 'center',
							width: '10%',
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							header: this.$t('label.useyn'),
							name: 'useYn',
							align: 'center',
							width: '5%'
						},
						{
							header: `Version`,
							name: 'updateVersion',
							align: 'center',
							width: '5%'
						},
						{
							header: this.$t('label.writer'),
							name: 'updateUserId',
							width: '10%'
						},
						{
							header: this.$t('label.created.date'),
							name: 'updateTimestamp',
							width: '10%',
							formatter: obj => changeDateFormat(obj.value)
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//detail
			detailPk: ['id'],
			publishUrl: '/api/entity/record/publish',
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'publish', isUse: true },
				{ id: 'cancelPublish', isUse: true },
				{ id: 'reference', isUse: true, className: `com.inzent.yellowpage.model.ModelRecord` },
				{ id: 'metaHistory', isUse: true, className: `com.inzent.yellowpage.model.ModelRecord`, entityIdKey: `id` },
				{ id: 'publishLog', isUse: true, resourceType: `R`, resourceIdKey: `id` }
			],
			detailTabList: [
				{ type: 'bundle', url: '/modelRecord/BasicInfo', label: this.$t('label.basicInfo') },
				{ type: 'bundle', url: '/modelRecord/DetailInfo', label: this.$t('label.detailInfo') },
				{ type: 'bundle', url: '/modelRecord/ExtendInfo', label: this.$t('label.extendInfo') },
				{
					id: `useInfo`,
					type: 'basic',
					label: this.$t('label.useInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('label.writer'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('label.created.date'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: 'Version',
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'publishStatus',
									label: this.$t('label.publishStatus'),
									readonly: true,
									formatter: info => {
										return 'M' === info
											? 'MAKE'
											: 'R' === info
											? 'REQUEST'
											: 'C' === info
											? 'CANCEL'
											: 'A' === info
											? 'APPROVE'
											: 'D' === info
											? 'DONE'
											: '';
									}
								},
								{
									type: 'text',
									vModel: 'publishId',
									label: this.$t('label.publish.id'),
									readonly: true,
									clickEvt: this.goPublishLogPage
								},
								{
									type: 'text',
									vModel: 'publishDateTime',
									label: this.$t('label.publishTimestamp.request'),
									readonly: true,
									formatter: value => changeDateFormat(value),
									clickEvt: this.goPublishLogPage
								},
								{
									type: 'text',
									vModel: 'publishTimestamp',
									label: this.$t('label.publishTimestamp.complete'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'publishMessage',
									label: this.$t('label.publishMessage'),
									readonly: true
								}
							]
						]
					]
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	},
	methods: {
		goPublishLogPage() {
			const useInfoComp = this.$refs.detail.$refs.component[this.$refs.detail.$refs.component.length - 1];

			const { id, publishId, publishDateTime } = useInfoComp.detailData;

			if (!id || !publishId || !publishDateTime) {
				this.$alert({ type: 'warn', message: this.$t('msg.no.data') });
				return;
			}

			openNewTab(`105000`, () => {
				localStorage.setItem(
					`searchObj`,
					JSON.stringify({
						dateType: `P`,
						fromDateTime: publishDateTime,
						publishId,
						resourceId: id,
						resourceType: `R`,
						_pageSize: 10
					})
				);
			});
		}
	}
};
</script>
