<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span style="display: inline-block; margin-top: 8px">{{ $t(`label.message.code`) }}</span>
						<span style="display: inline-block; margin-top: 8px" class="letterLength"> ({{ currentLength }} / {{ maxLength }}) </span>
						<button type="button" class="btn-icon" @click="openReferenceModal">
							<i class="icon-plus-circle"></i>
						</button>
					</label>
					<div class="detail-content-common" style="padding-left: 0px">
						<textarea :maxlength="maxLength" v-model="code" class="form-control view-disabled" @input="input" :style="style"></textarea>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { WindowResizeEventBus } from '@/utils/common.js';

export default {
	name: 'javaStyle',
	props: {
		pageType: String,
		sourceRecordIdArr: Array,
		targetRecordIdArr: Object,
		rawValueInfo: Object
	},
	data() {
		return {
			maxLength: 5000,
			currentLength: 0,
			regExp: ``,
			code: ``,
			style: {
				height: null
			}
		};
	},
	mounted() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		this.resize();
	},
	watch: {
		rawValueInfo: {
			immediate: true,
			deep: true,
			handler() {
				this.setData();
			}
		}
	},
	methods: {
		openReferenceModal() {
			const pageType = this.pageType;
			const className = `java.lang.Object`;
			const mappingExpression = this.code ? this.code : null;
			const sourceRecordIdArr = this.sourceRecordIdArr;
			const targetRecordIdArr = this.targetRecordIdArr;

			const _this = this;

			const modalInstance = this.$openModal({
				title: `Mapping Expression`,
				fullscreen: true,
				contentList: [
					{
						template: `<reference :pageType="pageType" :className="className" :sourceRecordIdArr="sourceRecordIdArr" :targetRecordIdArr="targetRecordIdArr" />`,
						components: {
							reference: require('@/views/modal/mapping/ruleDetail/reference.vue').default
						},
						data() {
							return {
								pageType,
								className,
								mappingExpression,
								sourceRecordIdArr,
								targetRecordIdArr
							};
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" class="btn btn-primary" @click="click"> ${this.$t(`label.ok`)} </button>`,
						methods: {
							async click() {
								const { expression, isValidation } = await modalInstance.$refs.content[0].$children[0].getData();

								if (!isValidation) return;

								_this.code += expression;
								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		input(evt) {
			this.code = evt.target.value.replace(new RegExp(this.regExp, 'g'), '');
			this.currentLength = this.code.length;
		},
		resize() {
			this.$nextTick(() => {
				const modalBody = document.querySelector(`#referenceModal`).parentNode;

				const adjustHeight = modalBody.clientHeight - 32 - 48 - 48 - 40;

				this.style.height = `${adjustHeight}px`;
			});
		},
		setData() {
			if (null === this.rawValueInfo) return;
			const { type, name } = this.rawValueInfo;

			if (`JavaOperationExpression` !== type) return;

			this.input({
				target: {
					value: name
				}
			});
		},
		getData() {
			return this.code;
		}
	}
};
</script>
