<template>
	<span @click="click" :style="{ cursor: `${execClickEvt ? 'pointer' : 'auto'}` }" :title="obj[vModel]">
		<input
			type="text"
			v-model="obj[vModel]"
			:class="classObject"
			:placeholder="placeholder"
			:readonly="readonly"
			:maxlength="maxLength"
			@keyup.enter="enter"
			@input="input"
			@change="change"
			:style="style"
		/>
	</span>
</template>

<script>
import { getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'inputText',
	props: {
		vModel: String,
		classObject: Object,
		readonly: {
			type: Boolean,
			default: false
		},
		placeholder: String,
		val: {
			type: String,
			default: null
		},
		regExpType: {
			type: String,
			default: 'default'
		},
		clickEvtType: {
			type: String,
			default: `newTab`
		},
		clickEvt: {
			type: Function,
			default: null
		},
		changeEvt: {
			type: Function,
			default: null
		}
	},
	data() {
		return {
			obj: {},
			maxLength: null,
			regExp: null
		};
	},
	computed: {
		style() {
			return this.execClickEvt ? { 'text-decoration': 'underline', color: '#007bff' } : null;
		},
		execClickEvt() {
			return null !== this.clickEvt;
		}
	},
	created() {
		for (const key in this.$data.obj) {
			this.$delete(this.$data.obj, key);
		}

		this.$set(this.obj, this.vModel, null);

		this.maxLength = getRegExpInfo(this.regExpType).maxLength;
		this.regExp = getRegExpInfo(this.regExpType).regExp;

		this.setData(this.val);
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	methods: {
		setData(val) {
			this.obj[this.vModel] = val;
			this.setLengthCnt();
		},
		getData() {
			return {
				[this.vModel]: this.obj[this.vModel] ? this.obj[this.vModel] : `num` === this.regExpType ? null : this.obj[this.vModel]
			};
		},
		clearValue() {
			this.obj[this.vModel] = null;
		},
		enter() {
			this.$emit('enter');
		},
		change() {
			if (null === this.changeEvt) return;
			this.changeEvt(this);
		},
		click() {
			if (!this.execClickEvt) return;
			this.clickEvt(this);
		},
		input(e) {
			this.obj[this.vModel] = e.target.value;
			this.setLengthCnt();
		},
		setLengthCnt() {
			if ('string' !== typeof this.obj[this.vModel]) return;

			this.obj[this.vModel] = this.obj[this.vModel].replace(new RegExp(this.regExp, 'g'), '');

			const length = this.obj[this.vModel].length;

			if (length > this.maxLength) return;

			this.$emit('setLengthCnt', { [this.vModel]: length });
		}
	}
};
</script>

<style lang="scss" scoped>
span {
	flex: 1 1 auto;
}
</style>
