<template>
	<span>
		<input :placeholder="$t(placeholder)" :class="classObject" :value="selectedName" @click="toggleDropdownItem" readonly />
		<component ref="component" :is="optionElement" :readonly="readonly" @selectedItem="selectedItem" @setDateRange="setDateRange"></component>
	</span>
</template>

<script>
import { HttpReq } from '@/utils/common.js';

export default {
	name: 'dateCalc',
	props: {
		vModel: String,
		classObject: Object,
		readonly: {
			type: Boolean,
			default: false
		},
		placeholder: String,
		optionInfo: Object,
		dateVModel: Array,
		val: {
			type: String,
			default: null
		},
		unit: String
	},
	data() {
		return {
			selectedName: null,
			optionElement: {
				template: `<ul :class="dropdownMenuClass">
                               <li>
							   	   <a
									  :class="{ 'dropdown-item': true, active: null === obj['${this.vModel}'] || 0 === String(obj['${this.vModel}']).trim().length }"
									  href="javascript:void(0)"
									  v-text="$t('label.unchecked')"
									  @click="selectedItem('', $t('label.unchecked'))"
									  v-if="${'undefined' === typeof this.optionInfo.isViewAll ? true : this.optionInfo.isViewAll}"
									  >
								   </a>
                                   <a
                                      :class="{
										  'dropdown-item': true, active: String(obj['${this.vModel}']) === String(${this.optionInfo.optionValue})
									  }"
                                      href="javascript:void(0)"
                                      v-for="${this.optionInfo.optionFor}"
                                      :key="${this.optionInfo.optionValue}"
                                      @click="selectedItem(${this.optionInfo.optionValue}, ${this.optionInfo.optionText})"
									  v-if="${'undefined' === typeof this.optionInfo.optionIf ? true : this.optionInfo.optionIf}" >
										{{ ${this.optionInfo.optionText} }}{{
											$t('label.${'h' === this.unit ? 'hours' : 'm' === this.unit ? 'minutes' : 's' === this.unit ? 'seconds' : ''}.before')
										}}
								   </a>
                                </li>
                           </ul>
                `,
				props: {
					readonly: Boolean
				},
				data: (() => {
					const rtnDataObj = {
						vModelName: this.vModel,
						optionListName: this.optionInfo.optionListName,

						obj: {
							[this.vModel]: this.val
						},

						[this.optionInfo.optionListName]: [],

						url: this.optionInfo.url,
						params: this.optionInfo.params ? this.optionInfo.params : null,

						dropdownMenuClass: {
							'dropdown-menu': true,
							show: false
						},

						unit: this.unit,
						dateRangeVModel: this.dateVModel,

						HttpReq: null
					};

					return function () {
						return rtnDataObj;
					};
				})(),
				async created() {
					if (this.url) {
						this.HttpReq = new HttpReq(this.url);

						const result = await this.HttpReq.read(this.params);

						if (`ok` !== result.data.result) return;

						this[this.optionListName] = result.data.object;
					}

					this.setData(this.obj[this.vModelName]);
				},
				methods: {
					toggleDropdownItem() {
						this.dropdownMenuClass.show = !this.dropdownMenuClass.show;
					},
					selectedItem(value, text) {
						this.obj[this.vModelName] = value;

						this.dropdownMenuClass.show = false;

						this.setDate(value);

						const msg = 'h' === this.unit ? 'hours' : 'm' === this.unit ? 'minutes' : 's' === this.unit ? 'seconds' : '';

						this.$emit('selectedItem', !value ? text : `${text}${this.$t(`label.${msg}.before`)}`);
					},
					setDate(value) {
						if (!value) return;

						const startDate = new Date();
						startDate.setDate(startDate.getDate());
						startDate.setHours(startDate.getHours());
						startDate.setMinutes(startDate.getMinutes());
						startDate.setSeconds(startDate.getSeconds());

						const endDate = new Date();
						endDate.setHours(endDate.getHours());
						endDate.setMinutes(endDate.getMinutes());
						endDate.setSeconds(endDate.getSeconds());

						switch (this.unit) {
							case 'h':
								startDate.setHours(startDate.getHours() - value);
								break;
							case 'm':
								startDate.setMinutes(startDate.getMinutes() - value);
								break;
							case 's':
								startDate.setSeconds(startDate.getSeconds() - value);
								break;
						}

						this.$emit('setDateRange', {
							startDate: startDate,
							endDate: endDate,
							dateRange: this.dateRangeVModel
						});
					},
					setData(val) {
						this.obj[this.vModelName] = val;

						this.$nextTick(() => {
							setTimeout(() => {
								const aElements = this.$el.getElementsByTagName('a');

								for (let i = 0; i < aElements.length; i++) {
									if (aElements[i].classList.contains('active')) {
										aElements[i].click();
										break;
									}
								}
							}, 0);
						});
					},
					getData() {
						return {
							[this.vModelName]: this.obj[this.vModelName] ? this.obj[this.vModelName] : ' '
						};
					}
				}
			}
		};
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	methods: {
		setDateRange(info) {
			this.$emit('setDateRange', info);
		},
		toggleDropdownItem() {
			this.$refs.component.toggleDropdownItem();
		},
		selectedItem(text) {
			this.selectedName = text;
		},
		setData(val) {
			this.$refs.component.setData(val);
		},
		getData() {
			return this.$refs.component.getData();
		}
	}
};
</script>
