<template>
	<div class="ct-content">
		<Button
			:list="buttonList"
			:viewer="viewer"
			:editor="editor"
			:totalCnt="totalCnt"
			:currentCnt="currentCnt"
			:gridInstanceInfo="gridInstanceInfo"
			:startTimerSeconds="startTimerSeconds"
			@initialize="initialize"
			@newTab="newTab"
			@add="add"
			@importExcel="importExcel"
			@migration="migration"
			@refresh="refresh"
			@download="download"
		/>
		<NoData v-if="!isGridView" />
		<Grid
			ref="grid"
			:info="gridInfo"
			:searchObj="gridSearchObj"
			:type="gridType"
			:resizeFunc="gridResizeFunc"
			@rowClicked="rowClicked"
			@gridCount="gridCount"
			@changeGridViewStatus="changeGridViewStatus"
			v-if="isGridView"
		/>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import Button from '@/components/Button.vue';
import NoData from '@/components/NoData.vue';
import Grid from '@/components/Grid.vue';
import { numberWithComma } from '@/utils/common';

export default {
	name: 'Main',
	props: {
		gridInfo: Object,
		gridSearchObj: Object,
		gridType: String,
		gridResizeFunc: Function,
		buttonList: Array,
		startTimerSeconds: Number,
		viewer: Boolean,
		editor: Boolean
	},
	data() {
		return {
			isGridView: false,
			totalCnt: null,
			currentCnt: null,
			gridInstanceInfo: null
		};
	},
	components: {
		Button,
		NoData,
		Grid
	},
	watch: {
		gridSearchObj() {
			this.isGridView = true;
		}
	},
	methods: {
		changeGridViewStatus() {
			this.isGridView = !this.isGridView;
		},
		initialize() {
			this.$emit('initialize');
		},
		add() {
			this.$emit('add');
		},
		newTab() {
			this.$emit('newTab');
		},
		importExcel() {
			this.$emit('importExcel');
		},
		migration() {
			this.$emit('migration', this.$refs.grid.$refs.tuiGrid.invoke('getCheckedRows'));
		},
		rowClicked(info) {
			this.gridInstanceInfo = cloneDeep(this.$refs.grid.getGrid().gridInstance);

			if (info) this.$emit('rowClicked', info);
		},
		gridCount({ currentCnt, totalCnt }) {
			this.currentCnt = numberWithComma(currentCnt);
			this.totalCnt = numberWithComma(totalCnt);
		},
		refresh() {
			this.$emit(`refresh`);
		},
		download() {
			this.$emit(`download`);
		}
	}
};
</script>
