<template>
	<div v-if="`add` === mode">
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.name') }}</span>
						<span class="letterLength" v-if="!readonly">
							{{ `(${object.name ? object.name.length : 0}/${maxLengthCnt[`name`]})` }}
						</span>
						<b class="icon-star"></b>
					</label>
					<input
						type="text"
						v-model="object.name"
						class="form-control view-disabled"
						:readonly="readonly"
						@input="inputEvt($event, `name`)"
						:maxlength="maxLengthCnt[`name`]"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.privilege') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privilegeId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in privilegeTypeList" :key="option.privilegeId" :value="option.privilegeId" :disabled="option.disabled">
							{{ option.privilegeId }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.privateYn') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privateYn" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in ynList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.useyn') }}</span>
					</label>
					<select v-model="object.useYn" class="form-control view-disabled" :readonly="true">
						<option v-for="option in ynList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.desc') }}</span>
						<span class="letterLength" v-if="!readonly">
							{{ `(${object.description ? object.description.length : 0}/${maxLengthCnt[`description`]})` }}
						</span>
					</label>
					<textarea
						v-model="object.description"
						:class="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
						@input="inputEvt($event, `description`)"
						:maxlength="maxLengthCnt[`description`]"
						style="height: 200px"
					/>
				</div>
			</div>
		</div>
	</div>
	<div v-else>
		<div class="row frm-row">
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<input
						type="text"
						v-model="object.id"
						class="form-control view-disabled dataKey"
						:readonly="'add' !== this.mode && 'clone' !== this.mode"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.name') }}</span>
						<span class="letterLength" v-if="!readonly">
							{{ `(${object.name ? object.name.length : 0}/${maxLengthCnt[`name`]})` }}
						</span>
						<b class="icon-star"></b>
					</label>
					<input
						type="text"
						v-model="object.name"
						class="form-control view-disabled"
						:readonly="readonly"
						@input="inputEvt($event, `name`)"
						:maxlength="maxLengthCnt[`name`]"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.privilege') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privilegeId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in privilegeTypeList" :key="option.privilegeId" :value="option.privilegeId" :disabled="option.disabled">
							{{ option.privilegeId }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.privateYn') }}</span>
					</label>
					<select v-model="object.privateYn" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in ynList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ ` Source ${this.$t('label.system.id')}` }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.sourceSystemId" class="form-control view-disabled" :readonly="true">
						<option
							v-for="sourceSystemInfo in sourceSystemInfoList"
							:key="sourceSystemInfo.id"
							:value="sourceSystemInfo.id"
							v-text="sourceSystemInfo.id"
						></option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.biz.name') }}</span>
					</label>
					<select v-model="object.bizId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in bizList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.linked.server.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.serverId" class="form-control view-disabled" :readonly="true">
						<option v-for="serverInfo in linkedServerList" :key="serverInfo.id" :value="serverInfo.id">
							{{ serverInfo.name }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.interface.class') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.interfaceClass" class="form-control view-disabled" :readonly="true">
						<option
							v-for="interfaceClass in interfaceClassList"
							:key="interfaceClass.id"
							:value="interfaceClass.id"
							v-text="interfaceClass.name"
						></option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.useyn') }}</span>
					</label>
					<select v-model="object.useYn" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in ynList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.desc') }}</span>
						<span class="letterLength" v-if="!readonly">
							{{ `(${object.description ? object.description.length : 0}/${maxLengthCnt[`description`]})` }}
						</span>
					</label>
					<textarea
						v-model="object.description"
						:class="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
						@input="inputEvt($event, `description`)"
						:maxlength="maxLengthCnt[`description`]"
						style="height: 200px"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';
import { parseHierarchyObj, HttpReq, getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'InterfaceBasicInfo',
	props: {
		detailData: Object,
		mode: String
	},
	data() {
		return {
			bizList: [],
			linkedServerList: [],
			ynList: [],
			privilegeTypeList: [],
			interfaceClassList: [],
			sourceSystemInfoList: [],

			object: {
				id: null,
				name: null,
				privilegeId: null,
				serverId: null,
				interfaceClass: null,
				sourceSystemId: null,
				bizId: null,
				privateYn: `N`,
				publishTimestamp: null,
				description: null,
				useYn: `Y`
			},

			httpReqList: [
				{
					url: `/api/entity/server/search`,
					param: {
						object: {},
						limit: null,
						next: null,
						reverseOrder: false
					},
					itemName: 'linkedServerList'
				},
				{
					url: `/api/entity/system/search`,
					param: {
						object: {},
						limit: null,
						next: null,
						reverseOrder: false
					},
					itemName: 'sourceSystemInfoList'
				},
				{
					url: `/api/entity/interfaceClass/search`,
					param: {
						object: {},
						limit: null,
						next: null,
						reverseOrder: false
					},
					itemName: 'interfaceClassList'
				},
				{
					url: `/api/page/properties`,
					param: {
						pk: {
							propertyId: 'List.Biz'
						},
						orderByKey: false
					},
					itemName: 'bizList'
				},
				{
					url: `/api/page/properties`,
					param: {
						pk: {
							propertyId: 'List.Yn'
						},
						orderByKey: false
					},
					itemName: 'ynList'
				},
				{
					url: `/api/entity/privilege/search`,
					param: {
						object: {
							privilegeType: `b`
						},
						limit: null,
						next: null,
						reverseOrder: false
					},
					itemName: 'privilegeTypeList'
				}
			],

			regExpInfo: {
				name: 'name',
				description: 'desc'
			},
			maxLengthCnt: {}
		};
	},
	watch: {
		detailData() {
			this.initData();
		},
		mode() {
			if ('add' === this.mode) {
				for (const key in this.object) {
					this.object[key] = null;
				}
			}
		}
	},
	computed: {
		...mapState(['menuList', 'userInfo']),
		...mapGetters(['isUserAdmin']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	async created() {
		for (const [key, value] of Object.entries(this.regExpInfo)) {
			this.maxLengthCnt[key] = getRegExpInfo(value).maxLength;
		}

		const promiseAllList = [];

		this.httpReqList.forEach(content => {
			promiseAllList.push(
				new Promise((resolve, reject) => {
					(async () => {
						const result = await new HttpReq(content.url).read(content.param ? content.param : null);

						if ('ok' === result.data.result) {
							if (`privilegeTypeList` === content.itemName) {
								const privilegeTypeList = result.data.object;

								const bTotalUserPrivileges = this.userInfo.totalUserPrivileges.filter(
									userPrivilege => `b` === userPrivilege.privilegeType
								);

								for (const info of privilegeTypeList) {
									if (this.isUserAdmin) {
										info.disabled = false;
									} else {
										info.disabled =
											`undefined` ===
											typeof bTotalUserPrivileges.find(userPrivilege => userPrivilege.privilegeId == info.privilegeId);
									}
								}

								this.privilegeTypeList = privilegeTypeList;
							} else {
								this[content.itemName] = result.data.object;
							}

							resolve(result);
						} else {
							reject(result);
						}
					})();
				})
			);
		});

		try {
			const resultList = await Promise.all(promiseAllList);

			for (const result of resultList) {
				if ('error' === result.data.result) throw result.data;
			}

			if (this.detailData.id) this.initData();
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('msg.load.data.warn') });
		}
	},
	methods: {
		initData() {
			for (const key in this.object) {
				this.object[key] = 'undefined' === typeof this.detailData[key] ? null : this.detailData[key];

				if ('undefined' === typeof this.detailData[key]) {
					if (`useYn` === key) this.object[key] = `Y`;
					else if (`privateYn` === key) this.object[key] = `N`;
					else this.object[key] = null;
				} else {
					this.object[key] = this.detailData[key];
				}
			}
		},
		getData() {
			if (`add` === this.mode) {
				const rtnObj = {
					publishStatus: this.detailData.publishStatus
				};

				const { name, privilegeId, privateYn, publishTimestamp, useYn, description } = cloneDeep(this.object);

				rtnObj.name = name;
				rtnObj.privilegeId = privilegeId;
				rtnObj.privateYn = privateYn;
				rtnObj.publishTimestamp = publishTimestamp;
				rtnObj.useYn = useYn;
				rtnObj.description = description;

				return rtnObj;
			} else {
				return {
					...parseHierarchyObj(cloneDeep(this.object)),
					publishStatus: this.detailData.publishStatus
				};
			}
		},
		inputEvt(event, key) {
			this.object[key] = event.target.value;
			this.object[key] = this.object[key].replace(
				new RegExp(getRegExpInfo(this.regExpInfo[key] ? this.regExpInfo[key] : 'default').regExp, `g`),
				``
			);
		}
	}
};
</script>
