<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.id') }}</span>
					</label>
					<input type="text" v-model="mappingId" class="form-control view-disabled dataKey" readonly />
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.name') }}</span>
					</label>
					<input type="text" v-model="mappingName" class="form-control view-disabled" readonly />
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.privilege') }}</span>
					</label>
					<input type="text" v-model="privilegeId" class="form-control view-disabled" readonly />
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.desc') }}</span>
					</label>
					<textarea v-model="mappingDesc" class="form-control view-disabled" :readonly="this.data.readonly" style="min-height: 150px" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { HttpReq } from '@/utils/common.js';

export default {
	name: 'MappingInfo',
	props: {
		data: Object
	},
	data() {
		return {
			mappingId: null,
			mappingName: null,
			privilegeId: null,
			mappingDesc: null
		};
	},
	async created() {
		if (`I` === this.data.type && this.data.requestMappingObject) {
			this.mappingId = this.data.requestMappingObject.mappingId;
			this.mappingName = this.data.requestMappingObject.mappingName;
			this.mappingDesc = this.data.requestMappingObject.mappingDesc;
		} else if (`O` === this.data.type && this.data.responseMappingObject) {
			this.mappingId = this.data.responseMappingObject.mappingId;
			this.mappingName = this.data.responseMappingObject.mappingName;
			this.mappingDesc = this.data.responseMappingObject.mappingDesc;
		} else {
			const namingRuleHttp = new HttpReq(`/api/page/properties`);

			const result = await namingRuleHttp.read({ pk: { propertyId: 'ImgOption.NamingRules' }, orderByKey: true });

			if (`ok` !== result.data.result) return;

			const namingRules = result.data.object.filter(info => {
				const propertyKey = info.pk.propertyKey;

				return (
					`InterfaceRequestMappingID` === propertyKey ||
					`InterfaceRequestMappingName` === propertyKey ||
					`InterfaceResponseMappingID` === propertyKey ||
					`InterfaceResponseMappingName` === propertyKey
				);
			});

			this.mappingId =
				`I` === this.data.type
					? namingRules.filter(info => `InterfaceRequestMappingID` === info.pk.propertyKey)[0].propertyValue
					: namingRules.filter(info => `InterfaceResponseMappingID` === info.pk.propertyKey)[0].propertyValue;

			this.mappingName =
				`I` === this.data.type
					? namingRules.filter(info => `InterfaceRequestMappingName` === info.pk.propertyKey)[0].propertyValue
					: namingRules.filter(info => `InterfaceResponseMappingName` === info.pk.propertyKey)[0].propertyValue;

			this.mappingId = this.mappingId.replace('{0}', this.data.interfaceId).replace('{1}', this.data.serviceId);
			this.mappingName = this.mappingName.replace('{0}', this.data.interfaceName).replace('{1}', this.data.serviceName);
		}

		this.privilegeId = this.data.privilegeId;
	},
	methods: {
		getData() {
			return {
				mappingId: this.mappingId,
				mappingName: this.mappingName,
				mappingDesc: this.mappingDesc
			};
		}
	}
};
</script>
