<template>
	<span>
		<header id="hd">
			<div class="hd-brand">
				<h1 class="logo text-hide">
					<a href="javascript:void(0);" @click="clickLogo">
						<img :src="logoFile" alt="INZENT" />
					</a>
				</h1>
				<span @click="sideMenuToggler">
					<button type="button" class="menu-toggler d-none d-lg-block" style="transition: none" :title="$t('label.sidebar')">
						<i class="icon-hamburger-back"></i>
					</button>
					<button type="button" class="menu-toggler d-lg-none" :title="$t('label.sidebar')">
						<i class="icon-hamburger-back"></i>
					</button>
				</span>
			</div>
			<ul class="breadcrumb">
				<li
					:class="{ 'breadcrumb-item': true, active: index === selectedMenuPathNameList.length - 1 }"
					v-for="(menuName, index) in selectedMenuPathNameList"
					:key="index"
				>
					{{ menuName }}
				</li>
			</ul>
			<div class="hd-side">
				<div :class="siteMapContentClass" :style="siteMapContentStyle">
					<div v-for="(subMenu, idx) in userAllMenuParentList" :key="idx" class="area">
						<ul>
							<li>{{ subMenu.menuName }}</li>
							<li
								v-for="menuInfo in userAllMenuList.filter(menuInfo => menuInfo.menuId.startsWith(subMenu.menuId.substring(0, 1)))"
								:key="menuInfo.menuId"
								@click="clickSiteMap(menuInfo.menuId)"
							>
								<img :src="getIcon(`/img/menu/${menuInfo.menuIcon}`)" v-if="getIcon(`/img/menu/${menuInfo.menuIcon}`)" />
								<i class="icon-lock" v-else />
								<a
									href="javascript:void(0);"
									v-if="logStatsMenu.filter(statsMenuInfo => statsMenuInfo.menuId === menuInfo.parentMenuId)[0]"
								>
									{{
										`${logStatsMenu.filter(statsMenuInfo => statsMenuInfo.menuId == menuInfo.parentMenuId)[0].menuName} 
										> ${menuInfo.menuName}`
									}}
								</a>
								<a href="javascript:void(0);" v-else> {{ menuInfo.menuName }} </a>
							</li>
						</ul>
					</div>
				</div>
				<input
					type="text"
					v-model="siteMapSearch"
					class="form-control sitemap-search input-search"
					:placeholder="$t('label.search')"
					@input="changeSiteMapSearch"
					v-if="this.siteMapToggleClass[`icon-close`]"
				/>
				<a
					href="javascript:void(0);"
					@click="toggleTheme"
					v-if="isUseTheme"
					:title="`sun` === theme ? $t(`label.night.mode`) : $t(`label.day.mode`)"
				>
					<img :src="getIcon(`/custom/img/${`sun` === theme ? `moon` : `sun`}.svg`)" />
				</a>
				<a href="javascript:void(0);" @click="toggleSiteMapContent" :title="$t('label.sitemap')"><i :class="siteMapToggleClass"></i></a>
				<a href="javascript:void(0);" @click="userInfo" :title="$t('label.mypage')"><i class="icon-profile"></i></a>
				<a href="javascript:void(0);" @click="logout" :title="$t('label.logout')"><i class="icon-logout"></i></a>
			</div>
		</header>
		<nav id="sidebar" class="panel" ref="sidebar">
			<div class="panel-content" @scroll="initContextMenu">
				<!-- home -->
				<Menu
					:rootId="`home`"
					:rootMenuInfo="homeMenuInfo"
					:selectedMenuPathIdList="selectedMenuPathIdList"
					@loadContent="loadContent"
					v-if="isHomeDataLoaded"
				/>
				<ul v-else>
					<li class="menu-item">
						<span>
							<a href="javascript:void(0);" class="menu-link"><i class="icon-home"></i> {{ $t('label.home') }} </a>
						</span>
					</li>
				</ul>
				<!-- home -->

				<div class="menu-divider"></div>

				<!-- bookMark -->
				<Menu
					:rootId="`bookMark`"
					:rootMenuInfo="bookMarkMenuInfo"
					:selectedMenuPathIdList="selectedMenuPathIdList"
					:bookMarkMenuList="bookMarkMenuInfo.subMenus[0].subMenus"
					@loadContent="loadContent"
					@setMenuBookmark="setMenuBookmark"
					v-if="isBookmarkDataLoaded && 0 < bookMarkMenuInfo.subMenus[0].subMenus.length"
				/>
				<div class="menu-collapse-item" v-else>
					<a class="menu-collapse-link disabled" data-toggle="collapse" aria-expanded="true">
						{{ $t('label.bookmark') }}<i class="icon-down"></i>
					</a>
					<p class="bookmark-text"><i class="icon-bookmark active"></i>{{ $t('msg.bookmark.icon') }}</p>
				</div>
				<!-- bookMark -->

				<div class="menu-divider"></div>

				<!-- menu -->
				<Menu
					:rootId="`gnb`"
					:rootMenuInfo="menuInfo"
					:selectedMenuPathIdList="selectedMenuPathIdList"
					:bookMarkMenuList="bookMarkMenuInfo.subMenus[0].subMenus"
					@loadContent="loadContent"
					@setMenuBookmark="setMenuBookmark"
					v-if="isMenuDataLoaded"
				/>
				<!-- menu -->
			</div>
		</nav>
		<div ref="backdrop" class="backdrop" @click="hideSideMenu" v-if="isViewBackdrop"></div>
		<div id="menuContextMenu" ref="menuContextMenu" v-show="isViewContextmenu" :style="menuContextMenuStyle">
			<ul>
				<li @click="newTab">
					<i class="icon-info" style="padding: 10px"></i>
					<span class="hide" style="padding-left: 10px">{{ $t('label.newTab') }}</span>
				</li>
			</ul>
		</div>
	</span>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';
import {
	DetailToggleEventBus,
	SideMenuToggleEventBus,
	HttpReq,
	logout as logoutFunc,
	openNewTab,
	MenuContextMenuEventBus,
	getHttpUrl,
	convertTreeToArray
} from '@/utils/common.js';
import Menu from '@/views/Menu.vue';

export default {
	name: 'Sidebar',
	data() {
		return {
			isViewBackdrop: false,

			allMenuList: [],
			homeMenuInfo: {
				subMenus: []
			},
			bookMarkMenuInfo: {
				subMenus: [
					{
						menuId: 'bookMark',
						menuName: this.$t('label.bookmark'),
						subMenus: []
					}
				]
			},
			menuInfo: {},
			selectedMenuPathNameList: [],
			selectedMenuPathIdList: [],

			bookmarkHttpReq: new HttpReq(`/api/entity/menuBookmark/search`),

			isHomeDataLoaded: false,
			isBookmarkDataLoaded: false,
			isMenuDataLoaded: false,

			configMenuBookmarkHttpReq: new HttpReq('/api/entity/menuBookmark/object'),

			openModal: false,

			isViewContextmenu: false,
			menuContextMenuStyle: {
				position: `absolute`,
				top: null,
				left: null,
				width: '200px',
				'z-index': 9999,
				background: 'white',
				padding: '8px 0px',
				'border-radius': '4px',
				'box-shadow': '20px 20px 20px grey',
				fontWeight: `bold`
			},
			contextmenuId: null,

			siteMapSearch: '',
			logStatsMenu: [],
			siteMapToggleClass: {
				'icon-list': true,
				'icon-close': false
			},
			siteMapContentClass: {
				'site-map-content': true,
				expand: false
			},
			siteMapContentStyle: {
				height: null,
				padding: null
			},
			siteMapRemoveClass: [],

			isOutLogo: false,
			logoFileName: null,
			logoFileExtension: null,
			logoHttpReq: new HttpReq(`/api/page/logoFileName`),

			theme: `sun`
		};
	},
	components: {
		Menu
	},
	async created() {
		MenuContextMenuEventBus.$on('initContextMenu', evt => {
			this.initContextMenu(evt);
		});

		this.theme = localStorage.getItem(`theme`) ? localStorage.getItem(`theme`) : `sun`;

		const result = await this.logoHttpReq.read({ type: 'menu' });

		if (`ok` !== result.data.result || !result.data.object) {
			this.isOutLogo = false;

			this.logoFileExtension = `svg`;
			this.logoFileName = `logo`;
		} else {
			this.isOutLogo = true;

			const fileNameArr = result.data.object.split(`.`);

			this.logoFileExtension = fileNameArr[fileNameArr.length - 1];
			this.logoFileName = result.data.object.replace(`.${this.logoFileExtension}`, ``);
		}
	},
	async mounted() {
		await this.getAllMenuInfo();

		await this.getHomeMenuInfo();

		await this.getBookMarkMenuInfo(true);

		this.getMenuInfo();

		this.logStatsMenu = this.allMenuList.filter(allMenuInfo => allMenuInfo.parentMenuId === '402000');
	},
	computed: {
		...mapState({
			userId: state => state.userInfo.userId,
			totalUserPrivileges: state => state.userInfo.totalUserPrivileges.map(privilegeInfo => privilegeInfo.privilegeId),
			isUseTheme: state => state.isUseTheme,
			homeMenu: state => state.homeMenu,
			userMenuInfo: state => state.menuInfo
		}),
		userAllMenuParentList() {
			if (!this.menuInfo.subMenus) return [];

			const rtnParentList = [];

			this.userAllMenuList.forEach(menuInfo => {
				let parentMenuInfo = this.allMenuList.filter(allMenuInfo => allMenuInfo.menuId === menuInfo.parentMenuId)[0];

				while (true) {
					if (!parentMenuInfo.parentMenuId) break;

					parentMenuInfo = this.allMenuList.filter(allMenuInfo => allMenuInfo.menuId === parentMenuInfo.parentMenuId)[0];
				}

				if (rtnParentList.some(rtnMenuInfo => rtnMenuInfo.menuId === parentMenuInfo.menuId)) return;

				rtnParentList.push(parentMenuInfo);
			});

			return rtnParentList;
		},
		userAllMenuList() {
			return this.allMenuList.filter(
				menuInfo =>
					menuInfo.menuUrl &&
					(!menuInfo.menuPrivilegeId ||
						`undefined` !== typeof this.totalUserPrivileges.find(userPrivilege => userPrivilege === menuInfo.menuPrivilegeId)) &&
					menuInfo.menuName.includes(this.siteMapSearch)
			);
		},
		logoFile() {
			if (!this.logoFileName || !this.logoFileExtension) return;

			const fileName = `${this.logoFileName}${`moon` === this.theme ? `_wh` : ``}.${this.logoFileExtension}`;

			return this.isOutLogo ? getHttpUrl(`/custom/img/${fileName}`) : require(`@/assets/img/${fileName}`);
		}
	},
	methods: {
		...mapMutations(['setMenuList']),
		sideMenuToggler(evt) {
			const toggleBtn = 'I' === evt.target.tagName ? evt.target.closest('button') : evt.target;

			if (toggleBtn.classList.contains('d-lg-none')) {
				this.isViewBackdrop = true;

				if (document.body.classList.contains('panel-open-sidebar')) {
					document.body.classList.remove('panel-open-sidebar');
				}

				if (document.body.classList.contains('fixed')) {
					document.body.classList.remove('fixed');

					this.$refs.sidebar.style.display = 'none';
				}

				document.body.classList.add('fixed');

				this.$refs.sidebar.style.display = 'block';

				document.body.classList.add('panel-open-sidebar');

				document.body.dispatchEvent(
					new CustomEvent('resize', {
						detail: {
							evt
						}
					})
				);
			} else {
				document.body.classList.toggle('sidebar-toggled');

				SideMenuToggleEventBus.$emit('MainResize');

				document.body.dispatchEvent(
					new CustomEvent('resize', {
						detail: {
							evt
						}
					})
				);
			}
		},
		hideSideMenu() {
			document.body.classList.remove('panel-open-sidebar');

			this.isViewBackdrop = false;

			this.$refs.sidebar.style.display = 'none';
			document.body.classList.remove('fixed');

			this.$refs.sidebar.removeAttribute('style');

			SideMenuToggleEventBus.$emit('MainResize');

			document.body.dispatchEvent(
				new CustomEvent('resize', {
					detail: {}
				})
			);
		},
		loadContent(menuInfoArr) {
			this.selectedMenuPathNameList = menuInfoArr.map(menuInfo => menuInfo.menuName);
			this.selectedMenuPathIdList = menuInfoArr.map(menuInfo => menuInfo.orgMenuId);

			sessionStorage.removeItem('selectedMenuPathIdList');
			sessionStorage.setItem('selectedMenuPathIdList', JSON.stringify(this.selectedMenuPathIdList));

			const info = menuInfoArr[menuInfoArr.length - 1];

			if (this.$refs.backdrop && `block` === getComputedStyle(this.$refs.backdrop).display) {
				this.hideSideMenu();
			}

			this.$emit('loadContent', { menuId: info.menuId, openWindow: info.openWindow });
		},
		clickLogo() {
			this.isHomeDataLoaded = false;

			this.selectedMenuPathIdList = [this.homeMenuInfo.subMenus[0].menuId];

			this.$nextTick(() => {
				this.isHomeDataLoaded = true;
			});
		},
		userInfo() {
			const modalInstance = this.$openModal({
				title: this.$t(`label.user.info`),
				modalWidth: `800px`,
				contentList: [
					{
						template: `<div style="height: 100%;" >
								     <UserInfo />
								   </div>
						`,
						components: {
							UserInfo: require('@/views/modal/user/UserInfo.vue').default
						}
					}
				],
				buttonList: [
					{
						template: `
							<div>
								<button type="button" class="btn btn-primary" v-if="'detail' === modalMode" @click="chgModalMode('modify')">
                                    <i class="icon-edit"></i>{{ $t('label.update') }}
								</button>
								<button type="button" class="btn" v-if="'detail' !== modalMode" @click="chgModalMode('detail')">
                                    {{ $t('label.cancel') }}
								</button>
								<button type="button" class="btn btn-primary" v-if="'detail' !== modalMode" @click="updateUserInfo">
                                    {{ $t('label.save') }}
								</button>
							</div>
						`,
						data() {
							return {
								modalMode: `detail`
							};
						},
						methods: {
							chgModalMode(mode) {
								this.modalMode = mode;
								modalInstance.$refs.content[0].$children[0].chgModalMode(mode);
							},
							updateUserInfo() {
								modalInstance.$refs.content[0].$children[0].updateUserInfo(() => {
									this.chgModalMode(`detail`);
								});
							}
						}
					}
				]
			});
		},
		logout() {
			logoutFunc();
		},
		async getAllMenuInfo() {
			this.allMenuList = convertTreeToArray(this.userMenuInfo.subMenus);

			this.setMenuList(this.allMenuList);
		},
		async getHomeMenuInfo() {
			this.isHomeDataLoaded = false;

			for (const info of this.allMenuList) {
				if (info.menuId === this.homeMenu) {
					const cloneInfo = cloneDeep(info);

					cloneInfo.menuId += '_home';
					cloneInfo.isNotRequireFavorites = true;
					cloneInfo.menuName = this.$t('label.home');

					this.homeMenuInfo.subMenus = [cloneInfo];

					if (localStorage.getItem('selectedMenuPathIdListNewTab')) {
						this.selectedMenuPathIdList = JSON.parse(localStorage.getItem('selectedMenuPathIdListNewTab'));
						localStorage.removeItem('selectedMenuPathIdListNewTab');
					} else {
						this.selectedMenuPathIdList = sessionStorage.getItem('selectedMenuPathIdList')
							? JSON.parse(sessionStorage.getItem('selectedMenuPathIdList'))
							: [cloneInfo.menuId];
					}

					this.isHomeDataLoaded = true;
					return;
				}
			}
		},
		async getBookMarkMenuInfo(initial = false) {
			if (initial) this.isBookmarkDataLoaded = false;

			const result = await this.bookmarkHttpReq.read({
				object: {
					pk: { userId: this.userId }
				},
				limit: this.allMenuList.length,
				reverseOrder: false
			});

			if (`ok` !== result.data.result) return;

			const bookMarkMenuInfoList = [];

			const menuBookmarkInfo = result.data.object.map(info => info.pk.menuId);

			for (const info of this.allMenuList) {
				if (!menuBookmarkInfo.includes(info.menuId)) continue;

				if (info.menuPrivilegeId && !this.totalUserPrivileges.includes(info.menuPrivilegeId)) {
					this.setMenuBookmark({ menuId: info.menuId, isDelete: true });
					continue;
				}

				const cloneInfo = cloneDeep(info);

				cloneInfo.menuId += '_bookmark';
				cloneInfo.menuName = this.$t(cloneInfo.menuName);

				bookMarkMenuInfoList.push(cloneInfo);
			}

			this.bookMarkMenuInfo.subMenus[0].subMenus = bookMarkMenuInfoList;
			this.isBookmarkDataLoaded = true;
		},
		async getMenuInfo() {
			this.isMenuDataLoaded = false;

			this.menuInfo = cloneDeep(this.userMenuInfo);

			this.isMenuDataLoaded = true;
		},
		async setMenuBookmark(info) {
			try {
				const httpReqResult = await this.configMenuBookmarkHttpReq[info.isDelete ? `delete` : `create`]({
					pk: { menuId: info.menuId, userId: this.userId }
				});

				if (`ok` !== httpReqResult.data.result) throw 'error';

				this.getBookMarkMenuInfo();
			} catch (error) {
				this.$alert({ type: `warn`, backdropMode: `full`, message: this.$t('msg.alert.fail') });
			}
		},
		initContextMenu(evt) {
			if (!evt) return;

			this.contextmenuId = evt.target.id ? evt.target.id : null;

			// 사용자가 동작 & 메뉴 클릭인지 확인
			const isTagInMenu =
				evt.isTrusted && this.contextmenuId && evt.target.className.includes('menu-link') && !this.contextmenuId.includes('_home');

			if (isTagInMenu) evt.preventDefault();

			if (!isTagInMenu || !this.contextmenuId || 'click' === evt.type) {
				this.isViewContextmenu = false;
			} else {
				this.isViewContextmenu = true;

				this.$nextTick(() => {
					this.menuContextMenuStyle.top = `${
						window.innerHeight <= evt.clientY + this.$refs.menuContextMenu.offsetHeight
							? evt.clientY - this.$refs.menuContextMenu.offsetHeight
							: evt.clientY
					}px`;

					this.menuContextMenuStyle.left = `${
						window.innerWidth <= evt.clientX + this.$refs.menuContextMenu.offsetWidth
							? window.innerWidth - this.$refs.menuContextMenu.offsetWidth
							: evt.clientX
					}px`;
				});
			}
		},
		newTab() {
			if (this.contextmenuId === this.selectedMenuPathIdList[this.selectedMenuPathIdList.length - 1]) {
				this.$alert({ type: 'warn', message: this.$t('msg.avoid.open.new.tab') });
				this.isViewContextmenu = false;
				return;
			}

			openNewTab(this.contextmenuId, () => {
				localStorage.removeItem(`searchObj`);
			});
		},
		toggleSiteMapContent() {
			this.siteMapContentStyle.height = null === this.siteMapContentStyle.height ? `100vh` : null;
			this.siteMapContentStyle.padding = null === this.siteMapContentStyle.padding ? `5% 10%` : null;

			this.siteMapContentClass.expand = !this.siteMapContentClass.expand;

			this.siteMapToggleClass[`icon-list`] = !this.siteMapToggleClass[`icon-list`];
			this.siteMapToggleClass[`icon-close`] = !this.siteMapToggleClass[`icon-close`];

			if (this.siteMapContentClass.expand) {
				for (const className of document.body.classList) {
					if (!className.includes('panel-open-') || className.includes('fixed')) continue;

					document.body.classList.remove(className);
					this.siteMapRemoveClass.push(className);
				}
			} else {
				for (const className of this.siteMapRemoveClass) {
					setTimeout(() => {
						document.body.classList.add(className);
					}, 100);
				}

				this.siteMapRemoveClass = [];
			}
		},
		clickSiteMap(menuId) {
			const selectedMenuPathIdList = [menuId];

			while (true) {
				const filterMenuList = this.allMenuList.filter(menuInfo => selectedMenuPathIdList[0] === menuInfo.menuId);

				if (0 === filterMenuList.length) break;

				const menuInfo = filterMenuList[0];

				if ('undefined' === typeof menuInfo.parentMenuId) break;

				selectedMenuPathIdList.unshift(menuInfo.parentMenuId);
			}

			sessionStorage.removeItem(`selectedMenuPathIdList`);
			sessionStorage.setItem(`selectedMenuPathIdList`, JSON.stringify(selectedMenuPathIdList));

			location.reload();
		},
		changeSiteMapSearch(evt) {
			this.siteMapSearch = evt.target.value;
		},
		getIcon(url) {
			try {
				return getHttpUrl(url);
			} catch (error) {
				return null;
			}
		},
		toggleTheme() {
			this.theme = `sun` === this.theme ? 'moon' : `sun`;

			if (`moon` === this.theme) document.body.classList.add(`moon`);
			else document.body.classList.remove(`moon`);

			localStorage.setItem(`theme`, this.theme);

			DetailToggleEventBus.$emit('MainResize');
		}
	}
};
</script>
