<template>
	<div>
		<div class="table-responsive">
			<TuiGrid ref="tuiGrid" v-bind="options" />
		</div>
	</div>
</template>
<script>
import { mapState } from 'vuex';
import * as xlsx from 'xlsx';
import DetailGrid from '@/components/detail/DetailGrid';
import {
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	HttpReq,
	makeGridOptions,
	openNewTab,
	escapeHtml,
	getNumFromStr,
	changeDateFormat
} from '@/utils/common.js';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
import moment from 'moment';

export default {
	name: 'DetailPublishGrid',
	extends: DetailGrid,
	props: {
		mode: String,
		detailData: Object,
		type: String
	},
	data() {
		return {
			options: {},
			params: {},
			HttpReq: null,
			resourceTypeList: {
				'com.inzent.yellowpage.model.InterfaceMeta': {
					label: 'label.interface',
					itemName: 'selectedInterfaceInfo',
					dataId: 'id',
					menuId: `103000`,
					searchLabel: `interfaceId`
				},
				'com.inzent.yellowpage.model.ServiceMeta': {
					label: 'label.service',
					itemName: 'selectedServiceInfo',
					dataId: 'id',
					menuId: `102000`,
					searchLabel: `serviceId`
				},
				'com.inzent.yellowpage.model.ModelRecord': {
					label: 'label.modelRecord',
					itemName: 'selectedModelRecordInfo',
					dataId: 'id',
					menuId: `101000`,
					searchLabel: `recordId`
				},
				'com.inzent.yellowpage.model.SystemMeta': {
					label: 'label.system',
					itemName: 'selectedSystemInfo',
					dataId: 'id',
					menuId: `203000`,
					searchLabel: `id`
				},
				'com.inzent.yellowpage.model.Server': {
					label: 'label.linked.server',
					itemName: 'selectedLinkedServerInfo',
					dataId: 'id',
					menuId: `202000`,
					searchLabel: `id`
				},
				'com.inzent.yellowpage.model.FieldMeta': {
					label: 'label.fieldMeta',
					dataId: 'fieldId'
				}
			}
		};
	},
	computed: {
		...mapState(['menuList'])
	},
	watch: {
		detailData() {
			this.initData();
		}
	},
	created() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);

		this.HttpReq = new HttpReq(`/api/reference/entity`);
		this.params = this.detailInfo.params;

		this.options = makeGridOptions({
			rowHeight: 30,
			minRowHeight: 30,
			treeColumnOptions: {
				name: 'className'
			},
			data: [],
			columns: [
				{
					name: 'className',
					header: this.$t('label.publishLog.resource.type'),
					width: '25%',
					formatter: ({ row }) => {
						return this.$t(this.resourceTypeList[row.className].label);
					}
				},
				{
					name: 'resourceName',
					header: this.$t('label.name'),
					width: '35%',
					formatter: ({ row }) => {
						return `<span ${0 === row.rowKey ? `` : `class="link-new-tab"`} >${escapeHtml(row.resourceName)}</span>`;
					}
				},
				{
					name: `updateTimestamp`,
					header: this.$t('label.created.date'),
					width: '20%',
					align: `center`
				},
				{
					name: `updateUserId`,
					header: this.$t('label.writer'),
					width: '20%'
				}
			],
			onGridUpdated: evt => {
				evt.instance.expand(0, false);
			},
			onGridMounted: evt => {
				evt.instance.on('click', evt => {
					if (!evt.nativeEvent.target.classList.contains(`link-new-tab`)) return;

					const rowInfo = evt.instance.getRow(evt.rowKey);
					const resourceInfo = this.resourceTypeList[rowInfo.className];
					const menuInfo = this.menuList.filter(menuInfo => resourceInfo.menuId === menuInfo.menuId).pop();

					if (this.detailData.id === rowInfo.resourceName) return;

					openNewTab(menuInfo.menuId, () => {
						localStorage.removeItem(`searchObj`);
						localStorage.removeItem(resourceInfo.itemName);

						localStorage.setItem(`searchObj`, JSON.stringify({ id: rowInfo.resourceName }));
						localStorage.setItem(resourceInfo.itemName, JSON.stringify({ [resourceInfo.searchLabel]: rowInfo.resourceName }));
					});
				});

				evt.instance.on('expand', () => {
					this.$nextTick(this.resize);
				});

				evt.instance.on('collapse', () => {
					this.$nextTick(this.resize);
				});
			}
		});
	},
	mounted() {
		this.initData();
	},
	methods: {
		async initData() {
			this.$refs.tuiGrid.invoke(`clear`);

			if (isEmpty(this.detailData)) return;

			const res = await this.HttpReq.read(
				{
					object: this.detailData,
					className: this.params.className
				},
				true
			);

			if ('ok' !== res.data.result) {
				this.$alert({ type: 'warn', message: `${res.data.error[0].message}` });
			} else {
				this.$refs.tuiGrid.invoke('resetData', [this.initTreeData(res.data.object)]);

				setTimeout(this.resize, 0);
			}
		},
		initTreeData(record) {
			const treeData = {
				className: record.className,
				resourceName: record.object[this.resourceTypeList[record.className].dataId],
				updateUserId: record.object.updateUserId,
				updateTimestamp: changeDateFormat(record.object.updateTimestamp)
			};

			if (0 < record.referencing.length) {
				treeData._children = [];

				record.referencing.forEach(field => {
					const subTreeData = {
						className: field.className,
						resourceName: field.object[this.resourceTypeList[field.className].dataId],
						updateUserId: field.object.updateUserId,
						updateTimestamp: changeDateFormat(field.object.updateTimestamp)
					};

					if (0 < field.referencing.length) {
						if (!subTreeData._children) {
							subTreeData._children = [];
						}

						field.referencing.forEach(reference => {
							subTreeData._children.push(this.initTreeData(reference));
						});
					}

					treeData._children.push(subTreeData);
				});
			}

			return treeData;
		},
		exportExcel() {
			const headerInfo = {};

			this.$refs.tuiGrid.invoke('getColumns').forEach(({ name, header }) => {
				headerInfo[name] = header;
			});

			const workbook = xlsx.utils.book_new();

			const firstSheet = xlsx.utils.json_to_sheet(
				[
					headerInfo,
					...cloneDeep(this.$refs.tuiGrid.invoke('getData')).map(({ className, resourceName, updateUserId, updateTimestamp }) => ({
						className: this.$t(this.resourceTypeList[className].label),
						resourceName,
						updateUserId,
						updateTimestamp
					}))
				],
				{ header: Object.keys(headerInfo), skipHeader: true }
			);

			firstSheet['!cols'] = [{ wpx: 120 }, { wpx: 250 }, { wpx: 150 }, { wpx: 150 }];

			xlsx.utils.book_append_sheet(workbook, firstSheet, this.$t(`label.referenceTreeInfo`));

			xlsx.writeFile(workbook, `Reference_${moment(new Date()).format('YYYYMMDD_HHmmss')}.xlsx`);
		},
		resize() {
			if (!this.$refs.tuiGrid) return;

			if (`modal` === this.type) {
				const modalBody = document.querySelector(`.modal-body`);

				const modalBodyComputedStyle = getComputedStyle(modalBody);

				const modalBodyPaddingLeft = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-left'));
				const modalBodyPaddingRight = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-right'));
				const modalBodyPaddingTop = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-top'));
				const modalBodyPaddingBottom = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-bottom'));

				this.$refs.tuiGrid.invoke('setWidth', modalBody.clientWidth - modalBodyPaddingLeft - modalBodyPaddingRight);
				this.$refs.tuiGrid.invoke('setHeight', modalBody.clientHeight - modalBodyPaddingTop - modalBodyPaddingBottom - 1);
			} else {
				const panel = document.getElementById('panel');

				const panelBody = panel.getElementsByClassName('panel-body')[0];
				const panelBodyComputedStyle = getComputedStyle(panelBody);

				const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
				const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
				const panelBodyPaddingTop = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-top'));
				const panelBodyPaddingBottom = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-bottom'));

				this.$refs.tuiGrid.invoke('setWidth', panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight);
				this.$refs.tuiGrid.invoke('setHeight', panelBody.clientHeight - panelBodyPaddingTop - panelBodyPaddingBottom);
			}
		}
	}
};
</script>
<style lang="scss" scoped>
.btn-icon {
	margin: 0px;
}
</style>
