<template>
	<div
		class="modal"
		tabindex="-1"
		role="dialog"
		data-backdrop="static"
		@click="hideModal"
		:style="{ width: modalBackWidth, display: settingInfo.showModal ? 'block' : 'none' }"
	>
		<div :class="settingInfo.classObject" :style="{ 'max-width': fullscreenWidth ? fullscreenWidth : settingInfo.modalWidth }">
			<div class="modal-content" :style="{ height: fullscreenHeight ? fullscreenHeight : settingInfo.modalHeight }">
				<div class="modal-header" v-if="settingInfo.isHeader">
					<slot name="modal-header"></slot>
				</div>
				<div class="modal-body">
					<slot name="modal-body"></slot>
				</div>
				<div class="modal-footer">
					<slot name="modal-footer"></slot>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { WindowResizeEventBus, SideMenuToggleEventBus } from '@/utils/common.js';
import commonMixin from '@/mixins/commonMixin.js';

export default {
	name: 'Modal',
	mixins: [commonMixin],
	props: {
		settingInfo: Object
	},
	data() {
		return {
			modalBackWidth: null,
			backdropMode: this.settingInfo.backdropMode
		};
	},
	computed: {
		fullscreenWidth() {
			return this.settingInfo.classObject['modal-fullscreen'] ? '95%' : '';
		},
		fullscreenHeight() {
			return this.settingInfo.classObject['modal-fullscreen'] ? '100%' : '';
		}
	},
	watch: {
		settingInfo() {
			this.backdropMode = this.settingInfo.backdropMode;
			this.modalBackWidth = this.getContentWidth(this.backdropMode);
		}
	},
	mounted() {
		this.modalBackWidth = this.getContentWidth(this.backdropMode);

		WindowResizeEventBus.$on('MainResize', () => {
			this.modalBackWidth = this.getContentWidth(this.backdropMode);
		});

		SideMenuToggleEventBus.$on('MainResize', () => {
			let isSidebar = document.body.classList.contains('sidebar-toggled');
			this.backdropMode = isSidebar ? 'full' : '';
			this.modalBackWidth = this.getContentWidth(isSidebar);
		});
	},
	methods: {
		hideModal() {
			if (this.settingInfo.deleteBackdrop) this.$emit('hideModal');
		}
	}
};
</script>
