<template>
	<component
		ref="component"
		:is="checkboxElement"
		:classObject="classObject"
		:readonly="readonly"
		:optionListData="optionListData"
		@changeEvt="changeEvt"
		style="padding-left: 0"
	></component>
</template>

<script>
import { HttpReq } from '@/utils/common.js';

export default {
	name: 'selectBox',
	props: {
		vModel: String,
		classObject: Object,
		readonly: {
			type: Boolean,
			default: false
		},
		checkboxInfo: Object,
		val: {
			type: Array,
			default: () => []
		},
		isEvent: {
			type: Boolean,
			default: true
		},
		optionListData: Array
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	methods: {
		setData(val) {
			this.$refs.component.setData(val);
		},
		getData() {
			return this.$refs.component.getData();
		},
		changeEvt(value) {
			this.$emit('changeEvt', value);
		}
	},
	data() {
		return {
			checkboxElement: {
				template: `
					<div>
						<label v-for="${this.checkboxInfo.optionFor}" :key="${this.checkboxInfo.optionText}" style="padding-right: 10px;">
							<input type="checkbox" class="custom-control-input" 
								v-model="obj['${this.vModel}']"
								:value="${this.checkboxInfo.optionText}"
								:disabled="readonly"
								${this.isEvent && this._events.changeEvt ? '@change="changeEvt"' : ''} 
								style="position: relative; width: 0.75rem; height: 1.05rem;" />
							<span class="custom-control-label" v-text="${this.checkboxInfo.optionValue}" style="padding-left: 5px;" />
						</label>
					</div>
				`,
				props: {
					classObject: Object,
					readonly: Boolean,
					optionListData: Array
				},
				data: (() => {
					const rtnDataObj = {
						vModelName: this.vModel,
						optionListName: this.checkboxInfo.optionListName,

						obj: {
							[this.vModel]: this.val
						},

						[this.checkboxInfo.optionListName]: [],

						url: this.checkboxInfo.url,
						params: this.checkboxInfo.params ? this.checkboxInfo.params : null,

						HttpReq: null
					};

					return function () {
						return rtnDataObj;
					};
				})(),
				watch: {
					optionListData: {
						immediate: true,
						handler() {
							this[this.optionListName] = this.optionListData;
						}
					}
				},
				async created() {
					if (!this.url) return;

					this.HttpReq = new HttpReq(this.url);

					const result = await this.HttpReq.read(this.params);

					if (`ok` !== result.data.result) return;

					this[this.optionListName] = result.data.object;
				},
				methods: {
					setData(val) {
						this.obj[this.vModelName] = val;
					},
					getData() {
						return {
							[this.vModelName]: this.obj[this.vModelName] ? this.obj[this.vModelName] : []
						};
					},
					changeEvt() {
						this.$emit('changeEvt', { key: this.vModelName, value: this.obj[this.vModelName] });
					}
				}
			}
		};
	}
};
</script>
