<template>
	<div style="height: 100%">
		<div class="empty" v-if="isShowNoData">
			<p>{{ $t(`msg.no.registered.servers.systems`) }}</p>
			<img :src="require('@/assets/img/empty.svg')" class="center-block" />
		</div>

		<div class="p-topology" @contextmenu="initContextmenu" style="position: relative" v-else>
			<div ref="topology" :style="topologyStyle"></div>
			<div :style="filterIconStyle" v-show="isShowFilterIcon">
				<img :src="require(`@/assets/img/topology-filter.svg`)" style="width: 100px; height: 100px" />
			</div>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import cytoscape from 'cytoscape';
import cloneDeep from 'lodash/cloneDeep';
import {
	HttpReq,
	getNumFromStr,
	numberWithComma,
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus
} from '@/utils/common.js';

export default {
	name: `DeployFlow`,
	data() {
		return {
			isShowNoData: true,

			serverList: [],
			sourceSystemList: [],
			targetSystemList: [],
			logStatsList: [],
			interfaceTypeList: [],

			filterServerIdList: [],
			filterSourceSystemIdList: [],
			filterTargetSystemIdList: [],

			topologyStyle: {
				width: null,
				height: null
			},

			filterIconStyle: {
				position: `fixed`,
				right: `30px`,
				bottom: `30px`
			},

			nodeWidth: 200,
			nodeHeight: 110,
			nodeMargin: 30,

			serverImg: new Image(),
			sourceSystemImg: new Image(),
			targetSystemImg: new Image(),

			nodeZIndex: 0,

			isShowFilterIcon: false,

			nodeTooltipFunc: null,
			edgeTooltipFunc: null
		};
	},
	computed: {
		...mapState(['userInfo']),
		totalServerHeight() {
			const serverList =
				0 < this.filterServerIdList.length
					? this.serverList.filter(serverInfo => `undefined` !== typeof this.filterServerIdList.find(id => id === serverInfo.id))
					: this.serverList;

			return serverList.length * this.nodeHeight + (serverList.length + 1) * this.nodeMargin;
		},
		totalSourceSystemHeight() {
			const sourceSystemList =
				0 < this.filterSourceSystemIdList.length
					? this.sourceSystemList.filter(
							systemInfo => `undefined` !== typeof this.filterSourceSystemIdList.find(id => id === systemInfo.id)
					  )
					: this.sourceSystemList;

			return sourceSystemList.length * this.nodeHeight + (sourceSystemList.length + 1) * this.nodeMargin;
		},
		totalTargetSystemHeight() {
			const targetSystemList =
				0 < this.filterTargetSystemIdList.length
					? this.targetSystemList.filter(
							systemInfo => `undefined` !== typeof this.filterTargetSystemIdList.find(id => id === systemInfo.id)
					  )
					: this.targetSystemList;

			return targetSystemList.length * this.nodeHeight + (targetSystemList.length + 1) * this.nodeMargin;
		}
	},
	created() {
		document.querySelector(`#ct`).style.background = document.body.classList.contains(`moon`) ? `#1f2337` : `#f1f1f1`;

		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);

		const filterServerIdList = localStorage.getItem(`topology-filter-server-${this.userInfo.userId}`);
		const filterSourceSystemIdList = localStorage.getItem(`topology-filter-source-${this.userInfo.userId}`);
		const filterTargetSystemIdList = localStorage.getItem(`topology-filter-target-${this.userInfo.userId}`);

		if (filterServerIdList) this.filterServerIdList = JSON.parse(filterServerIdList);

		if (filterSourceSystemIdList) this.filterSourceSystemIdList = JSON.parse(filterSourceSystemIdList);

		if (filterTargetSystemIdList) this.filterTargetSystemIdList = JSON.parse(filterTargetSystemIdList);

		this.isShowFilterIcon = filterServerIdList || filterSourceSystemIdList || filterTargetSystemIdList;
	},
	async mounted() {
		this.serverImg.src = require(`@/assets/img/server.svg`);
		this.sourceSystemImg.src = require(`@/assets/img/sourceSystem.svg`);
		this.targetSystemImg.src = require(`@/assets/img/targetSystem.svg`);

		let result = await new HttpReq(`/api/entity/server/search`).read({
			object: {},
			limit: null,
			next: null,
			reverseOrder: false
		});

		this.serverList = result.data.object;

		result = await new HttpReq(`/api/entity/system/search`).read({
			object: {},
			limit: null,
			next: null,
			reverseOrder: false
		});

		this.sourceSystemList = cloneDeep(result.data.object);
		this.targetSystemList = cloneDeep(result.data.object);

		this.nodeZIndex += this.serverList.length + this.sourceSystemList.length + this.targetSystemList.length;

		result = await new HttpReq(`/api/statistics/server`).read({
			object: {},
			limit: null,
			next: null,
			reverseOrder: false
		});

		this.logStatsList = result.data.object;

		result = await new HttpReq(`/api/page/properties`).read({ pk: { propertyId: 'Interface.Type' }, orderByKey: true });

		this.interfaceTypeList = result.data.object;

		this.isShowNoData = 0 === this.nodeZIndex;

		if (this.isShowNoData) return;

		this.$nextTick(() => {
			this.initData();

			this.refreshTopology();
		});
	},
	methods: {
		initData() {
			[`server`, `source`, `target`].forEach(type => {
				const list = `server` === type ? this.serverList : `source` === type ? this.sourceSystemList : this.targetSystemList;

				list.forEach(info => {
					if (`undefined` === typeof info.editCount) info.editCount = 0;
					if (`undefined` === typeof info.publishDoneCount) info.publishDoneCount = 0;
					if (`undefined` === typeof info.publishErrorCount) info.publishErrorCount = 0;
					if (`undefined` === typeof info.publishCount) info.publishCount = 0;

					this.logStatsList
						.filter(
							({ pk: { serverId, sourceSystemId, targetSystemId } }) =>
								info.id === (`server` === type ? serverId : `source` === type ? sourceSystemId : targetSystemId)
						)
						.forEach(({ editCount, publishDoneCount, publishErrorCount }) => {
							info.editCount += editCount;
							info.publishDoneCount += publishDoneCount;
							info.publishErrorCount += publishErrorCount;
							info.publishCount += publishDoneCount + publishErrorCount;
						});
				});

				list.sort((a, b) => (0 === b.publishCount - a.publishCount ? b.editCount - a.editCount : b.publishCount - a.publishCount));
			});
		},
		refreshTopology() {
			this.resize();

			const cy = cytoscape({
				container: this.$refs.topology
			});

			const nodes = [];
			const edges = [];

			const serverList =
				0 < this.filterServerIdList.length
					? this.serverList.filter(serverInfo => `undefined` !== typeof this.filterServerIdList.find(id => id === serverInfo.id))
					: this.serverList;

			serverList.forEach((serverInfo, idx) => {
				let x = this.$el.clientWidth / 2;
				let y = this.nodeHeight;
				y += (getNumFromStr(this.topologyStyle.height) - this.totalServerHeight) / 2;
				y += this.nodeHeight * idx + this.nodeMargin * (idx + 1);
				y -= this.nodeHeight / 2;

				nodes.push({
					group: 'nodes',
					data: {
						id: `server_${serverInfo.id}`,
						name: serverInfo.id,
						type: `server`,
						zIndex: 0,
						orgData: cloneDeep(serverInfo)
					},
					position: {
						x,
						y
					}
				});
			});

			[`source`, `target`].forEach(type => {
				let list = `source` === type ? this.sourceSystemList : this.targetSystemList;

				const filterList = `source` === type ? this.filterSourceSystemIdList : this.filterTargetSystemIdList;

				if (0 < filterList.length) list = list.filter(info => `undefined` !== typeof filterList.find(id => id === info.id));

				list.forEach((info, idx) => {
					let x = `source` === type ? this.nodeWidth / 2 + this.nodeMargin : this.$el.clientWidth - (this.nodeWidth / 2 + this.nodeMargin);
					let y = this.nodeHeight;
					y +=
						(getNumFromStr(this.topologyStyle.height) -
							(`source` === type ? this.totalSourceSystemHeight : this.totalTargetSystemHeight)) /
						2;
					y += this.nodeHeight * idx + this.nodeMargin * (idx + 1);
					y -= this.nodeHeight / 2;

					nodes.push({
						group: 'nodes',
						data: {
							id: `${type}_${info.id}`,
							name: info.id,
							type,
							zIndex: 0,
							orgData: cloneDeep(info)
						},
						position: {
							x,
							y
						}
					});
				});
			});

			this.logStatsList.forEach(logStatsInfo => {
				const { publishDoneCount, publishErrorCount } = logStatsInfo;

				if (0 === publishDoneCount + publishErrorCount) return;

				const { serverId, sourceSystemId, targetSystemId, sourceType, targetType } = logStatsInfo.pk;

				const sourceInterfaceTypeValue = this.interfaceTypeList.find(info => info.pk.propertyKey === sourceType).propertyValue;
				const targetInterfaceTypeValue = this.interfaceTypeList.find(info => info.pk.propertyKey === targetType).propertyValue;

				if (`undefined` === typeof nodes.find(info => info.data.id === `source_${sourceSystemId}`)) return;

				if (`undefined` === typeof nodes.find(info => info.data.id === `server_${serverId}`)) return;

				if (`undefined` === typeof nodes.find(info => info.data.id === `target_${targetSystemId}`)) return;

				[`source`, `target`].forEach(type => {
					let sourceId = `source` === type ? `source_${sourceSystemId}` : `server_${serverId}`;
					let targetId = `source` === type ? `server_${serverId}` : `target_${targetSystemId}`;
					let edgeId = `${sourceId}-${targetId}-${`source` === type ? sourceType : targetType}`;
					let info = edges.find(edgeInfo => edgeInfo.data.id === edgeId);

					if (info) {
						info.data.orgData.push(cloneDeep(logStatsInfo));
					} else {
						edges.push({
							group: `edges`,
							data: {
								id: edgeId,
								source: sourceId,
								target: targetId,
								value: `source` === type ? sourceInterfaceTypeValue : targetInterfaceTypeValue,
								interfaceType: `source` === type ? sourceType : targetType,
								orgData: [cloneDeep(logStatsInfo)]
							}
						});
					}
				});
			});

			cy.add(nodes);
			cy.add(edges);

			const isMoon = localStorage.getItem(`theme`) && `moon` === localStorage.getItem(`theme`);

			cy.style()
				.selector('node')
				.style({
					width: this.nodeWidth,
					height: this.nodeHeight,
					shape: () => 'round-rectangle',
					'border-width': '1',
					'border-color': '#151826',
					'background-fill': 'linear-gradient',
					'background-gradient-stop-colors': '#fff #A6A6A6',
					'background-gradient-stop-positions': '0 75 100'
				})
				.selector('edge')
				.style({
					width: '1',
					'line-color': evt =>
						evt._private.data.orgData.find(data => 0 < data.publishErrorCount) ? `#ed3137` : isMoon ? `#8a8b92` : `#808080`,
					'target-arrow-color': evt =>
						evt._private.data.orgData.find(data => 0 < data.publishErrorCount) ? `#ed3137` : isMoon ? `#8a8b92` : `#808080`,
					color: evt => (evt._private.data.orgData.find(data => 0 < data.publishErrorCount) ? `#ed3137` : isMoon ? `#fff` : `#151826`),
					'target-arrow-shape': 'triangle',
					'curve-style': 'bezier',
					'font-size': '11px',
					'font-weight': `bold`,
					'text-margin-y': `-10px`,
					label: 'data(value)'
				})
				.update();

			cy.on(`click`, `node`, evt => {
				evt.target._private.data.zIndex = Math.max(...cy.nodes().map(ele => ele._private.data.zIndex)) + 1;

				cy.$(`#${evt.target._private.data.id}`).style({
					'z-index': evt => evt._private.data.zIndex
				});
			});

			cy.on('drag', 'node', evt => {
				evt.target._private.dirty = true;

				evt.target._private.data.zIndex = Math.max(...cy.nodes().map(ele => ele._private.data.zIndex)) + 1;

				cy.$(`#${evt.target._private.data.id}`).style({
					'z-index': evt => evt._private.data.zIndex
				});

				this.nodeTooltipFunc = null;
			});

			cy.on('mouseover', 'node', evt => {
				const errorInfo = {};

				evt.target.connectedEdges().forEach(edge => {
					edge._private.data.orgData.forEach(
						({ publishErrorCount, pk: { serverId, sourceSystemId, targetSystemId, sourceType, targetType } }) => {
							if (`undefined` === typeof errorInfo[`server_${serverId}`]) errorInfo[`server_${serverId}`] = 0;
							if (`undefined` === typeof errorInfo[`source_${sourceSystemId}`]) errorInfo[`source_${sourceSystemId}`] = 0;
							if (`undefined` === typeof errorInfo[`target_${targetSystemId}`]) errorInfo[`target_${targetSystemId}`] = 0;
							if (`undefined` === typeof errorInfo[`source_${sourceSystemId}-server_${serverId}-${sourceType}`])
								errorInfo[`source_${sourceSystemId}-server_${serverId}-${sourceType}`] = 0;
							if (`undefined` === typeof errorInfo[`server_${serverId}-target_${targetSystemId}-${targetType}`])
								errorInfo[`server_${serverId}-target_${targetSystemId}-${targetType}`] = 0;

							errorInfo[`server_${serverId}`] += publishErrorCount;
							errorInfo[`source_${sourceSystemId}`] += publishErrorCount;
							errorInfo[`target_${targetSystemId}`] += publishErrorCount;
							errorInfo[`source_${sourceSystemId}-server_${serverId}-${sourceType}`] += publishErrorCount;
							errorInfo[`server_${serverId}-target_${targetSystemId}-${targetType}`] += publishErrorCount;
						}
					);
				});

				for (const key in errorInfo) {
					cy.$(`#${key}`).style({
						'overlay-opacity': 0.25,
						'overlay-color': 0 < errorInfo[key] ? `#ed3137` : `#000`
					});
				}

				const canvas = cy.container().querySelectorAll(`canvas`)[0];
				const ctx = canvas.getContext(`2d`);

				const toolTipWidth = ctx.measureText(evt.target._private.data.orgData.id).width + 20;
				const tooltipHeight = 30;

				const { x, y } = evt.position;
				const tooltipXPos = ctx.canvas.width < x + toolTipWidth ? x - toolTipWidth : x;
				const tooltipYPos = ctx.canvas.height < y + tooltipHeight ? y - tooltipHeight : y;

				this.nodeTooltipFunc = () => {
					ctx.beginPath();

					ctx.save();
					ctx.fillStyle = `#333333`;
					ctx.fillRect(tooltipXPos, tooltipYPos, toolTipWidth, tooltipHeight);
					ctx.restore();

					ctx.save();
					ctx.fillStyle = `#fff`;
					ctx.fillText(evt.target._private.data.orgData.id, tooltipXPos + 10, tooltipYPos + 20);
					ctx.restore();

					ctx.closePath();
				};

				this.nodeTooltipFunc();
			});

			cy.on('mouseout', 'node', () => {
				cy.nodes().css({ 'overlay-opacity': 0 });
				cy.edges().css({ 'overlay-opacity': 0 });

				this.nodeTooltipFunc = null;
			});

			cy.on('mouseover', 'edge', evt => {
				let publishDoneCount = 0;
				let publishErrorCount = 0;
				let editCount = 0;

				const errorInfo = {};

				evt.target._private.data.orgData.forEach(info => {
					publishDoneCount += info.publishDoneCount;
					publishErrorCount += info.publishErrorCount;
					editCount += info.editCount;

					const { serverId, sourceSystemId, targetSystemId, sourceType, targetType } = info.pk;

					if (`undefined` === typeof errorInfo[`server_${serverId}`]) errorInfo[`server_${serverId}`] = 0;
					if (`undefined` === typeof errorInfo[`source_${sourceSystemId}`]) errorInfo[`source_${sourceSystemId}`] = 0;
					if (`undefined` === typeof errorInfo[`target_${targetSystemId}`]) errorInfo[`target_${targetSystemId}`] = 0;
					if (`undefined` === typeof errorInfo[`source_${sourceSystemId}-server_${serverId}-${sourceType}`])
						errorInfo[`source_${sourceSystemId}-server_${serverId}-${sourceType}`] = 0;
					if (`undefined` === typeof errorInfo[`server_${serverId}-target_${targetSystemId}-${targetType}`])
						errorInfo[`server_${serverId}-target_${targetSystemId}-${targetType}`] = 0;

					errorInfo[`server_${serverId}`] += info.publishErrorCount;
					errorInfo[`source_${sourceSystemId}`] += info.publishErrorCount;
					errorInfo[`target_${targetSystemId}`] += info.publishErrorCount;
					errorInfo[`source_${sourceSystemId}-server_${serverId}-${sourceType}`] += info.publishErrorCount;
					errorInfo[`server_${serverId}-target_${targetSystemId}-${targetType}`] += info.publishErrorCount;
				});

				for (const key in errorInfo) {
					cy.$(`#${key}`).style({
						'overlay-opacity': 0.25,
						'overlay-color': 0 < errorInfo[key] ? `#ed3137` : `#000`
					});
				}

				const canvas = cy.container().querySelectorAll(`canvas`)[0];
				const ctx = canvas.getContext(`2d`);

				const interfaceTypeStr = evt.target._private.data.value;
				const editCountStr = `${this.$t(`label.editCount`)}: ${numberWithComma(editCount)}`;
				const publishDoneCountStr = `${this.$t(`label.publishDoneCount`)}: ${numberWithComma(publishDoneCount)}`;
				const publishErrorCountStr = `${this.$t(`label.publishErrorCount`)}: ${numberWithComma(publishErrorCount)}`;

				const toolTipWidth =
					Math.max(
						...[
							ctx.measureText(interfaceTypeStr).width,
							ctx.measureText(editCountStr).width,
							ctx.measureText(publishDoneCountStr).width,
							ctx.measureText(publishErrorCountStr).width
						]
					) + 30;
				const tooltipHeight = 90;

				const { x, y } = evt.position;
				const tooltipXPos = ctx.canvas.width < x + toolTipWidth ? x - toolTipWidth : x;
				const tooltipYPos = ctx.canvas.height < y + tooltipHeight ? y - tooltipHeight : y;

				this.edgeTooltipFunc = () => {
					ctx.beginPath();

					ctx.save();
					ctx.fillStyle = `#333333`;
					ctx.fillRect(tooltipXPos, tooltipYPos, toolTipWidth, tooltipHeight);
					ctx.restore();

					ctx.save();
					ctx.fillStyle = `#fff`;

					ctx.fillText(interfaceTypeStr, tooltipXPos + 10, tooltipYPos + 20);
					ctx.fillText(publishDoneCountStr, tooltipXPos + 10, tooltipYPos + 40);
					ctx.fillText(publishErrorCountStr, tooltipXPos + 10, tooltipYPos + 60);
					ctx.fillText(editCountStr, tooltipXPos + 10, tooltipYPos + 80);
					ctx.restore();

					ctx.closePath();
				};
			});

			cy.on('mouseout', 'edge', () => {
				cy.nodes().css({ 'overlay-opacity': 0 });
				cy.edges().css({ 'overlay-opacity': 0 });

				this.edgeTooltipFunc = null;
			});

			cy.onRender(() => {
				const canvas = cy.container().querySelectorAll(`canvas`)[0];
				const ctx = canvas.getContext(`2d`);
				const fontSize = 12;

				ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

				const nodes = cy.nodes().map(info => info._private);

				nodes.sort((a, b) => a.data.zIndex - b.data.zIndex);

				nodes.forEach(info => {
					ctx.beginPath();

					const { x1, x2, y1, y2 } = info.bodyBounds;

					let standardXPos = x1 + 20;
					let standardYPos = 0;

					ctx.clearRect(x1, y1, x2 - x1, y2 - y1);

					ctx.fillStyle = `#151826`;
					ctx.strokeStyle = `#808080`;
					ctx.font = `bold ${fontSize}px Arial`;

					//title
					ctx.save();
					standardYPos += y1 + fontSize + 20;
					ctx.font = `bold 24px Arial`;

					let id = info.data.orgData.id;

					if (this.nodeWidth < ctx.measureText(id).width + 40) id = `${id.slice(0, 4)}...${id.slice(id.length - 4, id.length)}`;

					ctx.fillText(id, standardXPos, standardYPos);
					ctx.restore();

					//title line
					standardYPos += 9;
					ctx.moveTo(standardXPos, standardYPos);
					ctx.lineTo(standardXPos + (x2 - x1) - 2 * 20, standardYPos);
					ctx.stroke();

					//publishDoneCount
					standardYPos += fontSize + 7;
					ctx.fillText(
						`${this.$t(`label.publishDoneCount`)}: ${numberWithComma(info.data.orgData.publishDoneCount)}`,
						standardXPos,
						standardYPos
					);

					//publishErrorCount
					standardYPos += fontSize + 7;
					ctx.fillText(
						`${this.$t(`label.publishErrorCount`)}: ${numberWithComma(info.data.orgData.publishErrorCount)}`,
						standardXPos,
						standardYPos
					);

					//editCount
					standardYPos += fontSize + 7;
					ctx.fillText(`${this.$t(`label.editCount`)}: ${numberWithComma(info.data.orgData.editCount)}`, standardXPos, standardYPos);

					ctx.drawImage(
						`server` === info.data.type ? this.serverImg : `source` === info.data.type ? this.sourceSystemImg : this.targetSystemImg,
						standardXPos + (x2 - x1) - (`server` === info.data.type ? 50 : 60),
						standardYPos - (`server` === info.data.type ? 22 : 20),
						50,
						50
					);

					ctx.closePath();
				});

				if (this.nodeTooltipFunc) this.nodeTooltipFunc();
				if (this.edgeTooltipFunc) this.edgeTooltipFunc();
			});

			cy.userZoomingEnabled(false);
			cy.userPanningEnabled(false);
			cy.boxSelectionEnabled(false);

			cy.on(`resize`, () => {
				cy.viewport({
					zoom: 1,
					pan: { x: 0, y: 0 }
				});
			});

			cy.emit(`resize`);

			delete window.topologyResize;

			window.topologyResize = () => {
				this.$startSpinner();

				setTimeout(() => {
					cy.resize();

					cy.filter(ele => `server` === ele._private.data.type && `undefined` === typeof ele._private.dirty).position({
						x: this.$el.clientWidth / 2
					});

					[`source`, `target`].forEach(type => {
						nodes.filter(nodeInfo => {
							if (type !== nodeInfo.data.type) return;

							cy.filter(ele => nodeInfo.data.id === ele._private.data.id && `undefined` === typeof ele._private.dirty).position({
								x:
									`source` === type
										? this.nodeWidth / 2 + this.nodeMargin
										: this.$el.clientWidth - (this.nodeWidth / 2 + this.nodeMargin)
							});
						});
					});

					const isMoon = localStorage.getItem(`theme`) && `moon` === localStorage.getItem(`theme`);

					cy.style()
						.selector('edge')
						.style({
							'line-color': evt =>
								evt._private.data.orgData.find(data => 0 < data.publishErrorCount) ? `#ed3137` : isMoon ? `#8a8b92` : `#808080`,
							'target-arrow-color': evt =>
								evt._private.data.orgData.find(data => 0 < data.publishErrorCount) ? `#ed3137` : isMoon ? `#8a8b92` : `#808080`,
							color: evt =>
								evt._private.data.orgData.find(data => 0 < data.publishErrorCount) ? `#ed3137` : isMoon ? `#fff` : `#151826`
						})
						.update();

					this.$stopSpinner();
				}, 300);
			};
		},
		initContextmenu(evt) {
			if (!evt) return;

			const _this = this;

			const {
				$refs: { content },
				hideModal
			} = this.$openModal({
				title: this.$t('label.search.filter'),
				modalWidth: '1300px',
				contentList: [
					{
						template: `<div>
								       <DeployFlowFilter ref="deployFlowFilter" />
								   </div>`,
						components: {
							DeployFlowFilter: require(`@/views/modal/deployFlow/DeployFlowFilter.vue`).default
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('label.ok') }}</button>`,
						methods: {
							confirm() {
								const { source, server, target } = content[0].$refs.deployFlowFilter.getData();

								localStorage.removeItem(`topology-filter-source-${_this.userInfo.userId}`);
								localStorage.removeItem(`topology-filter-server-${_this.userInfo.userId}`);
								localStorage.removeItem(`topology-filter-target-${_this.userInfo.userId}`);

								_this.filterSourceSystemIdList = [];
								_this.filterServerIdList = [];
								_this.filterTargetSystemIdList = [];

								if (0 < source.length) {
									localStorage.setItem(`topology-filter-source-${_this.userInfo.userId}`, JSON.stringify(source));
									_this.filterSourceSystemIdList = JSON.parse(
										localStorage.getItem(`topology-filter-source-${_this.userInfo.userId}`)
									);
								}

								if (0 < server.length) {
									localStorage.setItem(`topology-filter-server-${_this.userInfo.userId}`, JSON.stringify(server));
									_this.filterServerIdList = JSON.parse(localStorage.getItem(`topology-filter-server-${_this.userInfo.userId}`));
								}

								if (0 < target.length) {
									localStorage.setItem(`topology-filter-target-${_this.userInfo.userId}`, JSON.stringify(target));
									_this.filterTargetSystemIdList = JSON.parse(
										localStorage.getItem(`topology-filter-target-${_this.userInfo.userId}`)
									);
								}

								_this.isShowFilterIcon = 0 < source.length || 0 < server.length || 0 < target.length;

								_this.refreshTopology();

								hideModal();
							}
						}
					}
				]
			});
		},
		resize() {
			const ct = document.getElementById('ct');
			const ctComputedStyle = getComputedStyle(ct);

			const ctPaddingLeft = getNumFromStr(ctComputedStyle.getPropertyValue('padding-left'));
			const ctPaddingRight = getNumFromStr(ctComputedStyle.getPropertyValue('padding-right'));
			const ctPaddingTop = getNumFromStr(ctComputedStyle.getPropertyValue('padding-top'));
			const ctPaddingBottom = getNumFromStr(ctComputedStyle.getPropertyValue('padding-bottom'));

			let adjustWidth = window.innerWidth;
			adjustWidth -= ctPaddingLeft + ctPaddingRight;

			if (992 <= window.innerWidth && document.getElementById('sidebar') && !document.body.classList.contains('sidebar-toggled')) {
				const sidebar = document.getElementById('sidebar');

				const sidebarComputedStyle = getComputedStyle(sidebar);

				const sidebarMarginLeft = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-left'));
				const sidebarMarginRight = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-right'));

				adjustWidth -= sidebar.offsetWidth + sidebarMarginLeft + sidebarMarginRight;
			}

			this.topologyStyle.width = `${adjustWidth}px`;

			let adjustHeight = window.innerHeight;
			adjustHeight -= ctPaddingTop + ctPaddingBottom;
			adjustHeight = Math.max(...[adjustHeight, this.totalServerHeight, this.totalSourceSystemHeight, this.totalTargetSystemHeight]);

			this.topologyStyle.height = `${adjustHeight}px`;

			if (window.topologyResize) window.topologyResize();

			document.querySelector(`#ct`).style.background = document.body.classList.contains(`moon`) ? `#1f2337` : `#f1f1f1`;
		}
	},
	beforeDestroy() {
		WindowResizeEventBus.$off('MainResize', this.resize);
		SideMenuToggleEventBus.$off('MainResize', this.resize);
		DetailToggleEventBus.$off('MainResize', this.resize);
		SearchToggleEventBus.$off('MainResize', this.resize);

		if (document.querySelector(`#ct`)) document.querySelector(`#ct`).style.removeProperty(`background`);

		if (window.topologyResize) delete window.topologyResize;
	}
};
</script>
<style scoped>
.p-topology {
	height: 100%;
	overflow-y: auto;
	overflow-x: hidden;
}
</style>
