<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ $t(`label.constant`) }}
							<span class="letterLength" v-text="`(${currLength}/${maxLength})`"> </span>
						</span>
					</label>
					<div class="detail-content-regExp" style="padding-left: 8px">
						<span style="cursor: auto">
							<input type="text" v-model="constant" :maxlength="maxLength" @input="input" class="regExp-text view-disabled" />
						</span>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { getRegExpInfo } from '@/utils/common.js';

export default {
	name: `constant`,
	props: {
		rawValueInfo: Object
	},
	data() {
		return {
			currLength: 0,
			maxLength: 0,
			regExp: null,
			constant: null
		};
	},
	created() {
		const { maxLength, regExp } = getRegExpInfo(`value`);

		this.maxLength = maxLength;
		this.regExp = regExp;
	},
	methods: {
		input(evt) {
			this.constant = evt.target.value.replace(new RegExp(this.regExp, 'g'), '');
			this.currLength = this.constant.length;
		},
		setData() {
			if (null === this.rawValueInfo) return;

			const { type, name } = this.rawValueInfo;

			if (`ConstantExpression` !== type) return;

			this.constant = name;
			this.currLength = this.constant.length;
		},
		getData() {
			return this.constant;
		}
	},
	watch: {
		rawValueInfo: {
			immediate: true,
			deep: true,
			handler() {
				this.setData();
			}
		}
	}
};
</script>
