<template>
	<Modal :settingInfo="settingInfo" @hideModal="hideModal">
		<template slot="modal-body">
			<i :class="`iconb-${modalType}`"></i>
			<component :is="textComp"></component>
		</template>
		<template slot="modal-footer">
			<button type="button" class="btn" @click="hideModal">{{ $t('label.cancel') }}</button>
			<button type="button" class="btn btn-primary" @click="clickConfirm">{{ $t('label.confirm') }}</button>
		</template>
	</Modal>
</template>

<script>
import Modal from '@/components/Modal.vue';
import { MenuClickEventBus, ModalKeyupEventBus, escapeHtml } from '@/utils/common';

export default {
	name: 'Confirm',
	components: {
		Modal
	},
	data() {
		return {
			showModal: false,
			modalInfo: {},
			textComp: {
				template: `<span/>`
			}
		};
	},
	computed: {
		modalType() {
			return 'normal' === this.modalInfo.type ? 'compt' : 'warn';
		},
		settingInfo() {
			return {
				isHeader: false,
				showModal: this.showModal,
				backdropMode: this.modalInfo.backdropMode,
				deleteBackdrop: true,
				classObject: {
					'modal-dialog': true,
					'modal-dialog-centered': true,
					'modal-dialog-alert': true
				}
			};
		}
	},
	methods: {
		hideModal() {
			this.showModal = false;
			this.modalInfo.backdropMode = null;
			this.$destroy();
		},
		clickConfirm() {
			if (this.modalInfo.callback) {
				this.modalInfo.callback();
			}
		}
	},
	created() {
		MenuClickEventBus.$on('ModalDelete', this.hideModal);
	},
	destroyed() {
		document.body.removeChild(this.$el);
	},
	mounted() {
		ModalKeyupEventBus.$on('ModalKeyup', () => {
			this.hideModal();
		});

		this.textComp = {
			template: `
				<span>
					<p class="alert-text">${this.modalInfo.isXssMode ? escapeHtml(this.modalInfo.message) : this.modalInfo.message}</p>
				</span>
			`
		};
	}
};
</script>
