<template>
	<div ref="detailCustom" class="customLayout"></div>
</template>

<script>
import { getHttpUrl } from '@/utils/common.js';

export default {
	name: 'DetailCustom',
	props: {
		mode: String,
		detailInfo: Object,
		detailData: Object
	},
	watch: {
		detailData() {
			if (!this.$refs.detailCustom.querySelector('[data-ready]')) return;

			this.$refs.detailCustom.querySelector('[data-ready]').dispatchEvent(
				new CustomEvent('getDetailData', {
					detail: {
						mode: this.mode,
						detailData: this.detailData
					}
				})
			);
		}
	},
	computed: {
		src() {
			return getHttpUrl(`/api/page/customPage${this.detailInfo.url}`);
		}
	},
	mounted() {
		$(this.$refs.detailCustom).load(this.src);
	},
	methods: {
		getData() {
			this.$refs.detailCustom.querySelector('[data-ready]').dispatchEvent(new CustomEvent('getCustomData'));
		}
	},
	beforeDestroy() {
		this.$refs.detailCustom.querySelector('[data-ready]').dispatchEvent(new CustomEvent('destroy'));
	}
};
</script>
