<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span> Target Field </span>
					</label>
					<div class="detail-content-common" style="padding-left: 0px">
						<span style="cursor: auto">
							<input type="text" maxlength="70" class="form-control view-disabled" v-model="targetFieldPath" readonly="readonly" />
						</span>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> Mapping Expression </span>
					</label>
					<div class="input-group">
						<input type="text" readonly="readonly" class="form-control view-disabled" v-model="mappingExpression" />
						<button
							class="btn btn-icon"
							style="margin-left: 3px"
							@click="openReferenceModal"
							v-if="!readonly"
							:disabled="!fieldKey"
							:title="$t('label.search')"
						>
							<i class="icon-srch"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							Array Reference
							<span class="letterLength" v-text="`(${arrayReferenceCurrLength}/${arrayReferenceMaxLength})`"> </span>
						</span>
					</label>
					<div class="input-group">
						<input
							type="text"
							:readonly="readonly || !fieldKey"
							class="form-control view-disabled"
							@input="input"
							:maxlength="arrayReferenceMaxLength"
							v-model="arrayReference"
						/>
						<button
							class="btn btn-icon"
							style="margin-left: 3px"
							@click="openArrayReferenceModal"
							v-if="!readonly"
							:disabled="!fieldKey"
							:title="$t('label.search')"
						>
							<i class="icon-srch"></i>
						</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { convertTypeToClass } from '@/views/modal/mapping/ruleDetail/utils.js';

export default {
	name: 'MappingRuleDetail',
	props: {
		data: Object,
		readonly: Boolean
	},
	data() {
		return {
			clickFieldInfo: null,
			mappingExpression: null,
			arrayReference: null,
			sourceRecordIdArr: [],
			targetRecordIdArr: [],
			fieldKey: null,
			targetFieldPath: null,
			fieldType: null,
			fieldLength: null,
			arrayType: null,
			simpleMappingYn: `Y`,
			fieldInfoLabel: null,
			arrayReferenceCurrLength: 0,
			arrayReferenceMaxLength: 90
		};
	},
	mounted() {
		this.clickFieldInfo = this.data.clickFieldInfo;
		this.arrayReference = this.data.arraySizeParameter;
		this.mappingExpression = this.data.mappingExpression;
		this.sourceRecordIdArr = this.data.sourceRecordIdArr;
		this.targetRecordIdArr = this.data.targetRecordIdArr;

		this.fieldKey = this.data.fieldKey;
		this.targetFieldPath = this.data.clickFieldInfo.id ? this.data.clickFieldInfo.id : '\\';
		this.fieldType = this.data.clickFieldInfo.fieldType;
		this.fieldLength = this.data.clickFieldInfo.fieldLength;
		this.arrayType = this.data.clickFieldInfo.arrayType;

		// set format fieldType
		let formatFieldType = ``;

		if ('R' === this.fieldType) {
			formatFieldType += `Group`;
		} else {
			const fieldTypeTranslateObj = {
				B: 'Byte',
				S: 'Short',
				I: 'Int',
				L: 'Long',
				F: 'Float',
				D: 'Double',
				T: 'String',
				N: 'Numeric',
				P: 'Decimal',
				A: 'Raw',
				p: 'Timestamp',
				b: 'Boolean',
				R: 'Record',
				v: 'Individual'
			};

			formatFieldType += `${fieldTypeTranslateObj[this.fieldType]}(${this.fieldLength})`;

			if ('N' != this.arrayType) formatFieldType += `[${this.arrayType}]`;
		}

		if (this.targetFieldPath && this.fieldType && formatFieldType && this.data.clickFieldInfo.fieldName)
			this.fieldInfoLabel = `${this.$t('label.field.info')}: 
				${this.targetFieldPath} ${formatFieldType} ${this.data.clickFieldInfo.fieldName.replace(/(\s*)/g, '')}`;
	},
	methods: {
		openReferenceModal() {
			const _this = this;

			const modalInstance = this.$openModal({
				title: `Mapping Expression`,
				fullscreen: true,
				contentList: [
					{
						template: `<reference :pageType="pageType" :mappingExpression="mappingExpression" :className="className" :targetFieldType="targetFieldType" :sourceRecordIdArr="sourceRecordIdArr" :targetRecordIdArr="targetRecordIdArr" :fieldInfoLabel="fieldInfoLabel" />`,
						components: {
							reference: require('@/views/modal/mapping/ruleDetail/reference.vue').default
						},
						data() {
							return {
								pageType: `mapping`,
								mappingExpression: _this.mappingExpression ? _this.mappingExpression : null,
								className: convertTypeToClass(_this.fieldType),
								sourceRecordIdArr: _this.sourceRecordIdArr,
								targetRecordIdArr: _this.targetRecordIdArr,
								targetFieldType: _this.fieldType,
								fieldInfoLabel: _this.fieldInfoLabel
							};
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" class="btn btn-primary" @click="click"> ${this.$t(`label.ok`)} </button>`,
						methods: {
							async click() {
								const { expression, isValidation } = await modalInstance.$refs.content[0].$children[0].getData();

								if (!isValidation) return;

								const { tabList, selectedIdx } = modalInstance.$refs.content[0].$children[0];

								const selectedTabInfo = [`/constant`, `/record`].find(url => url === tabList[selectedIdx].url);

								_this.simpleMappingYn = `undefined` !== typeof selectedTabInfo ? `Y` : `N`;

								_this.mappingExpression = expression;

								_this.arrayReference = null;

								const mappingExpressionArr = _this.mappingExpression.split(`\\`);

								if (0 < mappingExpressionArr.filter(expression => 1 < expression.split(`[i`).length).length) {
									const arraySizeParameterArr = [];

									mappingExpressionArr.forEach(expression => {
										arraySizeParameterArr.push(1 < expression.split(`[i`).length ? `${expression.split(`[i`)[0]}[]` : expression);
									});

									arraySizeParameterArr.pop();

									_this.arrayReference = arraySizeParameterArr.join(`\\`);
								}

								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		openArrayReferenceModal() {
			const _this = this;

			const modalInstance = this.$openModal({
				title: `Array Reference`,
				fullscreen: true,
				contentList: [
					{
						template: `<record id="recordModal" :detailInfo="detailInfo" :sourceRecordIdArr="sourceRecordIdArr" :targetRecordIdArr="targetRecordIdArr" />`,
						components: {
							record: require('@/views/modal/mapping/ruleDetail/record.vue').default
						},
						data() {
							return {
								sourceRecordIdArr: _this.sourceRecordIdArr,
								targetRecordIdArr: _this.targetRecordIdArr,
								detailInfo: {
									arrayReference: _this.arrayReference,
									gridClick: function (evt) {
										if ('undefined' === typeof evt.rowKey || `rowHeader` === evt.targetType) return;

										const { path } = this.$refs.tuiGrid.invoke(`getRow`, evt.rowKey);

										this.selectedPath = !path || !path.endsWith(`]`) ? null : path;
									},
									resize: function () {
										if (!this.$refs.tuiGrid) return;

										this.$nextTick(() => {
											setTimeout(() => {
												const modalBody = document.querySelector(`#recordModal`).parentNode;

												const adjustWidth = modalBody.clientWidth - 48 - 30;

												const adjustHeight = modalBody.clientHeight - 32 - 56 - 56 - 24 - 32;

												this.$refs.tuiGrid.invoke('setWidth', adjustWidth);
												this.$refs.tuiGrid.invoke('setHeight', adjustHeight);
											}, 0);
										});
									}
								}
							};
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" class="btn btn-primary" @click="click"> ${this.$t(`label.ok`)} </button>`,
						methods: {
							click() {
								_this.arrayReference = modalInstance.$refs.content[0].$children[0].getData();
								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		input(evt) {
			this.arrayReference = evt.target.value.replace(new RegExp(this.regExp, 'g'), '');
			this.arrayReferenceCurrLength = this.arrayReference.length;
		},
		getData() {
			return {
				simpleMappingYn: this.simpleMappingYn,
				mappingExpression: this.mappingExpression,
				arrayReference: this.arrayReference
			};
		}
	}
};
</script>
