<template>
	<div class="propertyTab">
		<label class="control-label warningLabel" v-if="!readonly">
			<span>{{ $t(`msg.system.property.warn`) }}</span>
		</label>
		<div class="form-table form-table-responsive">
			<div class="form-table-wrap">
				<div class="form-table-head">
					<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('label.insert')">
						<i class="icon-plus-circle"></i>
					</button>
					<label class="col">{{ this.$t('label.property.type') }}</label>
					<label class="col">{{ this.$t('label.id') }}</label>
					<label class="col">{{ this.$t('label.name') }}</label>
					<label class="col">{{ this.$t('label.desc') }}</label>
					<label class="col">{{ this.$t('label.system.property') }}</label>
					<label class="col">{{ this.$t('label.interface.type') }}</label>
				</div>
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('label.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div style="width: 100%">
							<select
								v-model="propertyObj.propertyType"
								class="form-control view-disabled"
								:readonly="readonly"
								@change="propertyTypeChange(propertyObj)"
							>
								<option
									v-for="(propertyType, propertyTypeIdx) in propertyTypeList"
									:key="`${bodyIndex}_${propertyTypeIdx}`"
									:value="propertyType.value"
									v-text="propertyType.name"
								></option>
							</select>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<input
								type="text"
								:list="`pk.keyId_${bodyIndex}`"
								:class="{
									'form-control': readonly,
									'regExp-dataList': !readonly,
									'view-disabled': true
								}"
								:readonly="readonly"
								:maxlength="maxLengthCnt[`pk.keyId`]"
								v-model="propertyObj[`pk.keyId`]"
								@change="keyIdChange(propertyObj)"
								@input="inputEvt($event, bodyIndex, `pk.keyId`)"
							/>
							<datalist :id="`pk.keyId_${bodyIndex}`">
								<option
									v-for="(option, keyIdIndex) in getKeyIdList(propertyObj.propertyType)"
									:key="`keyId_${bodyIndex}_${keyIdIndex}`"
									:value="option.pk.propertyKey"
									v-text="option.pk.propertyKey"
								></option>
							</datalist>
							<span class="letterLength" v-if="!readonly">
								({{ propertyObj[`pk.keyId`] ? propertyObj[`pk.keyId`].length : 0 }} / {{ maxLengthCnt[`pk.keyId`] }})
							</span>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<input
								type="text"
								v-model="propertyObj[`name`]"
								:class="{
									'form-control': readonly,
									'regExp-dataList': !readonly,
									'view-disabled': true
								}"
								:readonly="readonly"
								:maxlength="maxLengthCnt.name"
								@input="inputEvt($event, bodyIndex, `name`)"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ propertyObj[`name`] ? propertyObj[`name`].length : 0 }} / {{ maxLengthCnt.name }})
							</span>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<input
								type="text"
								v-model="propertyObj[`description`]"
								:class="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true
								}"
								:readonly="readonly"
								:maxlength="maxLengthCnt.description"
								@input="inputEvt($event, bodyIndex, `description`)"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ propertyObj[`description`] ? propertyObj[`description`].length : 0 }} / {{ maxLengthCnt.description }})
							</span>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<input
								type="text"
								v-model="propertyObj[`property`]"
								:class="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true
								}"
								:readonly="readonly"
								:maxlength="maxLengthCnt.property"
								@input="inputEvt($event, bodyIndex, `property`)"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ propertyObj[`property`] ? propertyObj[`property`].length : 0 }} / {{ maxLengthCnt.property }})
							</span>
						</div>
					</div>
					<div class="col">
						<div style="width: 100%">
							<select v-model="propertyObj.interfaceType" class="form-control view-disabled" :readonly="readonly">
								<option value=""></option>
								<option
									v-for="(interfaceType, interfaceTypeIdx) in interfaceTypeList"
									:key="`${bodyIndex}_${interfaceTypeIdx}`"
									:value="interfaceType.pk.propertyKey"
									v-text="interfaceType.propertyValue"
								></option>
							</select>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import { HttpReq, parseFlattenObj, getUUID, getRegExpInfo, parseHierarchyObj } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: `systemProperties`,
	extends: DetailProperty,
	data() {
		return {
			interfaceTypeList: [],
			propertyTypeList: [
				{ name: 'Interface', value: 'I' },
				{ name: 'Service', value: 'S' },
				{ name: 'Both', value: 'B' }
			],
			interfaceKeyIdList: [],
			serviceKeyIdList: [],
			bothKeyIdList: [],
			regExpInfo: {
				'pk.keyId': 'id',
				name: 'name',
				description: 'desc',
				property: 'value'
			},
			curLengthCnt: {},
			maxLengthCnt: {}
		};
	},
	async created() {
		const result = await new HttpReq(`/api/page/properties`).read({ pk: { propertyId: 'Interface.Type' }, orderByKey: true });

		this.interfaceTypeList = result.data.object;

		const promiseList = [];

		this.propertyTypeList.forEach(info => {
			if (`B` === info.value) return;

			promiseList.push(
				new Promise(resolve => {
					(async () => {
						const result = await new HttpReq(`/api/page/properties`).read({
							pk: {
								propertyId: `Property.${info.name}`
							},
							orderByKey: true
						});

						resolve({
							propertyId: `Property.${info.name}`,
							dataArr: result.data.object
						});
					})();
				})
			);
		});

		(await Promise.all(promiseList)).forEach(value => {
			if (`Property.Interface` === value.propertyId) this.interfaceKeyIdList = value.dataArr;
			else if (`Property.Service` === value.propertyId) this.serviceKeyIdList = value.dataArr;
		});

		this.bothKeyIdList = [...cloneDeep(this.interfaceKeyIdList), ...cloneDeep(this.serviceKeyIdList)];

		for (const [key, value] of Object.entries(this.regExpInfo)) {
			this.maxLengthCnt[key] = getRegExpInfo(value).maxLength;
		}
	},
	methods: {
		getKeyIdList(type) {
			let keyIdList = [];

			if (`I` === type) keyIdList = this.interfaceKeyIdList;
			else if (`S` === type) keyIdList = this.serviceKeyIdList;
			else if (`B` === type) keyIdList = this.bothKeyIdList;

			return keyIdList;
		},
		inputEvt(event, bodyIndex, key) {
			this.propertyList[bodyIndex][key] = event.target.value;
			this.propertyList[bodyIndex][key] = this.propertyList[bodyIndex][key].replace(
				new RegExp(getRegExpInfo(this.regExpInfo[key] ? this.regExpInfo[key] : 'default').regExp, `g`),
				``
			);
		},
		addProperty() {
			this.propertyList.push({
				idx: getUUID(),
				'pk.keyId': null,
				name: null,
				description: null,
				property: null,
				interfaceType: null,
				propertyType: null
			});
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);
		},
		async setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			this.propertyList = [...this.detailData[`systemProperties`]];

			this.propertyList.forEach(propertyInfo => {
				propertyInfo.idx = getUUID();
			});

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		getData() {
			return {
				systemProperties: cloneDeep(this.propertyList).map(property => {
					delete property.id;
					return parseHierarchyObj(property);
				})
			};
		},
		async propertyTypeChange(propertyObj) {
			propertyObj['pk.keyId'] = null;
			propertyObj['name'] = null;
			propertyObj['description'] = null;
			propertyObj['property'] = null;
		},
		keyIdChange(propertyObj) {
			const info = this.getKeyIdList(propertyObj.propertyType).find(info => info.pk.propertyKey === propertyObj['pk.keyId']);

			if (!info) return;

			propertyObj['pk.keyId'] = info.pk.propertyKey;
			propertyObj['name'] = info.pk.propertyKey;
			propertyObj['description'] = info.propertyDesc;
			propertyObj['property'] = info.propertyValue;
		}
	}
};
</script>
