<template>
	<ul :id="rootId" :class="rootClass">
		<li :class="{ 'menu-item': menuInfo.menuUrl ? true : false }" v-for="(menuInfo, idx) in this.rootMenuInfo.subMenus" :key="idx">
			<span v-if="menuInfo.menuUrl ? true : false">
				<a
					:class="{ 'menu-link': true, active: -1 < selectedMenuPathIdList.findIndex(menuId => menuId === menuInfo.menuId) }"
					href="javascript:void(0);"
					:id="menuInfo.menuId"
					:ref="menuInfo.menuId"
					@click="
						loadContent([
							{
								orgMenuId: menuInfo.menuId,
								menuId: getOrgMenuId(menuInfo.menuId),
								menuName: menuInfo.menuName,
								openWindow: menuInfo.openWindow
							}
						])
					"
				>
					<i class="icon-home" v-if="-1 < menuInfo.menuId.indexOf('home')" />
					<img :src="getMenuIcon(menuInfo.menuIcon)" v-else-if="getMenuIcon(menuInfo.menuIcon)" />
					<i class="icon-lock" v-else />
					{{ menuInfo.menuName }}
				</a>
				<button
					:class="{
						'menu-bookmark': true,
						'icon-bookmark': true,
						active: -1 < bookMarkMenuList.findIndex(info => getOrgMenuId(info.menuId) === getOrgMenuId(menuInfo.menuId))
					}"
					@click="
						setMenuBookmark({
							menuId: getOrgMenuId(menuInfo.menuId),
							isDelete: -1 < bookMarkMenuList.findIndex(info => getOrgMenuId(info.menuId) === getOrgMenuId(menuInfo.menuId))
						})
					"
					v-if="!menuInfo.isNotRequireFavorites"
				></button>
			</span>
			<span v-else>
				<a
					class="menu-collapse-link collapsed"
					:href="`#menu_${menuInfo.menuId}`"
					:ref="menuInfo.menuId"
					data-window="N"
					data-toggle="collapse"
					aria-expanded="false"
				>
					{{ menuInfo.menuName }}
					<i class="icon-down"></i>
				</a>
				<Menu
					:rootId="`menu_${menuInfo.menuId}`"
					:rootMenuInfo="menuInfo"
					:rootClass="{ 'menu-collapse': true, collapse: true }"
					:selectedMenuPathIdList="selectedMenuPathIdList"
					:bookMarkMenuList="bookMarkMenuList"
					@loadContent="loadContent"
					@setMenuBookmark="setMenuBookmark"
				/>
			</span>
		</li>
	</ul>
</template>

<script>
import { getHttpUrl } from '@/utils/common.js';

export default {
	name: 'Menu',
	props: {
		rootId: String,
		rootMenuInfo: Object,
		rootClass: Object,
		bookMarkMenuList: {
			type: Array,
			default: () => []
		},
		selectedMenuPathIdList: {
			type: Array,
			default: () => []
		}
	},
	mounted() {
		for (const menuId of this.selectedMenuPathIdList) {
			if (!this.$refs[menuId]) continue;
			this.$refs[menuId][0].click();
		}
	},
	methods: {
		loadContent(menuInfoArr) {
			if (this.rootMenuInfo.menuId) {
				menuInfoArr.unshift({
					orgMenuId: this.rootMenuInfo.menuId,
					menuId: this.getOrgMenuId(this.rootMenuInfo.menuId),
					menuName: this.rootMenuInfo.menuName
				});
			}

			this.$emit('loadContent', menuInfoArr);
		},
		getOrgMenuId(menuId) {
			return menuId.split(`_`)[0];
		},
		setMenuBookmark(info) {
			this.$emit('setMenuBookmark', info);
		},
		getMenuIcon(menuIconUrl) {
			try {
				return getHttpUrl(`/img/menu/${menuIconUrl}`);
			} catch (error) {
				return null;
			}
		}
	}
};
</script>
