<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.id') }} </span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`id`])">
							{{ `(${curLengthCnt['id']}/${maxLengthCnt['id']})` }}
						</span>
						<b class="icon-star"></b>
					</label>
					<inputText
						ref="id"
						vModel="id"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
						:readonly="readonly || getReadonly(regExpInfo[`id`])"
						regExpType="id"
						@setLengthCnt="setLengthCnt"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.name') }}</span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`name`])">
							{{ `(${curLengthCnt['name']}/${maxLengthCnt['name']})` }}
						</span>
					</label>
					<inputText
						ref="name"
						vModel="name"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
						:readonly="readonly || getReadonly(regExpInfo[`name`])"
						regExpType="name"
						@setLengthCnt="setLengthCnt"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.privilege.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<select
						ref="privilegeId"
						v-model="privilegeId"
						class="form-control view-disabled"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
					>
						<option v-for="option in privilegeTypeList" :key="option.privilegeId" :value="option.privilegeId" :disabled="option.disabled">
							{{ option.privilegeId }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.privateYn') }}</span>
						<b class="icon-star"></b>
					</label>
					<selectBox
						ref="privateYn"
						vModel="privateYn"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: `/api/page/properties`,
							params: {
								pk: {
									propertyId: 'List.Yn'
								},
								orderByKey: true
							},
							optionListName: 'openWindowList',
							optionFor: 'option in openWindowList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}"
					/>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.type') }}</span>
						<b class="icon-star"></b>
					</label>
					<select
						ref="recordType"
						v-model="recordType"
						class="form-control view-disabled"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
					>
						<option v-for="(recordTypeObj, index) in recordTypeArr" :key="index" :value="recordTypeObj.pk.propertyKey">
							{{ recordTypeObj.propertyValue }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.metaDomain') }}</span>
					</label>
					<selectBox
						ref="metaDomain"
						vModel="metaDomain"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: `/api/entity/fieldMeta/group/search`,
							params: null,
							optionListName: 'openWindowList',
							optionFor: 'option in openWindowList',
							optionValue: 'option',
							optionText: 'option'
						}"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.useyn') }}</span>
					</label>
					<selectBox
						ref="useYn"
						vModel="useYn"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="`add` === mode ? true : readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: `/api/page/properties`,
							params: {
								pk: {
									propertyId: 'List.Yn'
								},
								orderByKey: true
							},
							optionListName: 'openWindowList',
							optionFor: 'option in openWindowList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}"
					/>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.desc') }}</span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`description`])">
							{{ `(${curLengthCnt['description']}/${maxLengthCnt['description']})` }}
						</span>
					</label>
					<textArea
						ref="description"
						vModel="description"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly(regExpInfo[`description`])"
						:height="'130px'"
						regExpType="desc"
						@setLengthCnt="setLengthCnt"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import merge from 'lodash/merge';
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { parseHierarchyObj, HttpReq, getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'ModelRecordDetailBasic',
	extends: DetailBasic,
	data() {
		return {
			privilegeTypeList: [],
			privilegeId: null,
			recordTypeArr: [],
			recordType: null,

			keyArr: [
				'id',
				'name',
				'privilegeId',
				'useYn',
				'privateYn',
				'metaDomain',
				'recordType',
				'publishTimestamp',
				'publishStatus',
				'publishMessage',
				'description'
			],

			regExpInfo: {
				id: {
					regExpType: 'id',
					isPK: true,
					readonly: false
				},
				name: {
					regExpType: 'name',
					isPK: false,
					readonly: false
				},
				description: {
					regExpType: 'desc',
					isPK: false,
					readonly: false
				}
			}
		};
	},
	async created() {
		let httpReq = new HttpReq(`/api/page/properties`);

		let result = await httpReq.read({ pk: { propertyId: 'List.Record.Type' }, orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.recordTypeArr = result.data.object.filter(
			info => `E` !== info.pk.propertyKey && `I` !== info.pk.propertyKey && `c` !== info.pk.propertyKey
		);

		httpReq = new HttpReq(`/api/entity/privilege/search`);

		result = await httpReq.read({
			object: {
				privilegeType: `b`
			},
			limit: null,
			next: null,
			reverseOrder: false
		});

		if (`ok` !== result.data.result) return;

		const privilegeTypeList = result.data.object;

		const bTotalUserPrivileges = this.userInfo.totalUserPrivileges.filter(userPrivilege => `b` === userPrivilege.privilegeType);

		for (const info of privilegeTypeList) {
			if (this.isUserAdmin) {
				info.disabled = false;
			} else {
				info.disabled = `undefined` === typeof bTotalUserPrivileges.find(userPrivilege => userPrivilege.privilegeId == info.privilegeId);
			}
		}

		this.privilegeTypeList = privilegeTypeList;
	},
	computed: {
		...mapState(['userInfo', 'regExpTypeList']),
		...mapGetters(['isUserAdmin'])
	},
	watch: {
		recordType() {
			this.$emit('changeRecordType', this.recordType);
		}
	},
	methods: {
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;

				if ('undefined' !== typeof this.detailData[key] && null !== this.detailData[key]) {
					detailDataFormalize[key] = String(this.detailData[key]);
				} else {
					if (`useYn` === key) detailDataFormalize[key] = `Y`;
					else if (`privateYn` === key) detailDataFormalize[key] = `N`;
					else detailDataFormalize[key] = null;
				}

				const regExpType = !Object.keys(this.regExpInfo).includes(key) ? 'default' : this.regExpInfo[key].regExpType;

				this.setLengthCnt({ [key]: 0 });
				this.maxLengthCnt[key] = getRegExpInfo(regExpType).maxLength;
			});

			for (const key in this.$refs) {
				if (!this.$refs[key].setData) continue;
				this.$refs[key].setData(detailDataFormalize[this.$refs[key]._props.vModel]);
			}

			this.privilegeId = detailDataFormalize.privilegeId;
			this.recordType = detailDataFormalize.recordType;
		},
		getData() {
			let rtnObj = {
				publishStatus: this.detailData.publishStatus
			};

			for (const key in this.$refs) {
				if ('recordType' === key) rtnObj.recordType = this.recordType;
				else if (`privilegeId` === key) rtnObj.privilegeId = this.privilegeId;
				else rtnObj = merge(rtnObj, this.$refs[key].getData());
			}

			return parseHierarchyObj(rtnObj);
		}
	}
};
</script>
