<template>
	<div id="modelRecordModal">
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 0 === selectedIdx }"
					@click="changeTab(0)"
					v-text="$t('label.basicInfo')"
				></a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 1 === selectedIdx }"
					@click="changeTab(1)"
					v-text="$t('label.detailInfo')"
				></a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 2 === selectedIdx }"
					@click="changeTab(2)"
					v-text="$t('label.extendInfo')"
				></a>
			</li>
		</ul>

		<div class="panel-body tab-content" style="padding-bottom: 0px">
			<div :class="{ [mode]: true, 'tab-pane': true, active: 0 === selectedIdx }">
				<DetailBasic ref="basicInfo" :detailInfo="basicInfo" :detailData="detailData" :mode="mode" />
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 1 === selectedIdx }" :style="detailInfoStyle">
				<component
					ref="detailInfo"
					:is="require(`@/views/modal${detailInfo.url}.vue`).default"
					:detailInfo="detailInfo"
					:detailData="recordObjData"
					recordType="I"
					:mode="mode"
				/>
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 2 === selectedIdx }">
				<component
					ref="extendInfo"
					:is="require(`@/views/detail${extendInfo.url}.vue`).default"
					:detailInfo="extendInfo"
					:detailData="recordObjData"
					:mode="mode"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
import { HttpReq } from '@/utils/common.js';
import DetailBasic from '@/components/detail/DetailBasic.vue';

export default {
	name: 'RecordModal',
	props: {
		cmd: String,
		mode: String,
		config: Object
	},
	components: {
		DetailBasic
	},
	data() {
		return {
			modalType: this.config.modalType,
			modalData: this.config.modalData,

			selectedIdx: 1,
			modalMode: 'detail',
			namingRuleHttp: new HttpReq(`/api/page/properties`),
			namingRules: [],
			basicInfo: {},
			detailInfo: { url: '/modelRecord/DetailInfo', label: this.$t('label.detailInfo') },
			extendInfo: { url: '/modelRecord/ExtendInfo', label: this.$t('label.extendInfo') },
			detailData: {},
			recordObjData: {}
		};
	},
	methods: {
		changeTab(idx) {
			this.selectedIdx = idx;

			if (1 === idx) {
				setTimeout(() => {
					this.$refs.detailInfo.resize();
				}, 0);
			}
		},
		namingFormatter(key) {
			return this.namingRules.filter(property => key === property.pk.propertyKey).map(property => property.propertyValue)[0];
		},
		getData() {
			let tabAllData = merge(tabAllData, this.$refs.basicInfo.getData());
			tabAllData = merge(tabAllData, this.$refs.detailInfo.getData());
			tabAllData = merge(tabAllData, this.$refs.extendInfo.getData());

			if (this.recordObjData.publishStatus) tabAllData.publishStatus = this.recordObjData.publishStatus;

			return tabAllData;
		}
	},
	computed: {
		detailInfoStyle() {
			return { 'margin-top': `${'detail' === this.mode || 'done' === this.mode ? '0px' : '-1.5rem'}` };
		},
		namingKeys() {
			let keys;

			switch (this.modalType) {
				case 'SI':
					keys = { id: 'ServiceRequestRecordID', name: 'ServiceRequestRecordName' };
					break;
				case 'SO':
					keys = { id: 'ServiceResponseRecordID', name: 'ServiceResponseRecordName' };
					break;
				case 'II':
					keys = { id: 'InterfaceRequestRecordID', name: 'InterfaceRequestRecordName' };
					break;
				case 'IO':
					keys = { id: 'InterfaceResponseRecordID', name: 'InterfaceResponseRecordName' };
					break;
			}

			return {
				id: this.namingFormatter(keys.id),
				name: this.namingFormatter(keys.name)
			};
		}
	},
	async created() {
		const result = await this.namingRuleHttp.read({ pk: { propertyId: 'ImgOption.NamingRules' }, orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.namingRules = result.data.object;

		this.recordObjData = this.modalData.recordObject ? this.modalData.recordObject : {};

		this.basicInfo = {
			type: 'basic',
			label: this.$t('label.basicInfo'),
			content: [
				[
					[
						{
							type: 'select',
							vModel: 'recordType',
							label: `${this.$t('label.record')} ${this.$t('label.type')}`,
							optionInfo: {
								url: `/api/page/properties`,
								params: {
									pk: {
										propertyId: 'List.Record.Type'
									},
									orderByKey: true
								},
								optionListName: 'recordTypeList',
								optionFor: 'option in recordTypeList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							},
							formatter: () => {
								return !isEmpty(this.recordObjData) && this.recordObjData.recordType ? this.recordObjData.recordType : 'I';
							},
							readonly: true
						},
						{
							type: 'text',
							vModel: 'id',
							label: `${this.$t('label.record')} ${this.$t('label.id')}`,
							formatter: () => {
								if (
									!isEmpty(this.recordObjData) &&
									(this.recordObjData.id || (this.recordObjData.recordType && 'I' !== this.recordObjData.recordType))
								) {
									return this.recordObjData.id;
								}

								return this.namingKeys.id.replace('{0}', this.modalData.id);
							},
							readonly: true
						},
						{
							type: 'text',
							vModel: 'privilegeId',
							label: this.$t('label.privilege'),
							readonly: true
						},
						{
							type: 'select',
							vModel: 'useYn',
							label: this.$t('label.useyn'),
							val: 'Y',
							optionInfo: {
								url: `/api/page/properties`,
								params: {
									pk: {
										propertyId: 'List.Yn'
									},
									orderByKey: true
								},
								optionListName: 'ynList',
								optionFor: 'option in ynList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							},
							readonly: `create` === this.cmd || `copy` === this.cmd
						}
					],
					[
						{
							type: 'text',
							vModel: 'name',
							label: `${this.$t('label.record')} ${this.$t('label.name')}`,
							formatter: () => {
								if (
									!isEmpty(this.recordObjData) &&
									(this.recordObjData.name || (this.recordObjData.recordType && 'I' !== this.recordObjData.recordType))
								) {
									return this.recordObjData.name;
								}

								return this.namingKeys.name.replace('{0}', this.modalData.id);
							},
							readonly: false
						},
						{
							type: 'select',
							vModel: 'privateYn',
							label: this.$t('label.privateYn'),
							optionInfo: {
								url: `/api/page/properties`,
								params: {
									pk: {
										propertyId: 'List.Yn'
									},
									orderByKey: true
								},
								optionListName: 'openWindowList',
								optionFor: 'option in openWindowList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							},
							readonly: true
						},
						{
							type: 'select',
							vModel: 'metaDomain',
							label: this.$t('label.metaDomain'),
							optionInfo: {
								url: `/api/entity/fieldMeta/group/search`,
								optionListName: 'metaDomainList',
								optionFor: 'option in metaDomainList',
								optionValue: 'option',
								optionText: 'option'
							},
							readonly: true
						}
					]
				],
				[
					[
						{
							type: 'textarea',
							vModel: 'description',
							label: this.$t('label.desc'),
							readonly: false,
							formatter: () => {
								return !isEmpty(this.recordObjData) && this.recordObjData.description ? this.recordObjData.description : null;
							}
						}
					]
				]
			]
		};

		this.$nextTick(() => {
			this.detailData = `create` === this.cmd || `copy` === this.cmd ? cloneDeep(this.modalData) : cloneDeep(this.modalData.recordObject);
		});
	}
};
</script>
<style lang="scss">
.modal-fullscreen {
	.panel-body {
		.form-table-wrap {
			max-height: calc(100vh - 72px - 14rem);
		}
	}
}
</style>
