<template>
	<div style="height: 100%">
		<div class="row frm-row" style="height: 100%">
			<div class="col-lg-12">
				<div class="form-group" style="height: 100%">
					<label class="control-label">
						<span> XML </span>
					</label>
					<div class="detail-content-common" style="padding-left: 0px; height: calc(100% - 24px)">
						<textarea
							maxlength="100"
							class="form-control view-disabled"
							readonly="readonly"
							style="height: 100%"
							v-text="xmlStr"
						></textarea>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { HttpReq } from '@/utils/common.js';
export default {
	name: 'publishLogXML',
	props: {
		detailData: Object
	},
	data() {
		return {
			xmlStr: null
		};
	},
	watch: {
		async detailData() {
			if (!this.detailData[`pk.publishDateTime`]) return;

			if (!this.detailData[`pk.publishId`]) return;

			const res = await new HttpReq(`/api/entity/publishLog/context`).read(this.detailData);

			this.xmlStr = `ok` === res.data.result ? res.data.object : ``;
		}
	}
};
</script>
