<template>
	<div :class="classObj" :style="styleObj">
		<Search
			:list="searchList"
			searchType="mainBasic"
			@search="
				searchObj => {
					$refs.detail.closePanel();
					search(searchObj);
				}
			"
			ref="search"
		/>
		<Main
			:buttonList="buttonList"
			:gridInfo="gridInfo"
			:gridSearchObj="gridSearchObj"
			:gridType="gridType"
			:viewer="viewer"
			:editor="editor"
			:startTimerSeconds="startTimerSeconds"
			@rowClicked="rowClicked"
			@initialize="initialize"
			@add="add"
			@newTab="newTab"
			@importExcel="importExcel"
			@migration="migration"
			@refresh="refresh"
			@download="download"
			ref="main"
		/>
		<Detail
			v-show="isDetailView && 0 < detailTabList.length"
			:mode="detailMode"
			:pk="detailPk"
			:buttonList="detailButtonList"
			:detailTabList="detailTabList"
			:info="detailInfo"
			:cudUrl="cudUrl"
			:publishUrl="publishUrl"
			:viewer="viewer"
			:editor="editor"
			@search="
				() => {
					search($refs.search.getSearchObj());
				}
			"
			@detailHorizonResize="detailHorizonResize"
			ref="detail"
		/>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import merge from 'lodash/merge';
import cloneDeep from 'lodash/cloneDeep';
import Search from '@/components/Search.vue';
import Main from '@/components/Main.vue';
import Detail from '@/components/detail/Detail.vue';
import moment from 'moment';
import { openNewTab, getHttpUrl, HttpReq, WindowResizeEventBus, getNumFromStr, getCurrentMenuId } from '@/utils/common.js';
export default {
	name: 'Basic',
	components: {
		Search,
		Main,
		Detail
	},
	props: {
		pageInfo: {
			type: Object,
			default: () => ({})
		}
	},
	data() {
		return {
			cudUrl: null,

			//auth
			viewer: false,
			editor: false,

			//Search
			searchList: [],

			//Button
			buttonList: [],

			// Grid
			gridInfo: {},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: [],
			detailButtonList: [],
			detailTabList: [],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			publishUrl: null,

			classObj: {
				basic: true,
				horizon: false
			},
			styleObj: {
				width: null
			},

			startTimerSeconds: null
		};
	},
	mounted() {
		this.initBasicInfo();
	},
	computed: {
		...mapState([`minPageSize`])
	},
	methods: {
		initBasicInfo() {
			if ('basic' !== this.pageInfo.type) return;

			//initComponent
			this.cudUrl = null;
			this.viewer = false;
			this.editor = false;
			this.searchList = [];
			this.buttonList = [];
			this.gridInfo = {};
			this.detailPk = [];
			this.detailButtonList = [];
			this.detailTabList = [];
			this.publishUrl = null;
			this.isDetailView = false;
			this.$refs.main.isGridView = false;

			//initPageInfo
			const { cudUrl, search, button, grid, detail, viewer, editor } = this.pageInfo;

			// url
			this.cudUrl = cudUrl;

			//auth
			this.viewer = viewer;
			this.editor = editor;

			//Search
			this.searchList = search.list;

			//Button
			this.buttonList = button.list;

			//Grid
			this.gridInfo = grid;

			//Detail
			if (detail) {
				this.detailPk = detail.pk ? detail.pk : [];
				this.detailButtonList = detail.button ? detail.button.list : [];
				this.detailTabList = detail.tabList ? detail.tabList : [];
				this.publishUrl = detail.publishUrl;
			}

			if (localStorage.getItem('searchObj')) {
				this.initNewTabSearchObj();
			} else if (search.load) {
				this.$nextTick(() => {
					this.search(this.$refs.search.getSearchObj());
				});
			}

			if (localStorage.getItem('detailObj')) {
				this.$nextTick(() => {
					const detailObj = JSON.parse(localStorage.getItem(`detailObj`));
					detailObj.isExpandDetailView = true;

					localStorage.removeItem(`detailObj`);

					this.initDetail('detail', detailObj);
				});
			}
		},
		add() {
			this.initDetail('add');
		},
		initialize() {
			this.$refs.search.initialize();
		},
		newTab() {
			const searchObjStr = JSON.stringify(this.$refs.search.getSearchObj());

			openNewTab(getCurrentMenuId(), () => {
				localStorage.removeItem(`searchObj`);
				localStorage.setItem(`searchObj`, searchObjStr);
			});
		},
		async importExcel() {
			const importUrl = this.buttonList.find(btn => 'importExcel' === btn.id).importUrl;
			const searchFunc = this.$refs.search.search;

			const modalInstane = this.$openModal({
				title: this.$t('label.excel.upload'),
				modalWidth: '600px',
				modalHeight: '180px',
				contentList: [
					{
						template: `<div class="input-group">
								       <input class="form-control" type="text" v-model="fileName" placeholder="${this.$t('msg.select.file')}" readonly/>
									   <button type="button" class="btn" @click="selectFile" style="margin-left: 5px; margin-right: 5px;">
									       ${this.$t('label.file.select')}
									   </button>
									   <button type="button" class="btn btn-primary" @click="uploadFile">
									       ${this.$t('label.upload')}
									   </button>
									</div>`,
						data() {
							return {
								fileInfo: null,
								fileName: null
							};
						},
						methods: {
							selectFile() {
								const fileInput = document.createElement('input');
								fileInput.setAttribute('type', 'file');
								fileInput.addEventListener('change', evt => {
									const fileName = evt.target.files[0].name;
									if ('xlsx' != fileName.substring(fileName.length - 4)) {
										this.$alert({ type: 'warn', message: this.$t('label.xlsx.file.format.only') });
										return;
									}
									this.fileInfo = evt.target.files[0];
									this.fileName = fileName;
								});

								fileInput.click();
							},
							async uploadFile() {
								if (!this.fileName) {
									this.$alert({ type: 'warn', message: this.$t('msg.select.file') });
									return;
								}

								this.$startSpinner();

								const req = new XMLHttpRequest();
								req.open('POST', getHttpUrl(importUrl), true);

								req.setRequestHeader(`Authorization`, localStorage.getItem('accessToken'));
								req.setRequestHeader(`X-iManager-Method`, `POST`);

								req.withCredentials = true;

								const formData = new FormData();
								formData.enctype = `multipart/form-data`;
								formData.append(`uploadFile`, this.fileInfo);

								req.send(formData);

								req.onload = event => {
									this.$stopSpinner();

									if (event.target.status != '200') {
										this.$alert({ type: 'warn', message: this.$t(`msg.alert.fail`) });
										return;
									}

									this.$alert({ type: 'normal', message: this.$t('msg.alert.success') });

									searchFunc();

									modalInstane.hideModal();
								};
							},
							getData() {
								return this.fileInfo;
							}
						}
					}
				]
			});
		},

		async migration(migrationList) {
			migrationList = migrationList
				.filter(row => 'C' !== row.migrationStatus)
				.map(rowInfo => ({
					publishDateTime: rowInfo['pk.publishDateTime'],
					publishId: rowInfo['pk.publishId']
				}));

			if (0 === migrationList.length) {
				this.$alert({ type: 'warn', message: this.$t('label.migration.selectError') });
				return;
			}

			const result = await new HttpReq('/api/entity/publishLog/migration/send').create({ publishLogPKs: migrationList });

			this.$alert({
				type: `ok` === result.data.result ? `normal` : `warn`,
				message: this.$t(`ok` === result.data.result ? `msg.alert.success` : `msg.alert.fail`),
				callback: () => {
					this.search(cloneDeep(this.gridSearchObj));
				}
			});
		},
		search(searchObj) {
			this.gridSearchObj = searchObj;
		},
		refresh() {
			this.search(cloneDeep(this.gridSearchObj));
		},
		download() {
			const downloadBtnInfo = this.buttonList.find(btn => 'download' === btn.id);

			if (!downloadBtnInfo) return;

			this.$startSpinner();

			const req = new XMLHttpRequest();
			req.open('POST', getHttpUrl(downloadBtnInfo.downloadUrl), true);

			req.setRequestHeader(`Authorization`, localStorage.getItem('accessToken'));
			req.setRequestHeader(`X-iManager-Method`, `GET`);

			req.withCredentials = true;

			req.responseType = 'blob';

			req.send(
				JSON.stringify({
					object: cloneDeep(this.$refs.search.getSearchObj()),
					limit: null,
					next: null,
					reverseOrder: false
				})
			);

			req.onload = event => {
				this.$stopSpinner();

				const disposition = req.getResponseHeader(`Content-Disposition`);

				let reqFileName = null;

				if (disposition && disposition.indexOf('attachment') !== -1) {
					let filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;
					let matches = filenameRegex.exec(disposition);

					if (matches != null && matches[1]) reqFileName = matches[1].replace(/['"]/g, '');
				}

				const today = moment(new Date()).format('YYYYMMDD_HHmmss');

				const blob = req.response;
				const file_name = downloadBtnInfo.title
					? `${downloadBtnInfo.title}_${today}.xlsx`
					: reqFileName
					? reqFileName
					: `Download_list_file_${today}.xlsx`;

				if (blob.size <= 0 || event.target.status != '200') {
					this.$alert({ type: 'warn', message: this.$t(`msg.alert.fail`) });
					return;
				}

				if (window.navigator && window.navigator.msSaveOrOpenBlob) {
					window.navigator.msSaveOrOpenBlob(blob, file_name);
				} else {
					let link = document.createElement('a');
					link.href = window.URL.createObjectURL(blob);
					link.download = file_name;
					link.click();
					URL.revokeObjectURL(link.href);
					link.remove();
				}
			};
		},
		rowClicked(info) {
			this.initDetail('detail', info);
		},
		initDetail(mode, info) {
			if (0 === this.detailTabList.length) {
				return;
			}

			if (!this.isDetailView) this.isDetailView = true;

			this.detailInfo = info;
			this.detailMode = mode;
			this.detailMode = null;

			this.$nextTick(() => {
				this.detailMode = mode;
			});
		},
		initNewTabSearchObj() {
			if (!localStorage.getItem('searchObj')) return;

			const tmpSearchObj = JSON.parse(localStorage.getItem('searchObj'));
			localStorage.removeItem('searchObj');

			this.gridSearchObj = merge(this.$refs.search.getSearchObj(), tmpSearchObj ? tmpSearchObj : {});

			this.searchList.forEach(searchInfo => {
				const key = 'pageSize' === searchInfo.vModel ? `_${searchInfo.vModel}` : searchInfo.vModel;

				if ('undefined' === typeof this.gridSearchObj[key]) {
					return;
				}

				if (`pageSize` === searchInfo.vModel && null === this.gridSearchObj[key]) {
					this.gridSearchObj[key] = this.minPageSize;
				}

				this.$set(searchInfo, 'val', this.gridSearchObj[key]);
			});
		},
		detailHorizonResize({ detailWidth }) {
			if (null === detailWidth) {
				this.classObj.horizon = false;
				this.styleObj.width = null;
			} else {
				let adjustWidth = window.innerWidth;

				if (992 <= window.innerWidth && document.getElementById('sidebar') && !document.body.classList.contains('sidebar-toggled')) {
					const sidebar = document.getElementById('sidebar');

					const sidebarComputedStyle = getComputedStyle(sidebar);

					const sidebarMarginLeft = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-left'));
					const sidebarMarginRight = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-right'));

					adjustWidth -= sidebar.offsetWidth + sidebarMarginLeft + sidebarMarginRight;
				}

				if (document.querySelector(`.horizon`) && `block` === document.querySelector(`#panel`).style.display) {
					adjustWidth -= getNumFromStr(getComputedStyle(document.querySelector(`.horizon .panel-content`)).width);
				}

				this.classObj.horizon = true;
				this.styleObj.width = `${adjustWidth - 48}px`;
			}

			this.$nextTick(() => {
				WindowResizeEventBus.$emit(`MainResize`);
			});
		}
	}
};
</script>
