<template>
	<div id="panel" ref="panel" class="panel panel-bottom">
		<div class="panel-dialog">
			<div class="panel-content">
				<header id="panel-header" class="panel-header sub-bar">
					<a
						href="javascript:void(0);"
						class="btn btn-icon backBtn"
						:title="$t('label.goBack')"
						@click="goPreviousPanel"
						v-if="'modify' === panelMode"
						><i class="icon-left"></i>
					</a>
					<h2 class="sub-bar-tit">{{ subBarTitle }}</h2>
					<div class="ml-auto">
						<button type="button" class="btn-icon" @click="expandCollapsePanel">
							<i class="icon-expand"></i>
						</button>
						<button type="button" class="btn-icon" @click="closePanel"><i class="icon-close"></i></button>
					</div>
				</header>

				<!-- tab menu -->
				<ul class="nav nav-tabs flex-shrink-0">
					<li class="nav-item">
						<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 0 === selectedIdx }" @click="changeTab(0)">{{
							this.$t('label.basicInfo')
						}}</a>
					</li>
					<li class="nav-item">
						<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 1 === selectedIdx }" @click="changeTab(1)">{{
							this.$t('label.mappingInfo')
						}}</a>
					</li>
					<li class="nav-item">
						<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 2 === selectedIdx }" @click="changeTab(2)">{{
							this.$t('label.useInfo')
						}}</a>
					</li>
					<li class="nav-item">
						<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 3 === selectedIdx }" @click="changeTab(3)">
							{{ $t('label.executionResult') }}
						</a>
					</li>
				</ul>
				<!-- tab content -->
				<div class="panel-body tab-content">
					<div :class="{ [panelMode]: true, 'tab-pane': true, active: 0 === selectedIdx }">
						<BasicInfo ref="basicInfo" :mode="panelMode" :detailInfo="basicInfo" :detailData="detailData" @setMappingId="setMappingId" />
					</div>
					<div :class="{ [panelMode]: true, 'tab-pane': true, active: 1 === selectedIdx }">
						<MappingInfo ref="mappingInfo" :mode="panelMode" :detailData="detailData" :mappingId="mappingId" />
					</div>
					<div :class="{ [panelMode]: true, 'tab-pane': true, active: 2 === selectedIdx }">
						<DetailBasic ref="useInfo" :detailInfo="useInfoConfigInfo" :detailData="detailData" :mode="panelMode" />
					</div>
					<DetailResult :class="{ 'tab-pane': true, active: 3 === selectedIdx && showResultTab }" :httpReqResult="httpReqResult" />
				</div>

				<!-- tab button -->
				<footer id="panel-footer" class="panel-footer sub-bar">
					<div class="ml-auto" v-if="'detail' === panelMode || 'done' === panelMode">
						<a href="javascript:void(0);" class="btn" @click="removeInfo" v-if="deleteBtn && editor">
							<i class="icon-delete"></i>{{ $t('label.delete') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="goModifyPanel" v-if="updateBtn && editor">
							<i class="icon-edit"></i>{{ $t('label.update') }}
						</a>
						<a href="javascript:void(0);" class="btn btn-primary" @click="goAddInfo" v-if="insertBtn && editor">
							<i class="icon-plus"></i>{{ `${$t('label.insert')} (${$t('label.copy')})` }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'add' === panelMode || 'clone' === panelMode">
						<a href="javascript:void(0);" class="btn btn-primary" @click="saveInfo" v-if="insertBtn && editor">
							<i class="icon-plus"></i>{{ $t('label.insert') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'modify' === panelMode" @click="updateInfo">
						<a href="javascript:void(0);" class="btn btn-primary" v-if="updateBtn && editor">
							<i class="icon-edit"></i>{{ $t('label.update') }}
						</a>
					</div>
				</footer>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import { HttpReq } from '@/utils/common.js';
import Detail from '@/components/detail/Detail.vue';
import BasicInfo from '@/views/detail/mapping/BasicInfo.vue';
import MappingInfo from '@/views/detail/mapping/MappingInfo.vue';
import DetailResult from '@/components/detail/DetailResult.vue';

export default {
	name: 'Detail',
	extends: Detail,
	props: {
		mode: {
			type: String,
			default: 'detail'
		},
		pk: Array,
		buttonList: Array,
		info: Object,
		cudUrl: String,
		viewer: Boolean,
		editor: Boolean
	},
	components: {
		BasicInfo,
		MappingInfo,
		DetailResult
	},
	data() {
		return {
			panelMode: this.mode,
			selectedIdx: 0,
			subBarTitle: '',

			tabAllData: {},

			insertBtn: false,
			updateBtn: false,
			deleteBtn: false,

			httpReq: null,
			httpReqResult: {},
			showResultTab: false,

			detailData: {},

			cloneInfo: {},
			mappingId: null,

			basicInfo: {
				content: [
					[
						{
							type: 'text',
							vModel: 'pk.mappingId',
							label: this.$t('label.mapping.id')
						},
						{
							type: 'text',
							vModel: 'mappingName',
							label: this.$t('label.mapping.name')
						}
					],
					[
						{
							type: 'textarea',
							vModel: 'mappingDesc',
							label: this.$t('label.desc')
						}
					]
				]
			},

			useInfoConfigInfo: {
				content: [
					[
						{
							type: 'text',
							vModel: 'updateVersion',
							label: 'Version',
							readonly: true,
							val: '0'
						},
						{
							type: 'text',
							vModel: 'updateUserId',
							label: this.$t('label.writer'),
							readonly: true
						},
						{
							type: 'text',
							vModel: 'updateTimestamp',
							label: this.$t('label.created.date'),
							readonly: true
						}
					]
				]
			}
		};
	},
	created() {
		this.httpReq = new HttpReq(this.cudUrl);
	},
	methods: {
		loadData(callBack) {
			this.tabAllData = merge(this.tabAllData, this.$refs.basicInfo.getData());
			this.tabAllData = merge(this.tabAllData, this.$refs.mappingInfo.getData());
			this.tabAllData = merge(this.tabAllData, this.$refs.useInfo.getData());

			if (callBack) callBack();
		},
		setMappingId(mappingId) {
			this.mappingId = mappingId;
		}
	}
};
</script>
