												name: 'updateUserId',
												width: '35%'
											}
										]
									}
								},

								gridSearchObj: {}
							};
						},
						mounted() {
							this.$nextTick(() => {
								this.search(this.$refs.search.getSearchObj());
							});
						},
						methods: {
							search(searchObj) {
								searchObj.equalSearch = true;
