											   				{value: 'C', text: '${this.$t('label.migration.cancel')}'}
														]`,
											optionValue: 'option.value',
											optionText: 'option.text'
										}
									},
									{
										type: 'modal',
										vModel: 'publishUserId',
										label: this.$t('label.publishLog.user'),
										placeholder: this.$t('msg.enterId'),
										regExpType: 'searchId',
										modalInfo: {
											title: this.$t('label.publishLog.user'),
											search: {
												list: [
													{
														type: 'text',
														vModel: 'userId',
														label: this.$t('label.id'),
														placeholder: this.$t('msg.enterId'),
														regExpType: 'searchId'
													},
													{
														type: 'text',
														vModel: 'userName',
														label: this.$t('label.name'),
														placeholder: this.$t('msg.enterName'),
														regExpType: 'name'
													},
													{
														type: 'dataList',
														vModel: 'pageSize',
														label: this.$t('label.listCnt'),
														placeholder: 'msg.coditionList',
														val: '10',
														optionInfo: {
															optionFor: 'option in [10, 100, 1000]',
															optionValue: 'option',
															optionText: 'option'
														}
													}
												]
											},
											button: {
												list: [
													{ id: 'initialize', isUse: true },
													{ id: 'download', isUse: true, downloadUrl: `/api/entity/publishLog/download` }
												]
											},
											grid: {
												url: `/api/entity/user/search`,
												totalCntUrl: `/api/entity/user/count`,
												paging: {
													isUse: true,
													side: 'server'
												},
												options: {
													columns: [
														{
															header: this.$t('label.id'),
															name: 'userId',
															width: '30%'
														},
														{
															header: this.$t('label.name'),
															name: 'userName',
															width: '40%'
														},
														{
															header: this.$t('label.user.department'),
															name: 'department',
															width: '30%'
														}
													]
												}
											},
											rowClickedCallback: ({ userId }) => userId
										}
									},
									{
										type: 'text',
										vModel: 'publishMessage',
										label: this.$t(`label.publishMessage`),
										placeholder: this.$t('msg.enterData')
									},
									{ type: 'text', vModel: 'targetId', label: this.$t(`label.publish.target`), placeholder: this.$t('msg.enterId') },
									{
										type: 'select',
										vModel: 'targetPublishDone',
										label: this.$t(`label.publish.target.done`),
										optionInfo: {
											optionFor: `option in [
																	{value: 'Y', text: '${this.$t('label.yes')}'},
                                               					   	{value: 'N', text: '${this.$t('label.no')}'},
																   	{value: 'S', text: 'Skip'},
																   	{value: 'E', text: 'Error'}
																]`,
											optionValue: 'option.value',
											optionText: 'option.text'
										}
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('label.listCnt'),
										placeholder: this.$t('msg.coditionList'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: `/api/entity/publishLog/search`,
									totalCntUrl: `/api/entity/publishLog/count`,
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('label.publishLog.date'),
												name: 'pk.publishDateTime',
												width: `25%`,
												align: `center`,
												formatter: obj => changeDateFormat(obj.value)
											},
											{
												header: this.$t('label.publishLog.resource.version'),
												name: 'resourceVersion',
												width: `10%`,
												align: `right`
											},
											{
												header: this.$t('label.publishStatus'),
												name: 'publishStatus',
												align: 'center',
												width: `15%`,
												formatter: info => {
													switch (info.row.publishStatus) {
														case 'A':
															return this.$t('label.publishLog.status.active');
														case 'D':
															return this.$t('label.publishLog.status.done');
														case 'F':
															return this.$t('label.publishLog.status.fail');
													}
												}
											},
											{
												header: this.$t('label.publishMessage'),
												name: 'publishMessage',
												width: `15%`
											},
											{
												header: this.$t('label.publishLog.user'),
												name: 'publishUserId',
												width: `15%`
											},
											{
												header: this.$t('label.migration.status'),
												name: 'migrationStatus',
												align: 'center',
												width: `10%`,
												formatter: info => {
													switch (info.row.migrationStatus) {
														case 'I':
															return this.$t('label.migration.init');
														case 'R':
															return this.$t('label.migration.ready');
														case 'D':
															return this.$t('label.migration.done');
														case 'C':
															return this.$t('label.migration.cancel');
													}
												}
											},
											{
												header: this.$t('label.migration.time'),
												name: 'migrationDateTime',
												width: `10%`,
												formatter: obj => changeDateFormat(obj.value)
											}
										]
									}
								},

								gridSearchObj: {}
							};
						},
						mounted() {
							this.$nextTick(() => {
								this.search(this.$refs.search.getSearchObj());
							});
						},
						methods: {
							search(searchObj) {
								searchObj.equalSearch = true;
								this.gridSearchObj = searchObj;
							},
							initialize() {
								this.$refs.search.initialize();
							},
							resize(gridObj) {
								if (!gridObj) return;

								this.$nextTick(() => {
									const modalBody = modalInstance.$el.querySelector(`.modal-body`);

									const modalBodyComputedStyle = getComputedStyle(modalBody);

									const modalBodyPaddingLeft = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-left'));
									const modalBodyPaddingRight = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-right'));
									const modalBodyPaddingTop = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-top'));
									const modalBodyPaddingBottom = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-bottom'));

									const adjustWidth = modalBody.clientWidth - modalBodyPaddingLeft - modalBodyPaddingRight;
									gridObj.invoke(`setWidth`, adjustWidth);

									const ctHeaderHeight = modalBody.querySelector(`.ct-header`).offsetHeight;
									const subBarHeight = modalBody.querySelector(`.sub-bar`).offsetHeight;

									const pagination = modalBody.querySelector(`.tui-grid-pagination`);
									const paginationComputedStyle = getComputedStyle(pagination);
									const paginationMarginTop = getNumFromStr(paginationComputedStyle.getPropertyValue('margin-top'));
									const paginationMarginBottom = getNumFromStr(paginationComputedStyle.getPropertyValue('margin-bottom'));

									const paginationHeight = pagination.offsetHeight;

									const adjustHeight =
										modalBody.clientHeight -
										modalBodyPaddingTop -
										modalBodyPaddingBottom -
										ctHeaderHeight -
										subBarHeight -
										paginationHeight -
										paginationMarginTop -
										paginationMarginBottom;

									gridObj.invoke(`setHeight`, adjustHeight);
								});
							},
							async rowClicked(info) {
								const {
									data: { object }
								} = await new HttpReq(`/api/entity/publishLog/object`).read(info);

								this.$openModal({
									title: this.$t(`label.publishLog`),
									modalWidth: `1280px`,
									modalHeight: `800px`,
									contentList: [
										{
											template: `
												<div style="height: 100%;">
													<ul class="nav nav-tabs flex-shrink-0">
														<li class="nav-item">
															<a href="javascript:void(0);" @click="changeTab(0)" :class="{ 'nav-link': true, active: selectedIdx === 0 }">${this.$t(`label.basicInfo`)}</a>
														</li>
														<li class="nav-item">
															<a href="javascript:void(0);" @click="changeTab(1)" :class="{ 'nav-link': true, active: selectedIdx === 1 }">XML</a>
														</li>
														<li class="nav-item">
															<a href="javascript:void(0);" @click="changeTab(2)" :class="{ 'nav-link': true, active: selectedIdx === 2 }">${this.$t('label.publishTargetInfo')}</a>
														</li>
													</ul>		
													<div class="panel-body tab-content" style="height: calc(100% - 48px);">
														<div :class="{ detail: true, 'tab-pane': true, active: selectedIdx === 0 }">
															<DetailBasic
																ref="component"
																:detailInfo="detailBasicInfo"
																:detailData="detailData"
																mode="detail"
															/>
														</div>
														<div :class="{ detail: true, 'tab-pane': true, active: selectedIdx === 1 }" style="height: 100%;">
															<xml :detailData="detailData" />
														</div>													
														<div :class="{ detail: true, 'tab-pane': true, active: selectedIdx === 2 }" style="height: 100%;">
															<DetailProperty
																ref="component"
																:detailInfo="detailPropertyInfo"
																:detailData="detailData"
																mode="detail"
															/>
														</div>														
													</div>
												</div>
											`,
											components: {
												DetailBasic: require('@/components/detail/DetailBasic').default,
												DetailProperty: require('@/components/detail/DetailProperty').default,
												xml: require('@/views/detail/publishLog/xml.vue').default
											},
											data() {
												return {
													detailBasicInfo: {
														type: 'basic',
														label: this.$t('label.basicInfo'),
														content: [
															[
																[
																	{
																		type: 'text',
																		vModel: 'resourceType',
																		label: this.$t('label.publishLog.resource.type'),
																		formatter: info => {
																			switch (info) {
																				case 'R':
																					return this.$t('label.modelRecord');
																				case 'S':
																					return this.$t('label.service');
																				case 'I':
																					return this.$t('label.interface');
																			}
																		}
																	},
																	{
																		type: 'text',
																		vModel: 'resourceId',
																		label: this.$t('label.publishLog.resource.id')
																	},
																	{
																		type: 'text',
																		vModel: 'resourceVersion',
																		label: this.$t('label.publishLog.resource.version')
																	}
																],
																[
																	{
																		type: 'select',
																		vModel: 'publishStatus',
																		label: this.$t('label.publishStatus'),
																		optionInfo: {
																			optionFor: `option in [
																							{value: 'A', text: '${this.$t('label.publishLog.status.active')}'},
																							{value: 'D', text: '${this.$t('label.publishLog.status.done')}'},
																							{value: 'F', text: '${this.$t('label.publishLog.status.fail')}'}]`,
																			optionValue: 'option.value',
																			optionText: 'option.text'
																		}
																	},
																	{
																		type: 'text',
																		vModel: 'pk.publishDateTime',
																		label: this.$t('label.publishLog.date'),
																		formatter: value => changeDateFormat(value)
																	},
																	{
																		type: 'text',
																		vModel: 'publishUserId',
																		label: this.$t('label.publishLog.user')
																	}
																],
																[
																	{
																		type: 'select',
																		vModel: 'migrationStatus',
																		label: this.$t('label.migration.status'),
																		optionInfo: {
																			optionFor: `option in [
																						{value: 'I', text: '${this.$t('label.migration.init')}'},
											   											{value: 'R', text: '${this.$t('label.migration.ready')}'},
											   											{value: 'D', text: '${this.$t('label.migration.done')}'},
											   											{value: 'C', text: '${this.$t('label.migration.cancel')}'}
																					]`,
																			optionValue: 'option.value',
																			optionText: 'option.text'
																		}
																	},
																	{
																		type: 'text',
																		vModel: 'migrationDateTime',
																		label: this.$t('label.migration.time'),
																		formatter: value => changeDateFormat(value)
																	},
																	{
																		type: 'text',
																		vModel: 'pk.publishId',
																		label: this.$t('label.id')
																	}
																]
															],
															[
																[
																	{
																		type: 'textarea',
																		vModel: 'publishMessage',
																		label: this.$t('label.publishMessage'),
																		height: `150px`
																	}
																]
															]
														]
													},
													detailPropertyInfo: {
														type: 'property',
														label: this.$t('label.publishTargetInfo'),
														dataKey: 'publishTargets',
														content: [
															{
																type: 'text',
																vModel: 'pk.targetId',
																label: this.$t('label.publishLog.target.id')
															},
															{
																type: 'text',
																vModel: 'pk.targetTypeName',
																label: this.$t('label.type')
															},
															{
																type: 'text',
																vModel: 'updateTimestamp',
																label: this.$t('label.publishTimestamp')
															},
															{
																type: 'text',
																vModel: 'publishCode',
																label: this.$t('label.publishCode')
															},
															{
																type: 'text',
																vModel: 'publishResult',
																label: this.$t('label.publishResult')
															},
															{
																type: 'text',
																vModel: 'publishDoneName',
																label: this.$t('label.publishLog.target.done')
															}
														],
														setPropertyCallBack: propertyList => {
															propertyList.forEach(property => {
																property.pk.targetTypeName =
																	`I` === property.pk.targetType
																		? this.$t(`label.server`)
																		: this.$t(`label.system`);

																property.publishDoneName =
																	`Y` === property.publishDone
																		? this.$t(`label.yes`)
																		: `N` === property.publishDone
																		? this.$t(`label.no`)
																		: `S` === property.publishDone
																		? `Skip`
																		: `Error`;

																if (property.updateTimestamp)
																	property.updateTimestamp = changeDateFormat(property.updateTimestamp);
															});

															return propertyList;
														}
													},
													detailData: {},
													selectedIdx: 0
												};
											},
											mounted() {
												this.detailData = parseFlattenObj(object);
											},
											methods: {
												changeTab(idx) {
													this.selectedIdx = idx;
												}
											}
										}
									]
								});
							}
						}
					}
				]
			});
		}
	}
};
</script>
