<script>
import Basic from '@/components/main/Basic.vue';
import { getPrivilege, HttpReq, escapeHtml } from '@/utils/common.js';

export default {
	name: `InterfaceClass`,
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	async mounted() {
		const res = await new HttpReq(`/api/page/properties`).read({ pk: { propertyId: `Interface.Type` }, orderByKey: false });

		if (`ok` !== res.data.result) return;

		this.interfaceTypeList = res.data.object;

		if (localStorage.getItem(`searchObj`)) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			interfaceTypeList: [],

			cudUrl: `/api/entity/interfaceClass/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: `text`, vModel: `id`, label: this.$t(`label.id`), placeholder: this.$t(`msg.enterId`), regExpType: `searchId` },
				{ type: `text`, vModel: `name`, label: this.$t(`label.name`), placeholder: this.$t(`msg.enterName`), regExpType: `name` },
				{
					type: `select`,
					vModel: `sourceInterfaceType`,
					label: `Source ${this.$t(`label.interface.type`)}`,
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: `Interface.Type`
							},
							orderByKey: true
						},
						optionListName: `interfaceTypeList`,
						optionFor: `option in interfaceTypeList`,
						optionValue: `option.pk.propertyKey`,
						optionText: `option.propertyValue`
					}
				},
				{
					type: `select`,
					vModel: `targetInterfaceType`,
					label: `Target ${this.$t(`label.interface.type`)}`,
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: `Interface.Type`
							},
							orderByKey: true
						},
						optionListName: `interfaceTypeList`,
						optionFor: `option in interfaceTypeList`,
						optionValue: `option.pk.propertyKey`,
						optionText: `option.propertyValue`
					}
				},
				{ type: `text`, vModel: `desc`, label: this.$t(`label.desc`), placeholder: this.$t(`msg.enterDesc`), regExpType: `desc` },
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: `add`, isUse: true },
				{ id: `initialize`, isUse: true },
				{ id: `newTab`, isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/interfaceClass/search`,
				totalCntUrl: `/api/entity/interfaceClass/count`,
				paging: {
					isUse: true,
					side: `server`
				},
				options: {
					columns: [
						{
							header: this.$t(`label.id`),
							name: `id`
						},
						{
							header: this.$t(`label.name`),
							name: `name`
						},
						{
							header: `Source ${this.$t(`label.interface.type`)}`,
							name: `sourceInterfaceType`,
							align: `center`,
							formatter: ({ value }) => {
								const { propertyValue } = this.interfaceTypeList.find(({ pk: { propertyKey } }) => propertyKey === value);
								return escapeHtml(propertyValue);
							}
						},
						{
							header: `Target ${this.$t(`label.interface.type`)}`,
							name: `targetInterfaceType`,
							align: `center`,
							formatter: ({ value }) => {
								const { propertyValue } = this.interfaceTypeList.find(({ pk: { propertyKey } }) => propertyKey === value);
								return escapeHtml(propertyValue);
							}
						},
						{
							header: this.$t(`label.desc`),
							name: `description`
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: `mainBasic`,

			//Detail
			detailPk: [`id`],

			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true }
			],

			detailTabList: [
				{
					type: 'bundle',
					url: '/interfaceClass/BasicInfo',
					label: this.$t('label.basicInfo')
				}
			]
		};
	}
};
</script>
