<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-wrap">
				<div class="form-table-head">
					<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('label.insert')">
						<i class="icon-plus-circle"></i>
					</button>
					<label class="col">{{ this.$t('label.id') }}</label>
					<label class="col">{{ this.$t('label.name') }}</label>
					<label class="col">{{ this.$t('label.property.value') }}</label>
					<label class="col">{{ this.$t('label.desc') }}</label>
					<label class="col">{{ this.$t('label.interface.type') }}</label>
				</div>
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('label.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div :class="`detail-content-${!getReadonly(propertyObj) && !propertyObj.require ? 'regExp' : 'common'}`">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="pk.keyId"
								:classObject="{
									'form-control': getReadonly(propertyObj),
									'regExp-text': !getReadonly(propertyObj),
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj[`pk.keyId`]"
								:readonly="readonly"
								regExpType="id"
								@setLengthCnt="info => setLengthCnt({ [`pk.keyId_${bodyIndex}`]: info['pk.keyId'] })"
							/>
							<span class="letterLength" v-if="!getReadonly(propertyObj)">
								{{
									`(${curLengthCnt[`pk.keyId_${bodyIndex}`]}
									 /${maxLengthCnt[`pk.keyId`]})`
								}}
							</span>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${!getReadonly(propertyObj) && !propertyObj.require ? 'regExp' : 'common'}`">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="name"
								:classObject="{
									'form-control': getReadonly(propertyObj),
									'regExp-text': !getReadonly(propertyObj),
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj[`name`]"
								:readonly="readonly"
								regExpType="name"
								@setLengthCnt="info => setLengthCnt({ [`name_${bodyIndex}`]: info.name })"
							/>
							<span class="letterLength" v-if="!getReadonly(propertyObj)">
								{{
									`(${curLengthCnt[`name_${bodyIndex}`]}
									 /${maxLengthCnt[`name`]})`
								}}
							</span>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${!getReadonly(propertyObj) && !propertyObj.require ? 'regExp' : 'common'}`">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="property"
								:classObject="{
									'form-control': getReadonly(propertyObj),
									'regExp-text': !getReadonly(propertyObj),
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj[`property`]"
								:readonly="readonly"
								regExpType="value"
								@setLengthCnt="info => setLengthCnt({ [`property_${bodyIndex}`]: info.property })"
							/>
							<span class="letterLength" v-if="!getReadonly(propertyObj)">
								{{
									`(${curLengthCnt[`property_${bodyIndex}`]}
									 /${maxLengthCnt[`property`]})`
								}}
							</span>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${!getReadonly(propertyObj) && !propertyObj.require ? 'regExp' : 'common'}`">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="description"
								:classObject="{
									'form-control': getReadonly(propertyObj),
									'regExp-text': !getReadonly(propertyObj),
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj[`description`]"
								:readonly="readonly"
								regExpType="desc"
								@setLengthCnt="info => setLengthCnt({ [`description_${bodyIndex}`]: info.description })"
							/>
							<span class="letterLength" v-if="!getReadonly(propertyObj)">
								{{
									`(${curLengthCnt[`description_${bodyIndex}`]}
									 /${maxLengthCnt[`description`]})`
								}}
							</span>
						</div>
					</div>
					<div class="col">
						<div style="width: 100%">
							<select v-model="propertyObj.interfaceType" class="form-control view-disabled" :readonly="readonly">
								<option value=""></option>
								<option
									v-for="(interfaceType, interfaceTypeIdx) in interfaceTypeList"
									:key="`${bodyIndex}_${interfaceTypeIdx}`"
									:value="interfaceType.pk.propertyKey"
									v-text="interfaceType.propertyValue"
								></option>
							</select>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import isEmpty from 'lodash/isEmpty';
import isNil from 'lodash/isNil';
import merge from 'lodash/merge';
import { HttpReq, parseFlattenObj, getUUID, parseHierarchyObj, getRegExpInfo } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: `ServerProperties`,
	extends: DetailProperty,
	data() {
		return {
			interfaceTypeList: [],
			regArr: [
				{ key: 'pk.keyId', regExpType: 'id' },
				{ key: 'name', regExpType: 'name' },
				{ key: 'description', regExpType: 'desc' },
				{ key: 'property', regExpType: 'value' }
			],
			regExpInfo: {
				'pk.keyId': 'id',
				name: 'name',
				description: 'desc',
				property: 'value'
			},
			curLengthCnt: {},
			maxLengthCnt: {}
		};
	},
	async created() {
		const httpReq = new HttpReq(`/api/page/properties`);

		const result = await httpReq.read({ pk: { propertyId: 'Interface.Type' }, orderByKey: true });

		this.interfaceTypeList = result.data.object;

		for (const [key, value] of Object.entries(this.regExpInfo)) {
			this.maxLengthCnt[key] = getRegExpInfo(value).maxLength;
		}
	},
	methods: {
		addProperty() {
			const rowInfo = {
				idx: getUUID(),
				'pk.keyId': null,
				name: null,
				description: null,
				property: null,
				interfaceType: null
			};

			for (const key in this.regExpInfo) {
				this.setLengthCnt({ [`${key}_${this.propertyList.length}`]: 0 });
			}

			this.propertyList.push(rowInfo);
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);

			this.$nextTick(() => {
				this.curLengthCnt = {};

				this.propertyList.forEach((property, bodyIndex) => {
					let rtnObj = {};

					this.$refs[`component${bodyIndex}`].forEach(comp => {
						rtnObj = merge(rtnObj, comp.getData());
					});

					for (const key in rtnObj) {
						this.setLengthCnt({ [`${key}_${bodyIndex}`]: isNil(rtnObj[key]) ? 0 : String(rtnObj[key]).length });
					}
				});
			});
		},
		setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			this.propertyList = [...this.detailData[`serverProperties`]];

			this.propertyList.forEach((propertyInfo, index) => {
				propertyInfo.idx = getUUID();
				const clonePropertyInfo = parseFlattenObj(propertyInfo);

				for (const key in this.regExpInfo) {
					this.setLengthCnt({ [`${key}_${index}`]: clonePropertyInfo[key] ? clonePropertyInfo[key].length : 0 });
				}
			});

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		getData() {
			const rtnArr = [];

			for (let i = 0; i < this.propertyList.length; i++) {
				let rtnObj = {
					interfaceType: this.propertyList[i].interfaceType
				};

				this.$refs[`component${i}`].forEach(comp => {
					rtnObj = merge(rtnObj, comp.getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				serverProperties: rtnArr
			};
		}
	}
};
</script>
