<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.metaDomain') }}</span>
					</label>
					<select v-model="metaDomain" class="form-control view-disabled" :readonly="readonly">
						<option v-for="(metaDomain, idx) in metaDomainList" :key="idx" :value="metaDomain">
							{{ metaDomain }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.request.model') }}</span>
					</label>
					<div class="input-group" :title="`${requestRecordId}${existRequestMapping ? `\n${$t(`msg.exist.service.mapping`)}` : ``}`">
						<input type="text" class="form-control view-disabled" v-model="requestRecordId" readonly="readonly" />
						<button
							class="btn btn-icon select-record-btn"
							:title="$t('label.insert')"
							v-if="!readonly"
							:disabled="existRequestMapping"
							@click="openRecordMngModal('I', 'create')"
						>
							<i class="icon-plus"></i>
						</button>
						<button
							class="btn btn-icon select-record-btn"
							:title="readonly ? $t('label.open') : $t('label.update')"
							@click="openRecordMngModal('I', 'update')"
						>
							<i class="icon-link"></i>
						</button>
						<button
							class="btn btn-icon select-record-btn"
							:title="$t('label.search')"
							v-if="!readonly"
							:disabled="existRequestMapping"
							@click="openRecordSrchModal('I')"
						>
							<i class="icon-srch"></i>
						</button>
						<button
							class="btn btn-icon select-record-btn"
							:title="$t('label.initialize')"
							v-if="!readonly"
							:disabled="existRequestMapping"
							@click="resetRecord('I')"
						>
							<i class="icon-reset"></i>
						</button>
					</div>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.response.model') }}</span>
					</label>
					<div
						class="input-group"
						:title="`${selectedResponseRecordId}${existResponseMapping ? `\n${$t(`msg.exist.service.mapping`)}` : ``}`"
					>
						<select v-model="selectedResponseRecordId" class="form-control view-disabled" :readonly="existResponseMapping">
							<option v-for="(responseRecordInfo, idx) in responseRecordList" :key="idx" :value="responseRecordInfo.id">
								{{ responseRecordInfo.id }}
							</option>
						</select>
						<button
							class="btn btn-icon select-record-btn"
							:title="$t('label.insert')"
							v-if="!readonly"
							:disabled="existResponseMapping"
							@click="openRecordMngModal('O', 'create')"
						>
							<i class="icon-plus"></i>
						</button>
						<button
							class="btn btn-icon select-record-btn"
							:title="readonly ? $t('label.open') : $t('label.update')"
							@click="openRecordMngModal('O', 'update')"
						>
							<i class="icon-link"></i>
						</button>
						<button
							class="btn btn-icon select-record-btn"
							:title="$t('label.search')"
							v-if="!readonly"
							:disabled="existResponseMapping"
							@click="openRecordSrchModal('O')"
						>
							<i class="icon-srch"></i>
						</button>
						<button
							class="btn btn-icon select-record-btn"
							:title="$t('label.initialize')"
							v-if="!readonly"
							:disabled="existResponseMapping"
							@click="resetRecord('O')"
						>
							<i class="icon-reset"></i>
						</button>
					</div>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.service.list') }}</span>
					</label>
					<div class="ml-auto form-inline m-full" v-if="!readonly">
						<button type="button" class="btn-icon" @click="srchService">
							<i class="icon-srch"></i>
						</button>
					</div>
					<div class="table-responsive">
						<TuiGrid ref="tuiGrid" v-bind="options" />
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';

import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import merge from 'lodash/merge';
import { Grid } from '@toast-ui/vue-grid';
import {
	HttpReq,
	getNumFromStr,
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	escapeHtml,
	parseFlattenObj,
	makeGridOptions
} from '@/utils/common.js';

export default {
	name: 'ModelRecordInfo',
	props: {
		detailData: Object,
		mode: String
	},
	components: {
		TuiGrid: Grid
	},
	data() {
		return {
			metaDomainList: [],
			selectedResponseRecordId: null,

			metaDomain: null,
			requestRecordObject: null,
			responseRecordList: [],

			options: {
				header: {
					height: 60,
					align: 'center',
					complexColumns: [
						{
							header: this.$t('label.request'),
							name: 'mergeColumn1',
							childNames: ['requestRecordId', 'requestMappingId']
						},
						{
							header: this.$t('label.response'),
							name: 'mergeColumn2',
							childNames: ['responseRecordId', 'responseMappingId']
						}
					]
				},
				columns: [
					{
						name: 'serviceObject',
						hidden: true
					},
					{
						name: 'serviceName',
						hidden: true
					},
					{
						name: 'requestRecordObject',
						hidden: true
					},
					{
						name: 'responseRecordObject',
						hidden: true
					},
					{
						name: 'requestMappingObject',
						hidden: true
					},
					{
						name: 'responseMappingObject',
						hidden: true
					},
					{
						header: this.$t('label.service.name'),
						name: 'serviceId',
						width: '20%',
						formatter: ({ row, value }) => {
							const removeHtml =
								this.readonly || row.serviceObject.serviceAutoGenerated
									? ``
									: `<i class="icon-minus-circle" title="${this.$t('label.delete')}"></i>`;

							return `${escapeHtml(value)}
									<span class="modal-btn")">
										<i class="icon-link" title="${this.readonly ? this.$t('label.open') : this.$t('label.edit')}"></i> 
										${removeHtml}
									</span>`;
						}
					},
					{
						header: this.$t('label.modelRecord.id'),
						name: 'requestRecordId',
						width: '20%',
						formatter: ({ row }) => {
							const requestRecordObject = row.requestRecordObject;
							const requestRecordId = requestRecordObject ? requestRecordObject.id : ``;

							if (this.readonly && !requestRecordId) return ``;

							const copyHtml = this.readonly ? `` : `<i class="icon-model" title="${this.$t('label.copy')}"></i>`;

							return `<div class="ellipsis" style="width: calc(100% - ${copyHtml ? 50 : 25}px);" title="${requestRecordId}">
                                        ${escapeHtml(requestRecordId)}
                                    </div>
									<span class="modal-btn">
										<i class="icon-link" title="${this.readonly ? this.$t('label.open') : this.$t('label.update')}"></i>
										${copyHtml}
								   </span>`;
						}
					},
					{
						header: this.$t('label.mapping.id'),
						name: 'requestMappingId',
						width: '20%',
						formatter: ({ row }) => {
							const requestMappingObject = row.requestMappingObject;
							const requestMappingId = requestMappingObject ? requestMappingObject.mappingId : ``;

							if (this.readonly && !requestMappingId) return ``;

							const resetHtml = this.readonly ? `` : `<i class="icon-reset" title="${this.$t('label.initialize')}"></i>`;

							return `<div class="ellipsis" style="width: calc(100% - ${resetHtml ? 50 : 25}px);" title="${requestMappingId}">
                                        ${escapeHtml(requestMappingId)}
                                    </div>
									<span class="modal-btn">
										<i class="icon-mapng" title="${this.readonly ? this.$t('label.open') : this.$t('label.edit')}"></i>
										${resetHtml}
									</span>`;
						}
					},
					{
						header: this.$t('label.modelRecord.id'),
						name: 'responseRecordId',
						width: '20%',
						formatter: ({ row }) => {
							const responseRecordObject = row.responseRecordObject;
							const responseRecordId = responseRecordObject ? responseRecordObject.id : ``;

							if (this.readonly && !responseRecordId) return ``;

							const copyHtml = this.readonly ? `` : `<i class="icon-model" title="${this.$t('label.copy')}"></i>`;

							return `<div class="ellipsis" style="width: calc(100% - ${copyHtml ? 50 : 25}px);" title="${responseRecordId}">
                                        ${escapeHtml(responseRecordId)}
                                    </div>
									<span class="modal-btn">
										<i class="icon-link" title="${this.readonly ? this.$t('label.open') : this.$t('label.update')}"></i>
										${copyHtml}
								   </span>
									`;
						}
					},
					{
						header: this.$t('label.mapping.id'),
						name: 'responseMappingId',
						width: '20%',
						formatter: ({ row }) => {
							const responseMappingObject = row.responseMappingObject;
							const responseMappingId = responseMappingObject ? responseMappingObject.mappingId : ``;

							if (this.readonly && !responseMappingId) return ``;

							const resetHtml = this.readonly ? `` : `<i class="icon-reset" title="${this.$t('label.initialize')}" ></i>`;

							return `<div class="ellipsis" style="width: calc(100% - ${resetHtml ? 50 : 25}px);" title="${responseMappingId}">
                                        ${escapeHtml(responseMappingId)}
                                    </div>
									<span class="modal-btn">
										<i class="icon-mapng" title="${this.readonly ? this.$t('label.open') : this.$t('label.edit')}"></i>
										${resetHtml}
									</span>`;
						}
					}
				],
				draggable: true,
				onGridMounted: evt => {
					if ('undefined' === typeof evt.instance.el) return;

					evt.instance.el.removeAttribute('header');
					evt.instance.el.removeAttribute('columnoptions');
					evt.instance.el.removeAttribute('onGridMounted');
					evt.instance.el.removeAttribute('draggable');
					evt.instance.el.removeAttribute('rowheaders');

					evt.instance.on(`click`, ({ columnName, rowKey, nativeEvent: { target } }) => {
						if (`serviceId` === columnName) {
							if (target.classList.contains(`icon-link`)) {
								this.serviceClick(rowKey);
							} else if (target.classList.contains(`icon-minus-circle`)) {
								this.removeService(rowKey);
							}
						} else if (`requestRecordId` === columnName) {
							const mod = target.classList.contains(`icon-link`) ? `update` : target.classList.contains(`icon-model`) ? `copy` : null;
							this.serviceRecordClick('I', mod, rowKey);
						} else if (`requestMappingId` === columnName) {
							if (target.classList.contains(`icon-reset`)) {
								this.serviceMappingResetClick('I', rowKey);
							} else if (target.classList.contains(`icon-mapng`)) {
								this.serviceMappingClick('I', rowKey);
							}
						} else if (`responseRecordId` === columnName) {
							const mod = target.classList.contains(`icon-link`) ? `update` : target.classList.contains(`icon-model`) ? `copy` : null;
							this.serviceRecordClick('O', mod, rowKey);
						} else if (`responseMappingId` === columnName) {
							if (target.classList.contains(`icon-reset`)) {
								this.serviceMappingResetClick('O', rowKey);
							} else if (target.classList.contains(`icon-mapng`)) {
								this.serviceMappingClick('O', rowKey);
							}
						}
					});
				}
			},

			modalRecordInfo: {
				title: this.$t('label.modelRecord'),
				search: {
					list: [
						{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId') },
						{
							type: 'dataList',
							vModel: 'pageSize',
							label: this.$t('label.listCnt'),
							placeholder: this.$t('msg.coditionList'),
							val: '10',
							optionInfo: {
								optionFor: 'option in [10, 100, 1000]',
								optionValue: 'option',
								optionText: 'option'
							}
						}
					]
				},
				button: {
					list: [{ id: 'initialize', isUse: true }]
				},
				grid: {
					url: `/api/entity/record/search`,
					totalCntUrl: `/api/entity/record/count`,
					paging: {
						isUse: true,
						side: 'server'
					},
					options: {
						columns: [
							{
								name: 'id',
								header: this.$t('label.id'),
								width: '50%'
							},
							{
								name: 'name',
								header: this.$t('label.name'),
								width: '50%'
							}
						]
					}
				}
			},

			existRequestMapping: false,
			existResponseMapping: false
		};
	},
	computed: {
		...mapState(['constantsInfo']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		},
		requestRecordId() {
			const id = this.requestRecordObject && this.requestRecordObject.id ? this.requestRecordObject.id : '';
			return id;
		}
	},
	async created() {
		this.options = makeGridOptions(this.options);

		const result = await new HttpReq(`/api/entity/fieldMeta/group/search`).read();

		if (`ok` !== result.data.result) return;

		this.metaDomainList = result.data.object;

		if (this.detailData.id) this.resetData();
	},
	mounted() {
		WindowResizeEventBus.$on('MainResize', () => {
			setTimeout(this.resize, 300);
		});

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);
	},
	methods: {
		generateService(serviceAutoGenerated, serviceInfo) {
			const dataList = this.$refs.tuiGrid.invoke('getData');

			if (`N` === serviceAutoGenerated) {
				dataList.forEach(data => {
					if (!data.serviceObject.serviceAutoGenerated) return;
					this.$refs.tuiGrid.invoke('removeRow', data.rowKey);
				});
			} else {
				if (0 < dataList.filter(info => info.serviceId === serviceInfo.id).length) return;

				dataList.forEach(data => {
					this.$refs.tuiGrid.invoke('removeRow', data.rowKey);
				});

				this.$refs.tuiGrid.invoke('appendRow', {
					serviceObject: serviceInfo,
					serviceId: serviceInfo.id
				});
			}
		},
		filterService(serviceInfo) {
			const dataList = this.$refs.tuiGrid.invoke('getData');

			dataList.forEach(data => {
				if (
					serviceInfo.interfaceType === data.serviceObject.interfaceType &&
					serviceInfo.targetSystemId === data.serviceObject.targetSystemId
				)
					return;

				this.$refs.tuiGrid.invoke('removeRow', data.rowKey);
			});
		},
		async openMappingMngModal(type, rowKey) {
			const { serviceId, serviceName, requestRecordObject, responseRecordObject, requestMappingObject, responseMappingObject } =
				this.$refs.tuiGrid.invoke('getRow', rowKey);

			if (`I` === type) {
				if (!this.requestRecordObject) return;
				if (!this.requestRecordObject.id) return;
				if (!requestRecordObject) return;
			}

			if (`O` === type) {
				if (0 === this.responseRecordList.length) return;
				if (!this.selectedResponseRecordId) return;
				if (!responseRecordObject) return;
			}

			let validateDataInfo = {},
				validateList = [];

			if (`add` === this.mode) {
				validateList = [`name`, `privilegeId`];

				const result = await this.$parent.checkValidate();

				if (!result.isValidate) return;

				validateDataInfo.id = result.id;

				this.$parent.$refs.component.forEach(info => {
					if (`DetailCustom` === info.$options.name) return;

					if (`ModelRecordInfo` === info.$options.name) return;

					if (!info.getData) return;

					const data = info.getData();

					validateList.forEach(validateKey => {
						if (!data[validateKey]) return;
						validateDataInfo[validateKey] = data[validateKey];
					});
				});
			} else {
				validateList = [`id`, `name`, `privilegeId`];

				this.$parent.$refs.component.forEach(info => {
					if (`ModelRecordInfo` === info.$options.name) return;

					if (!info.getData) return;

					const data = info.getData();

					validateList.forEach(validateKey => {
						if (!data[validateKey]) return;
						validateDataInfo[validateKey] = data[validateKey];
					});
				});
			}

			if (validateList.some(key => isEmpty(validateDataInfo[key]))) {
				this.$alert({ type: `warn`, message: this.$t('msg.interface.validation') });
				return;
			}

			const interfaceRecordInfo = `I` === type ? [this.requestRecordObject] : this.responseRecordList;

			let serviceRecordInfo = null;

			if (`I` === type) {
				serviceRecordInfo = [requestRecordObject];
			} else {
				if (this.constantsInfo.ifResMapSourceSvcResOnly) {
					serviceRecordInfo = [responseRecordObject];
				} else {
					serviceRecordInfo = [responseRecordObject];

					if (requestRecordObject && requestRecordObject.id) serviceRecordInfo.push(requestRecordObject);

					if (this.requestRecordObject && this.requestRecordObject.id) serviceRecordInfo.push(this.requestRecordObject);
				}
			}

			const mappingObject = `I` === type ? requestMappingObject : responseMappingObject;

			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t('label.mapping'),
				fullscreen: true,
				contentList: [
					{
						template: `<div>
								       <AddMapping ref="addMapping" :data="data" />
								   </div>`,
						components: {
							AddMapping: require(`@/views/modal/mapping/AddMapping.vue`).default
						},
						data() {
							return {
								data: {
									type,
									interfaceId: validateDataInfo.id,
									interfaceName: validateDataInfo.name,
									privilegeId: validateDataInfo.privilegeId,
									serviceId,
									serviceName,
									interfaceRecordInfo,
									serviceRecordInfo,
									mappingObject,
									readonly: _this.readonly
								}
							};
						}
					}
				],
				buttonList: (() => {
					return !this.readonly
						? [
								{
									template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('label.ok') }}</button>`,
									methods: {
										confirm() {
											const mappingInfo = modalInstance.$refs.content[0].$refs.addMapping.getData();

											_this.$refs.tuiGrid.invoke(
												'setValue',
												rowKey,
												`I` === type ? `requestMappingObject` : `responseMappingObject`,
												mappingInfo
											);

											if (`I` === type) _this.existRequestMapping = true;
											else _this.existResponseMapping = true;

											modalInstance.hideModal();
										}
									}
								}
						  ]
						: null;
				})()
			});
		},
		async openRecordMngModal(type, mod, rowKey = null) {
			if (!type || !mod) return;

			let serviceRecordObject = null;

			// model validation
			if (`number` === typeof rowKey) {
				//service model validation
				const { requestRecordObject, responseRecordObject } = this.$refs.tuiGrid.invoke('getRow', rowKey);
				serviceRecordObject = `I` === type ? requestRecordObject : responseRecordObject;

				if ('copy' === mod || 'update' === mod) {
					if (!serviceRecordObject) {
						this.$alert({ type: `warn`, message: this.$t('msg.no.data') });
						return;
					}
				}
			} else {
				//interface model validation
				if ('update' === mod) {
					if ((`I` === type && !this.requestRecordId) || (`O` === type && 0 === this.responseRecordList.length)) {
						this.$alert({ type: `warn`, message: this.$t('msg.no.data') });
						return;
					}
				}
			}

			if ('copy' === mod) {
				if ((`I` === type && this.existRequestMapping) || (`O` === type && this.existResponseMapping)) {
					this.$alert({ type: `warn`, message: this.$t(`msg.existMapping`) });
					return;
				}
			}

			if (('create' === mod || 'copy' === mod) && `I` === type && this.requestRecordObject) {
				const dataList = this.$refs.tuiGrid.invoke('getData');

				const interfaceRecordId = this.requestRecordObject.id;

				const useMappingIdArr = [];

				dataList.forEach(data => {
					const mappingObject = data.requestMappingObject;

					if (mappingObject && mappingObject.mappingDetails)
						mappingObject.mappingDetails.forEach(mappingDetail => {
							if (interfaceRecordId === mappingDetail.mappingRuleObject.recordId)
								useMappingIdArr.push(mappingDetail.mappingRuleObject.pk.mappingId);
						});
				});

				if (0 < useMappingIdArr.length) {
					this.$alert({ type: `warn`, message: `${useMappingIdArr.join(', ')} ${this.$t('msg.mapping.referenced')}` });
					return;
				}
			}

			//set Modal Data
			let validateDataInfo = {};
			const validateList = [`id`, `privateYn`, `privilegeId`, `metaDomain`];

			if (`add` === this.mode) {
				const result = await this.$parent.checkValidate();

				if (!result.isValidate) return;

				validateDataInfo.id = result.id;
			}

			const recordObj =
				'I' === type
					? cloneDeep(this.requestRecordObject)
					: cloneDeep(this.responseRecordList.filter(responseRecord => responseRecord.id === this.selectedResponseRecordId)[0]);

			if ('update' === mod && recordObj && !recordObj.mode) {
				// 검색을 통해 얻은 데이터 모델 정보 수정
				validateList.forEach(validateKey => {
					if (!recordObj[validateKey]) return;
					validateDataInfo[validateKey] = recordObj[validateKey];
				});
			} else {
				this.$parent.$refs.component.forEach(info => {
					if (`DetailCustom` === info.$options.name) return;

					if (`ModelRecordInfo` === info.$options.name) return;

					if (!info.getData) return;

					const data = info.getData();

					validateList.forEach(validateKey => {
						if (!data[validateKey]) return;
						validateDataInfo[validateKey] = data[validateKey];
					});
				});

				validateDataInfo.metaDomain = this.metaDomain;
			}

			if ('update' === mod) {
				validateDataInfo.recordObject = serviceRecordObject ? serviceRecordObject : recordObj;
			} else if ('copy' === mod) {
				validateDataInfo.recordObject = { fields: serviceRecordObject.fields };
			}

			// modal open or not
			if ((`create` === mod || `copy` === mod) && validateList.some(key => isEmpty(validateDataInfo[key]))) {
				this.$alert({ type: `warn`, message: this.$t('msg.interface.validation') });
				return;
			}

			const _this = this;

			const modalInstance = this.$openModal({
				title:
					`copy` === mod
						? `${this.$t('label.modelRecord')} ${this.$t(`label.copy`)} (${this.$t(`label.service`)} > ${this.$t(`label.interface`)})`
						: this.$t('label.modelRecord'),
				fullscreen: _this.readonly || `I` === type || (`O` === type && 'update' === mod),
				modalWidth: '450px',
				modalHeight: '200px',
				contentList: [
					{
						template: (() => {
							return _this.readonly || `I` === type || (`O` === type && 'update' === mod)
								? `<RecordModal ref="RecordModal" :cmd="cmd" :config="config" :mode="mode" />`
								: `<div style="height: 100%">
							   			<div class="row" v-if="!isNext">
							       			<div class="col-lg-12">
								       			<div class="form-group">
									       			<label class="control-label">
										       			<span>Response Form</span>
										   			</label>
										   			<input type="text" v-model="responseForm" class="form-control">
												</div>
								   			</div>
							   			</div>
						       			<RecordModal ref="RecordModal" :cmd="cmd" :config="config" v-else />
						   			</div>`;
						})(),
						components: {
							RecordModal: require(`@/views/modal/modelRecord/RecordModal.vue`).default
						},
						data() {
							return {
								cmd: mod,
								mode: _this.mode,
								config: {
									modalType: `I${type}`,
									modalData: validateDataInfo
								},

								isNext: false,
								responseForm: null
							};
						},
						watch: {
							isNext() {
								modalInstance.$el.firstChild.classList.add('modal-fullscreen');
								modalInstance.$el.firstChild.style['max-width'] = '95%';
								modalInstance.$el.firstChild.firstChild.style.height = '100%';

								if (this.responseForm) this.config.modalData.id += `_${this.responseForm}`;
							}
						}
					}
				],
				buttonList: (() => {
					if (this.readonly) return null;
					else
						return [
							{
								template: `<button type="button" @click="confirm" class="btn btn-primary">
										       {{ buttonName }}
										   </button>`,
								data() {
									return {
										buttonName: `copy` === mod && `I` === type ? this.$t('label.copy') : this.$t('label.ok')
									};
								},
								methods: {
									async confirm() {
										if (`O` === type && (`create` === mod || `copy` === mod) && !modalInstance.$refs.content[0].isNext) {
											if (`copy` === mod) this.buttonName = this.$t(`label.copy`);

											modalInstance.$refs.content[0].isNext = true;
										} else {
											// merge data & check validation
											const recordModalInstance = modalInstance.$refs.content[0].$refs.RecordModal;
											const tabAllData = recordModalInstance.getData();

											const result = await new HttpReq(`/api/entity/record/check`).read(tabAllData, true, result => {
												const errType = type => result.data.error.some(err => err.field.startsWith(type));

												if (errType(`fields`)) {
													recordModalInstance.changeTab(1);

													recordModalInstance.recordObjData.fields = cloneDeep(result.data.object.fields);
													recordModalInstance.recordObjData.options = cloneDeep(result.data.object.options);
													recordModalInstance.$refs.detailInfo.resetData();
												} else if (errType(`options`)) {
													recordModalInstance.changeTab(2);
												}

												this.$alert({
													type: `warn`,
													message: result.data.error
														.map(info => `${(info.className ? `${info.className}<hr>` : '') + info.message}`)
														.join(`,`)
												});
											});

											if (`error` === result.data.result) return;

											const data = merge('create' === mod ? { mode: mod } : {}, tabAllData);

											if (`I` === type) {
												if (serviceRecordObject && 'copy' !== mod) {
													//service requestRecordObject
													const { serviceObject } = _this.$refs.tuiGrid.invoke('getRow', rowKey);
													serviceObject.requestRecordObject = cloneDeep(data);

													_this.$refs.tuiGrid.invoke('setValue', rowKey, 'requestRecordObject', data);
												} else {
													//interface requestRecordObject
													_this.requestRecordObject = data;
												}
											} else {
												if (serviceRecordObject && 'copy' !== mod) {
													//service responseRecordObject
													const { serviceObject } = _this.$refs.tuiGrid.invoke('getRow', rowKey);
													serviceObject.responseRecordObject = cloneDeep(data);
													_this.$refs.tuiGrid.invoke('setValue', rowKey, 'responseRecordObject', data);
												} else {
													//interface responseRecordList
													const idx = _this.responseRecordList.findIndex(
														responseRecord => responseRecord.id === tabAllData.id
													);

													if (`create` === mod || `copy` === mod) {
														if (-1 < idx) {
															this.$alert({ type: `warn`, message: this.$t('msg.overlap.data') });
															return;
														} else {
															_this.responseRecordList.push(data);
														}
													} else {
														_this.responseRecordList[idx] = data;
													}

													_this.selectedResponseRecordId = tabAllData.id;
												}
											}

											modalInstance.hideModal();
										}
									}
								}
							}
						];
				})()
			});
		},
		openRecordSrchModal(type) {
			if (`I` === type && this.requestRecordObject) {
				const dataList = this.$refs.tuiGrid.invoke('getData');

				const interfaceRecordId = this.requestRecordObject.id;

				const useMappingIdArr = [];

				dataList.forEach(data => {
					const mappingObject = data.requestMappingObject;

					if (mappingObject && mappingObject.mappingDetails)
						mappingObject.mappingDetails.forEach(mappingDetail => {
							if (interfaceRecordId === mappingDetail.mappingRuleObject.recordId)
								useMappingIdArr.push(mappingDetail.mappingRuleObject.pk.mappingId);
						});
				});

				if (0 < useMappingIdArr.length) {
					this.$alert({ type: `warn`, message: `${useMappingIdArr.join(', ')} ${this.$t('msg.mapping.referenced')}` });
					return;
				}
			}

			const modalRecordInfo = this.modalRecordInfo;

			const modalInstance = this.$openModal({
				...modalRecordInfo,
				contentList: [
					{
						template: `<div>
								       <Search :list="searchList" @search="search" ref="search"/>
							    	   <Main
							   	   			:buttonList="buttonList"
								   			:gridInfo="gridInfo"
								   			:gridSearchObj="gridSearchObj"
								   			:gridType="'modalBasic'"
								   			:viewer="true"
								   			@rowClicked="rowClicked"
								   			@initialize="initialize"
							   			/>
						   		  </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [],

								//Button
								buttonList: [],

								// Grid
								gridInfo: {},
								gridSearchObj: {}
							};
						},
						created() {
							const { search, button, grid } = modalRecordInfo;

							//Search
							this.searchList = search.list;

							//Button
							this.buttonList = button.list;

							//Grid
							this.gridInfo = grid;
						},
						methods: {
							search(searchObj) {
								searchObj.recordType = `I`;
								this.gridSearchObj = searchObj;
							},
							rowClicked: async info => {
								const isOverlapData =
									(`I` === type && info.id === this.requestRecordId) ||
									(`O` === type && 0 < this.responseRecordList.filter(responseRecord => responseRecord.id === info.id).length);

								if (isOverlapData) {
									this.$alert({ type: `warn`, message: this.$t('msg.overlap.data') });
									return;
								}

								const result = await new HttpReq(`/api/entity/record/object`).read({ id: info.id });

								if (`ok` !== result.data.result) return;

								if (`I` === type) {
									this.requestRecordObject = result.data.object;
								} else {
									this.responseRecordList.push(result.data.object);
									this.selectedResponseRecordId = info.id;
								}

								modalInstance.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		},
		resetRecord(type) {
			if (`I` === type) {
				if (!this.requestRecordObject) return;
				if (!this.requestRecordObject.id) return;
			}

			if (`O` === type) {
				if (0 === this.responseRecordList.length) return;
				if (!this.selectedResponseRecordId) return;
			}

			const dataList = this.$refs.tuiGrid.invoke('getData');

			const interfaceRecordId = `I` === type ? this.requestRecordObject.id : this.selectedResponseRecordId;

			const useMappingIdArr = [];

			dataList.forEach(data => {
				const mappingObject = `I` === type ? data.requestMappingObject : data.responseMappingObject;

				if (mappingObject && mappingObject.mappingDetails)
					mappingObject.mappingDetails.forEach(mappingDetail => {
						const mappingRuleObject = mappingDetail.mappingRuleObject;

						const conditon =
							`I` === type
								? mappingRuleObject.mappingRuleSources.some(source => interfaceRecordId === source.recordId)
								: interfaceRecordId === mappingRuleObject.recordId;

						if (conditon) useMappingIdArr.push(mappingRuleObject.pk.mappingId);
					});
			});

			if (0 < useMappingIdArr.length) {
				this.$alert({ type: `warn`, message: `${useMappingIdArr.join(', ')} ${this.$t('msg.mapping.referenced')}` });
			} else {
				if (`I` === type) {
					this.requestRecordObject = null;
				} else {
					this.responseRecordList = this.responseRecordList.filter(responseRecordInfo => responseRecordInfo.id !== interfaceRecordId);
					this.selectedResponseRecordId = 0 === this.responseRecordList.length ? null : this.responseRecordList[0].id;
				}
			}
		},
		resize() {
			if (!this.$refs.tuiGrid) return;

			const panel = document.getElementById('panel');
			const panelBody = panel.getElementsByClassName('panel-body')[0];
			const panelBodyComputedStyle = getComputedStyle(panelBody);

			const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
			const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
			const panelBodyPaddingTop = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-top'));
			const panelBodyPaddingBottom = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-bottom'));

			const adjustWidth = panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight;
			const adjustHeight = panelBody.clientHeight - panelBodyPaddingTop - panelBodyPaddingBottom - (80 + 24 + 32);

			this.$refs.tuiGrid.invoke('setWidth', adjustWidth);
			this.$refs.tuiGrid.invoke('setHeight', 200 > adjustHeight ? 200 : adjustHeight);

			this.$el.style.width = `${adjustWidth}px`;
		},
		async resetData() {
			this.metaDomain = null;
			this.requestRecordObject = {};
			this.responseRecordList = [];

			for (const key in this.detailData) {
				const value = this.detailData[key];

				if (key.startsWith('requestRecordObject')) {
					this.requestRecordObject[key.replace('requestRecordObject.', '')] = value;
				} else if (`metaDomain` === key) {
					this.metaDomain = value;
				}
			}

			if (this.detailData.interfaceResponses)
				this.detailData.interfaceResponses.forEach(info => {
					if (info.recordObject) {
						this.responseRecordList.push(info.recordObject);
					} else if (this.detailData.requestRecordId === info.pk.recordId) {
						this.responseRecordList.push(cloneDeep(this.requestRecordObject));
					}
				});

			if (0 < this.responseRecordList.length) {
				this.selectedResponseRecordId = this.responseRecordList[0].id;
			}

			if (this.detailData.interfaceServices) {
				try {
					const promiseServiceList = [];

					this.detailData.interfaceServices.forEach(interfaceService => {
						promiseServiceList.push(new HttpReq(`/api/entity/service/object`).read({ id: interfaceService.pk.serviceId }));
					});

					const values = await Promise.all(promiseServiceList);

					this.$refs.tuiGrid.invoke('clear');

					if (this.detailData.interfaceServices)
						this.$refs.tuiGrid.invoke(
							'resetData',
							this.detailData.interfaceServices.map(interfaceService => {
								const serviceInfo = values.filter(value => value.data.object.id === interfaceService.pk.serviceId)[0];
								const object = serviceInfo ? serviceInfo.data.object : {};

								return {
									serviceObject: object,
									serviceId: object.id,
									serviceName: object.name,
									requestMappingObject: interfaceService.requestMappingObject ? interfaceService.requestMappingObject : null,
									responseMappingObject: interfaceService.responseMappingObject ? interfaceService.responseMappingObject : null,
									requestRecordObject: object.requestRecordObject ? object.requestRecordObject : null,
									responseRecordObject: object.responseRecordObject
										? object.responseRecordObject
										: object.requestRecordId && object.requestRecordId === object.responseRecordId
										? object.requestRecordObject
										: null
								};
							})
						);

					let coverDiv = this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').querySelector('#coverDiv');

					if (coverDiv) this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').removeChild(coverDiv);

					if (this.readonly) {
						coverDiv = document.createElement('div');

						coverDiv.setAttribute('id', 'coverDiv');
						coverDiv.style.width = '100%';
						coverDiv.style.height = '100%';
						coverDiv.style.backgroundColor = '#666';
						coverDiv.style.position = 'absolute';
						coverDiv.style.top = '0';
						coverDiv.style.opacity = '0.4';

						this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').appendChild(coverDiv);
					}
				} catch (error) {
					this.$alert({ type: 'warn', message: this.$t('msg.no.data') });
				}
			} else {
				this.$refs.tuiGrid.invoke('clear');
			}

			this.existRequestMapping = false;
			this.existResponseMapping = false;

			for (const { requestMappingObject, responseMappingObject } of this.$refs.tuiGrid.invoke('getData')) {
				if (requestMappingObject && !this.existRequestMapping) {
					this.existRequestMapping = true;
				}

				if (responseMappingObject && !this.existResponseMapping) {
					this.existResponseMapping = true;
				}
			}
		},
		async srchService() {
			const modalInfo = {
				title: this.$t('label.service'),
				search: {
					list: [
						{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId') },
						{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName') },
						{ type: 'text', vModel: 'interfaceType', label: this.$t('label.interface.type'), placeholder: this.$t(`msg.enterData`) },
						{ type: 'text', vModel: 'targetSystemId', label: this.$t('label.system.id'), placeholder: this.$t(`msg.enterData`) },
						{
							type: 'dataList',
							vModel: 'pageSize',
							label: this.$t('label.listCnt'),
							placeholder: this.$t('msg.coditionList'),
							val: '10',
							optionInfo: {
								optionFor: 'option in [10, 100, 1000]',
								optionValue: 'option',
								optionText: 'option'
							}
						}
					]
				},
				button: {
					list: [{ id: 'initialize', isUse: true }]
				},
				grid: {
					url: `/api/entity/service/search`,
					totalCntUrl: `/api/entity/service/count`,
					paging: {
						isUse: true,
						side: 'server'
					},
					options: {
						columns: [
							{
								name: 'id',
								header: this.$t('label.id'),
								width: '33%'
							},
							{
								name: 'name',
								header: this.$t('label.name'),
								width: '33%'
							},
							{
								name: 'serverId',
								header: this.$t('label.linked.server.id'),
								width: '33%'
							}
						]
					}
				}
			};

			if (`add` === this.mode) {
				const result = await this.$parent.checkValidate();

				modalInfo.search.list[2].val = result.targetInterfaceType;
				modalInfo.search.list[2].readonly = true;

				modalInfo.search.list[3].val = result.targetSystemId;
				modalInfo.search.list[3].readonly = true;
			}

			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t('label.service'),
				modalWidth: `1000px`,
				contentList: [
					{
						template: `<div>
								       <Search :list="searchList" @search="search" ref="search"/>
									   <Main
											:buttonList="buttonList"
											:gridInfo="gridInfo"
											:gridSearchObj="gridSearchObj"
											:gridType="'modalBasic'"
											:viewer="true"
											@rowClicked="rowClicked"
											@initialize="initialize"
										/>
								   </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: modalInfo.search.list,

								//Button
								buttonList: modalInfo.button.list,

								// Grid
								gridInfo: modalInfo.grid,
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							async rowClicked(info) {
								if (0 < _this.$refs.tuiGrid.invoke('getData').filter(row => row.serviceId === info.id).length) {
									this.$alert({ type: `warn`, message: this.$t('msg.overlap.data') });
									return;
								}

								const result = await new HttpReq(`/api/entity/service/object`).read({ id: info.id });

								if (`ok` !== result.data.result) return;

								const serviceObject = result.data.object;

								_this.$refs.tuiGrid.invoke('appendRow', {
									serviceObject,
									serviceId: serviceObject.id,
									serviceName: serviceObject.name,
									requestRecordObject: serviceObject.requestRecordObject,
									responseRecordObject: serviceObject.responseRecordObject
										? serviceObject.responseRecordObject
										: serviceObject.requestRecordId === serviceObject.responseRecordId
										? serviceObject.requestRecordObject
										: null,
									requestMappingId: '',
									responseMappingId: ''
								});

								modalInstance.hideModal();

								_this.resize();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		},
		serviceClick(rowKey) {
			const _this = this;

			const { serviceObject, requestMappingObject, responseMappingObject } = this.$refs.tuiGrid.invoke('getRow', rowKey);

			const existRequestMapping = requestMappingObject && requestMappingObject.mappingId ? true : false;
			const existResponseMapping = responseMappingObject && responseMappingObject.mappingId ? true : false;

			const modalInstance = this.$openModal({
				title: this.$t('label.service'),
				fullscreen: true,
				contentList: [
					{
						template: `<div>
								       <AddService ref="addService" :serviceObject="serviceObject" :mode="mode" :panelMode="panelMode" :existRequestMapping="existRequestMapping" :existResponseMapping="existResponseMapping" />
								   </div>`,
						components: {
							AddService: require(`@/views/modal/service/AddService.vue`).default
						},
						data() {
							return {
								serviceObject: parseFlattenObj(serviceObject),
								mode: `add` === _this.mode ? 'modify' : _this.mode,
								panelMode: _this.mode,
								existRequestMapping,
								existResponseMapping
							};
						}
					}
				],
				buttonList: (() => {
					return !this.readonly
						? [
								{
									template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('label.ok') }}</button>`,
									methods: {
										async confirm() {
											const info = await modalInstance.$refs.content[0].$refs.addService.getData();

											if (['name', 'privilegeId', 'privateYn'].some(key => isEmpty(info[key]))) {
												_this.$alert({ type: 'warn', message: _this.$t('msg.add.service.validation') });
												return;
											}

											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'serviceObject', info);
											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'serviceId', info.id);
											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'serviceName', info.name);
											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'requestRecordObject', info.requestRecordObject);
											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'responseRecordObject', info.responseRecordObject);

											modalInstance.hideModal();
										}
									}
								}
						  ]
						: null;
				})()
			});
		},
		removeService(rowKey) {
			this.$refs.tuiGrid.invoke('removeRow', rowKey);

			this.existRequestMapping = false;
			this.existResponseMapping = false;

			for (const { requestMappingObject, responseMappingObject } of this.$refs.tuiGrid.invoke('getData')) {
				if (requestMappingObject && !this.existRequestMapping) {
					this.existRequestMapping = true;
				}

				if (responseMappingObject && !this.existResponseMapping) {
					this.existResponseMapping = true;
				}
			}

			this.resize();
		},

		serviceRecordClick(type, mod, rowKey) {
			this.openRecordMngModal(type, mod, rowKey);
		},
		serviceMappingClick(type, rowKey) {
			this.openMappingMngModal(type, rowKey);
		},
		serviceMappingResetClick(type, rowKey) {
			this.$refs.tuiGrid.invoke('setValue', rowKey, `I` === type ? `requestMappingObject` : `responseMappingObject`, null);

			this.existRequestMapping = false;
			this.existResponseMapping = false;

			for (const { requestMappingObject, responseMappingObject } of this.$refs.tuiGrid.invoke('getData')) {
				if (requestMappingObject && !this.existRequestMapping) {
					this.existRequestMapping = true;
				}

				if (responseMappingObject && !this.existResponseMapping) {
					this.existResponseMapping = true;
				}
			}
		},
		getData() {
			const rtnObj = {
				metaDomain: this.metaDomain,
				requestRecordId: this.requestRecordId ? this.requestRecordId : null,
				requestRecordObject: this.requestRecordId ? this.requestRecordObject : null,
				interfaceResponses: this.responseRecordList.map(responseRecordInfo => {
					return {
						pk: {
							recordId: responseRecordInfo.id
						},
						recordObject: responseRecordInfo
					};
				}),
				interfaceServices: (() => {
					const rtnArr = [];

					this.$refs.tuiGrid.invoke('getData').forEach((info, idx) => {
						rtnArr.push({
							pk: {
								serviceId: info.serviceId
							},
							serviceOrder: idx,
							requestMappingId: info.requestMappingObject ? info.requestMappingObject.mappingId : null,
							responseMappingId: info.responseMappingObject ? info.responseMappingObject.mappingId : null,
							requestMappingObject: info.requestMappingObject ? info.requestMappingObject : null,
							responseMappingObject: info.responseMappingObject ? info.responseMappingObject : null
						});
					});

					return rtnArr;
				})()
			};

			return rtnObj;
		}
	},
	watch: {
		detailData() {
			this.resetData();
		}
	}
};
</script>

<style lang="scss">
.tui-grid-body-area {
	.modal-btn {
		float: right;
		cursor: pointer;
	}
}

.ellipsis {
	display: inline-block;
}
</style>
