<template>
	<div class="wrap" @contextmenu="conveyEvent" @click="conveyEvent">
		<SideBar @loadContent="loadContent" />
		<Dispatch :uuid="uuid" :menuId="menuId" />
	</div>
</template>

<script>
import { getUUID, MenuClickEventBus, MenuContextMenuEventBus, openNewTab, getCurrentMenuId } from '@/utils/common.js';
import SideBar from '@/views/SideBar.vue';
import Dispatch from '@/components/main/Dispatch.vue';

export default {
	name: 'Layout',
	components: {
		SideBar,
		Dispatch
	},
	data() {
		return {
			uuid: null,
			menuId: null
		};
	},
	methods: {
		loadContent(menuInfo) {
			if (`Y` === menuInfo.openWindow && `Y` !== localStorage.getItem('isOpenNewTab')) {
				openNewTab(getCurrentMenuId(), () => {
					localStorage.setItem('isOpenNewTab', 'Y');
				});
			} else {
				localStorage.removeItem('isOpenNewTab');
			}

			this.uuid = getUUID();
			this.menuId = menuInfo.menuId;

			const classList = [];

			document.body.classList.forEach(className => {
				if (`moon` === className) return;
				classList.push(className);
			});

			document.body.classList.remove(...classList);

			MenuClickEventBus.$emit('ModalDelete');
		},
		conveyEvent(evt) {
			MenuContextMenuEventBus.$emit('initContextMenu', evt);
		}
	}
};
</script>
