<template>
	<form style="width: 100%; display: inline" onsubmit="return false;">
		<input
			:type="type"
			v-model="obj[vModel]"
			:class="classObject"
			:maxlength="maxLength"
			:readonly="readonly"
			autocomplete="off"
			@input="input"
		/>
	</form>
</template>

<script>
import { getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'password',
	props: {
		vModel: String,
		classObject: Object,
		regExpType: {
			type: String,
			default: 'default'
		},
		readonly: {
			type: Boolean,
			default: false
		},
		val: {
			type: String,
			default: null
		}
	},
	data() {
		return {
			obj: {},
			maxLength: null,
			regExp: null,
			type: `password`
		};
	},
	created() {
		for (const key in this.$data.obj) {
			this.$delete(this.$data.obj, key);
		}

		this.$set(this.obj, this.vModel, null);

		this.maxLength = getRegExpInfo(this.regExpType).maxLength;
		this.regExp = getRegExpInfo(this.regExpType).regExp;

		this.setData(this.val);
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	methods: {
		setData(val) {
			this.obj[this.vModel] = val;
			this.setLengthCnt();
		},
		getData() {
			return {
				[this.vModel]: this.obj[this.vModel] ? this.obj[this.vModel] : `num` === this.regExpType ? null : this.obj[this.vModel]
			};
		},
		input(e) {
			this.obj[this.vModel] = e.target.value;
			this.setLengthCnt();
		},
		setLengthCnt() {
			if ('string' !== typeof this.obj[this.vModel]) return;

			this.obj[this.vModel] = this.obj[this.vModel].replace(new RegExp(this.regExp, 'g'), '');

			const length = this.obj[this.vModel].length;

			if (length > this.maxLength) return;

			this.$emit('setLengthCnt', { [this.vModel]: length });
		},
		changeType() {
			this.type = `password` === this.type ? `text` : `password`;
		}
	}
};
</script>
