<script>
import { mapState } from 'vuex';
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/service/Detail.vue';
import { getPrivilege, HttpReq, escapeHtml, changeDateFormat, openNewTab } from '@/utils/common.js';

export default {
	name: 'Service',
	extends: Basic,
	components: {
		Detail
	},
	computed: {
		...mapState(['constantsInfo'])
	},
	async created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;

		let httpReq = new HttpReq(`/api/page/properties`);

		let result = await httpReq.read({ pk: { propertyId: 'Interface.Type' }, orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.interfaceTypeList = result.data.object;

		const isSpecification = this.constantsInfo.isSpecification;

		if (isSpecification && this.editor) {
			this.buttonList.push({ id: 'importExcel', isUse: true, importUrl: `/api/entity/service/document` });
			this.detailButtonList.push({
				id: 'exportExcel',
				isUse: true,
				exportUrl: `/api/entity/service/document`,
				fileName: `ServiceTemplate`
			});
		}
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}

		if (localStorage.getItem('selectedServiceInfo')) {
			const selectedServiceInfo = JSON.parse(localStorage.getItem('selectedServiceInfo'));
			localStorage.removeItem('selectedServiceInfo');

			this.initDetail('detail', {
				id: selectedServiceInfo.serviceId,
				isNewTab: true
			});
		}
	},
	data() {
		return {
			interfaceTypeList: null,

			cudUrl: `/api/entity/service/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId'), regExpType: 'searchId' },
				{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName'), regExpType: 'name' },
				{
					type: 'modal',
					vModel: 'targetSystemId',
					label: this.$t('label.system.id'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('label.system'),
						search: {
							list: [
								{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId') },
								{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName') },
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('label.listCnt'),
									placeholder: 'msg.coditionList',
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/system/search`,
							totalCntUrl: `/api/entity/system/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('label.id'),
										name: 'id'
									},
									{
										header: this.$t('label.name'),
										name: 'name'
									}
								]
							}
						},
						rowClickedCallback: ({ id }) => id
					}
				},
				{
					type: 'text',
					vModel: 'bizId',
					label: this.$t('label.biz.name'),
					placeholder: this.$t('msg.enterData'),
					regExpType: 'name'
				},
				{
					type: 'modal',
					vModel: 'serverId',
					label: this.$t('label.linked.server.id'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('label.linked.server'),
						search: {
							list: [
								{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId') },
								{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName') },
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('label.listCnt'),
									placeholder: 'msg.coditionList',
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/server/search`,
							totalCntUrl: `/api/entity/server/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('label.id'),
										name: 'id',
										width: '40%'
									},
									{
										header: this.$t('label.name'),
										name: 'name',
										width: '60%'
									}
								]
							}
						},
						rowClickedCallback: ({ id }) => id
					}
				},
				{
					type: 'select',
					vModel: 'interfaceType',
					label: this.$t('label.interface.type'),
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'Interface.Type'
							},
							orderByKey: true
						},
						optionListName: 'interfaceType',
						optionFor: 'option in interfaceType',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'select',
					vModel: 'publishStatus',
					label: this.$t('label.publishStatus'),
					optionInfo: {
						optionFor:
							'option in [ {value: `M`, name: `MAKE`}, {value: `R`, name: `REQUEST`}, {value: `C`, name: `CANCEL`}, {value: `A`, name: `APPROVE`}, {value: `D`, name: `DONE`} ]',
						optionValue: 'option.value',
						optionText: 'option.name'
					}
				},
				{
					type: 'select',
					vModel: 'useYn',
					label: this.$t('label.useyn'),
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'List.Yn'
							},
							orderByKey: true
						},
						optionListName: 'info',
						optionFor: 'option in info',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'text',
					vModel: 'description',
					label: this.$t(`label.desc`),
					placeholder: this.$t('msg.enterDesc'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'download', isUse: true, downloadUrl: `/api/entity/service/download` }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/service/search`,
				totalCntUrl: `/api/entity/service/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'id',
							header: this.$t('label.id'),
							width: '10%'
						},
						{
							name: 'name',
							header: this.$t('label.name'),
							width: '10%'
						},
						{
							name: 'targetSystemId',
							header: this.$t('label.system.id'),
							width: '10%'
						},
						{
							header: this.$t('label.biz.name'),
							name: 'bizId',
							width: '5%'
						},
						{
							name: 'serverId',
							header: this.$t('label.linked.server.id'),
							width: '7%'
						},
						{
							name: 'interfaceType',
							header: this.$t('label.interface.type'),
							width: '8%',
							align: 'center',
							formatter: ({ value }) =>
								this.interfaceTypeList
									? escapeHtml(this.interfaceTypeList.find(interfaceType => interfaceType.pk.propertyKey === value).propertyValue)
									: null
						},
						{
							header: this.$t('label.publishStatus'),
							name: 'publishStatus',
							align: 'center',
							width: '5%',
							formatter: ({ value }) => {
								return 'M' === value
									? 'MAKE'
									: 'R' === value
									? 'REQUEST'
									: 'C' === value
									? 'CANCEL'
									: 'A' === value
									? 'APPROVE'
									: 'D' === value
									? 'DONE'
									: '';
							}
						},
						{
							header: this.$t('label.publishTimestamp.request'),
							name: 'publishDateTime',
							align: 'center',
							width: '10%',
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							header: this.$t('label.publishTimestamp.complete'),
							name: 'publishTimestamp',
							align: 'center',
							width: '10%',
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							header: this.$t('label.useyn'),
							name: 'useYn',
							align: 'center',
							width: '5%'
						},
						{
							header: `Version`,
							name: 'updateVersion',
							align: 'center',
							width: '5%'
						},
						{
							header: this.$t('label.writer'),
							name: 'updateUserId',
							width: '5%'
						},
						{
							header: this.$t('label.created.date'),
							name: 'updateTimestamp',
							align: 'center',
							width: '10%',
							formatter: obj => changeDateFormat(obj.value)
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['id'],
			publishUrl: '/api/entity/service/publish',
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'publish', isUse: true },
				{ id: 'cancelPublish', isUse: true },
				{ id: 'reference', isUse: true, className: `com.inzent.yellowpage.model.ServiceMeta` },
				{ id: 'metaHistory', isUse: true, className: `com.inzent.yellowpage.model.ServiceMeta`, entityIdKey: `id` },
				{ id: 'publishLog', isUse: true, resourceType: `S`, resourceIdKey: `id` }
			],
			detailTabList: [
				{
					type: 'custom',
					id: 'AddService',
					label: this.$t('label.service.id'),
					url: '/service/serviceAdd'
				},
				{
					type: 'bundle',
					label: this.$t('label.basicInfo'),
					url: '/service/BasicInfo'
				},
				{
					type: 'bundle',
					url: '/service/ExtendInfo',
					label: this.$t('label.extendInfo')
				},
				{
					id: `useInfo`,
					type: 'bundle',
					url: '/service/UseInfo',
					label: this.$t('label.useInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('label.writer'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('label.created.date'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: 'Version',
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'publishStatus',
									label: this.$t('label.publishStatus'),
									readonly: true,
									formatter: info => {
										return 'M' === info
											? 'MAKE'
											: 'R' === info
											? 'REQUEST'
											: 'C' === info
											? 'CANCEL'
											: 'A' === info
											? 'APPROVE'
											: 'D' === info
											? 'DONE'
											: '';
									}
								},
								{
									type: 'text',
									vModel: 'publishId',
									label: this.$t('label.publish.id'),
									readonly: true,
									clickEvt: this.goPublishLogPage
								},
								{
									type: 'text',
									vModel: 'publishDateTime',
									label: this.$t('label.publishTimestamp.request'),
									readonly: true,
									formatter: value => changeDateFormat(value),
									clickEvt: this.goPublishLogPage
								},
								{
									type: 'text',
									vModel: 'publishTimestamp',
									label: this.$t('label.publishTimestamp.complete'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'publishMessage',
									label: this.$t('label.publishMessage'),
									readonly: true
								}
							]
						]
					]
				}
			]
		};
	},
	methods: {
		goPublishLogPage() {
			const useInfoComp = this.$refs.detail.$refs.component[this.$refs.detail.$refs.component.length - 1];

			const { id, publishId, publishDateTime } = useInfoComp.detailData;

			if (!id || !publishId || !publishDateTime) {
				this.$alert({ type: 'warn', message: this.$t('msg.no.data') });
				return;
			}

			openNewTab(`105000`, () => {
				localStorage.setItem(
					`searchObj`,
					JSON.stringify({
						dateType: `P`,
						fromDateTime: publishDateTime,
						publishId,
						resourceId: id,
						resourceType: `S`,
						_pageSize: 10
					})
				);
			});
		}
	}
};
</script>
