<template>
	<div id="panel" ref="panel" :class="panelClass">
		<div class="panel-dialog">
			<div class="panel-content" :style="panelContentStyle">
				<div class="resize-bar" v-if="'add' !== panelMode"></div>

				<header id="panel-header" class="panel-header sub-bar">
					<a
						href="javascript:void(0);"
						class="btn btn-icon backBtn"
						:title="$t('label.goBack')"
						@click="goPreviousPanel"
						v-if="'modify' === panelMode"
						><i class="icon-left"></i>
					</a>
					<h2 class="sub-bar-tit" :style="{ width: `calc(100% - ${'modify' === panelMode ? 170 : 125}px )` }">
						{{ subBarTitle }}
						<span
							class="sub-bar-selected-tit ellipsis"
							:title="selectedInfoTitle ? selectedInfoTitle.substr(3) : ``"
							:style="{ width: `calc(100% - ${'modify' === panelMode ? 80 : 65}px )` }"
						>
							{{ selectedInfoTitle }}
						</span>
					</h2>
					<label class="control-label warningLabel" style="padding-bottom: 0px" v-if="editor && !newEditor">
						<b class="icon-star"></b>{{ $t(`msg.not.registered.rights.interface`) }}
					</label>
					<div class="ml-auto">
						<button
							type="button"
							class="btn-change-layout-horizon btn-icon"
							@click="changeLayout(`vertical`)"
							:title="$t(`label.detail.horizontal.view`)"
							v-if="'add' !== panelMode"
						>
							<img :src="getMenuIconUrl(`horizontal`)" class="center-block" />
						</button>
						<button
							type="button"
							class="btn-change-layout-vertical btn-icon"
							@click="changeLayout(`horizon`)"
							:title="$t(`label.detail.vertical.view`)"
							v-if="'add' !== panelMode"
						>
							<img :src="getMenuIconUrl(`vertical`)" class="center-block" />
						</button>
						<button type="button" class="btn-icon" @click="expandCollapsePanel" :title="$t('label.expand')" v-if="`add` !== panelMode">
							<i class="icon-expand"></i>
						</button>
						<button type="button" class="btn-icon" @click="closePanel"><i class="icon-close"></i></button>
					</div>
				</header>

				<ul class="nav nav-tabs flex-shrink-0">
					<li class="nav-item" v-for="(tabObj, index) in interfaceTabList" :key="tabObj.id">
						<a
							href="javascript:void(0);"
							:class="{ 'nav-link': true, active: selectedIdx === index }"
							@click="changeTab(index)"
							v-text="tabObj.label"
						>
						</a>
					</li>
					<li class="nav-item">
						<a
							href="javascript:void(0);"
							:class="{ 'nav-link': true, active: selectedIdx === interfaceTabList.length }"
							@click="changeTab(interfaceTabList.length)"
						>
							{{ $t('label.executionResult') }}
						</a>
					</li>
				</ul>
				<!-- tab-pane -->
				<div class="panel-body tab-content">
					<div
						:class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === idx }"
						v-for="(detailInfo, idx) in interfaceTabList"
						:key="idx"
					>
						<DetailBasic
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-if="'basic' === detailInfo.type"
						/>
						<DetailProperty
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'property' === detailInfo.type"
						/>
						<DetailGrid
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'grid' === detailInfo.type"
						/>
						<DetailCustom
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'custom' === detailInfo.type"
						/>
						<component
							ref="component"
							:is="require(`@/views/detail${detailInfo.url}.vue`).default"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'bundle' === detailInfo.type"
						/>
					</div>
					<DetailResult
						:class="{ 'tab-pane': true, active: selectedIdx === interfaceTabList.length && showResultTab }"
						:httpReqResult="httpReqResult"
					/>
				</div>

				<footer id="panel-footer" class="panel-footer sub-bar">
					<div class="ml-auto" v-if="'detail' === panelMode || 'done' === panelMode">
						<a href="javascript:void(0);" class="btn" @click="goMetaHistoryModal" v-if="metaHistoryBtn && metaHistoryPrivilege.viewer">
							<i class="icon-eye"></i>{{ $t('label.metaHistory') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="goPublishLogModal" v-if="publishLogBtn && viewer">
							<i class="icon-approve"></i>{{ $t('label.publishLog') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="goReferenceModal" v-if="referenceBtn && viewer">
							<i class="icon-edit"></i>{{ $t('label.referenceTreeInfo') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="publish(`create`)" v-if="publishBtn && newEditor">
							<i class="icon-approve"></i>{{ $t('label.publish') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="publish(`cancel`)" v-if="cancelPublishBtn && newEditor">
							<i class="icon-x"></i>{{ $t('label.publish.cancel') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="exportExcel" v-if="exportExcelBtn && viewer">
							<i class="icon-result"></i><span class="hide">{{ $t('label.download.statement') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn" @click="removeInfo" v-if="deleteBtn && newEditor">
							<i class="icon-delete"></i>{{ $t('label.delete') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="goModifyPanel" v-if="updateBtn && newEditor">
							<i class="icon-edit"></i>{{ $t('label.update') }}
						</a>
						<a href="javascript:void(0);" class="btn btn-primary" @click="restoreMeta" v-if="restoreBtn && newEditor">
							<i class="icon-edit"></i>{{ $t('label.restore') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'add' === panelMode || 'clone' === panelMode">
						<a href="javascript:void(0);" class="btn btn-primary" @click="saveInfo" v-if="insertBtn && newEditor">
							<i class="icon-plus"></i>{{ $t('label.insert') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'modify' === panelMode" @click="updateInfo">
						<a href="javascript:void(0);" class="btn btn-primary" v-if="updateBtn && newEditor">
							<i class="icon-edit"></i>{{ $t('label.update') }}
						</a>
					</div>
				</footer>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import Detail from '@/components/detail/Detail.vue';
import { HttpReq, parseFlattenObj, DetailToggleEventBus, getNumFromStr, WindowResizeEventBus } from '@/utils/common.js';

export default {
	name: 'InterfaceDetail',
	extends: Detail,
	data() {
		return {
			interfaceTabList: [],
			newEditor: false,
			isPublishService: false,
			isClickTabMenu: false
		};
	},
	computed: {
		...mapState(['userInfo', 'constantsInfo']),
		...mapGetters(['isUserAdmin'])
	},
	created() {
		this.isPublishService = this.constantsInfo.isPublishService;
	},
	watch: {
		panelMode: {
			immediate: true,
			handler() {
				let label = null;

				if (`add` === this.panelMode || 0 === this.selectedIdx) {
					label = 'first';
				} else if (this.selectedIdx === this.interfaceTabList.length - 1) {
					label = 'last';
				} else if (0 < this.interfaceTabList.length && this.interfaceTabList[this.selectedIdx]) {
					label = this.interfaceTabList[this.selectedIdx].label;
				}

				this.interfaceTabList = [];

				this.$nextTick(() => {
					this.interfaceTabList =
						`add` !== this.panelMode
							? this.tabList.filter(info => `interfaceAdd` !== info.id)
							: this.tabList.filter(info => `publishGrid` !== info.type);

					if (`first` === label || null === label) {
						this.selectedIdx = 0;
					} else if (`last` === label) {
						this.selectedIdx = this.interfaceTabList.length;
					} else {
						this.selectedIdx = this.interfaceTabList.findIndex(info => info.label === label);
					}

					this.$nextTick(() => {
						for (const key in this.detailData) {
							this.$set(this.detailData, key, this.detailData[key]);
						}

						if (!this.isClickTabMenu && `detail` === this.panelMode) {
							this.selectedIdx = 1;
						}
					});
				});
			}
		}
	},
	methods: {
		initDefaultDetailLayoutInfo() {
			this.panelClass.vertical = true;
			this.panelClass.horizon = false;
			this.panelClass.expand = `add` === this.panelMode ? true : this.constantsInfo.isDetailExpand;

			this.panelContentStyle.width = null;
			this.panelContentStyle.height = null;
			this.panelContentStyle[`margin-left`] = null;

			if (`add` !== this.panelMode) {
				localStorage.setItem(
					`detailLayoutInfo_${this.menuId}`,
					JSON.stringify({
						panelLayoutDirection: `vertical`,
						panelContentStyle: this.panelContentStyle,
						screenType: this.screenType,
						expand: this.panelClass.expand
					})
				);
			}
		},
		expandCollapsePanel(evt) {
			this.beforeDragEvt = null;

			this.panelContentStyle.width = null;
			this.panelContentStyle[`margin-left`] = null;
			this.panelContentStyle.height = null;

			this.panelClass.expand = `add` === this.panelMode ? true : !this.panelClass.expand;

			if (`add` !== this.panelMode) {
				localStorage.setItem(
					`detailLayoutInfo_${this.menuId}`,
					JSON.stringify({
						panelLayoutDirection: this.panelClass.horizon ? `horizon` : `vertical`,
						panelContentStyle: this.panelContentStyle,
						screenType: this.screenType,
						expand: this.panelClass.expand
					})
				);
			}

			this.$nextTick(() => {
				DetailToggleEventBus.$emit('MainResize');

				document.body.dispatchEvent(
					new CustomEvent('resize', {
						detail: {
							evt
						}
					})
				);
			});
		},
		bodyResize() {
			let isChangeVerticalLayout = false;

			if (991 < window.innerWidth) {
				if (`pc` !== this.screenType) isChangeVerticalLayout = true;
				else if (this.panelClass.horizon) {
					this.beforeDragEvt = null;

					if (this.panelContentStyle.width && document.body.clientWidth - 240 <= getNumFromStr(this.panelContentStyle.width)) {
						this.panelContentStyle.width = `${document.body.clientWidth - 240}px`;
						this.panelContentStyle[`margin-left`] = `calc(100% - ${this.panelContentStyle.width})`;

						if (`add` !== this.panelMode) {
							localStorage.setItem(
								`detailLayoutInfo_${this.menuId}`,
								JSON.stringify({
									panelLayoutDirection: `horizon`,
									panelContentStyle: this.panelContentStyle,
									screenType: this.screenType,
									expand: this.panelClass.expand
								})
							);
						}
					}

					this.$emit(`detailHorizonResize`, { detailWidth: document.querySelector(`#panel .panel-content`).clientWidth });
				}
			} else {
				if (this.panelClass.horizon) isChangeVerticalLayout = true;
				else if (767 < window.innerWidth) isChangeVerticalLayout = `tablet` !== this.screenType;
				else if (767 >= window.innerWidth) isChangeVerticalLayout = `mobile` !== this.screenType;
			}

			this.screenType = 991 < window.innerWidth ? `pc` : 767 < window.innerWidth ? `tablet` : `mobile`;

			if (isChangeVerticalLayout) this.changeLayout(`vertical`);

			if (this.panelClass.vertical) {
				if (document.body.clientHeight < document.querySelector(`#panel .panel-content`).clientHeight) {
					this.changeLayout(`vertical`);
				}
			}
		},
		detailResize(evt) {
			if (this.panelClass.vertical) {
				if (0 === evt.clientY) return;

				if (null !== this.panelContentStyle.width) this.panelContentStyle.width = null;
				if (null !== this.panelContentStyle[`margin-left`]) this.panelContentStyle[`margin-left`] = null;

				if (!this.beforeDragEvt) {
					this.beforeDragEvt = evt;
					this.panelContentStyle.height = `${document.querySelector(`#panel .panel-content`).clientHeight}px`;
				} else if (evt.clientY < this.beforeDragEvt.clientY) {
					this.panelContentStyle.height = `${getNumFromStr(this.panelContentStyle.height) + (this.beforeDragEvt.clientY - evt.clientY)}px`;
				} else {
					this.panelContentStyle.height = `${getNumFromStr(this.panelContentStyle.height) - (evt.clientY - this.beforeDragEvt.clientY)}px`;
				}

				if (document.body.clientHeight - (992 <= window.innerWidth ? 72 : 50) <= getNumFromStr(this.panelContentStyle.height)) {
					this.panelContentStyle.height = `${document.body.clientHeight - (992 <= window.innerWidth ? 72 : 50)}px`;
					return;
				}

				if (getNumFromStr(this.panelContentStyle.height) < 100) {
					this.panelContentStyle.height = `100px`;
					return;
				}
			} else {
				if (0 === evt.clientX) return;

				if (null !== this.panelContentStyle.height) {
					this.panelContentStyle.height = null;
				}

				if (!this.beforeDragEvt) {
					this.beforeDragEvt = evt;
					this.panelContentStyle.width = `${document.querySelector(`#panel .panel-content`).clientWidth}px`;
				} else if (evt.clientX < this.beforeDragEvt.clientX) {
					this.panelContentStyle.width = `${getNumFromStr(this.panelContentStyle.width) + (this.beforeDragEvt.clientX - evt.clientX)}px`;
				} else {
					this.panelContentStyle.width = `${getNumFromStr(this.panelContentStyle.width) - (evt.clientX - this.beforeDragEvt.clientX)}px`;
				}

				if (992 <= window.innerWidth && document.getElementById('sidebar') && !document.body.classList.contains('sidebar-toggled')) {
					if (document.body.clientWidth - 240 <= getNumFromStr(this.panelContentStyle.width)) {
						this.panelContentStyle.width = `${document.body.clientWidth - 240}px`;
						return;
					}

					if (getNumFromStr(this.panelContentStyle.width) < 240) {
						this.panelContentStyle.width = `240px`;
						return;
					}
				}

				this.panelContentStyle[`margin-left`] = `calc(100% - ${this.panelContentStyle.width})`;

				this.$emit(`detailHorizonResize`, { detailWidth: getNumFromStr(this.panelContentStyle.width) });
			}

			this.beforeDragEvt = evt;

			WindowResizeEventBus.$emit(`MainResize`);

			this.panelClass.expand = `add` === this.panelMode ? true : false;

			if (`add` !== this.panelMode) {
				localStorage.setItem(
					`detailLayoutInfo_${this.menuId}`,
					JSON.stringify({
						panelLayoutDirection: this.panelClass.horizon ? `horizon` : `vertical`,
						panelContentStyle: this.panelContentStyle,
						screenType: this.screenType,
						expand: this.panelClass.expand
					})
				);
			}
		},
		changeLayout(type) {
			this.panelClass.horizon = `horizon` === type;
			this.panelClass.vertical = `vertical` === type;
			this.panelClass.expand = `add` === this.panelMode ? true : this.constantsInfo.isDetailExpand;

			this.panelContentStyle.width = null;
			this.panelContentStyle.height = null;
			this.panelContentStyle[`margin-left`] = null;

			if (`add` !== this.panelMode) {
				localStorage.setItem(
					`detailLayoutInfo_${this.menuId}`,
					JSON.stringify({
						panelLayoutDirection: this.panelClass.horizon ? `horizon` : `vertical`,
						panelContentStyle: this.panelContentStyle,
						screenType: this.screenType,
						expand: this.panelClass.expand
					})
				);
			}

			this.beforeDragEvt = null;

			this.$nextTick(() => {
				this.$emit(`detailHorizonResize`, {
					detailWidth: this.panelClass.horizon ? document.querySelector(`#panel .panel-content`).clientWidth : null
				});
			});
		},
		async checkValidate() {
			const promise = new Promise(resolve => {
				const interfaceAdd = this.$refs.component.find(comp => comp.detailInfo && `interfaceAdd` === comp.detailInfo.id);

				const callback = evt => {
					interfaceAdd.$el.querySelector(`[data-ready]`).removeEventListener('setCustomData', callback);
					resolve(evt.detail);
				};

				interfaceAdd.$el.querySelector(`[data-ready]`).addEventListener('setCustomData', callback);

				interfaceAdd.getData();
			});

			return await promise;
		},
		async changeTab(idx) {
			if (`add` === this.panelMode) {
				const result = await this.checkValidate();

				if (!result.isValidate) return;

				if (this.$refs.component[idx] && `ModelRecordInfo` === this.$refs.component[idx].$options.name) {
					this.$refs.component[idx].filterService({
						interfaceType: result.targetInterfaceType,
						targetSystemId: result.targetSystemId
					});

					if (!this.isPublishService)
						this.$refs.component[idx].generateService(result.serviceAutoGenerated, {
							id: result.serviceId,
							serverId: result.serverId,
							interfaceType: result.targetInterfaceType,
							targetSystemId: result.targetSystemId,
							serviceAutoGenerated: result.serviceAutoGenerated,
							bizId: `Y` === result.serviceAutoGenerated ? result.bizId : ``
						});
				}

				if (this.$refs.component[idx] && `InterfaceExtendInfo` === this.$refs.component[idx].$options.name) {
					await this.$refs.component[idx].generateProperty({
						interfaceId: result.id,
						serverId: result.serverId,
						sourceSystemId: result.sourceSystemId,
						interfaceClass: result.interfaceClass
					});
				}
			}

			this.$nextTick(() => {
				if (this.$refs.component[idx] && this.$refs.component[idx].resize) this.$refs.component[idx].resize();
			});

			Detail.methods.changeTab.bind(this)(idx);

			this.isClickTabMenu = true;
		},
		async saveInfo() {
			const result = await this.checkValidate();

			if (!result.isValidate) return;

			if (!(await this.validateMappingInfo())) return;

			const modelRecordInfo = this.$refs.component.find(info => `ModelRecordInfo` === info.$options.name);

			modelRecordInfo.filterService({
				interfaceType: result.targetInterfaceType,
				targetSystemId: result.targetSystemId
			});

			if (!this.isPublishService)
				modelRecordInfo.generateService(result.serviceAutoGenerated, {
					id: result.serviceId,
					serverId: result.serverId,
					interfaceType: result.targetInterfaceType,
					targetSystemId: result.targetSystemId,
					serviceAutoGenerated: result.serviceAutoGenerated
				});

			const interfaceExtendInfo = this.$refs.component.find(info => `InterfaceExtendInfo` === info.$options.name);

			await interfaceExtendInfo.generateProperty({
				interfaceId: result.id,
				serverId: result.serverId,
				sourceSystemId: result.sourceSystemId,
				interfaceClass: result.interfaceClass
			});

			const dataList = modelRecordInfo.$refs.tuiGrid.invoke('getData');

			const promiseList = [];

			dataList.forEach(data => {
				if (data.requestRecordObject && 0 < data.requestRecordObject.fields.length) {
					const { id, fields } = data.requestRecordObject;

					fields.map(field => {
						field.pk.recordId = id;
					});
				}

				if (data.responseRecordObject && 0 < data.responseRecordObject.fields.length) {
					const { id, fields } = data.responseRecordObject;

					fields.map(field => {
						field.pk.recordId = id;
					});
				}

				const { serviceObject } = data;

				promiseList.push(
					new Promise((resolve, reject) => {
						(async () => {
							try {
								let result = null;

								const httpReq = new HttpReq(`/api/entity/service/object`);

								if (!this.isPublishService && serviceObject.serviceAutoGenerated) {
									result = await new HttpReq(`/api/entity/service/count`).read({ id: serviceObject.id });

									if (0 !== result.data.object) await httpReq.delete({ id: serviceObject.id });

									result = await httpReq.create(serviceObject);
								} else {
									result = await httpReq.update(serviceObject);
								}

								result.serviceObject = serviceObject;

								resolve(result);
							} catch (error) {
								reject(serviceObject);
							}
						})();
					})
				);
			});

			try {
				const resultList = await Promise.all(promiseList);

				const errorContentList = [];

				resultList.forEach(result => {
					if (`ok` === result.data.result) return;

					errorContentList.push({
						message: `${this.$t(`label.service`)} (${result.serviceObject.id}) ${this.$t(`label.edit`)} ${this.$t(`msg.alert.fail`)}`,
						stackTrace: result.data.error.map(({ message }) => message).join(`\n`)
					});
				});

				if (0 === errorContentList.length) {
					this.loadData(async () => {
						this.pk.forEach(pkInfo => {
							if ('undefined' === typeof this.tabAllData[pkInfo]) {
								this.tabAllData[pkInfo] = '';
							}
						});

						this.setData(await this.httpReq.create(this.tabAllData), 'msg.alert.insert');
					});
				} else {
					this.httpReqResult = {
						content: errorContentList
					};
					this.selectedIdx = this.interfaceTabList.length;
					this.showResultTab = true;
				}
			} catch (serviceObject) {
				this.httpReqResult = {
					content: [{ message: `${serviceObject.id} was failed.` }]
				};
				this.selectedIdx = this.interfaceTabList.length;
				this.showResultTab = true;
			}
		},
		async updateInfo() {
			if (!(await this.validateMappingInfo())) return;

			const modelRecordInfo = this.$refs.component.find(info => `ModelRecordInfo` === info.$options.name);

			const dataList = modelRecordInfo.$refs.tuiGrid.invoke('getData');

			dataList.forEach(data => {
				if (data.requestRecordObject && 0 < data.requestRecordObject.fields.length) {
					const { id, fields } = data.requestRecordObject;

					fields.map(field => {
						field.pk.recordId = id;
					});
				}

				if (data.responseRecordObject && 0 < data.responseRecordObject.fields.length) {
					const { id, fields } = data.responseRecordObject;

					fields.map(field => {
						field.pk.recordId = id;
					});
				}
			});

			const promiseList = [];

			dataList.forEach(data => {
				const { serviceObject } = data;

				delete serviceObject.updateTimestamp;

				promiseList.push(
					new Promise((resolve, reject) => {
						(async () => {
							try {
								const httpReq = new HttpReq(`/api/entity/service/object`);

								const result = await httpReq.update(serviceObject);

								result.serviceObject = serviceObject;
								resolve(result);
							} catch (error) {
								reject(serviceObject);
							}
						})();
					})
				);
			});

			try {
				const resultList = await Promise.all(promiseList);

				const errorContentList = [];

				resultList.forEach(result => {
					if (`ok` === result.data.result) return;

					errorContentList.push({
						message: `${this.$t(`label.service`)} (${result.serviceObject.id}) ${this.$t(`label.edit`)} ${this.$t(`msg.alert.fail`)}`,
						stackTrace: result.data.error.map(({ message }) => message).join(`\n`)
					});
				});

				if (0 === errorContentList.length) {
					this.loadData(async () => {
						this.pk.forEach(pkInfo => {
							if ('undefined' === typeof this.tabAllData[pkInfo]) {
								this.tabAllData[pkInfo] = this.cloneInfo[pkInfo];
							}
						});

						const result = await this.httpReq.update(this.tabAllData);

						this.setData(result, 'msg.alert.update');
					});
				} else {
					this.httpReqResult = {
						content: errorContentList
					};
					this.selectedIdx = this.interfaceTabList.length;
					this.showResultTab = true;
				}
			} catch (serviceObject) {
				this.httpReqResult = {
					content: [{ message: `${serviceObject.id} was failed.` }]
				};
				this.selectedIdx = this.interfaceTabList.length;
				this.showResultTab = true;
			}
		},
		async loadData(callBack) {
			this.$refs.component.forEach((comp, idx) => {
				if ('custom' !== this.interfaceTabList[idx].type && `useInfo` !== this.interfaceTabList[idx].id && comp.getData) {
					this.tabAllData = merge(this.tabAllData, comp.getData());
				}
			});

			if (this.tabAllData.requestRecordObject && 0 < this.tabAllData.requestRecordObject.fields.length) {
				const { id, fields } = this.tabAllData.requestRecordObject;

				fields.map(field => {
					field.pk.recordId = id;
				});
			}

			if (this.tabAllData.interfaceResponses && 0 < this.tabAllData.interfaceResponses.length) {
				this.tabAllData.interfaceResponses.forEach(interfaceResponse => {
					if (interfaceResponse.recordObject && 0 < interfaceResponse.recordObject.fields.length) {
						interfaceResponse.recordObject.fields.map(field => {
							field.pk.recordId = interfaceResponse.pk.recordId;
						});
					}
				});
			}

			if (`add` === this.panelMode || `modify` === this.panelMode) {
				if (this.tabAllData.requestRecordObject && this.tabAllData.requestRecordObject.id) {
					this.tabAllData.requestRecordObject.metaDomain = this.tabAllData.metaDomain;
				}

				if (this.tabAllData.interfaceResponses && 0 < this.tabAllData.interfaceResponses.length) {
					this.tabAllData.interfaceResponses.forEach(responseRecordObject => {
						if (responseRecordObject && responseRecordObject.id) {
							responseRecordObject.metaDomain = this.tabAllData.metaDomain;
						}
					});
				}
			}

			if (`detail` !== this.panelMode && this.tabAllData.interfaceProperties) {
				if (
					this.tabAllData.interfaceProperties.find(
						interfaceProperty =>
							null === interfaceProperty.pk.keyId ||
							0 === String(interfaceProperty.pk.keyId).trim().length ||
							null === interfaceProperty.property ||
							0 === String(interfaceProperty.property).trim().length
					)
				) {
					this.tabAllData = {};

					this.$alert({
						type: 'warn',
						message: this.$t(`msg.empty.extended.information`)
					});

					return;
				}
			}

			if (-1 === this.interfaceTabList.findIndex(tabInfo => 'custom' === tabInfo.type)) {
				if (callBack) callBack();
				return;
			}

			this.$startSpinner();

			const promiseAllList = [];

			this.$refs.component.forEach((comp, idx) => {
				if (!(comp.detailInfo && 'custom' === comp.detailInfo.type)) return;

				promiseAllList.push(
					new Promise(resolve => {
						const elementDiv = comp.$el.querySelector('[data-ready]');

						const customDataCallBack = e => {
							this.tabAllData = merge(this.tabAllData, e.detail);
							elementDiv.removeEventListener('setCustomData', customDataCallBack);
							resolve(idx);
						};

						elementDiv.addEventListener('setCustomData', customDataCallBack);

						comp.getData();
					})
				);
			});

			try {
				await Promise.all(promiseAllList);
				if (callBack) callBack();
			} catch (error) {
				this.$alert({ type: 'warn', message: this.$t('msg.no.data') });
			} finally {
				this.$stopSpinner();
			}
		},
		async setData(result, message, successCallback, type = null) {
			const resultState = 'ok' === result.data.result;

			this.httpReqResult = {
				state: resultState,
				content: resultState ? result.data.response : result.data.error
			};

			this.showResultTab = true;

			if (resultState) {
				this.selectedIdx = `add` === this.panelMode ? this.interfaceTabList.length - 1 : this.interfaceTabList.length;
			} else {
				this.selectedIdx = this.interfaceTabList.length;
				this.tabAllData = {};
				return;
			}

			if (`msg.alert.delete` !== message) {
				const param = {};

				for (const key in this.tabAllData) {
					if (-1 === this.pk.indexOf(key)) continue;
					param[key] = this.tabAllData[key];
				}

				if (0 === Object.keys(param).length) {
					for (const key in this.detailData) {
						if (-1 === this.pk.indexOf(key)) continue;
						param[key] = this.detailData[key];
					}
				}

				const info = await this.httpReq.read(param);

				if (`ok` === info.data.result) {
					this.detailData = parseFlattenObj(cloneDeep(info.data.object));

					if ('add' === this.panelMode || 'clone' === this.panelMode) {
						this.cloneInfo = parseFlattenObj(cloneDeep(info.data.object));
					}
				}
			} else {
				this.detailData = {};
				this.cloneInfo = {};
			}

			if (`delete` !== type) this.panelMode = 'done';

			this.tabAllData = {};

			this.$alert({ type: 'normal', message: this.$t(message) });

			this.$emit('search');

			if (successCallback) successCallback();
		},
		openPanel() {
			if (`detail` === this.panelMode) {
				if (this.isUserAdmin) {
					this.newEditor = true;
				} else {
					const bTotalUserPrivileges = this.userInfo.totalUserPrivileges.filter(
						userPrivilege => `b` === userPrivilege.privilegeType && userPrivilege.privilegeId === this.detailData.privilegeId
					);

					this.newEditor = this.editor && 0 < bTotalUserPrivileges.length;
				}
			} else {
				this.newEditor = this.editor;
			}

			const subBarTitleMsg = {
				detail: 'Detail',
				done: 'Detail',
				add: 'Insert',
				clone: 'Insert',
				modify: 'Update'
			};

			this.subBarTitle = subBarTitleMsg[this.panelMode];

			if ('done' !== this.panelMode && this.showResultTab) {
				this.selectedIdx = 0;
				this.showResultTab = false;
			}

			if (document.body.classList.contains('fixed')) {
				document.body.classList.remove(
					'panel-open-detail',
					'panel-open-add',
					'panel-open-modify',
					'panel-open-done',
					'panel-open-clone',
					'fixed'
				);

				this.$refs.panel.style.display = 'none';
			}

			document.body.classList.add('fixed');

			this.$refs.panel.style.display = 'block';

			document.body.classList.add(`panel-open-${this.panelMode}`);

			this.screenType = 991 < window.innerWidth ? `pc` : 767 < window.innerWidth ? `tablet` : `mobile`;

			if (`add` !== this.panelMode) {
				if (localStorage.getItem(`detailLayoutInfo_${this.menuId}`)) {
					const { panelLayoutDirection, panelContentStyle, screenType, expand } = JSON.parse(
						localStorage.getItem(`detailLayoutInfo_${this.menuId}`)
					);

					if (screenType !== this.screenType) {
						this.initDefaultDetailLayoutInfo();
					} else {
						this.panelClass.horizon = `horizon` === panelLayoutDirection;
						this.panelClass.vertical = `vertical` === panelLayoutDirection;
						this.panelClass.expand = expand;
						this.panelContentStyle = panelContentStyle;
					}
				} else {
					this.initDefaultDetailLayoutInfo();
				}
			} else {
				if (localStorage.getItem(`detailLayoutInfo_${this.menuId}`)) {
					const { panelLayoutDirection } = JSON.parse(localStorage.getItem(`detailLayoutInfo_${this.menuId}`));

					this.panelClass.horizon = `horizon` === panelLayoutDirection;
					this.panelClass.vertical = `vertical` === panelLayoutDirection;
					this.panelClass.expand = true;
					this.panelContentStyle.width = null;
					this.panelContentStyle.height = null;
					this.panelContentStyle[`margin-left`] = null;
				} else {
					this.initDefaultDetailLayoutInfo();
				}
			}

			this.$nextTick(() => {
				DetailToggleEventBus.$emit('MainResize');

				if (this.panelClass.horizon)
					this.$emit(`detailHorizonResize`, { detailWidth: document.querySelector(`#panel .panel-content`).clientWidth });
			});
		},
		publish(mode) {
			const checkContent = `create` === mode ? 0 === this.detailData.interfaceServices.length : 'R' !== this.detailData.publishStatus;
			const warnMessage = {
				type: 'warn',
				isXSSMode: `create` !== mode,
				message: this.$t(`create` === mode ? `msg.no.service.registered.interface` : `msg.alert.cancel.publish.status.warn`)
			};

			if (checkContent) {
				this.$alert(warnMessage);
				return;
			}

			if (this.isPublishService) {
				this.$confirm({
					type: 'warn',
					message: this.$t(`msg.deployed.interface`),
					callback: () => {
						Detail.methods.publish.bind(this)(mode);
					}
				});
			} else {
				Detail.methods.publish.bind(this)(mode);
			}
		},
		async validateMappingInfo() {
			function initTreeData(fields, parentId, depth = 1) {
				if (!fields) return;

				fields = cloneDeep(fields);

				const rtnArr = [];

				fields.forEach(field => {
					field.id = `${parentId}\\${field.pk.fieldId}`;

					if (field.recordObject) {
						if ('N' !== field.arrayType) field.id += `[i${depth}]`;

						rtnArr.push(...initTreeData(field.recordObject.fields, field['id'], 'N' !== field.arrayType ? depth + 1 : depth));
					}

					rtnArr.push(field.id);
				});

				return rtnArr;
			}

			const promise = new Promise(resolve => {
				const messageArr = [];

				this.loadData(async () => {
					const { requestRecordObject, interfaceResponses } = this.tabAllData;

					const interfaceReqRecordObject = requestRecordObject;
					const interfaceResRecordObjectArr = interfaceResponses.map(({ recordObject }) => recordObject);

					const modelRecordInfo = this.$refs.component.find(info => `ModelRecordInfo` === info.$options.name);

					const dataList = modelRecordInfo.$refs.tuiGrid.invoke('getData');

					dataList.forEach(data => {
						const { requestMappingObject, responseMappingObject, serviceObject } = data;

						const { requestRecordObject, responseRecordObject } = serviceObject;

						if (requestMappingObject && requestMappingObject.mappingDetails && 0 < requestMappingObject.mappingDetails.length) {
							requestMappingObject.mappingDetails.forEach(({ mappingRuleObject }) => {
								const sourceFieldIdList = initTreeData(interfaceReqRecordObject.fields, `$0`);

								mappingRuleObject.mappingRuleDetails.forEach(({ mappingExpression, targetFieldPath }) => {
									if (!mappingExpression.startsWith(`$`)) return;

									const findId = sourceFieldIdList.find(sourceFieldId => sourceFieldId === mappingExpression);

									if (!findId) {
										const tmpMappingExpression = mappingExpression.split(`\\`);
										tmpMappingExpression[0] = interfaceReqRecordObject.id;

										const tmpTargetFieldPath = targetFieldPath.split(`\\`);
										tmpTargetFieldPath[0] = requestRecordObject.id;

										const message =
											`${this.$t(`label.request`)} ${this.$t(`label.mapping`)} > ` +
											`${tmpMappingExpression.join(`:`)} (Source) > ` +
											`${tmpTargetFieldPath.join(`:`)} (Target) > ` +
											`${this.$t(`msg.source.field.not.found`)}`;

										messageArr.push(message);
									}
								});

								const targetFieldIdList = initTreeData(requestRecordObject.fields, ``);

								mappingRuleObject.mappingRuleDetails.forEach(({ mappingExpression, targetFieldPath }) => {
									if (!mappingExpression.startsWith(`$`)) return;

									const findId = targetFieldIdList.find(targetFieldId => targetFieldId === targetFieldPath);

									if (!findId) {
										const tmpMappingExpression = mappingExpression.split(`\\`);
										tmpMappingExpression[0] = interfaceReqRecordObject.id;

										const tmpTargetFieldPath = targetFieldPath.split(`\\`);
										tmpTargetFieldPath[0] = requestRecordObject.id;

										const message =
											`${this.$t(`label.request`)} ${this.$t(`label.mapping`)} > ` +
											`${tmpMappingExpression.join(`:`)} (Source) >` +
											`${tmpTargetFieldPath.join(`:`)} (Target) > ` +
											`${this.$t(`msg.target.field.not.found`)}`;

										messageArr.push(message);
									}
								});
							});
						}

						if (responseMappingObject && responseMappingObject.mappingDetails && 0 < responseMappingObject.mappingDetails.length) {
							responseMappingObject.mappingDetails.forEach(({ mappingRuleObject }) => {
								const sourceRecordObjectList = [responseRecordObject];

								if (requestRecordObject) sourceRecordObjectList.push(requestRecordObject);

								if (interfaceReqRecordObject) sourceRecordObjectList.push(interfaceReqRecordObject);

								const sourceFieldIdList = [];

								sourceRecordObjectList.forEach((recordObject, idx) => {
									sourceFieldIdList.push(...initTreeData(recordObject.fields, `$${idx}`));
								});

								mappingRuleObject.mappingRuleDetails.forEach(({ mappingExpression, targetFieldPath }) => {
									if (!mappingExpression.startsWith(`$`)) return;

									const findId = sourceFieldIdList.find(sourceFieldId => sourceFieldId === mappingExpression);

									if (!findId) {
										const tmpMappingExpression = mappingExpression.split(`\\`);
										tmpMappingExpression[0] = sourceRecordObjectList[tmpMappingExpression[0].split(`$`)[1]].id;

										const tmpTargetFieldPath = targetFieldPath.split(`\\`);
										tmpTargetFieldPath[0] = interfaceResRecordObjectArr.find(
											interfaceResRecordObject => interfaceResRecordObject.id === mappingRuleObject.recordId
										).id;

										const message =
											`${this.$t(`label.response`)} ${this.$t(`label.mapping`)} > ` +
											`${tmpMappingExpression.join(`:`)} (Source) > ` +
											`${tmpTargetFieldPath.join(`:`)} (Target) > ` +
											`${this.$t(`msg.source.field.not.found`)}`;

										messageArr.push(message);
									}
								});

								const targetFieldIdList = initTreeData(
									interfaceResRecordObjectArr.find(
										interfaceResRecordObject => interfaceResRecordObject.id === mappingRuleObject.recordId
									).fields,
									``
								);

								mappingRuleObject.mappingRuleDetails.forEach(({ mappingExpression, targetFieldPath }) => {
									if (!mappingExpression.startsWith(`$`)) return;

									const findId = targetFieldIdList.find(targetFieldId => targetFieldId === targetFieldPath);

									if (!findId) {
										const tmpMappingExpression = mappingExpression.split(`\\`);
										tmpMappingExpression[0] = sourceRecordObjectList[tmpMappingExpression[0].split(`$`)[1]].id;

										const tmpTargetFieldPath = targetFieldPath.split(`\\`);
										tmpTargetFieldPath[0] = interfaceResRecordObjectArr.find(
											interfaceResRecordObject => interfaceResRecordObject.id === mappingRuleObject.recordId
										).id;

										const message =
											`${this.$t(`label.response`)} ${this.$t(`label.mapping`)} > ` +
											`${tmpMappingExpression.join(`:`)} (Source) > ` +
											`${tmpTargetFieldPath.join(`:`)} (Target) > ` +
											`${this.$t(`msg.target.field.not.found`)}`;

										messageArr.push(message);
									}
								});
							});
						}
					});

					if (0 < messageArr.length) {
						this.httpReqResult = {
							content: messageArr.map(message => ({ message }))
						};

						this.selectedIdx = this.interfaceTabList.length;
						this.showResultTab = true;
					}

					resolve(messageArr);
				});
			});

			return 0 === (await promise).length;
		}
	}
};
</script>
