<script>
import SystemParent from '@/views/detail/system/SystemParent.vue';

export default {
	name: 'SystemConsumer',
	extends: SystemParent,
	methods: {
		getData() {
			const systemSources = [];

			this.getSystemData(systemSources);

			return {
				systemSources
			};
		},
		async setPropertyList() {
			const { systemSources } = this.detailData;

			this.setSystemList(systemSources);
		}
	}
};
</script>
