<template>
	<div class="interfaceClassTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-wrap">
				<div class="form-table-head">
					<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('label.insert')">
						<i class="icon-plus-circle"></i>
					</button>
					<label class="col">{{ this.$t('label.interface.class') }}</label>
					<label class="col">{{ `Source ${this.$t('label.interface.type')}` }}</label>
					<label class="col">{{ `Target ${this.$t('label.interface.type')}` }}</label>
				</div>
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('label.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div class="detail-content-common">
							<select
								class="form-control view-disabled"
								v-model="propertyObj['pk.interfaceClass']"
								:readonly="readonly"
								@change="interfaceClassChangeEvt(bodyIndex, $event)"
							>
								<option
									v-for="(interfaceClass, idx) in interfaceClassList"
									:value="interfaceClass.id"
									v-text="interfaceClass.name"
									:key="`interfaceClass_${idx}`"
								></option>
							</select>
						</div>
					</div>
					<div class="col">
						<div class="detail-content-common">
							<select class="form-control view-disabled" v-model="propertyObj[`sourceType`]" readonly>
								<option
									v-for="(interfaceType, idx) in interfaceTypeList"
									:value="interfaceType.pk.propertyKey"
									v-text="interfaceType.propertyValue"
									:key="`source_${idx}`"
								></option>
							</select>
						</div>
					</div>
					<div class="col">
						<div class="detail-content-common">
							<select class="form-control view-disabled" v-model="propertyObj[`targetType`]" readonly>
								<option
									v-for="(interfaceType, idx) in interfaceTypeList"
									:value="interfaceType.pk.propertyKey"
									v-text="interfaceType.propertyValue"
									:key="`target_${idx}`"
								></option>
							</select>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import isEmpty from 'lodash/isEmpty';
import DetailProperty from '@/components/detail/DetailProperty.vue';
import { HttpReq, parseFlattenObj, getUUID, parseHierarchyObj } from '@/utils/common.js';

export default {
	name: `ServerInterface`,
	extends: DetailProperty,
	async created() {
		let httpReq = new HttpReq(`/api/entity/interfaceClass/search`);

		let result = await httpReq.read({
			object: {},
			limit: null,
			next: null,
			reverseOrder: false
		});

		this.interfaceClassList = result.data.object;

		httpReq = new HttpReq(`/api/page/properties`);

		result = await httpReq.read({ pk: { propertyId: 'Interface.Type' }, orderByKey: true });

		this.interfaceTypeList = result.data.object;

		this.setPropertyList();
	},
	data() {
		return {
			interfaceClassList: [],
			interfaceTypeList: []
		};
	},
	methods: {
		addProperty() {
			const rowInfo = {
				idx: getUUID(),
				'pk.interfaceClass': null,
				sourceType: null,
				targetType: null
			};

			this.propertyList.push(rowInfo);
		},
		setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			if (0 === this.interfaceClassList.length) return;

			this.propertyList = [...this.detailData[`serverInterfaces`]].map(property => parseFlattenObj(property));

			this.propertyList.forEach(propertyInfo => {
				propertyInfo.idx = getUUID();

				const { sourceInterfaceType, targetInterfaceType } = this.interfaceClassList.find(
					({ id }) => id === propertyInfo[`pk.interfaceClass`]
				);

				propertyInfo.sourceType = sourceInterfaceType;
				propertyInfo.targetType = targetInterfaceType;
			});
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);
		},
		getData() {
			const rtnArr = [];

			this.propertyList.forEach(info => {
				const obj = {};

				for (const key in info) {
					if (`idx` === key) continue;
					obj[key] = info[key];
				}

				rtnArr.push(parseHierarchyObj(obj));
			});

			return {
				serverInterfaces: rtnArr
			};
		},
		interfaceClassChangeEvt(idx, evt) {
			const { sourceInterfaceType, targetInterfaceType } = this.interfaceClassList.find(({ id }) => id === evt.target.value);

			this.propertyList[idx].sourceType = sourceInterfaceType;
			this.propertyList[idx].targetType = targetInterfaceType;
		}
	}
};
</script>
