<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.mapping.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<input type="text" v-model="object.pk.mappingId" class="form-control view-disabled dataKey" :readonly="readonly" />
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.mapping.name') }}</span>
					</label>
					<input type="text" v-model="object.mappingName" class="form-control view-disabled" :readonly="readonly" />
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('label.desc') }}</span>
					</label>
					<textarea v-model="object.mappingDesc" class="form-control view-disabled" :readonly="readonly" style="min-height: 150px" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		mode: String,
		detailData: Object
	},
	name: 'MappingInfo',
	computed: {
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	data() {
		return {
			object: {
				pk: {
					mappingId: null
				},
				mappingName: null,
				mappingDesc: null
			}
		};
	},
	watch: {
		object: {
			deep: true,
			handler() {
				this.$emit('setMappingId', this.object.pk.mappingId);
			}
		}
	},
	methods: {
		getData() {
			return this.object;
		}
	}
};
</script>
