<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/qna/Detail.vue';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'QNA',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		this.initBundleInfo();
	},
	data() {
		return {
			cudUrl: `/api/entity/qna/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'text', vModel: 'title', label: this.$t('label.title'), placeholder: this.$t('msg.enterTitle') },
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/qna/search`,
				totalCntUrl: `/api/entity/qna/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('label.title'),
							name: 'title',
							width: '40%'
						},
						{
							header: this.$t('label.writer'),
							name: 'questionUserId',
							width: '30%'
						},
						{
							header: this.$t('label.created.date'),
							name: 'pk.questionCreateTimestamp',
							width: '30%',
							align: 'center',
							formatter: obj => changeDateFormat(obj.value)
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['pk.id', 'pk.questionCreateTimestamp'],
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: false },
				{
					id: 'update',
					isUse: detailData => {
						if (Object.keys(detailData).length === 0) return false;

						const updatable =
							detailData.questionUserId === this.$refs.detail.userInfo.userId
								? false
								: detailData.answerUserId === this.$refs.detail.userInfo.userId;

						return this.$refs.detail.isUserAdmin || updatable;
					}
				},
				{ id: 'delete', isUse: true }
			],
			detailTabList: [],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	},
	methods: {
		initBundleInfo() {
			this.detailTabList = [
				{
					type: 'basic',
					label: this.$t('label.question'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'title',
									label: this.$t('label.title'),
									readonly: object => {
										if (this.$refs.detail.addMode) return false;

										const isWriter = object.questionUserId === this.$refs.detail.userInfo.userId;

										if (this.$refs.detail.isUserAdmin) {
											return isWriter ? false : true;
										} else {
											return !object.answer && isWriter ? false : true;
										}
									}
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'question',
									label: this.$t('label.question') + this.$t('label.content'),
									height: '150px',
									readonly: object => {
										if (this.$refs.detail.addMode) return false;

										const isWriter = object.questionUserId === this.$refs.detail.userInfo.userId;

										if (this.$refs.detail.isUserAdmin) {
											return isWriter ? false : true;
										} else {
											return isWriter && !object.answer ? false : true;
										}
									}
								}
							]
						],
						[
							[
								{
									type: 'text',
									vModel: 'questionUserId',
									label: this.$t('label.writer'),
									readonly: true
								}
							],
							[
								{
									type: 'text',
									vModel: 'pk.questionCreateTimestamp',
									label: this.$t('label.created.date'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				},
				{
					id: 'Answer',
					type: 'basic',
					label: this.$t('label.answer'),
					content: [
						[
							[
								{
									type: 'textarea',
									vModel: 'answer',
									label: this.$t('label.answer') + this.$t('label.content'),
									height: '150px',
									readonly: object => {
										if ('detail' === this.$refs.detail.panelMode || 'done' === this.$refs.detail.panelMode) return true;

										const answerAvailable =
											object.questionUserId === this.$refs.detail.userInfo.userId
												? false
												: object.answerUserId === this.$refs.detail.userInfo.userId;

										return this.$refs.detail.isUserAdmin || (answerAvailable && this.editor) ? false : true;
									}
								}
							]
						],
						[
							[
								{
									type: 'text',
									vModel: 'answerUserId',
									label: this.$t('label.writer'),
									readonly: true
								}
							],
							[
								{
									type: 'text',
									vModel: 'answerUpdateTimestamp',
									label: this.$t('label.created.date'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			];

			if (localStorage.getItem('searchObj')) {
				this.initNewTabSearchObj();
			} else {
				this.$nextTick(() => {
					this.search(this.$refs.search.getSearchObj());
				});
			}
		}
	}
};
</script>
