<template>
	<div :class="classObject">
		<ul id="accordionResult" class="collapse-list collapse-list-result m-0">
			<li v-if="!httpReqResult.content">
				<!-- collapse-item class 제거 -->
				<button type="button" class="collapse-link collapsed" data-toggle="collapse" data-target="#collapseResult">
					<i class="iconb-compt mr-2"></i>
					<span class="txt">Successfully done.</span>
				</button>
				<div id="collapseResult" class="collapse" data-parent="#accordionResult"></div>
			</li>
			<li class="collapse-item" v-for="(resultObj, resultIdx) in httpReqResult.content" :key="resultIdx">
				<button type="button" class="collapse-link collapsed" data-toggle="collapse" :data-target="`#collapseResult${resultIdx}`">
					<i :class="resultObj.success ? 'iconb-compt mr-2' : 'iconb-danger mr-2'"></i>
					<span
						class="txt"
						v-text="`${resultObj.instanceId} was ${resultObj.success ? 'succeed.' : 'failed.'}`"
						v-if="httpReqResult.state"
					></span>

					<span
						class="txt"
						v-text="
							resultObj.className
								? resultObj.className
								: `${resultObj.field ? `Field(${resultObj.field}) : ` : ''} ${resultObj.message ? `${resultObj.message}` : ''}`
						"
						v-else
					></span>
				</button>
				<div :id="`collapseResult${resultIdx}`" class="collapse" data-parent="#accordionResult">
					<div class="collapse-content" v-if="resultObj.className">
						<pre>{{ resultObj.message ? resultObj.message : '' }}</pre>
					</div>
					<div class="collapse-content" v-else-if="resultObj.response || resultObj.stackTrace">
						<pre>{{ httpReqResult.state ? resultObj.response : resultObj.stackTrace }}</pre>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'DetailResult',
	props: {
		classObject: Object,
		httpReqResult: {
			type: Object,
			default: () => {
				return { content: [] };
			}
		}
	}
};
</script>

<style></style>
