<template>
	<div>
		<div class="row frm-row" v-for="(contentRow, contentRowIdx) in detailInfo.content" :key="contentRowIdx">
			<div :class="'col-lg-' + 12 / contentRow.length" v-for="(contentCol, contentColIdx) in contentRow" :key="contentColIdx">
				<div class="form-group" v-for="(colInfo, colInfoIdx) in contentCol" :key="colInfoIdx">
					<label class="control-label">
						<span>
							{{ colInfo.label }}
							<span class="letterLength" v-if="regExpTypeList.includes(colInfo.type) && !getReadonly(colInfo)">
								{{ `(${curLengthCnt[colInfo.vModel]}/${maxLengthCnt[colInfo.vModel]})` }}
							</span>
						</span>
						<b class="icon-star" v-if="colInfo.isPK || colInfo.isRequired"></b>
					</label>
					<label class="control-label warningLabel" v-if="colInfo.warning && !readonly">
						<span>{{ colInfo.warningLabel }}</span>
					</label>
					<div
						:class="`detail-content-${regExpTypeList.includes(colInfo.type) && !getReadonly(colInfo) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${regExpTypeList.includes(colInfo.type) && !getReadonly(colInfo) ? '8px' : '0px'}` }"
					>
						<div class="input-group" v-if="'text' === colInfo.type">
							<inputText
								:ref="`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
								:vModel="colInfo.vModel"
								:classObject="{
									'form-control': getReadonly(colInfo),
									[`regExp-${colInfo.type}`]: !getReadonly(colInfo),
									'view-disabled': true,
									dataKey: colInfo.isPK
								}"
								:readonly="readonly || getReadonly(colInfo)"
								:regExpType="colInfo.regExpType"
								:changeEvt="colInfo.changeEvt"
								@setLengthCnt="setLengthCnt"
							/>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="colInfo.clickEvt($refs[`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`][0])"
								v-if="readonly && colInfo.clickEvt"
								:title="$t('label.open')"
							>
								<i class="icon-link" style="margin-right: 0px"></i>
							</button>
						</div>
						<selectBox
							:ref="`selectBox_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || getReadonly(colInfo)"
							:optionInfo="colInfo.optionInfo"
							v-else-if="'select' === colInfo.type"
						/>
						<dataList
							:ref="`dataList_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{
								'form-control': getReadonly(colInfo),
								[`regExp-${colInfo.type}`]: !getReadonly(colInfo),
								'view-disabled': true
							}"
							:readonly="readonly || getReadonly(colInfo)"
							:optionInfo="colInfo.optionInfo"
							:regExpType="colInfo.regExpType"
							@setLengthCnt="setLengthCnt"
							v-else-if="'dataList' === colInfo.type"
						/>
						<textArea
							:ref="`textArea_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{
								'form-control': getReadonly(colInfo),
								[`regExp-${colInfo.type}`]: !getReadonly(colInfo),
								'view-disabled': true
							}"
							:readonly="readonly || getReadonly(colInfo)"
							:height="colInfo.height"
							:regExpType="colInfo.regExpType"
							@setLengthCnt="setLengthCnt"
							v-else-if="'textarea' === colInfo.type"
						/>
						<radioGroup
							:ref="`radioGroup_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{ 'custom-control': true, 'custom-radio single': true, 'view-disabled': true }"
							:readonly="readonly || getReadonly(colInfo)"
							:radioInfo="colInfo.radioInfo"
							v-else-if="'radio' === colInfo.type"
						/>
						<checkBoxGroup
							:ref="`checkBoxGroup_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:checkboxInfo="colInfo.checkboxInfo"
							:optionListData="this.transactionList"
							:classObject="{ 'custom-control': true, 'custom-checkbox single': true, 'view-disabled': true }"
							v-else-if="'checkBox' === colInfo.type"
						/>
						<dateRange
							:ref="`dateRange_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:readonly="readonly || getReadonly(colInfo)"
							v-else-if="'singleDaterange' === colInfo.type"
						/>
						<timePicker
							:ref="`timePicker_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
							:vModel="colInfo.vModel"
							:classObject="{ 'form-control': true }"
							:readonly="readonly || getReadonly(colInfo)"
							v-if="'time' === colInfo.type"
						/>
						<div class="input-group" v-else-if="'password' === colInfo.type" style="flex-wrap: nowrap">
							<passWord
								:ref="`passWord_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
								:vModel="colInfo.vModel"
								:classObject="{
									'form-control': getReadonly(colInfo),
									[`regExp-${colInfo.type}`]: !getReadonly(colInfo),
									'view-disabled': true
								}"
								:readonly="readonly || getReadonly(colInfo)"
								:regExpType="colInfo.regExpType"
								@setLengthCnt="setLengthCnt"
							/>
							<i
								class="icon-eye password-eye"
								@click="$refs[`passWord_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`][0].changeType()"
								v-if="!readonly"
							></i>
						</div>
						<div class="input-group" v-else-if="'modal' === colInfo.type">
							<inputText
								:ref="`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`"
								:vModel="colInfo.vModel"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: colInfo.isPK }"
								:readonly="true"
							/>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="colInfo.clickEvt($refs[`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`][0])"
								v-if="readonly && colInfo.clickEvt"
								:title="$t('label.open')"
							>
								<i class="icon-link" style="margin-right: 0px"></i>
							</button>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="openModal(colInfo.modalInfo, $refs[`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`])"
								v-if="!(readonly || getReadonly(colInfo))"
							>
								<i class="icon-srch"></i>{{ $t('label.search') }}
							</button>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="$refs[`inputText_${contentRowIdx}_${contentColIdx}_${colInfoIdx}`][0].setData(null)"
								v-if="!(readonly || getReadonly(colInfo))"
								:title="$t('label.initialize')"
							>
								<i class="icon-reset" style="margin-right: 0px"></i>
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import { mapState } from 'vuex';
import { getRegExpInfo } from '@/utils/common.js';
import inputText from '@/components/element/inputText';
import selectBox from '@/components/element/selectBox';
import dataList from '@/components/element/dataList';
import passWord from '@/components/element/passWord';
import textArea from '@/components/element/textArea';
import dateRange from '@/components/element/dateRange';
import timePicker from '@/components/element/timePicker';
import radioGroup from '@/components/element/radioGroup';
import checkBoxGroup from '@/components/element/checkBoxGroup';

export default {
	name: 'DetailBasic',
	props: {
		mode: {
			type: String,
			default: null
		},
		detailInfo: Object,
		detailData: Object
	},
	components: {
		inputText,
		selectBox,
		dataList,
		passWord,
		textArea,
		dateRange,
		timePicker,
		radioGroup,
		checkBoxGroup
	},
	data() {
		return {
			curLengthCnt: {},
			maxLengthCnt: {}
		};
	},
	computed: {
		...mapState(['regExpTypeList']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	watch: {
		detailData: {
			deep: true,
			handler() {
				this.initData();
			}
		}
	},
	methods: {
		initData() {
			const detailDataFormalize = {};

			this.detailInfo.content.forEach(row => {
				row.forEach(col => {
					col.forEach(info => {
						let val =
							'undefined' !== typeof this.detailData[info.vModel] && null !== this.detailData[info.vModel]
								? String(this.detailData[info.vModel])
								: 'undefined' !== typeof info.val
								? info.val
								: null;

						if (info.formatter) val = info.formatter(val);

						detailDataFormalize[info.vModel] = val;

						this.setLengthCnt({ [info.vModel]: 0 });
						this.maxLengthCnt[info.vModel] = getRegExpInfo(info.regExpType ? info.regExpType : 'default').maxLength;
					});
				});
			});

			for (const key in this.$refs) {
				this.$refs[key][0].setData(detailDataFormalize[this.$refs[key][0]._props.vModel]);
			}
		},
		getData() {
			let rtnObj = {};

			for (let key in this.$refs) {
				if (0 === this.$refs[key].length) continue;

				rtnObj = merge(rtnObj, this.$refs[key][0].getData());
			}

			return rtnObj;
		},
		getReadonly(colInfo) {
			const readonly = 'function' === typeof colInfo.readonly ? colInfo.readonly(this.detailData ? this.detailData : {}) : colInfo.readonly;
			return this.readonly || readonly || (colInfo.isPK && 'modify' === this.mode);
		},
		setLengthCnt(cntInfo) {
			this.curLengthCnt = { ...this.curLengthCnt, ...cntInfo };
		},
		openModal(modalInfo, refElement) {
			const modalInstane = this.$openModal({
				...modalInfo,
				modalWidth: `800px`,
				contentList: [
					{
						template: `<div>
						       <Search :list="searchList" @search="search" ref="search"/>
							     <Main
							   	   :buttonList="buttonList"
								   :gridInfo="gridInfo"
								   :gridSearchObj="gridSearchObj"
								   :gridType="'modalBasic'"
								   :viewer="true"
								   @rowClicked="rowClicked"
								   @initialize="initialize"
							   />
						   </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [],

								//Button
								buttonList: [],

								// Grid
								gridInfo: {},
								gridSearchObj: {}
							};
						},
						created() {
							const { search, button, grid } = modalInfo;

							//Search
							this.searchList = search.list;

							//Button
							this.buttonList = button.list;

							//Grid
							this.gridInfo = grid;
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								refElement[0].setData(modalInfo.rowClickedCallback(info));
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
