<template>
	<component
		ref="component"
		:is="optionElement"
		:classObject="classObject"
		:readonly="readonly"
		:optionListData="optionListData"
		@changeEvt="changeEvt"
	></component>
</template>

<script>
import { HttpReq } from '@/utils/common.js';

export default {
	name: 'selectBox',
	props: {
		vModel: String,
		classObject: Object,
		readonly: {
			type: Boolean,
			default: false
		},
		optionInfo: Object,
		isEvent: {
			type: Boolean,
			default: true
		},
		val: {
			type: String,
			default: null
		},
		optionListData: Array
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	methods: {
		changeEvt(value) {
			this.$emit('changeEvt', value);
		},
		setData(val) {
			this.$refs.component.setData(val);
		},
		getData() {
			return this.$refs.component.getData();
		}
	},
	data() {
		return {
			optionElement: {
				template: `
					<select v-model="obj['${this.vModel}']" :class="classObject" :readonly="readonly" 
					${this.isEvent && this._events.changeEvt ? '@change="changeEvt"' : ''}>
						<option
							value=" "
							v-text=""
							v-if="${'undefined' === typeof this.optionInfo.isViewEmpty ? false : this.optionInfo.isViewEmpty}"
						>
						</option>
						<option
							v-for="${this.optionInfo.optionFor}"
							:key="${this.optionInfo.optionValue}"
							:value="${this.optionInfo.optionValue}"
							v-text="${this.optionInfo.optionText}"
							:disabled="${'undefined' === typeof this.optionInfo.optionDisabled ? false : this.optionInfo.optionDisabled}" >
						</option>
					</select>
				`,
				props: {
					classObject: Object,
					readonly: Boolean,
					optionListData: Array
				},
				data: (() => {
					const rtnDataObj = {
						vModelName: this.vModel,
						optionListName: this.optionInfo.optionListName,

						obj: {
							[this.vModel]: this.val
						},

						[this.optionInfo.optionListName]: [],

						url: this.optionInfo.url,
						params: this.optionInfo.params ? this.optionInfo.params : null,

						HttpReq: null
					};

					return function () {
						return rtnDataObj;
					};
				})(),
				async created() {
					if (!this.url) return;

					this.HttpReq = new HttpReq(this.url);

					const result = await this.HttpReq.read(this.params);

					if (`ok` !== result.data.result) return;

					this[this.optionListName] = result.data.object;
				},
				watch: {
					optionListData: {
						immediate: true,
						handler() {
							this[this.optionListName] = this.optionListData;
						}
					}
				},
				methods: {
					changeEvt() {
						this.$emit('changeEvt', { key: this.vModelName, value: this.obj[this.vModelName] });
					},
					setData(val) {
						this.obj[this.vModelName] = val;
					},
					getData() {
						return {
							[this.vModelName]: this.obj[this.vModelName] ? this.obj[this.vModelName] : ' '
						};
					}
				}
			}
		};
	}
};
</script>
