<script>
import SystemParent from '@/views/detail/system/SystemParent.vue';

export default {
	name: 'SystemProvider',
	extends: SystemParent,
	methods: {
		getData() {
			const systemTargets = [];

			this.getSystemData(systemTargets);

			return {
				systemTargets
			};
		},
		async setPropertyList() {
			const { systemTargets } = this.detailData;

			this.setSystemList(systemTargets);
		}
	}
};
</script>
