import Vue from 'vue';
import store from '@/store';
import i18n from '@/i18n';
import CommonModal from '@/components/modal/CommonModal.vue';

Vue.use(CommonModal);

export default {
	install(Vue) {
		const modalConstructor = Vue.extend(CommonModal);

		Vue.prototype.$openModal = window.$openModal = function (modalInfo) {
			if (!modalInfo.modalWidth) modalInfo.modalWidth = `800px`;

			if (!modalInfo.modalHeight) modalInfo.modalHeight = `700px`;

			let modalData = {
				showModal: true,
				modalInfo
			};

			const modalInstane = new modalConstructor({
				el: document.createElement('div'),
				store,
				i18n,
				data() {
					return modalData;
				}
			});

			document.body.appendChild(modalInstane.$el);

			return modalInstane;
		};
	}
};
