<template>
	<div>
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 0 === selectedIdx }"
					@click="changeTab(0)"
					v-text="`Source ${this.$t('label.system')}`"
				></a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 1 === selectedIdx }"
					@click="changeTab(1)"
					v-text="$t('label.linked.server')"
				></a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 2 === selectedIdx }"
					@click="changeTab(2)"
					v-text="`Target ${this.$t('label.system')}`"
				></a>
			</li>
		</ul>

		<div class="panel-body tab-content" style="padding-bottom: 0px">
			<div :class="{ [mode]: true, 'tab-pane': true, active: 0 === selectedIdx }">
				<DeployFlowFilterGrid ref="source" type="source" />
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 1 === selectedIdx }">
				<DeployFlowFilterGrid ref="server" type="server" />
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 2 === selectedIdx }">
				<DeployFlowFilterGrid ref="target" type="target" />
			</div>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import DeployFlowFilterGrid from '@/views/modal/deployFlow/DeployFlowFilterGrid.vue';

export default {
	name: `DeployFlowFilter`,
	data() {
		return {
			mode: `detail`,
			selectedIdx: 0
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	components: {
		DeployFlowFilterGrid
	},
	methods: {
		changeTab(idx) {
			this.selectedIdx = idx;
			this.resize();
		},
		resize() {
			this.$refs[0 === this.selectedIdx ? `source` : 1 === this.selectedIdx ? `server` : `target`].resize();
		},
		getData() {
			return {
				source: this.$refs.source.getData(),
				server: this.$refs.server.getData(),
				target: this.$refs.target.getData()
			};
		}
	}
};
</script>
<style lang="scss" scoped>
.panel-body {
	height: 490px;
}
</style>
