<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-wrap">
				<div class="form-table-head">
					<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('label.insert')">
						<i class="icon-plus-circle"></i>
					</button>
					<label class="col">{{ this.$t('label.type') }}</label>
					<label class="col">{{ this.$t('label.desc') }}</label>
				</div>
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('label.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div class="detail-content-common">
							<select v-model="propertyObj['parameterType']" class="form-control view-disabled" :readonly="readonly">
								<option v-for="option in fieldTypeList" :key="option.key" :value="option.key">
									{{ option.value }}
								</option>
							</select>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${!readonly ? 'regExp' : 'common'}`">
							<inputText
								:ref="`component_${bodyIndex}`"
								vModel="parameterDesc"
								:classObject="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj['parameterDesc']"
								:readonly="readonly"
								regExpType="desc"
								@setLengthCnt="info => setLengthCnt({ [`parameterDesc_${bodyIndex}`]: info['parameterDesc'] })"
							/>
							<span class="letterLength" v-if="!readonly">
								{{
									`(${curLengthCnt[`parameterDesc_${bodyIndex}`]}
									 /${maxLengthCnt[`parameterDesc`]})`
								}}
							</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import isEmpty from 'lodash/isEmpty';
import isNil from 'lodash/isNil';
import merge from 'lodash/merge';
import { parseFlattenObj, getUUID, parseHierarchyObj, getRegExpInfo } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: `Parameter`,
	extends: DetailProperty,
	props: {
		fieldTypeList: {
			type: Array
		}
	},
	data() {
		return {
			regExpInfo: {
				parameterDesc: 'desc'
			},

			curLengthCnt: {},
			maxLengthCnt: {}
		};
	},
	async created() {
		for (const [key, value] of Object.entries(this.regExpInfo)) {
			this.maxLengthCnt[key] = getRegExpInfo(value).maxLength;
		}
	},
	methods: {
		addProperty() {
			const rowInfo = {
				idx: getUUID(),
				'pk.parameterOrder': this.propertyList.length,
				parameterType: null,
				parameterDesc: null
			};

			for (const key in this.regExpInfo) {
				this.setLengthCnt({ [`${key}_${this.propertyList.length}`]: 0 });
			}

			this.propertyList.push(rowInfo);
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);

			this.$nextTick(() => {
				this.curLengthCnt = {};

				const rtnArr = [];

				this.propertyList.forEach((property, bodyIndex) => {
					let rtnObj = {
						'pk.parameterOrder': bodyIndex,
						parameterType: this.propertyList[bodyIndex].parameterType
					};

					this.$refs[`component_${bodyIndex}`].forEach(comp => {
						rtnObj = merge(rtnObj, comp.getData());
					});

					for (const key in rtnObj) {
						this.setLengthCnt({ [`${key}_${bodyIndex}`]: isNil(rtnObj[key]) ? 0 : String(rtnObj[key]).length });
					}

					rtnArr.push(parseHierarchyObj(rtnObj));
				});

				this.propertyList = rtnArr;
			});
		},
		setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			this.propertyList = [...this.detailData[`functionParameters`]];

			this.propertyList.forEach((propertyInfo, index) => {
				propertyInfo.idx = getUUID();
				const clonePropertyInfo = parseFlattenObj(propertyInfo);

				for (const key in this.regExpInfo) {
					this.setLengthCnt({ [`${key}_${index}`]: clonePropertyInfo[key] ? clonePropertyInfo[key].length : 0 });
				}
			});

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		getData() {
			const rtnArr = [];

			for (let i = 0; i < this.propertyList.length; i++) {
				let rtnObj = {
					'pk.parameterOrder': this.propertyList[i]['pk.parameterOrder'],
					parameterType: this.propertyList[i].parameterType
				};

				this.$refs[`component_${i}`].forEach(comp => {
					rtnObj = merge(rtnObj, comp.getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				functionParameters: rtnArr
			};
		}
	}
};
</script>
