<template>
	<div>
		<Search :list="searchList" @search="search" ref="search" />
		<Main
			:buttonList="buttonList"
			:gridInfo="gridInfo"
			:gridSearchObj="gridSearchObj"
			:gridType="'modalBasic'"
			:viewer="true"
			@rowClicked="rowClicked"
			@initialize="initialize"
		/>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import { HttpReq } from '@/utils/common.js';
import Search from '@/components/Search.vue';
import Main from '@/components/Main.vue';
export default {
	name: 'recordModal',
	components: {
		Search,
		Main
	},
	async created() {
		const result = await this.propertyHttp.read({
			pk: {
				propertyId: 'List.FieldMeta.FieldType'
			},
			orderByKey: true
		});

		if (`ok` !== result.data.result) return;

		this.fieldTypeList = cloneDeep(result.data.object);
	},
	methods: {
		search(searchObj) {
			this.gridSearchObj = searchObj;
		},
		initialize() {
			this.$refs.search.initialize();
		},
		async rowClicked(info) {
			const result = await this.recordHttp.read({ id: info.id });

			if (`ok` !== result.data.result) {
				this.$alert({ type: 'warn', message: this.$t('msg.load.data.warn') });
				return;
			}

			if (this.rowClickedCallback) {
				this.rowClickedCallback(this.getTreeData(result.data.object));
			}
		},
		getTreeData(recordInfo) {
			const modelObj = {
				id: recordInfo.id,
				fieldId: recordInfo.id,
				fieldType: 'Group',
				fieldName: recordInfo.name
			};

			if (recordInfo.fields && 0 < recordInfo.fields.length) modelObj._children = this.getTreeChildData(recordInfo.id, recordInfo.fields);

			return modelObj;
		},
		getTreeChildData(parentId, fields) {
			const rtnArr = [];

			fields.forEach(field => {
				rtnArr.push({
					id: `${parentId}\\${field.fieldId}`,
					fieldId: field.fieldId,
					fieldType: null,
					fieldName: field.fieldName
				});

				const lastIndex = rtnArr.length - 1;

				if (field.recordObject) {
					rtnArr[lastIndex].fieldType = `Group(${field.recordObject.recordType})`;
					rtnArr[lastIndex]._children = this.getTreeChildData(rtnArr[lastIndex].id, field.recordObject.fields);
				} else {
					rtnArr[lastIndex].fieldType = `${this.fieldTypeList[field.fieldType]} (${field.fieldLength})`;
				}

				if ('N' != field.arrayType) {
					rtnArr[rtnArr.length - 1].fieldType += `[${field.arrayType}]`;
				}
			});

			return rtnArr;
		}
	},
	data() {
		return {
			propertyHttp: new HttpReq(`/api/page/properties`),
			recordHttp: new HttpReq(`/api/entity/record/object`),
			fieldTypeList: [],

			//Search
			searchList: [
				{ type: 'text', vModel: 'id', label: this.$t('label.modelRecord.id'), placeholder: this.$t('msg.enterId') },
				{ type: 'text', vModel: 'name', label: this.$t('label.modelRecord.name'), placeholder: this.$t('msg.enterName') },
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [{ id: 'initialize', isUse: true }],

			// Grid
			gridSearchObj: {},
			gridInfo: {
				url: `/api/entity/record/search`,
				totalCntUrl: `/api/entity/record/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('label.modelRecord.id'),
							name: 'id',
							width: '40%'
						},
						{
							header: this.$t('label.modelRecord.name'),
							name: 'name',
							width: '40%'
						},
						{
							header: this.$t('label.privilege.id'),
							name: 'privilegeId',
							width: '20%'
						}
					]
				}
			}
		};
	}
};
</script>
