<template>
	<div class="table-responsive">
		<TuiGrid ref="tuiGrid" v-bind="options" @click="onClick" v-if="isGridView" />
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import { Grid } from '@toast-ui/vue-grid';
import {
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	getNumFromStr,
	HttpReq,
	parseFlattenObj,
	makeGridOptions,
	getCurrentMenuId
} from '@/utils/common.js';
import { mapState } from 'vuex';

export default {
	name: 'Grid',
	props: {
		type: String,
		info: Object,
		searchObj: Object,
		resizeFunc: Function
	},
	components: {
		TuiGrid: Grid
	},
	data() {
		return {
			gridInfo: null,

			isGridView: false,
			totalCnt: 0,
			pageNo: 1,
			options: {},
			dataList: [],
			isServerPaging: false,
			serverPagingDataArr: null,
			sortColumnName: null,

			checkedRowInfo: {}
		};
	},
	computed: {
		...mapState(['constantsInfo']),
		paging() {
			return this.gridInfo.paging;
		},
		totalCntUrl() {
			return this.gridInfo.totalCntUrl;
		},
		url() {
			return this.gridInfo.url;
		}
	},
	created() {
		this.isServerPaging = `serverPaging` === this.paging.side;

		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);
	},
	async mounted() {
		const isError = await this.getTotalCnt();

		if (isError) return;

		await this.initGridOptions();
		await this.getDataList();
	},
	watch: {
		async searchObj() {
			this.pageNo = 1;
			this.dataList = [];

			const isError = await this.getTotalCnt();

			if (isError) return;

			await this.getDataList();
		},
		info: {
			immediate: true,
			deep: true,
			handler() {
				if ('mainBasic' === this.type) {
					const info = cloneDeep(this.info);
					this.gridInfo = this.constantsInfo.grid.gridOptionFunc(getCurrentMenuId(), info.url, info);
				} else {
					this.gridInfo = cloneDeep(this.info);
				}
			}
		}
	},
	methods: {
		async initGridOptions() {
			let formatterData = null;

			if (this.gridInfo.formatterDataUrlList) {
				formatterData = {};

				const promiseAllList = [];

				this.gridInfo.formatterDataUrlList.forEach(content => {
					promiseAllList.push(
						new Promise(resolve => {
							(async () => {
								const result = await new HttpReq(content.url).read(content.param ? content.param : null);

								resolve({
									key: content.key,
									data: result.data.object
								});
							})();
						})
					);
				});

				const resultList = await Promise.all(promiseAllList);

				resultList.forEach(({ key, data }) => {
					formatterData[key] = data;
				});
			}

			this.options = makeGridOptions(this.gridInfo.options, formatterData);

			if (this.paging.isUse) {
				this.options.pageOptions = {
					useClient: true
				};
			}

			this.isGridView = true;

			if (this.paging.isUse) {
				this.$nextTick(() => {
					this.$refs.tuiGrid.gridInstance.on(`beforePageMove`, async ({ page }) => {
						this.pageNo = Number(page);

						if (this.isServerPaging) {
							await this.getDataList();
						} else {
							if (`server` === this.paging.side) {
								if (this.pageNo > Math.ceil(this.$refs.tuiGrid.invoke(`getData`).length / this.searchObj._pageSize)) {
									await this.getDataList();
								}
							}
						}
					});

					let hasCheckbox = false;

					if (this.options.rowHeaders) {
						if (
							this.options.rowHeaders.find(
								rowHeader =>
									(`string` === typeof rowHeader && `checkbox` === rowHeader) || (rowHeader.type && `checkbox` === rowHeader.type)
							)
						) {
							hasCheckbox = true;
						}
					}

					this.$refs.tuiGrid.gridInstance.on(`afterPageMove`, evt => {
						this.resize();

						if (hasCheckbox) {
							this.$nextTick(() => {
								const pageSize = Number(this.searchObj._pageSize);
								const startRowIdx = (evt.page - 1) * pageSize;
								const endRowIdx = startRowIdx + pageSize;

								cloneDeep(this.$refs.tuiGrid.gridInstance.getData())
									.slice(startRowIdx, startRowIdx + endRowIdx)
									.forEach(info => {
										if (this.checkedRowInfo[JSON.stringify(this.cleanRowData(info.rowKey))]) {
											setTimeout(() => {
												this.$refs.tuiGrid.gridInstance.check(info.rowKey);
											}, 0);
										}
									});
							});
						}

						// setTimeout(() => {
						// 	this.$stopSpinner();
						// }, 100);
					});

					if (hasCheckbox) {
						this.$refs.tuiGrid.gridInstance.on(`check`, evt => {
							this.checkedRowInfo[JSON.stringify(this.cleanRowData(evt.rowKey))] = true;
						});

						this.$refs.tuiGrid.gridInstance.on(`uncheck`, evt => {
							delete this.checkedRowInfo[JSON.stringify(this.cleanRowData(evt.rowKey))];
						});

						this.$refs.tuiGrid.gridInstance.on(`uncheckAll`, () => {
							for (const key in this.checkedRowInfo) {
								delete this.checkedRowInfo[key];
							}
						});
					}
				});
			}

			this.$nextTick(() => {
				this.$refs.tuiGrid.gridInstance.on(`click`, async ({ columnName, targetType, instance }) => {
					if (`columnHeader` !== targetType) return;

					const { sortable } = this.options.columns.find(column => column.name === columnName);

					if (!sortable) return;

					// sort button click event
					// beforeSort 함수는 default sort가 될 경우 타지 않아 click 함수 사용
					const { columns } = instance.getSortState();

					if (!columns) return;

					if (0 === columns.length) return;

					this.sortColumnName = columns[0].columnName;

					//this.$startSpinner();

					if (this.paging.isUse && this.isServerPaging) {
						// reset data & sort
						await this.getDataList();
					} else {
						// sort
						const { sortColumnNameList, sortColumnTypeList } = this.getSortColumnList();

						if (0 === sortColumnNameList.length) return;

						for (let idx = 1; idx < sortColumnNameList.length; idx++) {
							// 연관 칼럼 정렬
							this.$refs.tuiGrid.invoke('sort', sortColumnNameList[idx], sortColumnTypeList[idx], true);
						}

						// setTimeout(() => {
						// 	this.$stopSpinner();
						// }, 100);
					}
				});
			});
		},
		async getTotalCnt() {
			if (!this.totalCntUrl) return;

			const result = await new HttpReq(this.totalCntUrl).read(this.searchObj);

			if ('ok' !== result.data.result) return true;

			const menuId = getCurrentMenuId();
			const maxListCount = this.constantsInfo.grid.maxListCount[menuId.replace(`_bookmark`, ``)];

			if (maxListCount && Number(result.data.object) > maxListCount) {
				if (!this.isGridView) this.$emit('changeGridViewStatus');

				window.$alert({
					type: `warn`,
					message: this.$t(`igate.add.search.criteria`, { 0: maxListCount })
				});

				return true;
			} else {
				this.totalCnt = Number(result.data.object);
			}
		},
		async getDataList() {
			const { sortColumnNameList, sortColumnTypeList } = this.getSortColumnList();

			// getDataList
			const param = { object: this.searchObj, limit: null, next: null, reverseOrder: false };

			const { side, isUse } = this.paging;

			if (isUse) {
				if ('client' === side) {
					param.limit = this.totalCnt;
				} else if ('server' === side) {
					const { pageOptionFunc } = this.constantsInfo.grid;

					const { limit, ascending } = pageOptionFunc(getCurrentMenuId(), this.url);

					param.limit = this.searchObj._pageSize >= limit ? this.searchObj._pageSize : limit;
					param.reverseOrder = !ascending;

					if (0 < this.dataList.length) {
						param.next = this.dataList[this.dataList.length - 1];
					}
				} else if ('serverPaging' === side) {
					delete param.next;
					delete param.reverseOrder;

					param.limit = this.searchObj._pageSize;
					param.pageNo = this.pageNo;
					param.sortColumnInfo = { nameList: sortColumnNameList, reverseList: sortColumnTypeList.map(columnType => !columnType) };

					if (null === this.serverPagingDataArr || this.totalCnt !== this.serverPagingDataArr.length) {
						this.serverPagingDataArr = Array.from({ length: this.totalCnt }, () => ({}));
					}
				}
			} else {
				if ('serverPaging' === side) {
					delete param.next;
					delete param.reverseOrder;

					param.pageNo = this.pageNo;
					param.sortColumnInfo = { nameList: [], typeList: [] };
				}
			}

			const result = await new HttpReq(this.url).read(param, true);

			if (`ok` !== result.data.result) return;

			const pageState = isUse ? { page: this.pageNo, perPage: Number(this.searchObj._pageSize) } : null;

			if (isUse && this.isServerPaging) {
				//serverPaging
				pageState.totalCount = this.totalCnt;

				const startRowIdx = (this.pageNo - 1) * Number(this.searchObj._pageSize);

				result.data.object.forEach((info, idx) => {
					this.serverPagingDataArr[startRowIdx + idx] = parseFlattenObj(info);
				});

				if (0 < sortColumnNameList.length) {
					sortColumnNameList.forEach((sortColumnName, idx) => {
						this.$refs.tuiGrid.invoke('resetData', this.serverPagingDataArr, {
							pageState,
							sortState: { columnName: sortColumnName, ascending: sortColumnTypeList[idx], multiple: true }
						});
					});
				} else {
					this.$refs.tuiGrid.invoke('resetData', this.serverPagingDataArr, { pageState });
				}
			} else {
				// isUse가 false일 때
				// client, server
				this.dataList = this.dataList.concat(result.data.object.map(info => parseFlattenObj(info)));

				if (isUse) pageState.totalCount = `server` === this.paging.side ? this.totalCnt : this.dataList.length;

				this.$refs.tuiGrid.invoke(`resetData`, cloneDeep(this.dataList), { pageState });

				sortColumnNameList.forEach((sortColumnName, idx) => {
					this.$refs.tuiGrid.invoke('sort', sortColumnName, sortColumnTypeList[idx], true);
				});
			}

			const dataListCnt = this.$refs.tuiGrid.invoke(`getData`).length;

			let currentCnt = 0;
			let totalCnt = 0;

			if (isUse && (`server` === side || `serverPaging` === side)) {
				currentCnt = this.totalCnt < dataListCnt ? this.totalCnt : dataListCnt;
				totalCnt = this.totalCnt;
			} else {
				currentCnt = dataListCnt;
				totalCnt = dataListCnt;
			}

			this.$emit('gridCount', { currentCnt, totalCnt });

			this.resize();
		},
		getSortColumnList() {
			const {
				columns: [column]
			} = this.$refs.tuiGrid.invoke(`getSortState`);

			const sortColumnNameList = [];
			const sortColumnTypeList = [];

			const isDefaultSort = `sortKey` === column.columnName;

			if (isDefaultSort && (`undefined` === typeof this.options.sortColumn || null !== this.sortColumnName)) {
				return {
					sortColumnNameList,
					sortColumnTypeList
				};
			}

			if (0 < this.options.columns.filter(({ sortable }) => sortable).length) {
				const { name, sortingType, sortWithColumn, sortWithColumnType } = this.options.columns.find(columnInfo => {
					if (isDefaultSort) {
						return this.options.sortColumn === columnInfo.name;
					} else {
						return columnInfo.name === column.columnName;
					}
				});

				sortColumnNameList.push(name);
				sortColumnTypeList.push(isDefaultSort ? (sortingType ? `asc` === sortingType : true) : column.ascending);

				const isReverse = isDefaultSort
					? false
					: (`undefined` === typeof sortingType ? true : `asc` === sortingType ? true : false) !== sortColumnTypeList[0];

				if (sortWithColumn) {
					sortWithColumn.forEach((sortWithColumnName, idx) => {
						sortColumnNameList.push(sortWithColumnName);

						let ascending = sortWithColumnType && sortWithColumnType[idx] ? `asc` === sortWithColumnType[idx] : true;

						if (isReverse) ascending = !ascending;

						sortColumnTypeList.push(ascending);
					});
				}
			}

			return {
				sortColumnNameList,
				sortColumnTypeList
			};
		},
		onClick(evt) {
			if (`_checked` === evt.columnName) {
				setTimeout(() => {
					this.$emit('rowClicked');
				}, 0);

				return;
			}

			if ('undefined' === typeof evt.rowKey || `rowHeader` === evt.targetType) return;

			const grid = evt.instance;

			grid.getData().forEach(data => {
				if (data.rowKey === evt.rowKey) return;

				grid.removeRowClassName(data.rowKey, 'row-selected');
			});

			if (grid.getCheckedRowKeys().includes(evt.rowKey)) {
				grid.uncheck(evt.rowKey);
			} else {
				grid.addRowClassName(evt.rowKey, 'row-selected');
				grid.check(evt.rowKey);
			}

			const rowInfo = grid.getRow(evt.rowKey);

			const filterList = ['sortKey', 'rowKey', 'rowSpanMap', 'uniqueKey', '_attributes', '_disabledPriority', '_relationListItemMap'];

			const paramRowInfo = {};

			for (const key in rowInfo) {
				if (-1 < filterList.indexOf(key)) continue;
				paramRowInfo[key] = rowInfo[key];
			}

			this.$emit('rowClicked', paramRowInfo);
		},
		resize() {
			if (this.resizeFunc) {
				this.resizeFunc(this.$refs.tuiGrid);
			} else {
				if ('mainBasic' === this.type) {
					this.mainBasicResize();
				} else if ('modalBasic' === this.type) {
					this.modalResize();
				}
			}
		},
		mainBasicResize() {
			if (!this.$refs.tuiGrid) return;

			const ct = document.getElementById('ct');
			const ctComputedStyle = getComputedStyle(ct);

			const ctPaddingLeft = getNumFromStr(ctComputedStyle.getPropertyValue('padding-left'));
			const ctPaddingRight = getNumFromStr(ctComputedStyle.getPropertyValue('padding-right'));
			const ctPaddingTop = getNumFromStr(ctComputedStyle.getPropertyValue('padding-top'));
			const ctPaddingBottom = getNumFromStr(ctComputedStyle.getPropertyValue('padding-bottom'));

			//width
			let adjustWidth = window.innerWidth;
			adjustWidth -= ctPaddingLeft + ctPaddingRight;

			if (992 <= window.innerWidth && document.getElementById('sidebar') && !document.body.classList.contains('sidebar-toggled')) {
				const sidebar = document.getElementById('sidebar');

				const sidebarComputedStyle = getComputedStyle(sidebar);

				const sidebarMarginLeft = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-left'));
				const sidebarMarginRight = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-right'));

				adjustWidth -= sidebar.offsetWidth + sidebarMarginLeft + sidebarMarginRight;
			}

			if (document.querySelector(`.horizon`) && `block` === document.querySelector(`#panel`).style.display) {
				adjustWidth -= getNumFromStr(getComputedStyle(document.querySelector(`.horizon .panel-content`)).width);
			}

			this.$el.style.width = `${adjustWidth}px`;
			this.$refs.tuiGrid.invoke('setWidth', adjustWidth);

			//height
			let adjustHeight = window.innerHeight;
			adjustHeight -= ctPaddingTop + ctPaddingBottom;

			//ctHeader
			const ctHeader = ct.getElementsByClassName('ct-header')[0];

			const ctHeaderComputedStyle = getComputedStyle(ctHeader);

			const ctHeaderMarginTop = getNumFromStr(ctHeaderComputedStyle.getPropertyValue('margin-top'));
			const ctHeaderMarginBottom = getNumFromStr(ctHeaderComputedStyle.getPropertyValue('margin-bottom'));

			adjustHeight -= ctHeader.offsetHeight + ctHeaderMarginTop + ctHeaderMarginBottom;

			//ctContent
			const ctContent = ct.getElementsByClassName('ct-content')[0];

			for (let i = 0; i < ctContent.children.length; i++) {
				if (ctContent.children[i].classList.contains('table-responsive')) continue;

				if (ctContent.children[i].classList.contains('empty')) continue;

				const tmpElement = ctContent.children[i];

				const tmpComputedStyle = getComputedStyle(tmpElement);

				const tmpMarginTop = getNumFromStr(tmpComputedStyle.getPropertyValue('margin-top'));
				const tmpMarginBottom = getNumFromStr(tmpComputedStyle.getPropertyValue('margin-bottom'));

				adjustHeight -= tmpElement.offsetHeight + tmpMarginTop + tmpMarginBottom;
			}

			const paginationElements = this.$refs.tuiGrid.getRootElement().getElementsByClassName('tui-pagination');

			if (0 < paginationElements.length) {
				const clientPaginationElement = paginationElements[0];

				const clientPaginationElementComputedStyle = getComputedStyle(clientPaginationElement);

				const clientPaginationElementMarginTop = getNumFromStr(clientPaginationElementComputedStyle.getPropertyValue('margin-top'));
				const clientPaginationElementMarginBottom = getNumFromStr(clientPaginationElementComputedStyle.getPropertyValue('margin-bottom'));

				adjustHeight -= clientPaginationElement.offsetHeight + clientPaginationElementMarginTop + clientPaginationElementMarginBottom;
			}

			//detail
			if (
				!document.querySelector(`.horizon`) &&
				null != document.getElementById('panel') &&
				'block' === document.getElementById('panel').style.display
			) {
				const panel = document.getElementById('panel');
				const panelContent = panel.getElementsByClassName('panel-content')[0];

				const panelContentComputedStyle = getComputedStyle(panelContent);

				const panelContentMarginTop = getNumFromStr(panelContentComputedStyle.getPropertyValue('margin-top'));
				const panelContentMarginBottom = getNumFromStr(panelContentComputedStyle.getPropertyValue('margin-bottom'));

				adjustHeight -= panelContent.offsetHeight + panelContentMarginTop + panelContentMarginBottom;
			}

			adjustHeight -= 20;

			this.$refs.tuiGrid.invoke('setHeight', adjustHeight);

			this.$nextTick(() => {
				const rsideAreaElement = this.$refs.tuiGrid.$el.querySelector(`.tui-grid-rside-area`);

				const bodyClientHeight = rsideAreaElement.querySelector(`.tui-grid-body-area`).clientHeight;
				const scrollbarYInnerBorderClientHeight = rsideAreaElement.querySelector(`.tui-grid-scrollbar-y-inner-border`).clientHeight;

				if (bodyClientHeight !== scrollbarYInnerBorderClientHeight) {
					rsideAreaElement.querySelector(`.tui-grid-scrollbar-y-inner-border`).style.height = `${bodyClientHeight}px`;
				}
			});
		},
		modalResize() {
			if (!this.$refs.tuiGrid) return;

			this.$nextTick(() => {
				const modalBody = this.$el.closest(`.modal-body`);

				if (!modalBody) return;

				const modalBodyComputedStyle = getComputedStyle(modalBody);

				const modalBodyPaddingLeft = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-left'));
				const modalBodyPaddingRight = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-right'));
				const modalBodyPaddingTop = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-top'));
				const modalBodyPaddingBottom = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-bottom'));

				const adjustWidth = modalBody.clientWidth - modalBodyPaddingLeft - modalBodyPaddingRight;
				this.$refs.tuiGrid.invoke(`setWidth`, adjustWidth);

				const ctHeaderHeight = modalBody.querySelector(`.ct-header`).offsetHeight;
				const subBarHeight = modalBody.querySelector(`.sub-bar`).offsetHeight;

				const pagination = modalBody.querySelector(`.tui-grid-pagination`);
				const paginationComputedStyle = getComputedStyle(pagination);
				const paginationMarginTop = getNumFromStr(paginationComputedStyle.getPropertyValue('margin-top'));
				const paginationMarginBottom = getNumFromStr(paginationComputedStyle.getPropertyValue('margin-bottom'));

				const paginationHeight = pagination.offsetHeight;

				const adjustHeight =
					modalBody.clientHeight -
					modalBodyPaddingTop -
					modalBodyPaddingBottom -
					ctHeaderHeight -
					subBarHeight -
					paginationHeight -
					paginationMarginTop -
					paginationMarginBottom;

				this.$refs.tuiGrid.invoke(`setHeight`, adjustHeight);
			});
		},
		getGrid() {
			return this.$refs.tuiGrid;
		},
		getCheckedRowInfo() {
			return Object.keys(this.checkedRowInfo).map(keyStr => JSON.parse(keyStr));
		},
		cleanRowData(rowKey) {
			const rowInfo = cloneDeep(this.$refs.tuiGrid.gridInstance.getRow(rowKey));

			for (const key in rowInfo) {
				if (
					![`rowKey`, `rowSpanMap`, `sortKey`, `uniqueKey`, `_attributes`, `_disabledPriority`, `_relationListItemMap`].find(
						removeName => removeName === key
					)
				)
					continue;

				delete rowInfo[key];
			}

			return rowInfo;
		}
	}
};
</script>
