<template>
	<div class="table-responsive">
		<TuiGrid ref="tuiGrid" v-bind="options" />
	</div>
</template>

<script>
import { Grid } from '@toast-ui/vue-grid';
import {
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	getNumFromStr,
	parseFlattenObj,
	makeGridOptions,
	HttpReq,
	escapeHtml,
	openNewTab
} from '@/utils/common.js';

export default {
	name: `ConsumerSystem`,
	components: {
		TuiGrid: Grid
	},
	props: {
		mode: String,
		detailInfo: Object,
		detailData: Object
	},
	data() {
		return {
			interfaceClassList: [],
			options: {
				columns: [
					{
						header: this.$t('label.system.id'),
						name: 'pk.id',
						width: '30%',
						formatter: ({ row }) => {
							const systemId = row[`pk.id`];
							const style = `width: calc(100% - 25px); display: inline-block;`;

							return `<div style="${style}" title="${systemId}">
										${escapeHtml(systemId)}
									</div>
									<span class="modal-btn" title="${this.$t('label.open')}">
										<i class="icon-link"></i>
									</span>`;
						}
					},
					{
						header: this.$t('label.linked.server.id'),
						name: 'pk.serverId',
						width: '30%'
					},
					{
						header: this.$t('label.interface.class'),
						name: 'pk.interfaceClass',
						width: '40%',
						align: `center`,
						formatter: ({ value }) => escapeHtml(this.interfaceClassList.find(interfaceClass => interfaceClass.id === value).name)
					}
				],
				onGridMounted: evt => {
					evt.instance.on('click', evt => {
						if (!evt.nativeEvent.target.classList.contains(`icon-link`)) return;

						const rowInfo = evt.instance.getRow(evt.rowKey);

						openNewTab(`203000`, () => {
							localStorage.setItem(
								`searchObj`,
								JSON.stringify({
									id: rowInfo[`pk.id`],
									_pageSize: 10
								})
							);

							localStorage.setItem(
								`selectedSystemInfo`,
								JSON.stringify({
									id: rowInfo[`pk.id`]
								})
							);
						});
					});
				}
			}
		};
	},
	watch: {
		detailData() {
			this.resetData();
		}
	},
	async created() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);

		this.options = makeGridOptions(this.options);

		this.resize();

		const httpReq = new HttpReq('/api/entity/interfaceClass/search');

		const result = await httpReq.read({
			object: {},
			limit: null,
			next: null,
			reverseOrder: false
		});

		if (`ok` !== result.data.result) return;

		this.interfaceClassList = result.data.object;

		this.resetData();
	},
	methods: {
		resetData() {
			if (!this.detailData[this.type]) {
				this.$refs.tuiGrid.invoke('resetData', []);
				return;
			}

			if (0 === this.interfaceClassList.length) return;

			this.$refs.tuiGrid.invoke(
				'resetData',
				this.detailData[this.type].map(dataObj => parseFlattenObj(dataObj))
			);

			this.resize();
		},
		resize() {
			if (!this.$refs.tuiGrid) return;

			this.$nextTick(() => {
				const panel = document.getElementById('panel');

				const panelBody = panel.getElementsByClassName('panel-body')[0];
				const panelBodyComputedStyle = getComputedStyle(panelBody);

				const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
				const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
				const panelBodyPaddingTop = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-top'));
				const panelBodyPaddingBottom = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-bottom'));

				this.$refs.tuiGrid.invoke('setWidth', panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight);
				this.$refs.tuiGrid.invoke('setHeight', panelBody.clientHeight - panelBodyPaddingTop - panelBodyPaddingBottom);
			});
		}
	}
};
</script>
