<template>
	<div id="mappingModal">
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 0 === selectedIdx }"
					@click="changeTab(0)"
					v-text="$t('label.basicInfo')"
				>
				</a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 1 === selectedIdx }"
					@click="changeTab(1)"
					v-text="$t('label.mappingInfo')"
				>
				</a>
			</li>
		</ul>
		<div class="panel-body tab-content">
			<div :class="{ 'tab-pane': true, active: 0 === selectedIdx }">
				<component ref="basicInfo" :data="data" :is="require(`@/views/modal/mapping/BasicInfo.vue`).default" />
			</div>
			<div :class="{ 'tab-pane': true, active: 1 === selectedIdx }">
				<component ref="mappingInfo" :data="data" :is="require(`@/views/modal/mapping/MappingInfo.vue`).default" />
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';

export default {
	name: 'AddMapping',
	props: {
		data: Object
	},
	data() {
		return {
			selectedIdx: 1
		};
	},
	methods: {
		changeTab(idx) {
			for (const key in this.$refs) {
				if (!this.$refs[key].resize) continue;
				setTimeout(() => {
					this.$refs[key].resize();
				}, 0);
			}

			this.selectedIdx = idx;
		},
		getData() {
			let rtnObj = {};

			for (const key in this.$refs) {
				rtnObj = merge(rtnObj, this.$refs[key].getData());
			}

			return rtnObj;
		}
	}
};
</script>
