<template>
	<div class="row frm-row topology">
		<div ref="pOrgTuiGrid" style="width: 47.5%">
			<div class="ml-auto form-inline m-full" style="position: relative; margin-bottom: 5px">
				<button
					type="button"
					class="btn-icon"
					style="margin-right: 0px"
					:title="$t('label.check.all')"
					@click="$refs.orgTuiGrid.invoke('checkAll')"
				>
					<i class="icon-check"></i>
				</button>
				<button type="button" class="btn-icon" :title="$t('label.uncheck.all')" @click="$refs.orgTuiGrid.invoke('uncheckAll')">
					<i class="icon-uncheck"></i>
				</button>
				<div class="form-inline m-full" style="position: absolute; right: 0px">
					<input
						type="text"
						autocomplete="off"
						class="form-control input-search"
						:placeholder="$t(`label.id`)"
						v-model="id"
						@keyup.enter="initList"
					/>
				</div>
			</div>
			<div class="table-responsive">
				<TuiGrid ref="orgTuiGrid" v-bind="orgTuiGridOptions" />
			</div>
		</div>
		<div style="width: 5%; position: relative; text-align: center">
			<div style="position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%)">
				<div>
					<a href="javascript:void(0);" class="btn btn-m" style="width: 34px; min-width: 34px" @click="addFilterList">
						<i class="icon-right"></i>
					</a>
				</div>
				<div style="margin-top: 5px">
					<a href="javascript:void(0);" class="btn btn-m" style="width: 34px; min-width: 34px" @click="removeFilterList">
						<i class="icon-left"></i>
					</a>
				</div>
			</div>
		</div>
		<div ref="pFilterTuiGrid" style="width: 47.5%">
			<div class="ml-auto form-inline m-full">
				<button
					type="button"
					class="btn-icon"
					:title="$t('label.check.all')"
					style="margin-right: 0px"
					@click="$refs.filterTuiGrid.invoke('checkAll')"
				>
					<i class="icon-check"></i>
				</button>
				<button type="button" class="btn-icon" :title="$t('label.uncheck.all')" @click="$refs.filterTuiGrid.invoke('uncheckAll')">
					<i class="icon-uncheck"></i>
				</button>
			</div>
			<div class="table-responsive">
				<TuiGrid ref="filterTuiGrid" v-bind="filterTuiGridOptions" />
			</div>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import { Grid as TuiGrid } from '@toast-ui/vue-grid';
import {
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	makeGridOptions,
	HttpReq
} from '@/utils/common.js';

export default {
	name: `DeployFlowFilterGrid`,
	props: {
		type: String
	},
	components: {
		TuiGrid
	},
	data() {
		return {
			orgTuiGridOptions: {},
			filterTuiGridOptions: {},

			orgList: [],
			filterList: [],

			id: null
		};
	},
	computed: {
		...mapState([`userInfo`])
	},
	created() {
		WindowResizeEventBus.$on('MainResize', this.resize);
		SideMenuToggleEventBus.$on('MainResize', this.resize);
		DetailToggleEventBus.$on('MainResize', this.resize);
		SearchToggleEventBus.$on('MainResize', this.resize);

		this.orgTuiGridOptions = makeGridOptions({
			rowHeaders: [{ type: 'checkbox', header: ' ' }],
			columns: [
				{
					header:
						`source` === this.type || `target` === this.type
							? `${this.$t(`label.all`)} ${this.$t('label.system.id')}`
							: `${this.$t(`label.all`)} ${this.$t('label.server')} ${this.$t('label.id')}`,
					name: `id`
				}
			],
			pageOptions: {
				useClient: true,
				perPage: 8
			}
		});

		this.filterTuiGridOptions = makeGridOptions({
			rowHeaders: [{ type: 'checkbox', header: ' ' }],
			columns: [
				{
					header:
						`source` === this.type || `target` === this.type
							? `${this.$t(`label.filter`)} ${this.$t('label.system.id')}`
							: `${this.$t(`label.filter`)} ${this.$t('label.server')} ${this.$t(`label.id`)}`,
					name: `id`
				}
			]
		});
	},
	async mounted() {
		this.initList();

		this.initFilterList();

		this.$refs.orgTuiGrid.gridInstance.on(`afterPageMove`, this.resize);
		this.$refs.filterTuiGrid.gridInstance.on(`afterPageMove`, this.resize);
	},
	beforeDestroy() {
		WindowResizeEventBus.$off('MainResize', this.resize);
		SideMenuToggleEventBus.$off('MainResize', this.resize);
		DetailToggleEventBus.$off('MainResize', this.resize);
		SearchToggleEventBus.$off('MainResize', this.resize);
	},
	methods: {
		async initList() {
			let url = null;

			if (`source` === this.type || `target` === this.type) {
				url = `/api/entity/system/search`;
			} else if (`server` === this.type) {
				url = `/api/entity/server/search`;
			}

			let result = await new HttpReq(url).read({
				object: {
					id: this.id
				},
				limit: null,
				next: null,
				reverseOrder: false
			});

			this.orgList = result.data.object;

			this.$refs.orgTuiGrid.invoke(
				`resetData`,
				this.orgList.map(info => ({ id: info.id }))
			);

			this.resize();
		},
		initFilterList() {
			let filterIdList = null;

			if (`source` === this.type) {
				filterIdList = localStorage.getItem(`topology-filter-source-${this.userInfo.userId}`);
			} else if (`server` === this.type) {
				filterIdList = localStorage.getItem(`topology-filter-server-${this.userInfo.userId}`);
			} else if (`target` === this.type) {
				filterIdList = localStorage.getItem(`topology-filter-target-${this.userInfo.userId}`);
			}

			this.filterList = filterIdList ? JSON.parse(filterIdList).map(id => ({ id })) : [];

			this.$refs.filterTuiGrid.invoke(`resetData`, this.filterList);

			this.resize();
		},
		addFilterList() {
			const checkedRows = this.$refs.orgTuiGrid.invoke(`getCheckedRows`);

			if (0 === checkedRows.length) {
				this.$alert({
					message: this.$t(`msg.no.target.selected`)
				});
				return;
			}

			checkedRows
				.map(({ id }) => ({ id }))
				.forEach(info => {
					if (0 !== this.$refs.filterTuiGrid.invoke('findRows', info).length) return;

					this.$refs.filterTuiGrid.invoke(`appendRow`, info);
				});

			this.resize();
		},
		removeFilterList() {
			const checkedRows = this.$refs.filterTuiGrid.invoke(`getCheckedRows`);

			if (0 === checkedRows.length) {
				this.$alert({
					message: this.$t(`msg.no.target.selected`)
				});
				return;
			}

			checkedRows.forEach(info => {
				this.$refs.filterTuiGrid.invoke(`removeRow`, info.rowKey);
			});

			this.resize();
		},
		resize() {
			this.$refs.orgTuiGrid.invoke('setHeight', 360);
			this.$refs.filterTuiGrid.invoke('setHeight', 360);

			this.$nextTick(() => {
				this.$refs.orgTuiGrid.invoke('setWidth', this.$refs.pOrgTuiGrid.clientWidth);
				this.$refs.filterTuiGrid.invoke('setWidth', this.$refs.pFilterTuiGrid.clientWidth);
			});
		},
		getData() {
			return this.$refs.filterTuiGrid.invoke(`getData`).map(({ id }) => id);
		}
	}
};
</script>

<style>
.topology .tui-grid-content-area,
.topology .tui-grid-layer-state {
	border-left: 1px solid #eee;
}
</style>
