<template>
	<date-range-picker :class="dateClass" v-model="obj[vModel]" v-bind="options" v-if="isShow" @update="update">
		<template #input="picker">
			{{ getDisplayDate(picker.startDate) }}
		</template>
	</date-range-picker>
</template>

<script>
import { mapState } from 'vuex';
import DateRangePicker from 'vue2-daterange-picker';
import moment from 'moment';
import merge from 'lodash/merge';

export default {
	name: 'datePicker',
	components: {
		DateRangePicker
	},
	props: {
		vModel: String,
		readonly: {
			type: Boolean,
			default: false
		},
		val: {
			type: String,
			default: null
		},
		dateRangeType: String,
		customOptions: Object,
		isEmptyPlaceholder: Boolean,
		dateFormat: String
	},
	data() {
		return {
			obj: {},
			isShow: false,
			options: {},
			fixDateFormat: 'YYYY-MM-DD',
			isValueInit: false,
			dateClass: {
				'input-daterange': true,
				'no-timepicker': true,
				placeholder: true
			}
		};
	},
	created() {
		for (const key in this.$data.obj) {
			this.$delete(this.$data.obj, key);
		}

		this.$set(this.obj, this.vModel, { startDate: null, endDate: null });

		this.options = {
			'locale-data': this.dateLocaleData,
			opens: 'right',
			singleDatePicker: true,
			showDropdowns: false,
			autoApply: false,
			timePicker: true,
			timePickerSeconds: true,
			appendToBody: false,
			disabled: this.readonly
		};

		if (this.customOptions) {
			this.options = merge(this.options, this.customOptions);
		}

		if (this.val) this.setData(this.val);

		this.isShow = false;

		this.$nextTick(() => {
			this.isShow = true;
		});
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	computed: {
		...mapState(['dateLocaleData'])
	},
	methods: {
		update() {
			this.isValueInit = true;
			this.dateClass[`placeholder`] = false;
		},
		getDate(val) {
			if (val instanceof Date) {
				return val;
			} else if (val) {
				let dateStr = val.match(/[0-9]{4}-[0-9]{2}-[0-9]{2}/)[0];
				dateStr = dateStr.replace(/-/g, '/');

				return new Date(dateStr);
			} else {
				const date = new Date();

				const time = 'to' === this.dateRangeType ? 59 : 0;

				date.setHours('to' === this.dateRangeType ? 23 : 0);
				date.setMinutes(time);
				date.setSeconds(time);
				date.setMilliseconds(time);

				return date;
			}
		},
		getDisplayDate(date) {
			if (!this.isValueInit && !this.isEmptyPlaceholder) return this.$t(`label.searchFileStamp`);

			return this.obj[this.vModel].startDate ? moment(date).format(this.dateFormat ? this.dateFormat : this.fixDateFormat) : '';
		},
		setData(val) {
			this.isValueInit = !val ? false : true;
			this.dateClass[`placeholder`] = !val ? true : false;

			this.isShow = false;
			this.obj[this.vModel].startDate = this.getDate(val);
			this.obj[this.vModel].endDate = this.getDate(val);
			this.$nextTick(() => {
				this.isShow = true;
			});
		},
		getData() {
			return {
				[this.vModel]: this.isValueInit
					? moment(this.obj[this.vModel].startDate).format(this.dateFormat ? this.dateFormat : this.fixDateFormat)
					: null
			};
		}
	}
};
</script>
<style lang="scss">
.placeholder {
	.form-control {
		font-weight: 400;
		color: #babbc2;
		border-color: #babbc2 !important;
	}
}
</style>
