<template>
	<div ref="mainCustom" class="customLayout"></div>
</template>

<script>
import { getHttpUrl } from '@/utils/common.js';

export default {
	name: 'Custom',
	props: {
		pageInfo: {
			type: Object,
			default: () => ({})
		}
	},
	computed: {
		src() {
			return getHttpUrl(`/api/page/customPage${this.pageInfo.url}`);
		}
	},
	mounted() {
		this.$startSpinner();

		$(this.$refs.mainCustom).load(this.src, () => {
			const { viewer, editor } = this.pageInfo;
			this.$refs.mainCustom.querySelector('[data-ready]').dispatchEvent(new CustomEvent(`privilege`, { detail: { viewer, editor } }));

			this.$stopSpinner();
		});
	},
	beforeDestroy() {
		this.$refs.mainCustom.querySelector('[data-ready]').dispatchEvent(new CustomEvent('destroy'));
		if (this.$refs.mainCustom.querySelector('#panel')) {
			this.$refs.mainCustom.querySelector('#panel').dispatchEvent(new CustomEvent('detailDestroy'));
		}
	}
};
</script>
