<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/notice/Detail.vue';
import { getPrivilege } from '@/utils/common.js';

export default {
	name: 'Notice',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		this.initNewTabSearchObj();
		this.search();
	},
	data() {
		return {
			cudUrl: `/api/entity/notice/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'text', vModel: 'title', label: this.$t('label.title'), placeholder: this.$t('msg.enterTitle') },
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/notice/search`,
				totalCntUrl: `/api/entity/notice/count`,
				paging: {
					isUse: true,
					side: 'server'
				},

				options: {
					columns: [
						{
							header: this.$t('label.title'),
							name: 'noticeTitle',
							width: '5%'
						},
						{
							header: this.$t('label.writer'),
							name: 'userId',
							width: '30%'
						},
						{
							header: this.$t('label.created.date'),
							name: 'pk.createTimestamp',
							width: '30%',
							align: 'center'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['pk.createTimestamp', 'pk.noticeId'],
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true }
			],
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('label.basicInfo'),
					content: [
						[
							{
								type: 'text',
								vModel: 'noticeTitle',
								label: this.$t('label.title'),
								isRequired: true
							}
						],
						[
							{
								type: 'textarea',
								vModel: 'noticeContent',
								label: this.$t('label.content'),
								height: '200px',
								isRequired: true
							}
						]
					]
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	}
};
</script>
