<template>
	<div>
		<div class="ml-auto form-inline m-full" v-if="!readonly">
			<button ref="checkAllBtn" type="button" class="btn-icon" @click="$refs.tuiGrid.invoke('checkAll')" :title="$t('label.check.all')">
				<i class="icon-check"></i>
			</button>
			<button type="button" class="btn-icon" @click="$refs.tuiGrid.invoke('uncheckAll')" :title="$t('label.uncheck.all')">
				<i class="icon-uncheck"></i>
			</button>
			<button type="button" class="btn-icon" @click="addField" :title="`${$t('label.single')} ${$t('label.fields.added')}`">
				<i class="icon-plus"></i>
			</button>
			<button type="button" class="btn-icon" @click="goFieldsAddNumModal" :title="`${$t('label.multiple')} ${$t('label.fields.added')}`">
				<i class="icon-plus-circle"></i>
			</button>
			<button type="button" class="btn-icon" @click="deleteField" :title="$t('label.fields.delete')">
				<i class="icon-minus-circle"></i>
			</button>
			<button type="button" class="btn-icon" @click="goOtherModelSelectModal('referenceModel', $event)" :title="$t('label.model.reference')">
				<i class="icon-share"></i>
			</button>
			<button type="button" class="btn-icon" @click="goOtherModelSelectModal('addModelFields', $event)" :title="$t('label.add.model.fields')">
				<i class="icon-created"></i>
			</button>
			<button type="button" class="btn-icon" @click="indexAutomaticNumbering" :title="$t('label.index.automatic.numbering')">
				<i class="icon-file-text"></i>
			</button>
			<button type="button" class="btn-icon" @click="dictionary" :title="$t('label.dictionary')">
				<i class="icon-srch"></i>
			</button>
			<button type="button" class="btn-icon" @click="openFieldMeta" :title="$t('label.fieldMeta')">
				<i class="icon-file-check"></i>
			</button>
		</div>
		<div class="table-responsive">
			<TuiGrid ref="tuiGrid" v-bind="options" v-if="isLoadColumnInfo" />
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import cloneDeep from 'lodash/cloneDeep';
import { Grid } from '@toast-ui/vue-grid';
import {
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	getNumFromStr,
	HttpReq,
	escapeHtml,
	parseHierarchyObj,
	makeGridOptions
} from '@/utils/common.js';

export default {
	name: 'DetailInfo',
	props: {
		mode: String,
		detailData: Object,
		recordType: String
	},
	components: {
		TuiGrid: Grid
	},
	data() {
		return {
			isLoadColumnInfo: false,
			options: {
				columns: [],
				header: {
					height: 60,
					complexColumns: []
				},
				columnOptions: {
					resizable: true,
					frozenCount: 0
				},
				treeColumnOptions: {
					name: 'pk.fieldId'
				},
				scrollX: true,
				draggable: true,
				rowHeaders: [{ type: 'checkbox', header: ' ' }, `rowNum`],
				editingEvent: `click`,
				onGridMounted: evt => {
					evt.instance.on('click', clickEvt => {
						if (this.readonly) clickEvt.stop();
					});

					evt.instance.on('dragStart', dragStartEvt => {
						const rowInfo = this.$refs.tuiGrid.invoke('getRow', dragStartEvt.rowKey);

						const floatingCellContent = dragStartEvt.floatingRow.getElementsByClassName('tui-grid-floating-tree-cell-content')[0];

						floatingCellContent.innerText = null === rowInfo['pk.fieldId'] ? '' : escapeHtml(rowInfo['pk.fieldId']);
					});

					evt.instance.on('drop', dropEvt => {
						this.$nextTick(this.resize);

						const targetRowInfo = this.$refs.tuiGrid.invoke('getRow', dropEvt.targetRowKey);

						if (targetRowInfo && targetRowInfo.refModelRecordType) {
							dropEvt.stop();
							return;
						}

						if (!dropEvt.appended) return;

						if (dropEvt.targetRowKey === dropEvt.rowKey) return;

						if (!(null === targetRowInfo.fieldType || 'R' !== targetRowInfo.fieldType)) return;

						this.$refs.tuiGrid.invoke('setValue', dropEvt.targetRowKey, 'fieldType', 'R');
					});

					evt.instance.on('expand', () => {
						this.$nextTick(this.resize);
					});

					evt.instance.on('collapse', () => {
						this.$nextTick(this.resize);
					});

					this.$nextTick(this.resize);
				},
				onGridUpdated: () => {
					this.resize();
				}
			},
			isCtrlDown: false
		};
	},
	async mounted() {
		document.body.addEventListener(`keydown`, this.keyDown);

		WindowResizeEventBus.$on('MainResize', this.resizeTimeout);

		SideMenuToggleEventBus.$on('MainResize', this.resizeTimeout);

		DetailToggleEventBus.$on('MainResize', this.resizeTimeout);

		SearchToggleEventBus.$on('MainResize', this.resizeTimeout);

		await this.getExtendColumnInfo();

		this.$el.addEventListener(`change`, this.changeCheckbox);
	},
	computed: {
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	watch: {
		detailData() {
			this.resetData();
		}
	},
	methods: {
		openFieldMeta() {
			let dataObj = {};

			for (const key in this.$parent.$refs) {
				if (Array.isArray(this.$parent.$refs[key])) {
					this.$parent.$refs[key].forEach(component => {
						if (component.getData) dataObj = merge(dataObj, component.getData());
					});
				} else {
					if (!this.$parent.$refs[key].getData) continue;

					dataObj = merge(dataObj, this.$parent.$refs[key].getData());
				}
			}

			if (` ` === dataObj.metaDomain) {
				this.$alert({
					type: `warn`,
					backdropMode: `full`,
					message: this.$t(`msg.set.metadomain.basic.information`)
				});

				return;
			}

			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t('label.fieldMeta'),
				fullscreen: true,
				contentList: [
					{
						template: `<AddFieldMeta ref="addFieldMeta" metaDomain="${dataObj.metaDomain}" />`,
						components: {
							AddFieldMeta: require('@/views/modal/fieldMeta/addFieldMeta.vue').default
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('label.ok') }}</button>`,
						methods: {
							confirm() {
								const fieldMetaList = modalInstance.$refs.content[0].$refs.addFieldMeta.getData();

								if (0 === fieldMetaList.length) {
									this.$alert({ type: 'warn', message: this.$t(`msg.no.target.selected`) });
									return;
								}

								fieldMetaList.forEach(info => {
									const rowInfo = {};

									_this.options.columns.forEach(column => {
										rowInfo[column.name] = 'undefined' === typeof info[column.name] ? null : info[column.name];
									});

									_this.$refs.tuiGrid.invoke('appendRow', rowInfo);
								});

								_this.resize();

								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		changeCheckbox(evt) {
			if (`INPUT` !== evt.target.tagName) return;

			if (`checkbox` !== evt.target.type) return;

			const { checkedvalue, uncheckedvalue, name, rowkey } = evt.target.dataset;

			this.$refs.tuiGrid.invoke('setValue', rowkey, name, evt.target.checked ? checkedvalue : uncheckedvalue);
		},
		async keyDown(evt) {
			if (0 === this.$el.clientWidth) return;

			this.isCtrlDown = evt.ctrlKey;

			if (!(this.isCtrlDown && `v` === evt.key)) return;

			if (`INPUT` === evt.target.tagName) return;

			document.querySelector(`.hd-side > a`).focus();

			if (this.readonly) return;

			const textarea = document.createElement(`textarea`);
			textarea.style.width = `0px`;
			textarea.style.height = `0px`;

			document.body.appendChild(textarea);

			textarea.focus();

			document.execCommand('paste');

			const pasteText = await new Promise(resolve => {
				setTimeout(() => {
					document.body.removeChild(textarea);
					resolve(textarea.value);
				}, 0);
			});

			const row = [];

			const arr = pasteText.split(`\n`);

			arr.forEach(info => {
				const colArr = info.split(`\t`);

				if (!colArr) return;

				if (`undefined` === typeof colArr[0]) return;

				if (`undefined` === typeof colArr[1]) return;

				if (1 < colArr.length && isNaN(colArr[0])) {
					this.$alert({ type: 'warn', message: this.$t(`label.record.paste.with.level`) });

					return;
				}

				row.push({
					col: colArr.map(col => col.trim()),
					isAppend: false
				});
			});

			const recordObj = cloneDeep(this.detailData);
			recordObj.fields = [];

			await this.copyPasteCovertData(row, 0, recordObj);

			const appendDataArr = this.initTreeData(recordObj.fields);

			const gridDataArr = this.$refs.tuiGrid.invoke(`getData`);

			appendDataArr.forEach(data => {
				const duplicationRow = gridDataArr.find(gridData => gridData[`pk.fieldId`] === data[`pk.fieldId`]);

				if (duplicationRow) this.$refs.tuiGrid.invoke(`removeRow`, duplicationRow.rowKey);

				this.$refs.tuiGrid.invoke(`appendTreeRow`, data, { parentRowKey: -1 });
			});

			this.resizeTimeout();

			document.querySelector(`.hd-side > a`).blur();
		},
		fieldValidation(arr) {
			arr.forEach(field => {
				const functionName = field.validateMessage ? `addRowClassName` : `removeRowClassName`;
				this.$refs.tuiGrid.invoke(functionName, field.rowKey, `red`);

				if (field.recordObject) this.fieldValidation(field.recordObject.fields);
			});
		},
		resetData() {
			if (!this.$refs.tuiGrid) return;

			let coverDiv = this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').querySelector('#coverDiv');

			if (coverDiv) this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').removeChild(coverDiv);

			if (this.detailData && !this.detailData.fields) {
				this.$refs.tuiGrid.invoke('clear');
				return;
			}

			this.$refs.tuiGrid.invoke('resetData', this.initTreeData(this.detailData.fields));

			this.fieldValidation(this.$refs.tuiGrid.invoke(`getData`));

			if (this.readonly) {
				coverDiv = document.createElement('div');

				coverDiv.setAttribute('id', 'coverDiv');
				coverDiv.style.width = '100%';
				coverDiv.style.height = '100%';
				coverDiv.style.backgroundColor = '#666';
				coverDiv.style.position = 'absolute';
				coverDiv.style.top = '0';
				coverDiv.style.opacity = '0.4';

				this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').appendChild(coverDiv);
			}
		},
		async getExtendColumnInfo() {
			const httpReq = new HttpReq(`/api/page/columns`);

			const result = await httpReq.read();

			if (`ok` !== result.data.result) return;

			const config = JSON.parse(result.data.object);

			const extendColumnInfo = config.Configuration.Record.GroupHeaders.GroupHeader;

			extendColumnInfo.forEach((complexColumnInfo, index) => {
				const complexColumn = { name: `mergeColumn${index + 1}`, header: complexColumnInfo.label, childNames: [] };

				complexColumnInfo.Column.forEach((columnInfo, subIndex) => {
					const column = { name: columnInfo.name, header: columnInfo.label, width: columnInfo.width, align: columnInfo.align };

					if (0 === index && 0 === subIndex) {
						column['minWidth'] = '150';
					}

					if ('undefined' !== typeof columnInfo.availableDefaultValue) column.defaultValue = columnInfo.availableDefaultValue;

					if ('edit' === columnInfo.editType) {
						if ('integer' === columnInfo.availableValue || 'numeric' === columnInfo.availableValue) {
							column.editor = class CustomTextditor {
								constructor(props) {
									const input = document.createElement('input');

									input.setAttribute(`type`, `text`);

									input.addEventListener('input', evt => {
										evt.target.value = Number(evt.target.value.replace(/[^0-9]/g, ''));
									});

									input.classList.add('form-control');

									input.value = props.value;

									this.el = input;
								}
								getElement() {
									return this.el;
								}
								getValue() {
									return this.el.value;
								}
							};
						} else {
							column.editor = 'text';
							column.formatter = info => escapeHtml(info.value);
						}
					} else if ('checkbox' === columnInfo.editType) {
						let checkedValue = 'Y',
							unCheckedValue = 'N';

						if (columnInfo.availableValue)
							columnInfo.availableValue.split(',').forEach(availableValueInfo => {
								if ('true' == availableValueInfo.split('=')[1]) checkedValue = availableValueInfo.split('=')[0];
								else unCheckedValue = availableValueInfo.split('=')[0];
							});

						column.align = 'center';
						column.formatter = info => {
							return this.readonly
								? info.value
									? info.value
									: unCheckedValue
								: `<input type="checkbox" 
										${checkedValue === info.value ? `checked` : ``} ${info.row._attributes.disabled ? 'disabled' : ''}
										data-rowkey="${info.row.rowKey}" data-name="${info.column.name}" data-checkedValue="${checkedValue}" data-unCheckedValue="${unCheckedValue}">`;
						};
					} else if ('select' === columnInfo.editType) {
						column.editor = class CustomSelectEditor {
							constructor(props) {
								const select = document.createElement('select');
								select.classList.add('form-control');
								select.style.minWidth = 'unset';

								columnInfo.availableValue.split(',').forEach(availableValueInfo => {
									const option = document.createElement('option');
									option.setAttribute('value', availableValueInfo.split('=')[0]);
									option.innerText = availableValueInfo.split('=')[1];

									if (props.value === availableValueInfo.split('=')[0]) {
										option.setAttribute('selected', 'selected');
									}

									select.appendChild(option);
								});

								this.el = select;
							}
							getElement() {
								return this.el;
							}
							getValue() {
								return this.el.value;
							}
						};

						column.formatter = info => {
							if (null === info.value) return '';

							let rtnValue = null;

							columnInfo.availableValue.split(',').forEach(availableValueInfo => {
								if (availableValueInfo.split('=')[0] !== info.value) return;

								rtnValue = availableValueInfo.split('=')[1];
							});

							return escapeHtml(rtnValue);
						};

						column.copyOptions = {
							useFormattedValue: true
						};
					} else {
						column.escapeHTML = true;
					}

					this.options.columns.push(column);

					complexColumn.childNames.push(columnInfo.name);
				});

				this.options.header.complexColumns.push(complexColumn);
			});

			this.options = makeGridOptions(this.options);

			this.isLoadColumnInfo = true;

			this.$nextTick(() => {
				this.resetData();
			});
		},
		initTreeData(fields, isDisabled) {
			if (!fields) return;

			fields = cloneDeep(fields);

			const rtnArr = [];

			fields.forEach(field => {
				field['pk.fieldId'] = field.pk.fieldId;
				field['pk.recordId'] = field.pk.recordId;

				if (field.options) {
					field.options.forEach(optionInfo => {
						field[optionInfo.pk.keyId] = optionInfo.property;
					});

					delete field.options;
				}

				if (!field._attributes) field._attributes = {};

				if (isDisabled) {
					field._attributes.disabled = true;
				} else {
					this.options.columns.forEach(info => {
						if (null === field[info.name] || 'undefined' === typeof field[info.name])
							field[info.name] = 'undefined' === typeof info.defaultValue ? null : info.defaultValue;
					});
				}

				if (field.recordObject) {
					field.fieldType = 'R';

					field._attributes.expanded = true;

					if ('E' !== field.recordObject.recordType) field.refModelRecordType = field.recordObject.recordType;

					field._children = this.initTreeData(
						field.recordObject.fields,
						`undefined` === typeof isDisabled ? `E` !== field.recordObject.recordType : isDisabled
					);
				}

				delete field.pk;
				delete field.options;
				delete field.recordObject;
				delete field.lvl;

				rtnArr.push(field);
			});

			return rtnArr;
		},
		resize() {
			if (!this.$refs.tuiGrid) return;

			const panel = document.getElementById('panel');

			const panelBody = panel.getElementsByClassName('panel-body')[0];
			const panelBodyComputedStyle = getComputedStyle(panelBody);

			const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
			const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
			const panelBodyPaddingTop = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-top'));
			const panelBodyPaddingBottom = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-bottom'));

			const adjustWidth = panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight;
			const adjustHeight = panelBody.clientHeight - panelBodyPaddingTop - panelBodyPaddingBottom - 5 - (this.readonly ? 0 : 32);

			this.$refs.tuiGrid.invoke('setWidth', adjustWidth);
			this.$refs.tuiGrid.invoke('setHeight', adjustHeight);
		},
		resizeTimeout() {
			setTimeout(() => {
				this.resize();
			}, 300);
		},
		addField(evt) {
			this.changeButtonBlur(evt);

			const rowInfo = {};

			this.options.columns.forEach(info => {
				rowInfo[info.name] = 'undefined' === typeof info.defaultValue ? null : info.defaultValue;
			});

			this.$refs.tuiGrid.invoke('appendRow', rowInfo);

			this.resize();
		},
		goFieldsAddNumModal(evt) {
			this.changeButtonBlur(evt);

			const _this = this;

			const modalInstane = this.$openModal({
				title: this.$t('label.number.fields.added'),
				modalWidth: '250px',
				modalHeight: '180px',
				contentList: [
					{
						template: `<div><input type="text" class="form-control" v-model="addCnt" maxlength="2" @input="addCnt = addCnt.replace(/[^0-9]/g, '')"></div>`,
						data() {
							return {
								addCnt: 1
							};
						},
						methods: {
							getData() {
								return this.addCnt;
							}
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('label.ok') }}</button>`,
						methods: {
							confirm() {
								const addCnt = modalInstane.$refs.content[0].getData();

								for (let i = 0; i < addCnt; i++) {
									const rowInfo = {};

									_this.options.columns.forEach(info => {
										rowInfo[info.name] = 'undefined' === typeof info.defaultValue ? null : info.defaultValue;
									});

									_this.$refs.tuiGrid.invoke('appendRow', rowInfo);
								}

								_this.resize();

								modalInstane.hideModal();
							}
						}
					}
				]
			});
		},
		deleteField(evt) {
			this.changeButtonBlur(evt);

			if (0 === this.$refs.tuiGrid.invoke('getRowCount')) {
				this.$alert({ type: 'warn', message: this.$t('msg.no.target.selected') });
				return;
			}

			const checkedRowKeys = this.$refs.tuiGrid.invoke('getCheckedRowKeys');

			if (0 === checkedRowKeys.length) return;

			checkedRowKeys.forEach(rowKey => {
				const rowInfo = this.$refs.tuiGrid.invoke('getRow', rowKey);

				if (null == rowInfo) return;

				this.$refs.tuiGrid.invoke('removeRow', rowKey);
			});

			this.$refs.tuiGrid.invoke('blur');
			this.resize();
		},
		goOtherModelSelectModal(mod, evt) {
			this.changeButtonBlur(evt);

			const _this = this;

			let initRecordType = ``;
			let optionAddInfo = {};

			if (`referenceModel` === mod) {
				if (!this.recordType) {
					this.$alert({ type: 'warn', message: this.$t('msg.record.field.reference.validation') });
					return;
				}

				switch (this.recordType) {
					case `H`:
						initRecordType = 'c';
						optionAddInfo = {
							optionIf: `option.pk.propertyKey != 'E' && option.pk.propertyKey != 'I'`,
							optionText: `'c' === option.pk.propertyKey? '${this.$t(`label.all`)}' : option.propertyValue`,
							isViewAll: false
						};
						break;

					case `R`:
					case `I`:
						initRecordType = 'R';
						optionAddInfo = { optionIf: `option.pk.propertyKey == 'R'`, optionText: `option.propertyValue`, isViewAll: false };
						break;
				}
			}

			const modalInstane = this.$openModal({
				title: `referenceModel` === mod ? this.$t('label.model.reference') : this.$t('label.add.model.fields'),
				modalWidth: '800px',
				contentList: [
					{
						template: `<div>
							  <Search :list="searchList" @search="search" ref="search" />
							   <Main
							   	   :buttonList="buttonList"
								   :gridInfo="gridInfo"
								   :gridSearchObj="gridSearchObj"
								   :gridType="'modalBasic'"
								   :viewer="true"
								   @rowClicked="rowClicked"
								   @initialize="initialize"
							   />
						   </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								gridSearchObj: {},
								buttonList: [{ id: 'initialize', isUse: true }],
								searchList: [
									{
										type: 'text',
										vModel: 'id',
										label: this.$t('label.modelRecord.id'),
										placeholder: this.$t('msg.enterId'),
										regExpType: 'searchId'
									},
									{
										type: 'text',
										vModel: 'name',
										label: this.$t('label.modelRecord.name'),
										placeholder: this.$t('msg.enterName'),
										regExpType: 'name'
									},
									{
										type: 'select',
										vModel: 'recordType',
										label: this.$t(`label.type`),
										val: initRecordType,
										optionInfo: merge(
											{
												url: `/api/page/properties`,
												params: {
													pk: {
														propertyId: 'List.Record.Type'
													},
													orderByKey: false
												},
												optionListName: 'recordType',
												optionFor: 'option in recordType',
												optionValue: 'option.pk.propertyKey',
												optionIf: `option.pk.propertyKey != 'E'`,
												optionText: 'option.propertyValue'
											},
											optionAddInfo
										)
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('label.listCnt'),
										placeholder: this.$t('msg.coditionList'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],
								gridInfo: {
									url: `/api/entity/record/search`,
									totalCntUrl: `/api/entity/record/count`,
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('label.modelRecord.id'),
												name: 'id',
												width: '35%'
											},
											{
												header: this.$t('label.modelRecord.name'),
												name: 'name',
												width: '35%'
											},
											{
												header: this.$t(`label.type`),
												name: `recordType`,
												align: `center`,
												formatter: ({ value }) =>
													escapeHtml(
														this.recordTypeList.find(recordType => recordType.pk.propertyKey === value).propertyValue
													),
												width: '15%'
											},
											{
												header: this.$t('label.privilege.id'),
												name: 'privilegeId',
												width: '15%'
											}
										]
									}
								},
								recordTypeList: []
							};
						},
						async created() {
							const httpReq = new HttpReq(`/api/page/properties`);

							const result = await httpReq.read({ pk: { propertyId: 'List.Record.Type' }, orderByKey: true });

							if (`ok` !== result.data.result) return;

							this.recordTypeList = result.data.object;
						},
						methods: {
							initialize() {
								this.$refs.search.initialize();
							},
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							async rowClicked(info) {
								if (info.id === _this.detailData.id) {
									this.$alert({ type: 'warn', message: this.$t('label.same.modelId') });
								} else {
									const httpReq = new HttpReq(`/api/entity/record/object`);

									const result = await httpReq.read({ id: info.id });

									if (`ok` !== result.data.result) return;

									if (`referenceModel` === mod) {
										_this.$refs.tuiGrid.invoke(
											'appendRow',
											_this.initTreeData([
												{
													pk: {
														fieldId: info.id,
														recordId: _this.detailData.id
													},
													fieldName: info.name,
													fieldType: 'R',
													recordObject: {
														fields: result.data.object.fields,
														recordType: result.data.object.recordType
													},
													subRecordId: info.id
												}
											])[0]
										);
									} else {
										if (null === result.data.object.fields || 0 === result.data.object.fields.length) {
											this.$alert({
												type: 'warn',
												message: this.$t(`label.nofiled.modelId`)
											});
											return;
										}

										_this.initTreeData(result.data.object.fields).forEach(field => {
											_this.$refs.tuiGrid.invoke('appendRow', field);
										});
									}

									modalInstane.hideModal();

									_this.resize();
								}
							}
						}
					}
				]
			});
		},
		indexAutomaticNumbering(evt) {
			this.changeButtonBlur(evt);

			if (0 === this.$refs.tuiGrid.invoke('getRowCount')) {
				this.$alert({ type: 'warn', message: this.$t('msg.no.target') });
				return;
			}

			this.$confirm({
				type: 'warn',
				message: this.$t('msg.automatically.number'),
				callback: () => {
					let idx = 0;

					for (let i = 0; i < this.$refs.tuiGrid.invoke('getRowCount'); i++) {
						const rowInfo = this.$refs.tuiGrid.invoke('getRowAt', i);

						if (null !== this.$refs.tuiGrid.invoke('getParentRow', rowInfo.rowKey)) continue;

						this.$refs.tuiGrid.invoke('setValue', rowInfo.rowKey, 'fieldIndex', `${++idx}`.padStart(4, '0'));
					}
				}
			});
		},
		dictionary(evt) {
			this.changeButtonBlur(evt);

			if (0 === this.$refs.tuiGrid.invoke('getRowCount')) {
				this.$alert({ type: 'warn', message: this.$t('msg.no.target.selected') });
				return;
			}

			let dataObj = {};

			for (const key in this.$parent.$refs) {
				if (Array.isArray(this.$parent.$refs[key])) {
					this.$parent.$refs[key].forEach(component => {
						if (component.getData) dataObj = merge(dataObj, component.getData());
					});
				} else {
					if (!this.$parent.$refs[key].getData) continue;

					dataObj = merge(dataObj, this.$parent.$refs[key].getData());
				}
			}

			const { metaDomain, fields } = dataObj;

			if (0 === metaDomain.trim().length) {
				this.$alert({ type: 'warn', message: this.$t('msg.set.metadomain.basic.information') });
				return;
			}

			for (let i = 0; i < fields.length; i++) {
				if (null === fields[i].pk.fieldId && null === fields[i].fieldName) {
					this.$alert({ type: 'warn', message: this.$t('msg.data.validation.dictionary') });
					return;
				}
			}

			this.$confirm({
				type: 'warn',
				message: this.$t('msg.data.dictionary'),
				callback: async () => {
					const httpReq = new HttpReq(`/api/entity/record/applyMeta`);

					const obj = await httpReq.create({ metaDomain: metaDomain, ...this.getData() });

					if ('ok' !== obj.data.result) return;

					this.$refs.tuiGrid.invoke('resetData', this.initTreeData(obj.data.object.fields));

					this.resize();
				}
			});
		},
		getData() {
			this.$refs.tuiGrid.invoke('blur');
			this.$refs.tuiGrid.invoke('cancelEditing');

			const deleteKeyList = [
				'rowKey',
				'rowSpanMap',
				'sortKey',
				'uniqueKey',
				'_attributes',
				'_disabledPriority',
				'_leaf',
				'_relationListItemMap'
			];

			const setChildDataStructure = info => {
				deleteKeyList.forEach(deleteKey => {
					delete info[deleteKey];
				});

				const fieldOptionsArr = [];

				// 확장정보
				['FieldTrim', 'FieldFullWidth', 'ext1', 'FieldHttp'].forEach(key => {
					if ('undefined' === typeof info[key] || null === info[key] || 0 === info[key].trim().length) return;

					fieldOptionsArr.push({
						pk: {
							keyId: key
						},
						property: info[key]
					});
				});

				if (0 < fieldOptionsArr.length) info.options = fieldOptionsArr;

				if (`R` === info.fieldType) info.recordObject = { fields: [], recordType: info.refModelRecordType ? info.refModelRecordType : 'E' };

				const children = info._children;

				delete info._children;
				delete info.validateMessage;

				if (0 === children.length) return info;

				children.forEach(childrenInfo => {
					info.recordObject.fields.push(cloneDeep(parseHierarchyObj(this.$refs.tuiGrid.invoke('getRow', childrenInfo.rowKey))));
				});

				info.recordObject.fields.forEach(fieldInfo => {
					setChildDataStructure(fieldInfo);
				});

				return info;
			};

			const fields = [];

			for (let i = 0; i < this.$refs.tuiGrid.invoke('getRowCount'); i++) {
				const rowInfo = this.$refs.tuiGrid.invoke('getRowAt', i);

				if (null !== this.$refs.tuiGrid.invoke('getParentRow', rowInfo.rowKey)) continue;

				fields.push(setChildDataStructure(cloneDeep(parseHierarchyObj(rowInfo))));
			}

			return {
				fields
			};
		},
		async copyPasteCovertData(row, rowIdx, recordObj) {
			if (!recordObj) return;

			let fieldOrder = 0;

			for (let i = rowIdx; i < row.length; i++) {
				const { col, isAppend } = row[i];

				if (isAppend) continue;

				const lvl = Number(col[0]);

				if (0 === recordObj.fields.length || lvl === recordObj.fields[recordObj.fields.length - 1].lvl) {
					const field = {
						lvl,
						pk: {
							recordId: recordObj.id,
							fieldId: col[1]
						},
						fieldName: col[2],
						fieldIndex: col[3],
						fieldType: (() => {
							const fieldTypeObj = {
								Byte: 'B',
								Short: `S`,
								Int: `I`,
								Long: `L`,
								Float: `F`,
								Double: `D`,
								TimeStamp: `p`,
								String: `T`,
								Numeric: `N`,
								PackedDecimal: `P`,
								Raw: `A`,
								Record: `R`
							};

							return fieldTypeObj[col[4]];
						})(),
						fieldLength: Number(col[5]),
						fieldScale: Number(col[6]),
						arrayType: (() => {
							const arrayTypeObj = {
								Not: `N`,
								Fixed: `F`,
								Variable: `V`
							};

							return arrayTypeObj[col[7]];
						})(),
						referenceFieldId: col[8],
						fieldDefaultValue: col[9],
						fieldHiddenYn: col[10] ? col[10].charAt(0) : `N`,
						fieldRequireYn: col[11] ? col[11].charAt(0) : `N`,
						fieldValidValue: col[12],
						codecId: col[13],
						fieldOptions: col[14],

						fieldOrder: fieldOrder++,

						subRecordId: null,
						recordObject: null
					};

					if (`R` === field.fieldType) {
						const subRecordObject = {
							fields: []
						};

						if (`Y` === col[15]) {
							subRecordObject.id = col[16];

							const httpReq = new HttpReq(`/api/entity/record/object`);
							const res = await httpReq.read({ id: subRecordObject.id });

							if (`ok` === res.data.result) {
								subRecordObject.fields = res.data.object.fields;
							}

							subRecordObject.recordType = `R`;
						} else {
							subRecordObject.id = `${recordObj.id}@${field.pk.fieldId}`;
							subRecordObject.recordType = `E`;
						}

						field.subRecordId = subRecordObject.id;
						field.recordObject = subRecordObject;
					}

					recordObj.fields.push(field);

					row[i].isAppend = true;
				} else if (lvl > recordObj.fields[recordObj.fields.length - 1].lvl) {
					await this.copyPasteCovertData(row, i, recordObj.fields[recordObj.fields.length - 1].recordObject);
				} else {
					break;
				}
			}
		},
		changeButtonBlur(evt) {
			if (`I` === evt.target.tagName) evt.target.parentNode.blur();
			else evt.target.blur();
		}
	},
	beforeDestroy() {
		WindowResizeEventBus.$off('MainResize', this.resizeTimeout);
		SideMenuToggleEventBus.$off('MainResize', this.resizeTimeout);
		DetailToggleEventBus.$off('MainResize', this.resizeTimeout);
		SearchToggleEventBus.$off('MainResize', this.resizeTimeout);

		document.body.removeEventListener(`keydown`, this.keyDown);
		this.$el.removeEventListener(`change`, this.changeCheckbox);
	}
};
</script>
<style lang="scss" scoped>
.btn-icon {
	margin: 0px;
}
</style>
