<template>
	<div class="modifed-root">
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('label.before') }}
						</span>
					</label>
					<div>
						<div class="detail-content-common diff-form" id="before" :style="beforeContentsStyle" @wheel="contentsWheel">
							<span v-html="beforeContents" />
						</div>
					</div>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('label.after') }}
						</span>
					</label>
					<div>
						<div class="detail-content-common diff-form" id="after" :style="afterContentsStyle" @wheel="contentsWheel">
							<span v-html="afterContents" />
						</div>
					</div>
				</div>
			</div>
		</div>
		<div v-show="isViewSpinner">
			<div class="modifed-spinner-bg"></div>
			<div class="spinner"></div>
		</div>
	</div>
</template>
<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import {
	escapeHtml,
	getHttpUrl,
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	getNumFromStr,
	getContextPath
} from '@/utils/common.js';

export default {
	name: 'Modifed',
	extends: DetailBasic,
	data() {
		return {
			isViewSpinner: false,
			beforeContents: null,
			afterContents: null,
			beforeContentsStyle: { height: null },
			afterContentsStyle: { height: null },
			worker: null,
			WORKER_RESOURCE: `
            self.onmessage = e => {
               importScripts(e.data.url);

               const diff = Diff.diffWordsWithSpace(e.data.beforeDataString, e.data.afterDataString, {
                  ignoreWhitespace: false,
                  newlineIsToken: false,
               });

               postMessage({ diff: diff });
            }
         `
		};
	},
	mounted() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);
	},
	methods: {
		contentsWheel(evt) {
			const deltaYValue = 50;

			this.$el.querySelector(`#before`).scrollTop += 0 < evt.deltaY ? deltaYValue : -deltaYValue;
			this.$el.querySelector(`#after`).scrollTop += 0 < evt.deltaY ? deltaYValue : -deltaYValue;
		},
		initData() {
			if (!this.detailData.beforeDataString && !this.detailData.afterDataString) return;

			if (!this.detailData.beforeDataString) this.detailData.beforeDataString = ``;
			if (!this.detailData.afterDataString) this.detailData.afterDataString = ``;

			this.beforeContents = ``;
			this.afterContents = ``;

			if (this.worker) this.worker.terminate();
			this.worker = new Worker(URL.createObjectURL(new Blob([this.WORKER_RESOURCE], { type: `application/javascript` })));

			this.isViewSpinner = true;

			this.worker.postMessage({
				url:
					'production' === process.env.NODE_ENV
						? `${location.origin}${getContextPath()}/custom/js/diff.js`
						: getHttpUrl(`/custom/js/diff.js`),
				beforeDataString: this.detailData.beforeDataString,
				afterDataString: this.detailData.afterDataString
			});

			this.worker.onmessage = e => {
				const diff = e.data.diff;

				const beforeArr = [];
				const afterArr = [];

				diff.forEach(diff => {
					const content = escapeHtml(diff.value).replace(/\s/g, `&nbsp;`);

					if (diff.added) afterArr.push(`<ins class="diff-ins">${content}</ins>`);
					else if (diff.removed) beforeArr.push(`<del class="diff-del">${content}</del>`);
					else {
						beforeArr.push(content);
						afterArr.push(content);
					}
				});

				this.beforeContents = beforeArr.join('');
				this.afterContents = afterArr.join('');

				this.isViewSpinner = false;
			};
		},
		resize() {
			const panel = document.getElementById('panel');
			const panelBody = panel.getElementsByClassName('panel-body')[0];
			const panelBodyComputedStyle = getComputedStyle(panelBody);

			// const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
			// const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
			const panelBodyPaddingTop = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-top'));
			const panelBodyPaddingBottom = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-bottom'));

			// const adjustWidth = panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight;
			const adjustHeight = panelBody.clientHeight - panelBodyPaddingTop - panelBodyPaddingBottom - 24;

			// this.beforeContentsStyle.width = `${adjustWidth / 2}px`;
			this.beforeContentsStyle.height = `${adjustHeight}px`;

			// this.afterContentsStyle.width = `${adjustWidth / 2}px`;
			this.afterContentsStyle.height = `${adjustHeight}px`;
		}
	}
};
</script>

<style lang="scss">
.modifed-root {
	position: relative;
}

.modifed-spinner-bg {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	opacity: 0.6;
}

.diff-form {
	background-color: #f5f6fb;
	width: 100%;
	height: 300px;
	padding: 0.375rem 0.5rem;
	border: 1px solid #babbc2;
	border-radius: 0.25rem;
	overflow-y: scroll;
}
.diff-ins {
	background: #ddffdd;
	color: #406619;
	text-decoration: none;
}
.diff-del {
	background: #fee8e9;
	color: #b30000;
	text-decoration: none;
}
</style>
