<script>
import merge from 'lodash/merge';
import Basic from '@/components/main/Basic.vue';
import { getPrivilege, changeDateFormat, openNewTab } from '@/utils/common.js';

export default {
	name: 'PublishLog',
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else if (localStorage.getItem('afterPublishSearchObj')) {
			const afterPublishSearchObj = JSON.parse(localStorage.getItem('afterPublishSearchObj'));
			localStorage.removeItem('afterPublishSearchObj');

			const searchObj = {
				dateType: `P`,
				fromDateTime: afterPublishSearchObj.fromDateTime,
				resourceType: afterPublishSearchObj.resourceType,
				resourceId: afterPublishSearchObj.resourceId,
				_pageSize: this.searchList.find(searchInfo => `pageSize` === searchInfo.vModel).val
			};

			this.gridSearchObj = merge(this.$refs.search.getSearchObj(), searchObj ? searchObj : {});

			this.searchList.forEach(searchInfo => {
				if (![`pageSize`, `dateType`, `fromDateTime`, `resourceType`, `resourceId`].find(name => name === searchInfo.vModel)) return;

				this.$set(searchInfo, 'val', this.gridSearchObj[`pageSize` === searchInfo.vModel ? `_pageSize` : searchInfo.vModel]);
			});

			this.startTimerSeconds = 10;
		}
	},
	data() {
		return {
			cudUrl: `/api/entity/publishLog/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'select',
					vModel: 'dateType',
					label: this.$t('label.date.type'),
					val: `P`,
					optionInfo: {
						optionFor: `option in [
							{value: 'P', text: '${this.$t(`label.publish`)}'}, 
							{value: 'M', text: '${this.$t(`label.migration`)}'}
						]`,
						optionValue: 'option.value',
						optionText: 'option.text',
						isViewAll: false
					}
				},
				{
					type: 'singleDaterange',
					vModel: 'fromDateTime',
					label: this.$t('label.from'),
					dateRangeType: 'from'
				},
				{
					type: 'singleDaterange',
					vModel: 'toDateTime',
					label: this.$t('label.to'),
					dateRangeType: 'to'
				},
				{
					type: 'text',
					vModel: 'pk.publishId',
					label: this.$t('label.publish.id'),
					placeholder: this.$t('msg.enterId')
				},
				{
					type: 'select',
					vModel: 'resourceType',
					label: this.$t('label.publishLog.resource.type'),
					optionInfo: {
						optionFor: `option in [{value: 'R', text: '${this.$t('label.modelRecord')}'},
                                               {value: 'S', text: '${this.$t('label.service')}'},
                                               {value: 'I', text: '${this.$t('label.interface')}'}]`,
						optionValue: 'option.value',
						optionText: 'option.text'
					}
				},
				{
					type: 'text',
					vModel: 'resourceId',
					label: this.$t('label.publishLog.resource.id'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId'
				},
				{
					type: 'select',
					vModel: 'publishStatus',
					label: this.$t('label.publishStatus'),
					optionInfo: {
						optionFor: `option in [{value: 'A', text: '${this.$t('label.publishLog.status.active')}'},
                                               {value: 'D', text: '${this.$t('label.publishLog.status.done')}'},
                                               {value: 'F', text: '${this.$t('label.publishLog.status.fail')}'}]`,
						optionValue: 'option.value',
						optionText: 'option.text'
					}
				},
				{
					type: 'select',
					vModel: 'migrationStatus',
					label: this.$t('label.migration.status'),
					optionInfo: {
						optionFor: `option in [{value: 'I', text: '${this.$t('label.migration.init')}'},
											   {value: 'R', text: '${this.$t('label.migration.ready')}'},
											   {value: 'D', text: '${this.$t('label.migration.done')}'},
											   {value: 'C', text: '${this.$t('label.migration.cancel')}'}]`,
						optionValue: 'option.value',
						optionText: 'option.text'
					}
				},
				{
					type: 'modal',
					vModel: 'publishUserId',
					label: this.$t('label.publishLog.user'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('label.publishLog.user'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'userId',
									label: this.$t('label.id'),
									placeholder: this.$t('msg.enterId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'userName',
									label: this.$t('label.name'),
									placeholder: this.$t('msg.enterName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('label.listCnt'),
									placeholder: 'msg.coditionList',
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/user/search`,
							totalCntUrl: `/api/entity/user/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('label.id'),
										name: 'userId',
										width: '30%'
									},
									{
										header: this.$t('label.name'),
										name: 'userName',
										width: '40%'
									},
									{
										header: this.$t('label.user.department'),
										name: 'department',
										width: '30%'
									}
								]
							}
						},
						rowClickedCallback: ({ userId }) => userId
					}
				},
				{ type: 'text', vModel: 'publishMessage', label: this.$t(`label.publishMessage`), placeholder: this.$t('msg.enterData') },
				{ type: 'text', vModel: 'targetId', label: this.$t(`label.publish.target`), placeholder: this.$t('msg.enterId') },
				{
					type: 'select',
					vModel: 'targetPublishDone',
					label: this.$t(`label.publish.target.done`),
					optionInfo: {
						optionFor: `option in [{value: 'Y', text: '${this.$t('label.yes')}'},
                                               {value: 'N', text: '${this.$t('label.no')}'},
                                               {value: 'S', text: 'Skip'},
											   {value: 'E', text: 'Error'}]`,
						optionValue: 'option.value',
						optionText: 'option.text'
					}
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'migration', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'refresh', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'download', isUse: true, downloadUrl: `/api/entity/publishLog/download` }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/publishLog/search`,
				totalCntUrl: `/api/entity/publishLog/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					rowHeaders: ['checkbox'],
					columns: [
						{
							header: this.$t('label.publishLog.date'),
							name: 'pk.publishDateTime',
							width: `20%`,
							align: `center`,
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							header: this.$t('label.publishLog.resource.type'),
							name: 'resourceType',
							align: 'center',
							width: `10%`,
							formatter: info => {
								switch (info.row.resourceType) {
									case 'R':
										return this.$t('label.modelRecord');
									case 'S':
										return this.$t('label.service');
									case 'I':
										return this.$t('label.interface');
								}
							}
						},
						{
							header: this.$t('label.publishLog.resource.id'),
							name: 'resourceId',
							width: `10%`
						},
						{
							header: this.$t('label.publishLog.resource.version'),
							name: 'resourceVersion',
							width: `10%`,
							align: `right`
						},
						{
							header: this.$t('label.publishStatus'),
							name: 'publishStatus',
							align: 'center',
							width: `10%`,
							formatter: info => {
								switch (info.row.publishStatus) {
									case 'A':
										return this.$t('label.publishLog.status.active');
									case 'D':
										return this.$t('label.publishLog.status.done');
									case 'F':
										return this.$t('label.publishLog.status.fail');
								}
							}
						},
						{
							header: this.$t('label.publishMessage'),
							name: 'publishMessage',
							width: `10%`
						},
						{
							header: this.$t('label.publishLog.user'),
							name: 'publishUserId',
							width: `10%`
						},
						{
							header: this.$t('label.migration.status'),
							name: 'migrationStatus',
							align: 'center',
							width: `10%`,
							formatter: info => {
								switch (info.row.migrationStatus) {
									case 'I':
										return this.$t('label.migration.init');
									case 'R':
										return this.$t('label.migration.ready');
									case 'D':
										return this.$t('label.migration.done');
									case 'C':
										return this.$t('label.migration.cancel');
								}
							}
						},
						{
							header: this.$t('label.migration.time'),
							name: 'migrationDateTime',
							width: `10%`,
							formatter: obj => changeDateFormat(obj.value)
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['pk.publishId', 'pk.publishDateTime'],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('label.basicInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'resourceType',
									label: this.$t('label.publishLog.resource.type'),
									formatter: info => {
										switch (info) {
											case 'R':
												return this.$t('label.modelRecord');
											case 'S':
												return this.$t('label.service');
											case 'I':
												return this.$t('label.interface');
										}
									}
								},
								{
									type: 'text',
									vModel: 'resourceId',
									label: this.$t('label.publishLog.resource.id'),
									clickEvt: comp => {
										this.goTargetPage(comp, `basicInfo`);
									}
								},
								{
									type: 'text',
									vModel: 'resourceVersion',
									label: this.$t('label.publishLog.resource.version')
								}
							],
							[
								{
									type: 'select',
									vModel: 'publishStatus',
									label: this.$t('label.publishStatus'),
									optionInfo: {
										optionFor: `option in [{value: 'A', text: '${this.$t('label.publishLog.status.active')}'},
                                              			   {value: 'D', text: '${this.$t('label.publishLog.status.done')}'},
                                               			   {value: 'F', text: '${this.$t('label.publishLog.status.fail')}'}]`,
										optionValue: 'option.value',
										optionText: 'option.text'
									}
								},
								{
									type: 'text',
									vModel: 'pk.publishDateTime',
									label: this.$t('label.publishLog.date'),
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'publishUserId',
									label: this.$t('label.publishLog.user')
								}
							],
							[
								{
									type: 'select',
									vModel: 'migrationStatus',
									label: this.$t('label.migration.status'),
									optionInfo: {
										optionFor: `option in [{value: 'I', text: '${this.$t('label.migration.init')}'},
											   {value: 'R', text: '${this.$t('label.migration.ready')}'},
											   {value: 'D', text: '${this.$t('label.migration.done')}'},
											   {value: 'C', text: '${this.$t('label.migration.cancel')}'}]`,
										optionValue: 'option.value',
										optionText: 'option.text'
									}
								},
								{
									type: 'text',
									vModel: 'migrationDateTime',
									label: this.$t('label.migration.time'),
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'pk.publishId',
									label: this.$t('label.id')
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'publishMessage',
									label: this.$t('label.publishMessage'),
									height: `150px`
								}
							]
						]
					]
				},
				{
					type: `bundle`,
					label: `XML`,
					url: `/publishLog/xml`
				},
				{
					type: 'property',
					label: this.$t('label.publishTargetInfo'),
					dataKey: 'publishTargets',
					content: [
						{
							type: 'text',
							vModel: 'pk.targetId',
							label: this.$t('label.publishLog.target.id'),
							clickEvt: comp => {
								this.goTargetPage(comp, `property`);
							}
						},
						{
							type: 'text',
							vModel: 'pk.targetTypeName',
							label: this.$t('label.type')
						},
						{
							type: 'text',
							vModel: 'updateTimestamp',
							label: this.$t('label.publishTimestamp')
						},
						{
							type: 'text',
							vModel: 'publishCode',
							label: this.$t('label.publishCode'),
							clickEvt: this.openDetailPublishResult
						},
						{
							type: 'text',
							vModel: 'publishResult',
							label: this.$t('label.publishResult'),
							clickEvt: this.openDetailPublishResult
						},
						{
							type: 'text',
							vModel: 'publishDoneName',
							label: this.$t('label.publishLog.target.done')
						}
					],
					setPropertyCallBack: propertyList => {
						propertyList.forEach(property => {
							property.pk.targetTypeName = `I` === property.pk.targetType ? this.$t(`label.server`) : this.$t(`label.system`);
							property.publishDoneName =
								`Y` === property.publishDone
									? this.$t(`label.yes`)
									: `N` === property.publishDone
									? this.$t(`label.no`)
									: `S` === property.publishDone
									? `Skip`
									: `Error`;
							if (property.updateTimestamp) property.updateTimestamp = changeDateFormat(property.updateTimestamp);
						});

						return propertyList;
					}
				}
			]
		};
	},
	methods: {
		goTargetPage(comp, type) {
			if (`basicInfo` === type) {
				const {
					detailData: { resourceType, resourceId }
				} = this.$refs.detail.$refs.component[0];

				if (!resourceType || !resourceId) return;

				if (`R` !== resourceType && `S` !== resourceType && `I` !== resourceType) return;

				const menuId = `R` === resourceType ? `101000` : `S` === resourceType ? `102000` : `103000`;

				openNewTab(menuId, () => {
					localStorage.setItem(
						`searchObj`,
						JSON.stringify({
							id: resourceId,
							_pageSize: 10
						})
					);

					localStorage.setItem(
						`R` === resourceType ? `selectedModelRecordInfo` : `S` === resourceType ? `selectedServiceInfo` : `selectedInterfaceInfo`,
						JSON.stringify({
							[`R` === resourceType ? `recordId` : `S` === resourceType ? `serviceId` : `interfaceId`]: resourceId
						})
					);
				});
			} else {
				const rowIdx = comp.$vnode.data.ref.split(`_`)[1];

				const { propertyList } = this.$refs.detail.$refs.component[2];

				if (!propertyList || 0 === propertyList.length) return;

				const targetId = propertyList[rowIdx][`pk.targetId`];
				const targetType = propertyList[rowIdx][`pk.targetType`];

				if (`I` !== targetType && `P` !== targetType) return;

				const menuId = `I` === targetType ? `202000` : `203000`;

				openNewTab(menuId, () => {
					localStorage.setItem(
						`searchObj`,
						JSON.stringify({
							id: targetId,
							_pageSize: 10
						})
					);

					localStorage.setItem(
						`I` === targetType ? `selectedLinkedServerInfo` : `selectedSystemInfo`,
						JSON.stringify({
							id: targetId
						})
					);
				});
			}
		},
		openDetailPublishResult(comp) {
			const rowIdx = comp.$vnode.data.ref.split(`_`)[1];

			const { propertyList } = this.$refs.detail.$refs.component[2];

			if (!propertyList || 0 === propertyList.length) return;

			const { publishDone, publishCode, publishResult } = propertyList[rowIdx];

			if (`E` !== publishDone) return;

			this.$openModal({
				title: `${this.$t('label.publishCode')}, ${this.$t('label.publishResult')}`,
				fullscreen: true,
				contentList: [
					{
						template: `
								      <div class="row frm-row" style="height: 100%">
									      <div class="col-lg-12" style="height: 100%">
										      <div class="form-group">
											      <label class="control-label">
												      <span>{{ $t('label.publishCode') }}</span>
												  </label>
												  <span :title="publishCode">
												      <input type="text" class="form-control view-disabled" readonly="readonly" v-model="publishCode">
												  </span>
											  </div>
											  <div class="form-group" style="height: calc(100% - 72px)">
											      <label class="control-label">
												      <span>{{ $t('label.publishResult') }}</span>
												  </label>
												  <textarea readonly="readonly" class="form-control view-disabled" style="height: calc(100% - 24px)" v-text="publishResult"></textarea>
											  </div>											  
										  </div>
									  </div>
								  `,
						data() {
							return {
								publishCode: null,
								publishResult: null
							};
						},
						created() {
							this.publishCode = publishCode;
							this.publishResult = publishResult;
						}
					}
				]
			});
		}
	}
};
</script>
