<template>
	<div>
		<ul class="collapse-list collapse-list-result m-0">
			<li class="collapse-item">
				<button type="button" class="collapse-link collapsed" data-toggle="collapse" data-target="#mappingRule" @click="collapseResize">
					<span style="margin-right: 5px">{{ $t('label.mapping.rule') }}</span>
				</button>
				<div class="collapse" id="mappingRule">
					<div class="card card-body">
						<div class="form-inline m-full">
							<button type="button" class="btn-icon" @click="addModel('T')">
								<i class="icon-plus-circle"></i>
							</button>
							<button type="button" class="btn-icon" @click="removeRule"><i class="icon-minus-circle"></i></button>
						</div>
						<div class="table-responsive">
							<TuiGrid ref="mappingRuleGrid" v-bind="mappingRuleOptions" @click="onRuleClick" />
						</div>
					</div>
				</div>
			</li>
			<li class="collapse-item">
				<button type="button" class="collapse-link" data-toggle="collapse" data-target="#mappingInfo">
					<span style="margin-right: 5px">{{ $t('label.model.info') }}</span>
				</button>
				<div class="collapse show" id="mappingInfo">
					<div class="card card-body">
						<div class="row frm-row" style="margin-bottom: 0px">
							<div class="col-lg-12">
								<button type="button" class="btn-icon" @click="addModel('S')"><i class="icon-plus-circle"></i></button>
								<button type="button" class="btn-icon"><i class="icon-minus-circle"></i></button>
								<button type="button" class="btn-icon"><i class="icon-reset"></i></button>
								<button type="button" class="btn-icon"><i class="icon-deal"></i></button>
								<button type="button" class="btn-icon"><i class="icon-delete"></i></button>
							</div>
						</div>
						<div class="row frm-row">
							<div ref="sourceGridParent" class="table-responsive">
								<TuiGrid ref="sourceGrid" v-bind="sourceModelOptions" />
							</div>

							<div ref="mappingArrow">
								<!-- arrow -->
							</div>
							<div ref="targetGridParent" class="table-responsive">
								<TuiGrid ref="targetGrid" v-bind="targetModelOptions" />
							</div>
						</div>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
import { WindowResizeEventBus, SideMenuToggleEventBus, DetailToggleEventBus, SearchToggleEventBus, getNumFromStr } from '@/utils/common.js';
import { Grid } from '@toast-ui/vue-grid';
import merge from 'lodash/merge';
import RecordModal from '@/views/detail/mapping/RecordModal.vue';
import { makeGridOptions } from '@/utils/common';

export default {
	props: {
		mode: String,
		detailData: Object,
		mappingId: String
	},
	name: 'MappingInfo',
	components: {
		TuiGrid: Grid
	},
	computed: {
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	watch: {
		mappingId() {
			const mappingRuleInfo = this.$refs.mappingRuleGrid.invoke('getData');

			mappingRuleInfo.forEach(ruleInfo => {
				ruleInfo.pk.mappingId = this.mappingId;
			});

			this.$refs.mappingRuleGrid.invoke('resetData', mappingRuleInfo);
		}
	},
	created() {
		this.mappingRuleOptions = makeGridOptions({ ...{ bodyHeight: 200 }, ...this.mappingRuleOptions });
		this.sourceModelOptions = makeGridOptions({ ...{ bodyHeight: 200 }, ...this.sourceModelOptions });
		this.targetModelOptions = makeGridOptions({ ...{ bodyHeight: 200 }, ...this.targetModelOptions });
	},
	methods: {
		getData() {
			const mappingRuleInfo = this.$refs.mappingRuleGrid.invoke('getData');
			const filterList = ['sortKey', 'rowKey', 'rowSpanMap', 'uniqueKey', '_attributes', '_disabledPriority', '_relationListItemMap'];
			const mappingDetailData = [];

			mappingRuleInfo.forEach(ruleInfo => {
				let tempInfo = {};

				for (const key in ruleInfo) {
					if (-1 < filterList.indexOf(key)) continue;
					tempInfo[key] = ruleInfo[key];
				}

				mappingDetailData.push(merge(tempInfo));
			});

			return { mappingDetails: mappingDetailData };
		},
		async onRuleClick(evt) {
			if ('undefined' === typeof evt.rowKey) return;

			const grid = evt.instance;

			grid.getData().forEach(data => {
				if (data.rowKey === evt.rowKey) return;

				grid.removeRowClassName(data.rowKey, 'row-selected');
			});

			grid.addRowClassName(evt.rowKey, 'row-selected');
		},
		removeRule() {
			this.$refs.mappingRuleGrid.invoke('removeRow', this.$refs.mappingRuleGrid.invoke('getFocusedCell').rowKey);
		},
		addModel(type) {
			//type: S(source), T(target)

			if (!this.mappingId) {
				this.$alert({ type: 'warn', message: this.$t('msg.mapping.validation') });
				return;
			}

			const _this = this;

			const modalInstane = this.$openModal({
				title: this.$t('label.modelRecord'),
				modalWidth: '800px',
				contentList: [
					{
						extends: RecordModal,
						methods: {
							rowClickedCallback(treeRowData) {
								if ('T' === type) {
									_this.$refs.mappingRuleGrid.invoke(
										'appendRow',
										{
											pk: {
												mappingId: _this.mappingId,
												mappingOrder: _this.$refs.mappingRuleGrid.invoke('getRowCount')
											}
										},
										{ focus: true }
									);

									const rowKey = _this.$refs.mappingRuleGrid.invoke('getFocusedCell').rowKey;

									_this.$refs.mappingRuleGrid.invoke('addRowClassName', rowKey, 'row-selected');

									_this.$refs.mappingRuleGrid.invoke('getData').forEach(data => {
										if (data.rowKey === rowKey) return;
										_this.$refs.mappingRuleGrid.invoke('removeRowClassName', data.rowKey, 'row-selected');
									});

									_this.$refs.sourceGrid.invoke('clear');
									_this.$refs.targetGrid.invoke('resetData', [treeRowData]);
								} else {
									_this.$refs.sourceGrid.invoke('appendRow', treeRowData);
								}

								modalInstane.hideModal();
							}
						}
					}
				]
			});
		},
		getReadonly(colInfo) {
			const readonly = 'function' === typeof colInfo.readonly ? colInfo.readonly(this.detailData ? this.detailData : {}) : colInfo.readonly;
			return readonly || (colInfo.isPK && 'modify' === this.mode);
		},
		collapseResize() {
			this.resize();
		},
		resize() {
			if (!this.$refs.mappingRuleGrid || !this.$refs.sourceGrid || !this.$refs.targetGrid) return;

			const panel = document.getElementById('panel');

			const panelBody = panel.getElementsByClassName('panel-body')[0];
			const panelBodyComputedStyle = getComputedStyle(panelBody);

			const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
			const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));

			const panelWidth = panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight;

			const sourceGridParentWidth = Math.floor(panelWidth * 0.35);
			const targetGridParentWidth = Math.floor(panelWidth * 0.35);
			const mappingArrowWidth = panelWidth - (sourceGridParentWidth + targetGridParentWidth);

			this.$refs.sourceGridParent.style.width = `${sourceGridParentWidth}px`;
			this.$refs.mappingArrow.style.width = `${mappingArrowWidth}px`;
			this.$refs.targetGridParent.style.width = `${targetGridParentWidth}px`;

			this.$refs.mappingRuleGrid.invoke('setWidth', panelWidth);
			this.$refs.sourceGrid.invoke('setWidth', sourceGridParentWidth);
			this.$refs.targetGrid.invoke('setWidth', targetGridParentWidth);
		}
	},
	async mounted() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);
	},
	data() {
		return {
			mappingRuleOptions: {
				draggable: true,
				columns: [
					{
						name: 'pk',
						header: 'Mapping ID&Order',
						formatter: value => {
							return `${value.row.pk.mappingId}$${value.row.pk.mappingOrder}`;
						}
					},
					{
						name: 'targetForm',
						header: 'Target Form',
						editor: 'text'
					},
					{
						name: 'mappingDesc',
						header: this.$t('label.desc'),
						editor: 'text'
					}
				]
			},

			sourceModelOptions: {
				columns: [
					{
						header: 'id',
						hidden: true
					},
					{
						name: 'fieldId',
						header: 'FieldId'
					},
					{
						name: 'fieldType',
						header: 'FieldType'
					},
					{
						name: 'fieldName',
						header: 'FieldName'
					}
				]
			},

			targetModelOptions: {
				columns: [
					{
						header: 'id',
						hidden: true
					},
					{
						name: 'fieldId',
						header: 'FieldId'
					},
					{
						name: 'fieldType',
						header: 'FieldType'
					},
					{
						name: 'fieldName',
						header: 'FieldName'
					}
				]
			}
		};
	}
};
</script>
