<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-wrap">
				<div class="form-table-head">
					<button type="button" class="btn-icon" @click="openModal" v-if="!readonly" :title="$t('label.insert')">
						<i class="icon-plus-circle"></i>
					</button>
					<label class="col">{{ this.$t('label.linked.server.id') }}</label>
					<label class="col">{{ this.$t('label.interface.class') }}</label>
				</div>
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('label.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div class="input-group" style="width: 100%">
							<inputText
								:ref="`component_${bodyIndex}`"
								vModel="pk.serverId"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['pk.serverId']"
								:readonly="true"
							/>
							<button
								type="button"
								class="btn"
								style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
								@click="goLinkedServerPage($refs[`component_${bodyIndex}`][0])"
								v-if="readonly"
								:title="$t('label.open')"
							>
								<i class="icon-link" style="margin-right: 0px"></i>
							</button>
						</div>
					</div>

					<div class="col">
						<div style="width: 100%">
							<inputText
								:ref="`component_${bodyIndex}`"
								vModel="pk.interfaceClass"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['pk.interfaceClass']"
								:readonly="true"
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import isEmpty from 'lodash/isEmpty';
import DetailProperty from '@/components/detail/DetailProperty.vue';
import { HttpReq, getUUID, escapeHtml, openNewTab } from '@/utils/common';

export default {
	name: 'SystemParent',
	extends: DetailProperty,
	data() {
		return {
			interfaceClassList: [],
			interfaceTypeList: []
		};
	},
	async created() {
		let result = await new HttpReq(`/api/entity/interfaceClass/search`).read({
			object: {},
			limit: null,
			next: null,
			reverseOrder: false
		});

		if (`ok` !== result.data.result) return;

		this.interfaceClassList = result.data.object;

		result = await new HttpReq(`/api/page/properties`).read({
			pk: {
				propertyId: `Interface.Type`
			},
			orderByKey: true
		});

		if (`ok` !== result.data.result) return;

		this.interfaceTypeList = result.data.object;
	},
	methods: {
		getSystemData(obj) {
			this.propertyList.forEach(property => {
				obj.push({
					pk: {
						serverId: property['pk.serverId'],
						interfaceClass: property['interfaceClassOrg']
					}
				});
			});
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);
		},
		getInterfaceClassValue(interfaceClass) {
			const interfaceClassObj = this.interfaceClassList.find(interfaceClassObj => interfaceClassObj.id === interfaceClass);
			return interfaceClassObj.id;
		},
		async setSystemList(obj) {
			if (isEmpty(obj)) {
				this.propertyList = [];
				return;
			}

			this.propertyList = obj.map(info => ({
				idx: getUUID(),
				'pk.serverId': info.pk.serverId,
				'pk.interfaceClass': this.getInterfaceClassValue(info.pk.interfaceClass),
				interfaceClassOrg: info.pk.interfaceClass
			}));
		},
		openModal() {
			const _this = this;

			const modalInstane = this.$openModal({
				title: this.$t('label.linked.server'),
				modalWidth: '800px',
				contentList: [
					{
						template: `<div>
							  	       <Search :list="searchList" @search="search" ref="search" />
									   <Main
											:buttonList="buttonList"
											:gridInfo="gridInfo"
											:gridSearchObj="gridSearchObj"
											:gridType="'modalBasic'"
											:viewer="true"
											@rowClicked="rowClicked"
											@initialize="initialize"
										/>
						   			</div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								gridSearchObj: {},
								buttonList: [{ id: 'initialize', isUse: true }],
								searchList: [
									{
										type: 'text',
										vModel: 'pk.id',
										label: this.$t('label.id'),
										placeholder: this.$t('msg.enterId'),
										regExpType: 'searchId'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('label.listCnt'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],
								gridInfo: {
									url: `/api/entity/serverInterface/search`,
									totalCntUrl: `/api/entity/serverInterface/count`,
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('label.id'),
												name: 'pk.id',
												width: '25%'
											},
											{
												header: this.$t('label.interface.class'),
												name: 'pk.interfaceClass',
												width: '25%',
												formatter: ({ value }) => {
													return escapeHtml(_this.getInterfaceClassValue(value));
												}
											},
											{
												header: `Source ${this.$t('label.interface.type')}`,
												name: 'sourceType',
												width: '25%',
												formatter: function ({ row }) {
													const { sourceInterfaceType } = this.interfaceClassList.find(
														info => info.id === row[`pk.interfaceClass`]
													);

													return escapeHtml(
														this.interfaceTypeList.find(info => info.pk.propertyKey === sourceInterfaceType).propertyValue
													);
												}.bind(_this)
											},
											{
												header: `Target ${this.$t('label.interface.type')}`,
												name: 'targetType',
												width: '25%',
												formatter: function ({ row }) {
													const { targetInterfaceType } = this.interfaceClassList.find(
														info => info.id === row[`pk.interfaceClass`]
													);

													return escapeHtml(
														this.interfaceTypeList.find(info => info.pk.propertyKey === targetInterfaceType).propertyValue
													);
												}.bind(_this)
											}
										]
									}
								}
							};
						},
						methods: {
							initialize() {
								this.$refs.search.initialize();
							},
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked: info => {
								for (const property of this.propertyList) {
									if (info[`pk.id`] == property[`pk.serverId`] && info[`pk.interfaceClass`] == property[`interfaceClassOrg`]) {
										this.$alert({ type: 'warn', message: this.$t('msg.overlap.data') });
										return;
									}
								}

								this.propertyList.push({
									idx: getUUID(),
									'pk.serverId': info['pk.id'],
									'pk.interfaceClass': this.getInterfaceClassValue(info['pk.interfaceClass']),
									interfaceClassOrg: info['pk.interfaceClass']
								});

								modalInstane.hideModal();
							}
						}
					}
				]
			});
		},
		goLinkedServerPage(comp) {
			const rowIdx = comp.$vnode.data.ref.split(`_`)[1];

			const serverId = this.propertyList[rowIdx][`pk.serverId`];

			openNewTab(`202000`, () => {
				localStorage.setItem(
					`searchObj`,
					JSON.stringify({
						id: serverId,
						_pageSize: 10
					})
				);

				localStorage.setItem(
					`selectedLinkedServerInfo`,
					JSON.stringify({
						id: serverId
					})
				);
			});
		}
	}
};
</script>
