<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.id') }} </span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`id`])">
							{{ `(${curLengthCnt['id']}/${maxLengthCnt['id']})` }}
						</span>
						<b class="icon-star"></b>
					</label>
					<inputText
						ref="id"
						vModel="id"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
						:readonly="readonly || getReadonly(regExpInfo[`id`])"
						regExpType="id"
						@setLengthCnt="setLengthCnt"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.name') }} </span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`name`])">
							{{ `(${curLengthCnt['name']}/${maxLengthCnt['name']})` }}
						</span>
						<b class="icon-star"></b>
					</label>
					<inputText
						ref="name"
						vModel="name"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
						:readonly="readonly || getReadonly(regExpInfo[`name`])"
						regExpType="name"
						@setLengthCnt="setLengthCnt"
					/>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ `Source ${this.$t('label.interface.type')}` }}</span>
						<b class="icon-star"></b>
					</label>
					<select class="form-control view-disabled" v-model="selectedSourceInterfaceType" :readonly="readonly">
						<option
							v-for="(interfaceType, interfaceTypeIdx) in interfaceTypeList"
							:key="interfaceTypeIdx"
							:value="interfaceType.pk.propertyKey"
							v-text="interfaceType.propertyValue"
						></option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ `Target ${this.$t('label.interface.type')}` }}</span>
						<b class="icon-star"></b>
					</label>
					<select class="form-control view-disabled" v-model="selectedTargetInterfaceType" :readonly="readonly">
						<option
							v-for="(interfaceType, interfaceTypeIdx) in interfaceTypeList"
							:key="interfaceTypeIdx"
							:value="interfaceType.pk.propertyKey"
							v-text="interfaceType.propertyValue"
						></option>
					</select>
				</div>
			</div>
		</div>

		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('label.desc') }}</span>
						<span class="letterLength" v-if="!getReadonly(regExpInfo[`description`])">
							{{ `(${curLengthCnt['description']}/${maxLengthCnt['description']})` }}
						</span>
					</label>
					<textArea
						ref="description"
						vModel="description"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly(regExpInfo[`description`])"
						:height="'130px'"
						regExpType="desc"
						@setLengthCnt="setLengthCnt"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { parseHierarchyObj, HttpReq, getRegExpInfo } from '@/utils/common.js';

export default {
	name: `BasicInfo`,
	extends: DetailBasic,
	data() {
		return {
			regExpInfo: {
				id: {
					regExpType: 'id',
					isPK: true,
					readonly: false
				},
				name: {
					regExpType: 'name',
					isPK: false,
					readonly: false
				},
				description: {
					regExpType: 'desc',
					isPK: false,
					readonly: false
				}
			},

			interfaceTypeList: [],

			selectedSourceInterfaceType: null,
			selectedTargetInterfaceType: null
		};
	},
	async created() {
		const res = await new HttpReq(`/api/page/properties`).read({ pk: { propertyId: `Interface.Type` }, orderByKey: false });

		if (`ok` !== res.data.result) return;

		this.interfaceTypeList = res.data.object;
	},
	methods: {
		initData() {
			[`id`, `name`, `description`, `sourceInterfaceType`, `targetInterfaceType`].forEach(key => {
				const val = 'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;

				if (![`sourceInterfaceType`, `targetInterfaceType`].find(name => name === key)) this.$refs[key].setData(val);

				if ([`id`, `name`, `description`].find(name => name === key)) {
					this.setLengthCnt({ [key]: null === val ? 0 : String(val).length });
					this.maxLengthCnt[key] = getRegExpInfo(this.regExpInfo[key].regExpType ? this.regExpInfo[key].regExpType : 'default').maxLength;
				} else if (`sourceInterfaceType` === key) {
					this.selectedSourceInterfaceType = val;
				} else if (`targetInterfaceType` === key) {
					this.selectedTargetInterfaceType = val;
				}
			});
		},
		getData() {
			return parseHierarchyObj({
				id: this.$refs.id.getData().id,
				name: this.$refs.name.getData().name,
				description: this.$refs.description.getData().description,
				sourceInterfaceType: this.selectedSourceInterfaceType,
				targetInterfaceType: this.selectedTargetInterfaceType
			});
		}
	}
};
</script>
