<template>
	<div class="row frm-row" style="width: 100%">
		<div class="col-lg-4" v-for="(timeObj, index) in timeUnit" :key="index">
			<input :class="classObject" :value="`${obj[timeObj]} ${timeMsg(timeObj)}`" @click="toggleDropdownItem(timeObj)" :disabled="readonly" />
			<ul :class="dropdownMenuClass[timeObj]">
				<li style="height: 200px; overflow-y: auto">
					<a
						class="dropdown-item"
						href="javascript:void(0)"
						v-for="option in getTimeList(timeObj)"
						:key="option"
						:value="option"
						v-text="option"
						@click="selectedItem(timeObj, option)"
					>
					</a>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
export default {
	name: 'TimePicker',
	props: {
		vModel: String,
		classObject: {
			type: Object,
			default: () => {}
		},
		readonly: {
			type: Boolean,
			default: false
		},
		val: {
			type: String,
			default: null
		},
		customOptions: Object
	},
	data() {
		return {
			obj: {},
			timeUnit: ['hour', 'minute', 'second'],

			dropdownMenuClass: {
				hour: {
					'dropdown-menu': true,
					show: false
				},
				minute: {
					'dropdown-menu': true,
					show: false
				},
				second: {
					'dropdown-menu': true,
					show: false
				}
			}
		};
	},
	created() {
		for (const key in this.$data.obj) {
			this.$delete(this.$data.obj, key);
		}

		for (const unit of this.timeUnit) {
			this.$set(this.obj, unit, null);
		}

		this.setData();
	},
	watch: {
		val() {
			this.setData(this.val);
		},
		readonly() {
			if (!this.readonly) return;

			for (const menu in this.dropdownMenuClass) {
				this.dropdownMenuClass[menu].show = false;
			}
		}
	},
	methods: {
		timeMsg(type) {
			return 'hour' === type ? this.$t('label.hours') : 'minute' === type ? this.$t('label.minutes') : this.$t('label.seconds');
		},
		getTimeList(type) {
			const timeList = [];
			const timeUnit = 'hour' === type ? 24 : 60;

			for (let i = 0; i < timeUnit; i++) {
				timeList.push(i < 10 ? `0${i}` : i);
			}

			return timeList;
		},
		getDisplayTime(type, value) {
			let time = '00';

			if (value) {
				value = value.replace(/[^.0-9]/g, '');

				switch (type) {
					case 'hour':
						time = value.slice(0, 2);
						break;
					case 'minute':
						time = value.slice(2, 4);
						break;
					case 'second':
						time = value.slice(4, 6);
						break;
				}
			}

			return time;
		},
		toggleDropdownItem(type) {
			for (const menu in this.dropdownMenuClass) {
				if (menu === type) this.dropdownMenuClass[menu].show = !this.dropdownMenuClass[menu].show;
				else this.dropdownMenuClass[menu].show = false;
			}
		},
		selectedItem(type, value) {
			this.dropdownMenuClass[type].show = false;
			this.obj[type] = value;
		},
		setData(val) {
			for (const type of this.timeUnit) {
				this.obj[type] = this.getDisplayTime(type, val);
			}
		},
		getData() {
			return {
				[this.vModel]: Object.values(this.obj).join(``)
			};
		}
	}
};
</script>
