<script>
import Modified from '@/views/detail/metaHistory/Modified.vue';

export default {
	name: 'Modified',
	extends: Modified,
	mounted() {
		this.$nextTick(() => {
			this.resize();
		});
	},
	methods: {
		resize() {
			const modalBody = this.$el.closest(`.modal-body`);

			const adjustHeight = modalBody.clientHeight - 32 - 24 - 24;

			this.beforeContentsStyle.height = `${adjustHeight}px`;
			this.afterContentsStyle.height = `${adjustHeight}px`;
		}
	}
};
</script>
