<script>
import DetailInfo from '@/views/detail/modelRecord/DetailInfo.vue';
import { getNumFromStr } from '@/utils/common.js';

export default {
	name: 'DetailInfo',
	extends: DetailInfo,
	methods: {
		resize() {
			const modal = document.getElementById('modelRecordModal');

			const panelBody = modal.getElementsByClassName('panel-body')[0];
			const panelBodyComputedStyle = getComputedStyle(panelBody);

			//width
			const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
			const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
			const adjustWidth = modal.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight;

			//height
			const modalHeaderArea = modal.getElementsByClassName('nav-tabs')[0].clientHeight;
			const buttonArea =
				'undefined' === typeof modal.getElementsByClassName('ml-auto') ? modal.getElementsByClassName('ml-auto')[0].clientHeight : 24;
			const adjustHeight = modal.parentNode.clientHeight - modalHeaderArea - buttonArea - 48;

			this.$refs.tuiGrid.invoke('setWidth', adjustWidth);
			this.$refs.tuiGrid.invoke('setHeight', adjustHeight);
		}
	}
};
</script>
