<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('label.insert')">
					<i class="icon-plus-circle"></i>
				</button>
				<label class="col" v-for="(headerObj, headerIndex) in detailInfo.content" :key="headerIndex">{{ headerObj.label }}</label>
			</div>
			<div class="form-table-wrap">
				<div class="form-table-body" v-for="(detailObj, bodyIndex) in propertyList" :key="`${detailObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('label.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col" v-for="(detailSubObj, index) in detailInfo.content" :key="`${detailObj.idx}_${index}`">
						<div
							:class="`detail-content-${
								regExpTypeList.includes(detailSubObj.type) && !getReadonly(detailSubObj) ? 'regExp' : 'common'
							}`"
						>
							<inputText
								:ref="`component_${bodyIndex}_${index}`"
								:vModel="detailSubObj.vModel"
								:classObject="{
									'form-control': getReadonly(detailSubObj),
									[`regExp-${detailSubObj.type}`]: !getReadonly(detailSubObj),
									'view-disabled': true,
									dataKey: detailSubObj.isRequired
								}"
								:val="detailObj[detailSubObj.vModel]"
								:regExpType="detailSubObj.regExpType"
								:readonly="readonly || detailSubObj.readonly"
								@setLengthCnt="info => setLengthCnt({ [`${detailSubObj.vModel}_${bodyIndex}`]: info[detailSubObj.vModel] })"
								v-if="'text' === detailSubObj.type"
							/>
							<dataList
								:ref="`component_${bodyIndex}_${index}`"
								:vModel="detailSubObj.vModel"
								:classObject="{
									'form-control': getReadonly(detailSubObj),
									[`regExp-${detailSubObj.type}`]: !getReadonly(detailSubObj),
									'view-disabled': true,
									dataKey: detailSubObj.isRequired
								}"
								:val="detailObj[detailSubObj.vModel]"
								:readonly="readonly || detailSubObj.readonly"
								:optionInfo="detailSubObj.optionInfo"
								:regExpType="detailSubObj.regExpType"
								@setLengthCnt="info => setLengthCnt({ [`${detailSubObj.vModel}_${bodyIndex}`]: info[detailSubObj.vModel] })"
								v-else-if="'dataList' === detailSubObj.type"
							/>
							<selectBox
								:ref="`component_${bodyIndex}_${index}`"
								:vModel="detailSubObj.vModel"
								:classObject="{ 'form-control': true, 'view-disabled': true }"
								:val="detailObj[detailSubObj.vModel]"
								:readonly="readonly || detailSubObj.readonly"
								:optionInfo="detailSubObj.optionInfo"
								v-else-if="'select' === detailSubObj.type"
							/>
							<dateRange
								:ref="`component_${bodyIndex}_${index}`"
								:vModel="detailSubObj.vModel"
								:val="detailObj[detailSubObj.vModel]"
								:readonly="readonly || detailSubObj.readonly"
								v-else-if="'singleDaterange' === detailSubObj.type"
							/>
							<passWord
								:ref="`component_${bodyIndex}_${index}`"
								:vModel="detailSubObj.vModel"
								:classObject="{ 'form-control': true, 'view-disabled': true }"
								:readonly="readonly || detailSubObj.readonly"
								v-else-if="'password' === detailSubObj.type"
							/>
							<div class="input-group" v-else-if="'modal' === detailSubObj.type">
								<inputText
									:ref="`component_${bodyIndex}_${index}`"
									:vModel="detailSubObj.vModel"
									:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: detailSubObj.isRequired }"
									:val="detailObj[detailSubObj.vModel]"
									:readonly="true"
									style="width: calc(100% - 110px)"
								/>
								<button
									type="button"
									class="btn"
									style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
									@click="detailSubObj.clickEvt($refs[`component_${bodyIndex}_${index}`][0])"
									v-if="readonly && detailSubObj.clickEvt"
									:title="$t('label.open')"
								>
									<i class="icon-link" style="margin-right: 0px"></i>
								</button>
								<button
									type="button"
									class="btn"
									style="margin-left: 3px; min-width: 0px"
									@click="openModal(detailSubObj.modalInfo, $refs[`component_${bodyIndex}_${index}`])"
									v-if="!(readonly || detailSubObj.readonly)"
								>
									<i class="icon-srch"></i>{{ $t('label.search') }}
								</button>
								<button
									type="button"
									class="btn"
									style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
									@click="$refs[`component_${bodyIndex}_${index}`][0].setData(null)"
									v-if="!(readonly || detailSubObj.readonly)"
									:title="$t('label.initialize')"
								>
									<i class="icon-reset" style="margin-right: 0px"></i>
								</button>
							</div>
							<span class="letterLength" v-if="regExpTypeList.includes(detailSubObj.type) && !getReadonly(detailSubObj)">
								{{
									`(${curLengthCnt[`${detailSubObj.vModel}_${bodyIndex}`]}
									 /${maxLengthCnt[detailSubObj.vModel]})`
								}}
							</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';
import isNil from 'lodash/isNil';
import { parseFlattenObj, HttpReq, getUUID, getRegExpInfo, parseHierarchyObj } from '@/utils/common.js';
import inputText from '@/components/element/inputText';
import selectBox from '@/components/element/selectBox';
import dataList from '@/components/element/dataList';
import dateRange from '@/components/element/dateRange';
import passWord from '@/components/element/passWord';

export default {
	name: 'DetailProperty',
	data() {
		return {
			propertyList: [],
			httpReq: null,

			curLengthCnt: {},
			maxLengthCnt: {},

			regexpElmList: []
		};
	},
	props: {
		mode: String,
		detailInfo: Object,
		detailData: Object
	},
	components: {
		inputText,
		dataList,
		selectBox,
		dateRange,
		passWord
	},
	computed: {
		...mapState(['regExpTypeList']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	created() {
		if (this.detailInfo.mergeUrl) {
			this.httpReq = new HttpReq(this.detailInfo.mergeUrl);
		}

		if (!this.detailInfo.content) return;

		this.detailInfo.content.forEach(element => {
			this.maxLengthCnt[element.vModel] = getRegExpInfo(element.regExpType ? element.regExpType : 'default').maxLength;
		});
	},
	watch: {
		detailData: {
			deep: true,
			handler() {
				this.setPropertyList();
			}
		}
	},
	methods: {
		getData() {
			const dataKey = this.detailInfo.dataKey;

			const rtnArr = [];

			this.propertyList.forEach((property, bodyIndex) => {
				let rtnObj = {};

				this.detailInfo.content.forEach((info, index) => {
					rtnObj = merge(rtnObj, this.$refs[`component_${bodyIndex}_${index}`][0].getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			});

			return dataKey ? { [dataKey]: rtnArr } : rtnArr;
		},
		addProperty() {
			const addRowType = this.detailInfo.addRowInfo ? this.detailInfo.addRowInfo.type : 'common';

			if ('modal' == addRowType) {
				this.openModal(this.detailInfo.addRowInfo.modalInfo);
			} else {
				const rowInfo = { idx: getUUID() };

				this.detailInfo.content.forEach(element => {
					const key = `${element.vModel}_${this.propertyList.length}`;
					this.setLengthCnt({ [key]: 0 });

					rowInfo[element.vModel] = element.val ? element.val : null;
				});

				this.propertyList.push(rowInfo);
			}
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);

			this.curLengthCnt = {};

			this.$nextTick(() => {
				this.propertyList.forEach((property, bodyIndex) => {
					this.detailInfo.content.forEach((info, index) => {
						const key = `${info.vModel}_${bodyIndex}`;
						const dataObj = this.$refs[`component_${bodyIndex}_${index}`][0].getData();

						this.setLengthCnt({ [key]: isNil(dataObj[info.vModel]) ? 0 : String(dataObj[info.vModel]).length });
					});
				});
			});
		},
		async setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			if ('undefined' !== typeof this.detailData[this.detailInfo.dataKey]) {
				this.propertyList = [...this.detailData[this.detailInfo.dataKey]];

				let result = null;

				if (this.httpReq) {
					result = await this.httpReq.read(merge(this.detailInfo.params, this.detailData));

					if ('ok' !== result.data.result) {
						this.$alert({ type: 'warn', message: `${result.data.error[0].message}` });
						return;
					}
				}

				if (this.detailInfo.setPropertyCallBack) {
					this.propertyList = this.detailInfo.setPropertyCallBack(this.propertyList, result ? result.data.object : null);
				}

				this.regexpElmList = this.detailInfo.content.filter(element => this.regExpTypeList.includes(element.type));

				this.propertyList.forEach((propertyInfo, index) => {
					propertyInfo.idx = getUUID();

					this.regexpElmList.forEach(element => {
						const key = `${element.vModel}_${index}`;

						this.setLengthCnt({ [key]: propertyInfo[element.vModel] ? propertyInfo[element.vModel].length : 0 });
					});
				});
			} else {
				this.propertyList = [];
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		setLengthCnt(cntInfo) {
			this.curLengthCnt = { ...this.curLengthCnt, ...cntInfo };
		},
		getReadonly(detailSubObj) {
			return this.readonly || detailSubObj.readonly;
		},
		openModal(modalInfo, refElement) {
			const modalInstane = this.$openModal({
				...modalInfo,
				modalWidth: `800px`,
				contentList: [
					{
						template: `<div>
						       	<Search :list="searchList" @search="search" ref="search"/>
								<Main
							   	   :buttonList="buttonList"
								   :gridInfo="gridInfo"
								   :gridSearchObj="gridSearchObj"
								   :gridType="'modalBasic'"
								   :viewer="true"
								   @rowClicked="rowClicked"
								   @initialize="initialize"
							   	/>
						   </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [],

								//Button
								buttonList: [],

								// Grid
								gridInfo: {},
								gridSearchObj: {},

								rowInfo: {}
							};
						},
						created() {
							const { search, button, grid } = modalInfo;

							//Search
							this.searchList = search.list;

							//Button
							this.buttonList = button.list;

							//Grid
							this.gridInfo = grid;
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked: async info => {
								const rowInfo = await modalInfo.rowClickedCallback(info, this.propertyList);

								if (refElement) {
									refElement[0].setData(rowInfo);
								} else {
									if (modalInfo.addMultiRow) {
										rowInfo.propertyInfo.forEach(propertyInfo => {
											this.propertyList.push({ ...propertyInfo, ...{ idx: getUUID() } });
										});
									} else {
										if (rowInfo.duplicate) {
											this.$alert({ type: 'warn', message: this.$t('msg.overlap.data') });
											return;
										}

										this.propertyList.push({ ...rowInfo.propertyInfo, ...{ idx: getUUID() } });
									}
								}

								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
