<script>
import { mapState } from 'vuex';
import { encryptPassword, parseFlattenObj } from '@/utils/common.js';
import Detail from '@/components/detail/Detail.vue';

export default {
	name: 'Detail',
	extends: Detail,
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		loadData(callBack) {
			const emailRule = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
			const mobilephoneRule = /^\d{3}-\d{3,4}-\d{4}$/;
			const telephoneRule = /^\d{2,3}-\d{3,4}-\d{4}$/;

			Detail.methods.loadData.call(this, () => {
				if (!('detail' === this.panelMode || 'done' === this.panelMode)) {
					const messageType =
						this.tabAllData.email && !emailRule.test(this.tabAllData.email)
							? 'email'
							: this.tabAllData.mobilephone && !mobilephoneRule.test(this.tabAllData.mobilephone)
							? 'mobilephone'
							: this.tabAllData.telephone && !telephoneRule.test(this.tabAllData.telephone)
							? 'telephone'
							: null;

					if (null !== messageType) {
						this.$alert({ type: 'warn', isXSSMode: true, message: this.$t(`msg.user.${messageType}.format`) });
						return;
					}
				}

				this.tabAllData.newPassword = encryptPassword(this.tabAllData.newPassword);

				if (callBack) callBack();
			});
		},
		async setData(result, message, successCallback, type = null, isSearch = true) {
			if ('ok' === result.data.result && ('add' === this.panelMode || 'clone' === this.panelMode)) {
				const againResult = await this.httpReq.read({ userId: this.tabAllData.userId });
				result.data.object = againResult.data.object;
			}

			Detail.methods.setData.call(
				this,
				result,
				message,
				() => {
					if ('add' === this.panelMode || 'clone' === this.panelMode) {
						delete this.cloneInfo.newPassword;
					}

					if (successCallback) successCallback();
				},
				type,
				isSearch
			);
		},
		async getTargetObj() {
			if (null === this.panelMode) {
				return;
			}

			this.detailData = {};

			if ('add' !== this.panelMode) {
				const param = {};

				for (const key in this.cloneInfo) {
					if (-1 === this.pk.indexOf(key)) continue;
					param[key] = this.cloneInfo[key];
				}

				const result = await this.httpReq.read(param);

				if (`ok` !== result.data.result) return;

				this.detailData = parseFlattenObj(result.data.object);

				// if ('clone' === this.panelMode) {
				// 	this.pk.forEach(pkInfo => {
				// 		this.detailData[pkInfo] = null;
				// 	});
				// }
			} else {
				const result = await this.httpReq.read({ userId: this.userInfo.userId });

				if (`ok` !== result.data.result) return;

				this.detailData = {
					totalUserPrivileges: result.data.object.totalUserPrivileges
				};
			}

			this.setButtonList();

			this.openPanel();

			this.setSelectedInfoTitle();
		}
	}
};
</script>
