<template>
	<div class="ct-header">
		<button
			type="button"
			class="btn-filter collapsed d-md-none"
			data-toggle="collapse"
			data-target=".collapse-filter"
			@click.prevent="collapseFilter"
		>
			{{ $t('label.searchFilter') }}
			<i class="icon-down"></i>
		</button>

		<div id="collapse-filter" class="collapse collapse-filter">
			<div class="filter no-gutters">
				<div class="col" v-for="(info, idx) in searchList.slice(0, startSearchExpandCnt)" :key="idx">
					<label class="form-control-label reset" v-if="'text' === info.type">
						<b class="control-label">
							{{ info.label }}
							<span class="letterLength">
								{{ `(${curLengthCnt[info.vModel]}/${maxLengthCnt[info.vModel]})` }}
								<b class="icon-star" v-if="info.isRequired"></b>
							</span>
						</b>
						<inputText
							:ref="`inputText_${idx}`"
							:vModel="info.vModel"
							:classObject="{ 'form-control': true }"
							:placeholder="info.placeholder"
							:readonly="info.readonly"
							:regExpType="info.regExpType"
							@enter="search"
							@setLengthCnt="setLengthCnt"
						/>

						<i
							class="icon-close"
							@click="
								$refs[`inputText_${idx}`][0].setData(null);
								curLengthCnt[info.vModel] = 0;
							"
							v-if="!info.readonly"
						></i>
					</label>
					<label class="form-control-label label-select" v-else-if="'dateCalc' === info.type">
						<b class="control-label">{{ info.label }}</b>
						<dateCalc
							:ref="`dateCalc_${idx}`"
							:vModel="info.vModel"
							:classObject="{ 'form-control': true }"
							:optionInfo="info.optionInfo"
							:unit="info.unit"
							:dateVModel="info.dateVModel"
							@setDateRange="setDateRange"
						/>
					</label>

					<label class="form-control-label" v-if="'time' === info.type">
						<b class="control-label">{{ info.label }}</b>
						<timePicker
							:ref="`timePicker_${idx}`"
							:vModel="info.vModel"
							:readonly="info.readonly"
							:classObject="{ 'form-control': true }"
						/>
					</label>

					<label class="form-control-label label-select" v-else-if="'select' === info.type">
						<b class="control-label">{{ info.label }}</b>
						<dropDown
							:ref="`dropDown_${idx}`"
							:vModel="info.vModel"
							:classObject="{ 'form-control': true }"
							:optionInfo="info.optionInfo"
						/>
					</label>

					<label class="form-control-label daterange" v-else-if="'singleDate' === info.type">
						<b class="control-label">{{ info.label }}</b>
						<datePicker
							:ref="`expandArea_datePicker_${idx}`"
							:vModel="info.vModel"
							:placeholder="info.placeholder"
							:dateRangeType="info.dateRangeType"
							:customOptions="info.options"
							:dateFormat="info.dateFormat"
						/>
					</label>

					<label class="form-control-label daterange" v-else-if="'singleDaterange' === info.type">
						<b class="control-label">{{ info.label }} </b>
						<dateRange
							:ref="`dateRange_${idx}`"
							:vModel="info.vModel"
							:dateRangeType="info.dateRangeType"
							:customOptions="info.options"
						/>
					</label>

					<label class="form-control-label" v-else-if="'dataList' === info.type">
						<b class="control-label">
							{{ info.label }}
							<span class="letterLength">
								{{ `(${curLengthCnt[info.vModel]}/${maxLengthCnt[info.vModel]})` }}
								<b class="icon-star" v-if="info.isRequired"></b>
							</span>
						</b>
						<dataList
							:ref="`dataList_${idx}`"
							:vModel="info.vModel"
							:classObject="{ 'form-control': true }"
							:placeholder="info.placeholder"
							:optionInfo="info.optionInfo"
							:regExpType="info.regExpType"
							@setLengthCnt="setLengthCnt"
							@enter="search"
							@blur="blurEvt"
						/>
					</label>

					<label class="form-control-label reset" v-else-if="'modal' === info.type">
						<b class="control-label">
							{{ info.label }}
							<i class="icon-srch" @click="openModal(info.modalInfo, $refs[`inputText_${idx}`])"></i>
							<span class="letterLength">
								{{ `(${curLengthCnt[info.vModel]}/${maxLengthCnt[info.vModel]})` }}
								<b class="icon-star" v-if="info.isRequired"></b>
							</span>
						</b>
						<inputText
							:ref="`inputText_${idx}`"
							:vModel="info.vModel"
							:classObject="{ 'form-control': true }"
							:placeholder="info.placeholder"
							:regExpType="info.regExpType"
							@enter="search"
							@setLengthCnt="setLengthCnt"
						/>
						<i
							class="icon-close"
							@click="
								$refs[`inputText_${idx}`][0].setData(null);
								curLengthCnt[info.vModel] = 0;
							"
						></i>
					</label>
				</div>
				<div class="col-auto">
					<button type="button" class="btn btn-primary" @click="search" style="z-index: 1">
						<i class="icon-srch"></i>
						{{ $t('label.search') }}
					</button>
				</div>
			</div>

			<!-- expand -->
			<div
				class="toggleSearchExpandBtn collapsed"
				data-toggle="collapse"
				:data-target="`#${expandDataTarget}`"
				@click="collapseFilter"
				v-show="startSearchExpandCnt < searchList.length"
			>
				<i class="icon-down"></i>
			</div>

			<div :id="expandDataTarget" :class="`collapse toggleSearchExpandArea`" v-show="startSearchExpandCnt < searchList.length">
				<div class="filter no-gutters">
					<div class="col" v-for="(info, idx) in searchList.slice(startSearchExpandCnt)" :key="`expandArea${idx}`">
						<label class="form-control-label reset" v-if="'text' === info.type">
							<b class="control-label"
								>{{ info.label }}
								<span class="letterLength">
									{{ `(${curLengthCnt[info.vModel]}/${maxLengthCnt[info.vModel]})` }}
									<b class="icon-star" v-if="info.isRequired"></b>
								</span>
							</b>
							<inputText
								:ref="`expandArea_inputText_${idx}`"
								:vModel="info.vModel"
								:classObject="{ 'form-control': true }"
								:placeholder="info.placeholder"
								:regExpType="info.regExpType"
								@enter="search"
								@setLengthCnt="setLengthCnt"
							/>
							<i
								class="icon-close"
								@click="
									$refs[`expandArea_inputText_${idx}`][0].setData(null);
									curLengthCnt[info.vModel] = 0;
								"
							></i>
						</label>

						<label class="form-control-label label-select" v-else-if="'dateCalc' === info.type">
							<b class="control-label">{{ info.label }}</b>
							<dateCalc
								:ref="`expandArea_dateCalc_${idx}`"
								:vModel="info.vModel"
								:classObject="{ 'form-control': true }"
								:optionInfo="info.optionInfo"
								:unit="info.unit"
								:dateVModel="info.dateVModel"
								@setDateRange="setDateRange"
							/>
						</label>

						<label class="form-control-label reset" v-if="'time' === info.type">
							<b class="control-label">{{ info.label }}</b>
							<timePicker
								:ref="`expandArea_timePicker_${idx}`"
								:vModel="info.vModel"
								:classObject="{ 'form-control': true }"
								:placeholder="info.placeholder"
								:readonly="info.readonly"
								@enter="search"
							/>
						</label>

						<label class="form-control-label label-select" v-else-if="'select' === info.type">
							<b class="control-label">{{ info.label }}</b>
							<dropDown
								:ref="`expandArea_dropDown_${idx}`"
								:vModel="info.vModel"
								:classObject="{ 'form-control': true }"
								:optionInfo="info.optionInfo"
							/>
						</label>

						<label class="form-control-label daterange" v-else-if="'singleDate' === info.type">
							<b class="control-label">{{ info.label }}</b>
							<datePicker
								:ref="`expandArea_datePicker_${idx}`"
								:vModel="info.vModel"
								:dateRangeType="info.dateRangeType"
								:customOptions="info.options"
								:dateFormat="info.dateFormat"
							/>
						</label>

						<label class="form-control-label" v-else-if="'singleDaterange' === info.type">
							<b class="control-label">{{ info.label }}</b>
							<dateRange
								:ref="`expandArea_dateRange_${idx}`"
								:vModel="info.vModel"
								:dateRangeType="info.dateRangeType"
								:customOptions="info.options"
							/>
						</label>

						<label class="form-control-label" v-else-if="'dataList' === info.type">
							<b class="control-label">
								{{ info.label }}
								<span class="letterLength">
									{{ `(${curLengthCnt[info.vModel]}/${maxLengthCnt[info.vModel]})` }}
									<b class="icon-star" v-if="info.isRequired"></b>
								</span>
							</b>
							<dataList
								:ref="`expandArea_dataList_${idx}`"
								:vModel="info.vModel"
								:classObject="{ 'form-control': true }"
								:placeholder="info.placeholder"
								:optionInfo="info.optionInfo"
								:regExpType="info.regExpType"
								@setLengthCnt="setLengthCnt"
								@enter="search"
								@blur="blurEvt"
							/>
						</label>

						<label class="form-control-label reset" v-else-if="'modal' === info.type">
							<b class="control-label">
								{{ info.label }}
								<i class="icon-srch" @click="openModal(info.modalInfo, $refs[`expandArea_inputText_${idx}`])"></i>
								<span class="letterLength">
									{{ `(${curLengthCnt[info.vModel]}/${maxLengthCnt[info.vModel]})` }}
									<b class="icon-star" v-if="info.isRequired"></b>
								</span>
							</b>
							<inputText
								:ref="`expandArea_inputText_${idx}`"
								:vModel="info.vModel"
								:classObject="{ 'form-control': true }"
								:placeholder="info.placeholder"
								:regExpType="info.regExpType"
								@enter="search"
								@setLengthCnt="setLengthCnt"
							/>
							<i
								class="icon-close"
								@click="
									$refs[`expandArea_inputText_${idx}`][0].setData(null);
									curLengthCnt[info.vModel] = 0;
								"
							></i>
						</label>
					</div>
					<div class="col-auto">
						<button type="button" class="btn btn-primary" @click="search" style="z-index: 1">
							<i class="icon-srch"></i>
							{{ $t('label.search') }}
						</button>
					</div>
				</div>
			</div>
			<!-- expand -->
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';
import { mapState } from 'vuex';
import { SearchToggleEventBus, getRegExpInfo, getUUID } from '@/utils/common.js';
import inputText from '@/components/element/inputText';
import dropDown from '@/components/element/dropDown';
import datePicker from '@/components/element/datePicker';
import dateRange from '@/components/element/dateRange';
import dataList from '@/components/element/dataList';
import dateCalc from '@/components/element/dateCalc';
import timePicker from '@/components/element/timePicker';
import cloneDeep from 'lodash/cloneDeep';
import { getCurrentMenuId } from '../utils/common';

export default {
	name: 'Search',
	props: {
		list: Array,
		searchType: String
	},
	components: {
		inputText,
		dropDown,
		dataList,
		datePicker,
		dateRange,
		dateCalc,
		timePicker
	},
	computed: {
		...mapState(['listCounts', 'regExpList', 'minPageSize', 'constantsInfo'])
	},
	data() {
		return {
			searchList: [],
			startSearchExpandCnt: 5,

			curLengthCnt: {},
			maxLengthCnt: {},
			expandDataTarget: 'toggle' + getUUID()
		};
	},
	watch: {
		list: {
			immediate: true,
			deep: true,
			handler() {
				if (`mainBasic` === this.searchType && 0 < this.list.length) {
					this.searchList = this.constantsInfo.search.searchListFunc(getCurrentMenuId(), cloneDeep(this.list));
				} else {
					this.searchList = cloneDeep(this.list);
				}

				for (const info of this.searchList) {
					if (`pageSize` === info.vModel) info.regExpType = 'pageSize';

					this.setLengthCnt({ [info.vModel]: 0 });
					this.maxLengthCnt[info.vModel] = getRegExpInfo(info.regExpType ? info.regExpType : 'default').maxLength;
				}

				this.$nextTick(this.setSearchListVal);
			}
		}
	},
	methods: {
		setSearchListVal(isClear) {
			for (const key in this.$refs) {
				const props = this.$refs[key][0]._props;

				let val = null;

				if (`pageSize` === props.vModel) {
					val = isClear ? this.listCounts[0] : this.searchList.find(info => info.vModel === props.vModel).val;
				} else {
					val = this.searchList.find(info => info.vModel === props.vModel).val;
					val = `undefined` === typeof val ? null : val;
				}

				this.$refs[key][0].setData(val);

				if (!isClear) continue;

				if (![`inputText`, `password`, `textArea`].find(name => name === this.$refs[key][0].$options.name)) continue;

				this.setLengthCnt({ [props.vModel]: null === val ? 0 : val.length });
			}
		},
		setDateRange(info) {
			for (const key in this.$refs) {
				const val = info.dateRange.filter(info => info.vModel === this.$refs[key][0]._props.vModel);

				if (isEmpty(val)) continue;

				this.$refs[key][0].setData('from' === val[0].dateRangeType ? info.startDate : info.endDate);
			}
		},
		setLengthCnt(cntInfo) {
			this.curLengthCnt = { ...this.curLengthCnt, ...cntInfo };
		},
		collapseFilter() {
			setTimeout(() => {
				SearchToggleEventBus.$emit('MainResize');
			}, 0);
		},
		openModal(modalInfo, refElement) {
			const modalInstane = this.$openModal({
				...modalInfo,
				modalWidth: `800px`,
				modalHeight: `800px`,
				contentList: [
					{
						template: `<div>
						       <Search :list="searchList" @search="search" ref="search"/>
							   <Main
							   	   :buttonList="buttonList"
								   :gridInfo="gridInfo"
								   :gridSearchObj="gridSearchObj"
								   :gridType="'modalBasic'"
								   :viewer="true"
								   @rowClicked="rowClicked"
								   @initialize="initialize"
							   />
						   </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [],

								//Button
								buttonList: [],

								// Grid
								gridInfo: {},
								gridSearchObj: {}
							};
						},
						created() {
							const { search, button, grid } = modalInfo;

							//Search
							this.searchList = search.list;

							//Button
							this.buttonList = button.list;

							//Grid
							this.gridInfo = grid;
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								refElement[0].setData(modalInfo.rowClickedCallback(info));
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		},
		initialize() {
			this.setSearchListVal(true);
		},
		getSearchObj() {
			let resultObj = {};

			for (let key in this.$refs) {
				let searchData = this.$refs[key][0].vModel;
				let searchInfo = this.$refs[key][0].getData();

				if (0 === this.$refs[key].length) continue;

				if (!searchInfo[searchData] && Object.keys(resultObj).includes(searchData)) continue;

				resultObj = merge(resultObj, searchInfo);
			}

			resultObj._pageSize = resultObj.pageSize;

			delete resultObj.pageSize;

			return resultObj;
		},
		search() {
			this.blurEvt();
			this.$emit('search', this.getSearchObj());
		},
		blurEvt() {
			for (const key in this.$refs) {
				const targetData = this.$refs[key][0];

				if (`pageSize` !== targetData._props.vModel) continue;

				if (0 === targetData.getData().pageSize.length || Number(targetData.getData().pageSize) < Number(this.minPageSize))
					targetData.setData(this.minPageSize);
			}
		}
	}
};
</script>
