<template>
	<component ref="component" :is="optionElement" :classObject="classObject" :readonly="readonly" :optionListData="optionListData"></component>
</template>

<script>
import { HttpReq } from '@/utils/common.js';

export default {
	name: 'selectBox',
	props: {
		vModel: String,
		classObject: Object,
		readonly: {
			type: Boolean,
			default: false
		},
		radioInfo: Object,
		val: {
			type: String,
			default: null
		},
		optionListData: Array
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	methods: {
		setData(val) {
			this.$refs.component.setData(val);
		},
		getData() {
			return this.$refs.component.getData();
		}
	},
	data() {
		return {
			optionElement: {
				template: `
					<div>
						<label v-for="${this.radioInfo.optionFor}" :key="${this.radioInfo.optionText}"  style="padding-right: 30px;">
							<input type="radio" class="custom-control-input" 
								v-model="obj['${this.vModel}']"
								:value="${this.radioInfo.optionText}"
								:disabled="readonly" 
								${this.isEvent && this._events.changeEvt ? '@change="changeEvt"' : ''} />
							<span class="custom-control-label" v-text="${this.radioInfo.optionValue}" style="padding-left: 5px;"/>
						</label>
					</div>
				`,
				props: {
					classObject: Object,
					readonly: Boolean,
					optionListData: Array
				},
				data: (() => {
					const rtnDataObj = {
						vModelName: this.vModel,
						optionListName: this.radioInfo.optionListName,

						obj: {
							[this.vModel]: this.val
						},

						[this.radioInfo.optionListName]: [],

						url: this.radioInfo.url,
						params: this.radioInfo.params ? this.radioInfo.params : null,

						HttpReq: null
					};

					return function () {
						return rtnDataObj;
					};
				})(),
				watch: {
					optionListData: {
						immediate: true,
						handler() {
							this[this.optionListName] = this.optionListData;
						}
					}
				},
				async created() {
					if (!this.url) return;

					this.HttpReq = new HttpReq(this.url);

					const result = await this.HttpReq.read(this.params);

					if (`ok` !== result.data.result) return;

					this[this.optionListName] = result.data.object;
				},
				methods: {
					setData(val) {
						this.obj[this.vModelName] = val;
					},
					getData() {
						return {
							[this.vModelName]: this.obj[this.vModelName] ? this.obj[this.vModelName] : ' '
						};
					},
					changeEvt() {
						this.$emit('changeEvt', { key: this.vModelName, value: this.obj[this.vModelName] });
					}
				}
			}
		};
	}
};
</script>
