import Vue from 'vue';
import i18n from '@/i18n';
import Alert from '@/components/modal/Alert.vue';

Vue.use(Alert);

export default {
	install(Vue) {
		const modalConstructor = Vue.extend(Alert);

		Vue.prototype.$alert = window.$alert = function (modalInfo = {}) {
			let modalData = {
				showModal: true,
				modalInfo
			};

			const modalInstane = new modalConstructor({
				el: document.createElement('div'),
				i18n,
				data() {
					return modalData;
				}
			});

			document.body.appendChild(modalInstane.$el);
		};
	}
};
