export function convertTypeToClass(classType) {
	let className = null;

	switch (classType) {
		case `B`:
		case `S`:
		case `I`:
		case `L`:
		case `F`:
		case `D`:
		case `N`:
		case `P`:
			className = `java.lang.Number`;
			break;
		case `T`:
			className = `java.lang.String`;
			break;
		case `A`:
			className = `com.inzent.igate.itools.mapping.utils.RawValue`;
			break;
		case `p`:
			className = `java.sql.Timestamp`;
			break;
		case `b`:
			className = `java.lang.Boolean`;
			break;
		case `R`:
			className = `com.inzent.igate.message.Record`;
			break;
	}

	return className;
}

export function convertClassToType(className) {
	let type = null;

	switch (className) {
		case `PackedDecimal`:
		case `Byte`:
		case `Short`:
		case `Int`:
		case `Long`:
		case `Float`:
		case `Double`:
		case `Numeric`:
		case `java.lang.Number`:
			type = `N`;
			break;

		case `String`:
		case `java.lang.String`:
			type = `T`;
			break;

		case `Raw`:
		case `com.inzent.igate.itools.mapping.utils.RawValue`:
			type = `A`;
			break;

		case `Timestamp`:
		case `java.sql.Timestamp`:
			type = `p`;
			break;

		case `Boolean`:
		case `java.lang.Boolean`:
			type = `b`;
			break;

		case `Group`:
		case `Record`:
		case `com.inzent.igate.message.Record`:
			type = `R`;
			break;

		case `java.lang.Object`:
			type = ` `;
			break;
	}

	return type;
}
