<template>
	<div id="referenceModal">
		<label class="control-label warningLabel" style="color: #000">
			<span>{{ fieldInfoLabel ? fieldInfoLabel : convertClassName }}</span>
		</label>
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item" v-for="(tabObj, index) in tabList" :key="tabObj.id">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: selectedIdx === index }"
					@click="changeTab(index)"
					v-text="tabObj.label"
				>
				</a>
			</li>
		</ul>
		<div class="panel-body tab-content">
			<div :class="{ [modalMode]: true, 'tab-pane': true, active: selectedIdx === idx }" v-for="(detailInfo, idx) in tabList" :key="idx">
				<component
					ref="component"
					:is="require(`@/views/modal/mapping/ruleDetail${detailInfo.url}.vue`).default"
					:detailInfo="detailInfo"
					:detailData="detailData"
					:mode="modalMode"
					:pageType="pageType"
					:className="className"
					:sourceRecordIdArr="sourceRecordIdArr"
					:targetRecordIdArr="targetRecordIdArr"
					:rawValueInfo="rawValueInfo"
					:targetFieldType="targetFieldType"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic';
import { HttpReq } from '@/utils/common.js';
import { convertTypeToClass } from '@/views/modal/mapping/ruleDetail/utils.js';

export default {
	name: 'reference',
	components: {
		DetailBasic
	},
	props: {
		pageType: String,
		mappingExpression: String,
		className: String,
		sourceRecordIdArr: Array,
		targetRecordIdArr: Object,
		targetFieldType: String,
		fieldInfoLabel: String
	},
	data() {
		return {
			modalMode: `add`,
			detailData: null,
			selectedIdx: 0,
			tabList: [],
			rawValueInfo: null,
			convertClassName: null
		};
	},
	created() {
		// 상수
		const constant = {
			type: 'bundle',
			label: this.$t('label.constant'),
			url: `/constant`
		};

		this.tabList.push(constant);

		// 모델
		const record = {
			type: `bundle`,
			label: this.$t(`label.modelRecord`),
			url: `/record`,
			gridClick: function (evt) {
				if ('undefined' === typeof evt.rowKey || `rowHeader` === evt.targetType) return;

				const { path, fieldType } = this.$refs.tuiGrid.invoke(`getRow`, evt.rowKey);

				if (this.targetFieldType) this.selectedPath = fieldType !== this.targetFieldType ? null : path;
				else {
					let formatClassName = null;

					switch (this.className) {
						case `Number`:
							formatClassName = `N`;
							break;
						case `String`:
							formatClassName = `T`;
							break;
						case `RawValue`:
							formatClassName = `A`;
							break;
						case `Timestamp`:
							formatClassName = `p`;
							break;
						case `Boolean`:
							formatClassName = `b`;
							break;
						case `Record`:
							formatClassName = `R`;
							break;
					}

					this.selectedPath = fieldType !== formatClassName ? null : path;
				}
			},
			resize: function () {
				if (!this.$refs.tuiGrid) return;

				this.$nextTick(() => {
					const modalBody = document.querySelector(`#referenceModal`).parentNode;

					const adjustWidth = modalBody.clientWidth - 48 - 76;
					const adjustHeight = modalBody.clientHeight - 1 - 32 - 24 - 48 - 48 - 56 - 16 - 24 - 16 - 56;

					this.$refs.tuiGrid.invoke('setWidth', adjustWidth);
					this.$refs.tuiGrid.invoke('setHeight', adjustHeight);
				});
			}
		};

		this.tabList.push(record);

		// 함수
		const func = {
			type: `bundle`,
			label: this.$t('label.function'),
			url: `/function`
		};

		this.tabList.push(func);

		// Java Style
		const javaStyle = {
			type: `bundle`,
			label: `Java Style`,
			url: `/javaStyle`
		};

		this.tabList.push(javaStyle);
	},
	async mounted() {
		this.convertClassName = convertTypeToClass(this.className) ? convertTypeToClass(this.className) : this.className;

		if (!this.mappingExpression) return;

		const {
			data: {
				object: { type, name, args }
			}
		} = await new HttpReq(`/api/mapping/expression`).read({ expression: this.mappingExpression });

		const pageMappingTabList = {
			ConstantExpression: `/constant`,
			RecordExpression: `/record`,
			FunctionExpression: `/function`,
			JavaOperationExpression: `/javaStyle`
		};

		for (let i = 0; i < this.tabList.length; i++) {
			if (pageMappingTabList[type] === this.tabList[i].url) {
				this.changeTab(i);
				break;
			}
		}

		this.rawValueInfo = {
			type,
			name: `undefined` === typeof name ? this.mappingExpression : name,
			args
		};
	},
	methods: {
		changeTab(idx) {
			this.selectedIdx = idx;
		},
		async getData() {
			let isValidation = true;
			const expression = this.$refs.component[this.selectedIdx].getData();

			await new HttpReq(`/api/mapping/expression`).read({ expression }, true, result => {
				isValidation = false;

				window.$alert({
					type: `warn`,
					message: result.data.error.map(({ className }) => className).join(`,`)
				});
			});

			return {
				expression,
				isValidation
			};
		}
	}
};
</script>
