<template>
	<div class="row frm-row topology">
		<div ref="pFieldMetaGrid">
			<Search :list="searchList" @search="search" ref="search" />
			<Main
				:buttonList="buttonList"
				:gridInfo="gridInfo"
				:gridSearchObj="gridSearchObj"
				:gridResizeFunc="gridResizeFunc"
				gridType="modalBasic"
				:viewer="true"
				:editor="false"
				@initialize="initialize"
				ref="main"
			/>
		</div>
		<div style="text-align: center; width: 50px">
			<div style="position: relative; top: 50%; left: 50%; transform: translate(-50%, -50%)">
				<div>
					<a href="javascript:void(0);" class="btn btn-m" style="width: 34px; min-width: 34px" @click="addFieldMeta">
						<i class="icon-right"></i>
					</a>
				</div>
				<div style="margin-top: 5px">
					<a href="javascript:void(0);" class="btn btn-m" style="width: 34px; min-width: 34px" @click="removeFieldMeta">
						<i class="icon-left"></i>
					</a>
				</div>
			</div>
		</div>
		<div ref="pFilterFieldMetaGrid">
			<div class="table-responsive">
				<TuiGrid ref="filterFieldMetaGrid" v-bind="filterFieldMetaGridOptions" />
			</div>
		</div>
	</div>
</template>

<script>
import { Grid as TuiGrid } from '@toast-ui/vue-grid';
import cloneDeep from 'lodash/cloneDeep';
import Search from '@/components/Search.vue';
import Main from '@/components/Main.vue';
import { WindowResizeEventBus, SideMenuToggleEventBus, getNumFromStr, makeGridOptions } from '@/utils/common.js';

export default {
	name: `addFieldMeta`,
	props: {
		metaDomain: String
	},
	components: {
		TuiGrid,
		Search,
		Main
	},
	data() {
		return {
			searchList: [
				{
					type: 'text',
					vModel: 'pk.fieldId',
					label: this.$t('label.id'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'fieldName',
					label: this.$t('label.name'),
					placeholder: this.$t('msg.enterName'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'pk.metaDomain',
					label: this.$t('label.metaDomain'),
					val: this.metaDomain,
					optionInfo: {
						isViewAll: false,
						optionFor: `option in ['${this.metaDomain}']`,
						optionValue: 'option',
						optionText: 'option'
					}
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],
			buttonList: [
				{
					id: 'initialize',
					isUse: true
				}
			],
			gridInfo: {
				url: '/api/entity/fieldMeta/search',
				totalCntUrl: '/api/entity/fieldMeta/count',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					rowHeaders: [
						{
							type: 'checkbox',
							header: ' '
						}
					],
					columns: [
						{
							header: this.$t('label.id'),
							name: 'pk.fieldId',
							width: '30%'
						},
						{
							header: this.$t('label.name'),
							name: 'fieldName',
							width: '30%'
						},
						{
							header: this.$t('label.type'),
							name: 'fieldType',
							align: 'center',
							width: '20%',
							formatter: value => {
								const msgObj = {
									B: this.$t('label.fieldMeta.fieldType.byte'),
									S: this.$t('label.fieldMeta.fieldType.short'),
									I: this.$t('label.fieldMeta.fieldType.int'),
									L: this.$t('label.fieldMeta.fieldType.long'),
									F: this.$t('label.fieldMeta.fieldType.float'),
									D: this.$t('label.fieldMeta.fieldType.double'),
									N: this.$t('label.fieldMeta.fieldType.numeric'),
									p: this.$t('label.fieldMeta.fieldType.timeStamp'),
									b: this.$t('label.fieldMeta.fieldType.boolean'),
									v: this.$t('label.fieldMeta.fieldType.individual'),
									A: this.$t('label.fieldMeta.fieldType.raw'),
									P: this.$t('label.fieldMeta.fieldType.packedDecimal'),
									R: this.$t('label.fieldMeta.fieldType.record'),
									T: this.$t('label.fieldMeta.fieldType.string')
								};

								return msgObj[value.row.fieldType];
							}
						},
						{
							header: this.$t('label.length'),
							name: 'fieldLength',
							width: '10%',
							align: `right`
						},
						{
							header: `Scale`,
							name: 'fieldScale',
							width: '10%',
							align: `right`
						}
					]
				}
			},
			gridSearchObj: {},
			gridResizeFunc: this.resize,

			filterFieldMetaGridOptions: makeGridOptions({
				rowHeaders: [{ type: 'checkbox', header: ' ' }],
				columns: [
					{
						header: this.$t('label.id'),
						name: 'pk.fieldId',
						width: '30%'
					},
					{
						header: this.$t('label.name'),
						name: 'fieldName',
						width: '30%'
					},
					{
						header: this.$t('label.type'),
						name: 'fieldType',
						align: 'center',
						width: '20%',
						formatter: value => {
							const msgObj = {
								B: this.$t('label.fieldMeta.fieldType.byte'),
								S: this.$t('label.fieldMeta.fieldType.short'),
								I: this.$t('label.fieldMeta.fieldType.int'),
								L: this.$t('label.fieldMeta.fieldType.long'),
								F: this.$t('label.fieldMeta.fieldType.float'),
								D: this.$t('label.fieldMeta.fieldType.double'),
								N: this.$t('label.fieldMeta.fieldType.numeric'),
								p: this.$t('label.fieldMeta.fieldType.timeStamp'),
								b: this.$t('label.fieldMeta.fieldType.boolean'),
								v: this.$t('label.fieldMeta.fieldType.individual'),
								A: this.$t('label.fieldMeta.fieldType.raw'),
								P: this.$t('label.fieldMeta.fieldType.packedDecimal'),
								R: this.$t('label.fieldMeta.fieldType.record'),
								T: this.$t('label.fieldMeta.fieldType.string')
							};

							return msgObj[value.row.fieldType];
						}
					},
					{
						header: this.$t('label.length'),
						name: 'fieldLength',
						width: '10%',
						align: `right`
					},
					{
						header: `Scale`,
						name: 'fieldScale',
						width: '10%',
						align: `right`
					}
				]
			})
		};
	},
	created() {
		WindowResizeEventBus.$on('MainResize', this.resize);
		SideMenuToggleEventBus.$on('MainResize', this.resize);
	},
	mounted() {
		this.$nextTick(() => {
			this.search(this.$refs.search.getSearchObj());
		});
	},
	methods: {
		addFieldMeta() {
			const checkedRowInfo = this.$refs.main.$refs.grid.getCheckedRowInfo();

			if (0 === checkedRowInfo.length) return;

			const filterFieldMetaGridDataList = this.$refs.filterFieldMetaGrid.invoke(`getData`);

			checkedRowInfo.forEach(info => {
				if (0 < filterFieldMetaGridDataList.length) {
					const findObj = filterFieldMetaGridDataList.find(
						filterInfo => info[`pk.metaDomain`] === filterInfo[`pk.metaDomain`] && info[`pk.fieldId`] === filterInfo[`pk.fieldId`]
					);

					if (findObj) return;
				}

				this.$refs.filterFieldMetaGrid.invoke(`appendRow`, cloneDeep(info));
			});

			this.$refs.main.$refs.grid.getGrid().invoke(`uncheckAll`, true);

			this.resize();
		},
		removeFieldMeta() {
			const checkedRows = this.$refs.filterFieldMetaGrid.invoke(`getCheckedRows`);

			if (0 === checkedRows.length) {
				this.$alert({
					message: this.$t(`msg.no.target.selected`)
				});

				return;
			}

			checkedRows.forEach(info => {
				this.$refs.filterFieldMetaGrid.invoke(`removeRow`, info.rowKey);
			});

			this.resize();
		},
		initialize() {
			this.$refs.search.initialize();
		},
		search(searchObj) {
			this.gridSearchObj = searchObj;
		},
		resize(gridObj) {
			this.$nextTick(() => {
				if (!document.contains(this.$el)) return;

				const modalBody = this.$el.closest(`.modal-body`);

				const modalBodyComputedStyle = getComputedStyle(modalBody);

				const modalBodyPaddingLeft = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-left'));
				const modalBodyPaddingRight = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-right'));
				const modalBodyPaddingTop = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-top'));
				const modalBodyPaddingBottom = getNumFromStr(modalBodyComputedStyle.getPropertyValue('padding-bottom'));

				const adjustWidth = this.$el.closest(`.modal-body`).clientWidth - modalBodyPaddingLeft - modalBodyPaddingRight - 50 - 5;

				if (gridObj) gridObj.invoke('setWidth', adjustWidth / 2);
				if (this.$refs.filterFieldMetaGrid) this.$refs.filterFieldMetaGrid.invoke('setWidth', adjustWidth / 2);

				this.$refs.pFieldMetaGrid.style.width = `${adjustWidth / 2}px`;
				this.$refs.pFilterFieldMetaGrid.style.width = `${adjustWidth / 2}px`;

				const ctHeaderHeight = modalBody.querySelector(`.ct-header`).offsetHeight;
				const subBarHeight = modalBody.querySelector(`.sub-bar`).offsetHeight;
				const paginationHeight = modalBody.querySelector(`.tui-grid-pagination`).offsetHeight;

				if (gridObj)
					gridObj.invoke(
						'setHeight',
						this.$el.closest(`.modal-body`).clientHeight -
							modalBodyPaddingTop -
							modalBodyPaddingBottom -
							ctHeaderHeight -
							subBarHeight -
							paginationHeight -
							50
					);

				if (this.$refs.filterFieldMetaGrid)
					this.$refs.filterFieldMetaGrid.invoke(
						'setHeight',
						this.$el.closest(`.modal-body`).clientHeight - modalBodyPaddingTop - modalBodyPaddingBottom - paginationHeight - 50
					);
			});
		},
		getData() {
			return this.$refs.filterFieldMetaGrid.invoke(`getData`);
		}
	}
};
</script>

<style lang="scss" scoped>
.topology {
	height: 100%;

	margin-left: 0px;
	margin-right: 0px;

	.ct-content {
		height: calc(100% - 77px);
		overflow: hidden;
	}
}
</style>
