<template>
	<div>
		<div class="col-lg-12">
			<div class="form-group">
				<label class="control-label">
					<span>{{ $t(`label.modelRecord`) }}</span>
				</label>
				<div class="detail-content-common" style="padding-left: 0px">
					<select class="form-control view-disabled" v-model="selectedRecordInfo" @change="selectedRecordChange">
						<option v-for="(recordInfo, idx) in recordInfoList" :key="idx" :value="recordInfo" v-text="recordInfo.id"></option>
					</select>
				</div>
			</div>
			<div class="form-group">
				<label class="control-label">
					<span>{{ $t(`label.list`) }}</span>
				</label>
				<TuiGrid ref="tuiGrid" v-bind="options" @click="gridClick" />
			</div>
			<div class="form-group">
				<label class="control-label">
					<span>{{ $t(`label.path`) }}</span>
				</label>
				<div class="detail-content-common" style="padding-left: 0px">
					<span>
						<input type="text" readonly="readonly" class="form-control view-disabled" v-model="selectedPath" />
					</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import { Grid } from '@toast-ui/vue-grid';
import { WindowResizeEventBus, makeGridOptions, escapeHtml } from '@/utils/common.js';

export default {
	name: 'record',
	props: {
		detailInfo: Object,
		sourceRecordIdArr: Array,
		targetRecordIdArr: Object,
		rawValueInfo: Object,
		targetFieldType: String,
		className: String
	},
	components: {
		TuiGrid: Grid
	},
	data() {
		return {
			options: {
				treeColumnOptions: {
					name: 'fieldId'
				},
				columns: [
					{
						header: `${this.$t('label.field')}`,
						name: 'fieldId'
					},
					{
						header: `${this.$t('label.field')} ${this.$t(`label.type`)}`,
						name: `fieldType`,
						formatter: ({ row }) => {
							let rtnFieldType = this.fieldTypeName[row.fieldType];

							if (`R` === row.fieldType) rtnFieldType += row.recordType ? `(${row.recordType})` : ``;
							else rtnFieldType += `(${row.fieldLength})`;

							if (row.arrayType && `N` !== row.arrayType) rtnFieldType += `[${row.arrayType}]`;

							return escapeHtml(rtnFieldType);
						}
					},
					{
						header: `${this.$t('label.field')} ${this.$t(`label.name`)}`,
						name: `fieldName`
					},
					{
						name: `path`,
						hidden: true
					}
				],
				data: []
			},
			recordInfoList: [],
			selectedRecordInfo: null,
			selectedPath: null,
			selectionRowKey: null,
			fieldTypeName: {
				B: `Byte`,
				S: `Short`,
				I: `Int`,
				L: `Long`,
				F: `Float`,
				D: `Double`,
				b: `Boolean`,
				p: `TimeStamp`,
				T: `String`,
				N: `Numeric`,
				P: `PackedDecimal`,
				A: `Raw`,
				R: `Group`
			}
		};
	},
	created() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		this.options = makeGridOptions(this.options);
	},

	async mounted() {
		this.resize();

		// Source
		this.sourceRecordIdArr.forEach((record, idx) => {
			this.recordInfoList.push({
				id: `$${idx}${record.id} - ${record.name}`,
				field: this.initTreeData(record, `$${idx}`)
			});
		});

		// set Array Reference Modal
		if (undefined === this.rawValueInfo) {
			this.setData();

			return;
		}

		// Target
		if (this.targetRecordIdArr) {
			this.recordInfoList.push({
				id: `#${this.targetRecordIdArr.id} - ${this.targetRecordIdArr.name}`,
				field: this.initTreeData(this.targetRecordIdArr, `#`)
			});
		}

		this.setData();
	},
	watch: {
		selectedPath() {
			this.addSelectionClass();
		},
		selectedRecordInfo() {
			this.$refs.tuiGrid.invoke(`clear`);

			if (`object` === typeof this.selectedRecordInfo) {
				this.$refs.tuiGrid.invoke(`resetData`, this.selectedRecordInfo.field);
				this.addSelectionClass();
			}

			this.$nextTick(this.resize);
		},
		rawValueInfo: {
			immediate: true,
			deep: true,
			handler() {
				this.setData();
			}
		}
	},
	methods: {
		addSelectionClass() {
			if (this.selectionRowKey) this.$refs.tuiGrid.invoke('removeRowClassName', this.selectionRowKey, 'refModelField');

			if (!this.selectedPath) return;

			this.$refs.tuiGrid.invoke('getData').forEach(row => {
				if (row.path === this.selectedPath) {
					this.$refs.tuiGrid.invoke('addRowClassName', row.rowKey, 'refModelField');

					this.selectionRowKey = row.rowKey;
				}
			});
		},
		initTreeData(recordObject, path, depth = 1) {
			if (!recordObject.fields) return;

			const { id, name } = recordObject;
			const fields = cloneDeep(recordObject.fields);

			const rtnArr = [];

			const titleFieldData = {
				fieldId: id,
				fieldName: name,
				fieldType: 'R',
				_attributes: {
					expanded: true,
					className: {
						selection: ['refModelField']
					}
				},
				_children: []
			};

			if (!path.includes('\\')) rtnArr.push(titleFieldData);

			fields.forEach(field => {
				const data = {
					path: `${path}\\${field.pk.fieldId}${
						field.arrayType && `N` !== field.arrayType ? (undefined === this.rawValueInfo ? `[]` : `[i${depth}]`) : ``
					}`,
					fieldId: field.pk.fieldId,
					fieldName: field.fieldName,
					fieldType: field.fieldType,
					fieldLength: field.fieldLength,
					arrayType: field.arrayType
				};

				if (field.recordObject) {
					data.recordType = field.recordObject.recordType;
					data._attributes = {
						expanded: true,
						className: {
							selection: ['refModelField']
						}
					};
					data._children = this.initTreeData(field.recordObject, data.path, field.arrayType && 'N' !== field.arrayType ? depth + 1 : depth);
				}

				if (!path.includes('\\')) rtnArr[0]._children.push(data);
				else rtnArr.push(data);
			});

			return rtnArr;
		},
		resize() {
			this.detailInfo.resize.call(this);
		},
		gridClick(evt) {
			this.detailInfo.gridClick.call(this, evt);
		},
		selectedRecordChange() {
			this.selectedPath = null;
		},
		setData() {
			if (0 === this.recordInfoList.length) return;

			// set Array Reference Modal
			if (undefined === this.rawValueInfo) {
				this.selectedRecordInfo = this.recordInfoList[0];
				this.selectedPath = this.detailInfo.arrayReference ? this.detailInfo.arrayReference : null;
			}

			if (!this.rawValueInfo) return;

			const { type, name } = this.rawValueInfo;

			if (`RecordExpression` !== type) return;

			this.selectedPath = name;

			this.addSelectionClass();

			for (const recordInfo of this.recordInfoList) {
				if (recordInfo.id.startsWith(this.selectedPath.split('\\')[0])) {
					this.selectedRecordInfo = recordInfo;
					break;
				}
			}
		},
		getData() {
			return this.selectedPath;
		}
	}
};
</script>
