<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-wrap">
				<div class="form-table-head">
					<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('label.insert')">
						<i class="icon-plus-circle"></i>
					</button>
					<label class="col">{{ this.$t('label.id') }}</label>
					<label class="col">{{ this.$t('label.system.property') }}</label>
					<label class="col">{{ this.$t('label.desc') }}</label>
				</div>
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('label.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<dataList
								:ref="`component${bodyIndex}`"
								vModel="pk.keyId"
								:classObject="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj['pk.keyId']"
								:readonly="readonly"
								:optionInfo="keyIdOptionInfo"
								@changeEvt="changeEvt($event, bodyIndex)"
								regExpType="id"
								@setLengthCnt="setLengthCntKeyId($event, bodyIndex)"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ curLengthCnt[`${`pk.keyId`}_${bodyIndex}`] }} / {{ maxLengthCnt[`pk.keyId`] }})
							</span>
						</div>
					</div>

					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="property"
								:classObject="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj['property']"
								:readonly="readonly"
								regExpType="id"
								@setLengthCnt="info => setLengthCnt({ [`property_${bodyIndex}`]: info[`property`] })"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ curLengthCnt[`property_${bodyIndex}`] }} / {{ maxLengthCnt[`property`] }})
							</span>
						</div>
					</div>

					<div class="col">
						<div :class="`detail-content-common`">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="description"
								:classObject="{
									'form-control': true,
									'regExp-text': false,
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj['description']"
								:readonly="true"
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import isNil from 'lodash/isNil';
import isEmpty from 'lodash/isEmpty';
import { parseHierarchyObj, parseFlattenObj, getUUID, HttpReq, getRegExpInfo } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: 'RecordExtendInfo',
	extends: DetailProperty,
	data() {
		return {
			recordPropertyList: [],
			keyIdOptionInfo: {
				optionFor: 'option in []',
				optionValue: 'option.pk.propertyKey',
				optionText: 'option.pk.propertyKey'
			},
			regExpInfo: {
				'pk.keyId': 'id',
				property: 'value'
			},
			curLengthCnt: {},
			maxLengthCnt: {}
		};
	},
	async created() {
		const result = await new HttpReq(`/api/page/properties`).read({
			pk: {
				propertyId: `Property.Record`
			},
			orderByKey: true
		});

		this.recordPropertyList = result.data.object;

		const recordPropertyList = JSON.stringify(this.recordPropertyList).replace(/"/g, `'`);

		this.keyIdOptionInfo.optionFor = `option in ${recordPropertyList}`;

		for (const [key, value] of Object.entries(this.regExpInfo)) {
			this.maxLengthCnt[key] = getRegExpInfo(value).maxLength;
		}
	},
	methods: {
		getData() {
			const rtnArr = [];

			for (let i = 0; i < this.propertyList.length; i++) {
				let rtnObj = {};

				this.$refs[`component${i}`].forEach(comp => {
					rtnObj = merge(rtnObj, comp.getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				options: rtnArr
			};
		},
		addProperty() {
			for (const key in this.regExpInfo) {
				this.setLengthCnt({ [`${key}_${this.propertyList.length}`]: 0 });
			}

			this.propertyList.push({
				idx: getUUID(),
				'pk.keyId': '',
				name: '',
				property: '',
				description: ''
			});
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);

			this.$nextTick(() => {
				this.curLengthCnt = {};

				this.propertyList.forEach((property, bodyIndex) => {
					let rtnObj = {};

					this.$refs[`component${bodyIndex}`].forEach(comp => {
						rtnObj = merge(rtnObj, comp.getData());
					});

					for (const key in rtnObj) {
						if (`description` === key) continue;
						this.setLengthCnt({ [`${key}_${bodyIndex}`]: isNil(rtnObj[key]) ? 0 : String(rtnObj[key]).length });
					}
				});
			});
		},
		async setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			if ('undefined' !== typeof this.detailData.options) {
				this.propertyList = [...this.detailData.options];

				this.propertyList.forEach(propertyInfo => {
					propertyInfo.idx = getUUID();

					const recordInfo = this.recordPropertyList.find(recordInfo => propertyInfo.pk.keyId === recordInfo.pk.propertyKey);

					if (recordInfo) propertyInfo.description = recordInfo.propertyDesc;
				});
			} else {
				this.propertyList = [];
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));

			this.propertyList.forEach((propertyInfo, index) => {
				for (const key in this.regExpInfo) {
					this.setLengthCnt({ [`${key}_${index}`]: propertyInfo[key] ? propertyInfo[key].length : 0 });
				}
			});
		},
		changeEvt(info, index) {
			const recordInfo = this.recordPropertyList.find(recordInfo => info.value === recordInfo.pk.propertyKey);

			if (!recordInfo) return;

			this.propertyList[index]['pk.keyId'] = recordInfo.pk.propertyKey;
			this.propertyList[index]['property'] = recordInfo.propertyValue;
			this.propertyList[index]['description'] = recordInfo.propertyDesc;
		},
		setLengthCntKeyId(info, index) {
			const dataObj = this.$refs[`component${index}`].find(component => `dataList` === component.$options.name).getData();

			const recordInfo = this.recordPropertyList.find(recordInfo => dataObj[`pk.keyId`] === recordInfo.pk.propertyKey);

			this.propertyList[index]['description'] = recordInfo ? recordInfo.propertyDesc : null;

			DetailProperty.methods.setLengthCnt.bind(this)({ [`${`pk.keyId`}_${index}`]: info[`pk.keyId`] });
		}
	}
};
</script>
