<template>
	<div ref="mainExternal" class="customLayout">
		<iframe id="mainExternalFrame" :src="src"> </iframe>
	</div>
</template>

<script>
export default {
	name: 'External',
	props: {
		pageInfo: {
			type: Object,
			default: () => ({})
		}
	},
	computed: {
		src() {
			return this.pageInfo.url;
		}
	}
};
</script>
