<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/linkedServer/Detail.vue';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'Interface',
	extends: Basic,
	components: {
		Detail
	},
	async created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}

		if (localStorage.getItem('selectedLinkedServerInfo')) {
			const selectedLinkedServerInfo = JSON.parse(localStorage.getItem('selectedLinkedServerInfo'));
			localStorage.removeItem('selectedLinkedServerInfo');

			this.initDetail('detail', {
				id: selectedLinkedServerInfo.id,
				isNewTab: true
			});
		}
	},
	data() {
		return {
			interfaceClassList: null,

			cudUrl: `/api/entity/server/object`,

			//auth
			viewer: null,
			editor: null,

			searchList: [
				{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId'), regExpType: 'searchId' },
				{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName'), regExpType: 'name' },
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/server/search`,
				totalCntUrl: `/api/entity/server/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('label.id'),
							name: 'id',
							width: '40%'
						},
						{
							header: this.$t('label.name'),
							name: 'name',
							width: '60%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['id'],
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true }
			],
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('label.basicInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'id',
									label: this.$t('label.id'),
									isPK: true,
									regExpType: 'id'
								}
							],
							[
								{
									type: 'text',
									vModel: 'name',
									label: this.$t('label.name'),
									regExpType: 'name'
								}
							],
							[
								{
									type: 'select',
									vModel: 'useYn',
									label: this.$t('label.useyn'),
									val: 'Y',
									optionInfo: {
										url: `/api/page/properties`,
										params: {
											pk: {
												propertyId: 'List.Yn'
											},
											orderByKey: true
										},
										optionListName: 'ynList',
										optionFor: 'option in ynList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'description',
									label: this.$t('label.desc'),
									height: '150px',
									regExpType: 'desc'
								}
							]
						]
					]
				},
				{
					type: 'bundle',
					label: this.$t('label.interface'),
					url: '/linkedServer/ServerInterface'
				},
				{
					type: 'bundle',
					label: this.$t('label.property'),
					url: '/linkedServer/ServerProperty'
				},
				{
					type: 'bundle',
					label: 'Consumer ' + this.$t('label.system'),
					url: '/linkedServer/ConsumerSystem'
				},
				{
					type: 'bundle',
					label: 'Provider ' + this.$t('label.system'),
					url: '/linkedServer/ProviderSystem'
				},
				{
					type: 'basic',
					label: this.$t('label.useInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'updateVersion',
									label: 'Version',
									readonly: true,
									val: '0'
								},
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('label.writer'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('label.created.date'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	}
};
</script>
