<script>
import { mapState } from 'vuex';
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/interface/Detail.vue';
import { getPrivilege, HttpReq, escapeHtml, changeDateFormat, openNewTab } from '@/utils/common.js';

export default {
	name: 'Interface',
	extends: Basic,
	components: {
		Detail
	},
	computed: {
		...mapState(['constantsInfo'])
	},
	async created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;

		let httpReq = new HttpReq(`/api/entity/interfaceClass/search`);

		let result = await httpReq.read({
			object: {},
			limit: null,
			next: null,
			reverseOrder: false
		});

		if (`ok` !== result.data.result) return;

		this.interfaceClassList = result.data.object;

		const isSpecification = this.constantsInfo.isSpecification;

		if (isSpecification && this.editor) {
			this.buttonList.push({ id: 'importExcel', isUse: true, importUrl: `/api/entity/interface/document` });
			this.detailButtonList.push({
				id: 'exportExcel',
				isUse: true,
				exportUrl: `/api/entity/interface/document`,
				fileName: `InterfaceTemplate`
			});
		}
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}

		if (localStorage.getItem('selectedInterfaceInfo')) {
			const selectedInterfaceInfo = JSON.parse(localStorage.getItem('selectedInterfaceInfo'));
			localStorage.removeItem('selectedInterfaceInfo');

			this.initDetail('detail', {
				id: selectedInterfaceInfo.interfaceId,
				isNewTab: true
			});
		}
	},
	data() {
		return {
			interfaceClassList: null,

			cudUrl: `/api/entity/interface/object`,

			//auth
			viewer: null,
			editor: null,

			searchList: [
				{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId'), regExpType: 'searchId' },
				{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName'), regExpType: 'name' },
				{
					type: 'modal',
					vModel: 'sourceSystemId',
					label: `Source ${this.$t('label.system.id')}`,
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('label.system'),
						search: {
							list: [
								{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId') },
								{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName') },
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('label.listCnt'),
									placeholder: 'msg.coditionList',
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/system/search`,
							totalCntUrl: `/api/entity/system/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('label.id'),
										name: 'id'
									},
									{
										header: this.$t('label.name'),
										name: 'name'
									}
								]
							}
						},
						rowClickedCallback: ({ id }) => id
					}
				},
				{
					type: 'text',
					vModel: 'bizId',
					label: this.$t('label.biz.name'),
					placeholder: this.$t('msg.enterData'),
					regExpType: 'name'
				},
				{
					type: 'modal',
					vModel: 'serverId',
					label: this.$t('label.linked.server.id'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('label.linked.server'),
						search: {
							list: [
								{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId') },
								{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName') },
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('label.listCnt'),
									placeholder: 'msg.coditionList',
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/server/search`,
							totalCntUrl: `/api/entity/server/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('label.id'),
										name: 'id',
										width: '40%'
									},
									{
										header: this.$t('label.name'),
										name: 'name',
										width: '60%'
									}
								]
							}
						},
						rowClickedCallback: ({ id }) => id
					}
				},
				{
					type: 'select',
					vModel: 'interfaceClass',
					label: this.$t('label.interface.class'),
					optionInfo: {
						url: `/api/entity/interfaceClass/search`,
						params: {
							object: {},
							limit: null,
							next: null,
							reverseOrder: false
						},
						optionListName: 'interfaceClass',
						optionFor: 'option in interfaceClass',
						optionValue: 'option.id',
						optionText: 'option.name'
					}
				},
				{
					type: 'modal',
					vModel: 'serviceId',
					label: this.$t('label.service.id'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('label.service'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'id',
									label: this.$t('label.id'),
									placeholder: this.$t('msg.enterId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'name',
									label: this.$t('label.name'),
									placeholder: this.$t('msg.enterName'),
									regExpType: 'name'
								},
								{
									type: 'text',
									vModel: 'description',
									label: this.$t(`label.desc`),
									placeholder: this.$t('msg.enterDesc'),
									regExpType: 'desc'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('label.listCnt'),
									placeholder: 'msg.coditionList',
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/service/search`,
							totalCntUrl: `/api/entity/service/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('label.id'),
										name: 'id'
									},
									{
										header: this.$t('label.name'),
										name: 'name'
									},
									{
										header: this.$t('label.desc'),
										name: 'description'
									}
								]
							}
						},
						rowClickedCallback: ({ id }) => id
					}
				},
				{ type: 'text', vModel: 'serviceName', label: this.$t('label.service.name'), placeholder: this.$t('msg.enterName') },
				{
					type: 'modal',
					vModel: 'targetSystemId',
					label: `Target ${this.$t('label.system.id')}`,
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('label.system'),
						search: {
							list: [
								{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId') },
								{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName') },
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('label.listCnt'),
									placeholder: 'msg.coditionList',
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/system/search`,
							totalCntUrl: `/api/entity/system/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('label.id'),
										name: 'id'
									},
									{
										header: this.$t('label.name'),
										name: 'name'
									}
								]
							}
						},
						rowClickedCallback: ({ id }) => id
					}
				},
				{
					type: 'text',
					vModel: 'serviceBizId',
					label: `${this.$t('label.service')} ${this.$t('label.biz.name')}`,
					placeholder: this.$t('msg.enterData'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'serviceInterfaceType',
					label: this.$t('label.type'),
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'Interface.Type'
							},
							orderByKey: true
						},
						optionListName: 'interfaceType',
						optionFor: 'option in interfaceType',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'select',
					vModel: 'useYn',
					label: this.$t('label.useyn'),
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'List.Yn'
							},
							orderByKey: true
						},
						optionListName: 'info',
						optionFor: 'option in info',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'select',
					vModel: 'publishStatus',
					label: this.$t('label.publishStatus'),
					optionInfo: {
						optionFor:
							'option in [ {value: `M`, name: `MAKE`}, {value: `R`, name: `REQUEST`}, {value: `C`, name: `CANCEL`}, {value: `A`, name: `APPROVE`}, {value: `D`, name: `DONE`} ]',
						optionValue: 'option.value',
						optionText: 'option.name'
					}
				},
				{
					type: 'text',
					vModel: 'description',
					label: this.$t(`label.desc`),
					placeholder: this.$t('msg.enterDesc'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'download', isUse: true, downloadUrl: `/api/entity/interface/download` }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/interface/search`,
				totalCntUrl: `/api/entity/interface/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('label.id'),
							name: 'id',
							width: '10%'
						},
						{
							header: this.$t('label.name'),
							name: 'name',
							width: '8%'
						},
						{
							header: `Source ${this.$t('label.system.id')}`,
							name: 'sourceSystemId',
							width: '7%'
						},
						{
							header: this.$t('label.biz.name'),
							name: 'bizId',
							width: '7%'
						},
						{
							header: this.$t('label.linked.server.id'),
							name: 'serverId',
							width: '8%'
						},
						{
							header: this.$t('label.interface.class'),
							name: 'interfaceClass',
							align: 'center',
							width: '10%',
							formatter: ({ value }) =>
								this.interfaceClassList
									? escapeHtml(this.interfaceClassList.find(interfaceClass => interfaceClass.id === value).name)
									: null
						},
						{
							header: this.$t('label.publishStatus'),
							name: 'publishStatus',
							align: 'center',
							width: '5%',
							formatter: ({ value }) => {
								return 'M' === value
									? 'MAKE'
									: 'R' === value
									? 'REQUEST'
									: 'C' === value
									? 'CANCEL'
									: 'A' === value
									? 'APPROVE'
									: 'D' === value
									? 'DONE'
									: '';
							}
						},
						{
							header: this.$t('label.publishTimestamp.request'),
							name: 'publishDateTime',
							width: '10%',
							align: 'center',
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							header: this.$t('label.publishTimestamp.complete'),
							name: 'publishTimestamp',
							width: '10%',
							align: 'center',
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							header: this.$t('label.useyn'),
							name: 'useYn',
							align: 'center',
							width: '5%'
						},
						{
							header: `Version`,
							name: 'updateVersion',
							width: '5%',
							align: 'center'
						},
						{
							header: this.$t('label.writer'),
							name: 'updateUserId',
							width: '5%'
						},
						{
							header: this.$t('label.created.date'),
							name: 'updateTimestamp',
							align: 'center',
							width: '10%',
							formatter: obj => changeDateFormat(obj.value)
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['id'],
			publishUrl: '/api/entity/interface/publish',
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'publish', isUse: true },
				{ id: 'cancelPublish', isUse: true },
				{ id: 'reference', isUse: true, className: `com.inzent.yellowpage.model.InterfaceMeta` },
				{ id: 'metaHistory', isUse: true, className: `com.inzent.yellowpage.model.InterfaceMeta`, entityIdKey: `id` },
				{ id: 'publishLog', isUse: true, resourceType: `I`, resourceIdKey: `id` }
			],
			detailTabList: [
				{
					id: 'interfaceAdd',
					type: 'custom',
					label: this.$t('label.interface.id'),
					url: '/interface/interfaceAdd'
				},
				{
					type: 'bundle',
					label: this.$t('label.basicInfo'),
					url: '/interface/BasicInfo'
				},
				{
					type: 'bundle',
					label: this.$t('label.model.information'),
					url: '/interface/ModelRecordInfo'
				},
				{ type: 'bundle', url: '/interface/ExtendInfo', label: this.$t('label.extendInfo') },
				{
					id: `useInfo`,
					type: 'bundle',
					label: this.$t('label.useInfo'),
					url: '/interface/UseInfo',
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('label.writer'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('label.created.date'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: 'Version',
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'publishStatus',
									label: this.$t('label.publishStatus'),
									readonly: true,
									formatter: info => {
										return 'M' === info
											? 'MAKE'
											: 'R' === info
											? 'REQUEST'
											: 'C' === info
											? 'CANCEL'
											: 'A' === info
											? 'APPROVE'
											: 'D' === info
											? 'DONE'
											: '';
									}
								},
								{
									type: 'text',
									vModel: 'publishId',
									label: this.$t('label.publish.id'),
									readonly: true,
									clickEvt: this.goPublishLogPage
								},
								{
									type: 'text',
									vModel: 'publishDateTime',
									label: this.$t('label.publishTimestamp.request'),
									readonly: true,
									formatter: value => changeDateFormat(value),
									clickEvt: this.goPublishLogPage
								},
								{
									type: 'text',
									vModel: 'publishTimestamp',
									label: this.$t('label.publishTimestamp.complete'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'publishMessage',
									label: this.$t('label.publishMessage'),
									readonly: true
								}
							]
						]
					]
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	},
	methods: {
		goPublishLogPage() {
			const useInfoComp = this.$refs.detail.$refs.component[this.$refs.detail.$refs.component.length - 1];

			const { id, publishId, publishDateTime } = useInfoComp.detailData;

			if (!id || !publishId || !publishDateTime) {
				this.$alert({ type: 'warn', message: this.$t('msg.no.data') });
				return;
			}

			openNewTab(`105000`, () => {
				localStorage.setItem(
					`searchObj`,
					JSON.stringify({
						dateType: `P`,
						fromDateTime: publishDateTime,
						publishId,
						resourceId: id,
						resourceType: `I`,
						_pageSize: 10
					})
				);
			});
		}
	}
};
</script>
