<template>
	<div id="panel" ref="panel" :class="panelClass">
		<div class="panel-dialog">
			<div class="panel-content" :style="panelContentStyle">
				<div class="resize-bar"></div>

				<header id="panel-header" class="panel-header sub-bar">
					<a
						href="javascript:void(0);"
						class="btn btn-icon backBtn"
						:title="$t('label.goBack')"
						@click="goPreviousPanel"
						v-if="'modify' === panelMode"
						><i class="icon-left"></i>
					</a>
					<h2 class="sub-bar-tit" :style="{ width: `calc(100% - ${'modify' === panelMode ? 170 : 125}px )` }">
						{{ subBarTitle }}
						<span
							class="sub-bar-selected-tit ellipsis"
							:title="selectedInfoTitle ? selectedInfoTitle.substr(3) : ``"
							:style="{ width: `calc(100% - ${'modify' === panelMode ? 80 : 65}px )` }"
						>
							{{ selectedInfoTitle }}
						</span>
					</h2>
					<div class="ml-auto">
						<button
							type="button"
							class="btn-change-layout-horizon btn-icon"
							@click="changeLayout(`vertical`)"
							:title="$t(`label.detail.horizontal.view`)"
						>
							<img :src="getMenuIconUrl(`horizontal`)" class="center-block" />
						</button>
						<button
							type="button"
							class="btn-change-layout-vertical btn-icon"
							@click="changeLayout(`horizon`)"
							:title="$t(`label.detail.vertical.view`)"
						>
							<img :src="getMenuIconUrl(`vertical`)" class="center-block" />
						</button>
						<button type="button" class="btn-icon" @click="expandCollapsePanel" :title="$t('label.expand')">
							<i class="icon-expand"></i>
						</button>
						<button type="button" class="btn-icon" @click="closePanel" :title="$t('label.close')"><i class="icon-close"></i></button>
					</div>
				</header>

				<ul class="nav nav-tabs flex-shrink-0">
					<li class="nav-item" v-for="(tabObj, index) in qnaTabList" :key="tabObj.id">
						<a
							href="javascript:void(0);"
							:class="{ 'nav-link': true, active: selectedIdx === index }"
							@click="changeTab(index)"
							v-text="tabObj.label"
						>
						</a>
					</li>
					<li class="nav-item">
						<a
							href="javascript:void(0);"
							:class="{ 'nav-link': true, active: selectedIdx === qnaTabList.length }"
							@click="changeTab(qnaTabList.length)"
						>
							{{ $t('label.executionResult') }}
						</a>
					</li>
				</ul>
				<!-- tab-pane -->
				<div class="panel-body tab-content">
					<div
						:class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === idx }"
						v-for="(detailInfo, idx) in qnaTabList"
						:key="idx"
					>
						<DetailBasic
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-if="'basic' === detailInfo.type"
						/>
						<DetailProperty
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'property' === detailInfo.type"
						/>
						<DetailGrid
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'grid' === detailInfo.type"
						/>
						<DetailCustom
							ref="component"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'custom' === detailInfo.type"
						/>
						<component
							ref="component"
							:is="require(`@/views/detail${detailInfo.url}.vue`).default"
							:detailInfo="detailInfo"
							:detailData="detailData"
							:mode="panelMode"
							v-else-if="'bundle' === detailInfo.type"
						/>
					</div>
					<DetailResult
						:class="{ 'tab-pane': true, active: selectedIdx === qnaTabList.length && showResultTab }"
						:httpReqResult="httpReqResult"
					/>
				</div>

				<footer id="panel-footer" class="panel-footer sub-bar">
					<div class="ml-auto" v-if="'detail' === panelMode || 'done' === panelMode">
						<a href="javascript:void(0);" class="btn" @click="publish(`create`)" v-if="publishBtn && editor">
							<i class="icon-approve"></i>{{ $t('label.publish') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="publish(`cancel`)" v-if="cancelPublishBtn && editor">
							<i class="icon-x"></i>{{ $t('label.publish.cancel') }}
						</a>
						<a href="javascript:void(0);" class="btn btn-primary" @click="exportExcel" v-if="exportExcelBtn && viewer">
							<i class="icon-result"></i><span class="hide">{{ $t('label.excel.download') }}</span>
						</a>
						<a href="javascript:void(0);" class="btn" @click="removeInfo" v-if="deleteBtn && editor">
							<i class="icon-delete"></i>{{ $t('label.delete') }}
						</a>
						<a href="javascript:void(0);" class="btn" @click="goModifyPanel" v-if="updateBtn && editor">
							<i class="icon-edit"></i>{{ $t('label.update') }}
						</a>
						<a href="javascript:void(0);" class="btn btn-primary" @click="restoreMeta" v-if="restoreBtn && editor">
							<i class="icon-edit"></i>{{ $t('label.restore') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'add' === panelMode || 'clone' === panelMode">
						<a href="javascript:void(0);" class="btn btn-primary" @click="saveInfo" v-if="insertBtn && editor">
							<i class="icon-plus"></i>{{ $t('label.insert') }}
						</a>
					</div>
					<div class="ml-auto" v-else-if="'modify' === panelMode" @click="updateInfo">
						<a href="javascript:void(0);" class="btn btn-primary" v-if="updateBtn && editor">
							<i class="icon-edit"></i>{{ $t('label.update') }}
						</a>
					</div>
				</footer>
			</div>
		</div>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import { mapState, mapGetters } from 'vuex';
import Detail from '@/components/detail/Detail.vue';
import { parseFlattenObj } from '@/utils/common.js';

export default {
	name: 'Detail',
	extends: Detail,
	computed: {
		...mapGetters(['isUserAdmin']),
		...mapState(['userInfo']),
		addMode() {
			return 'add' === this.panelMode || 'clone' === this.panelMode;
		}
	},
	data() {
		return {
			qnaTabList: []
		};
	},
	watch: {
		panelMode: {
			immediate: true,
			handler() {
				let label = null;

				if (`add` === this.panelMode || 0 === this.selectedIdx) {
					label = 'first';
				} else if (this.selectedIdx === this.qnaTabList.length - 1) {
					label = 'last';
				} else if (0 < this.qnaTabList.length && this.qnaTabList[this.selectedIdx]) {
					label = this.qnaTabList[this.selectedIdx].label;
				}

				this.qnaTabList = `add` === this.panelMode ? this.tabList.filter(info => `Answer` !== info.id) : cloneDeep(this.tabList);

				if (`first` === label || null === label) {
					this.selectedIdx = 0;
				} else if (`last` === label) {
					this.selectedIdx = this.qnaTabList.length;
				} else {
					this.selectedIdx = this.qnaTabList.findIndex(info => info.label === label);
				}

				this.$nextTick(() => {
					if (0 === Object.keys(this.detailData).length) return;
					this.$set(this.detailData, cloneDeep(this.detailData));
				});
			}
		}
	},
	methods: {
		loadData(callBack) {
			this.$refs.component.forEach(comp => {
				if (comp.getData) this.tabAllData = merge(this.tabAllData, comp.getData());
			});

			this.tabAllData.pk = {
				questionCreateTimestamp: this.detailData[`pk.questionCreateTimestamp`]
			};

			if (callBack) callBack();
		},
		setData(result, message, successCallback, type = null) {
			const resultState = 'ok' === result.data.result;

			this.httpReqResult = {
				state: resultState,
				content: resultState ? result.data.response : result.data.error
			};

			this.selectedIdx = this.qnaTabList.length;
			this.showResultTab = true;
			this.tabAllData = {};

			if (!resultState) return;

			if (result.data.object) {
				this.detailData = parseFlattenObj(cloneDeep(result.data.object));

				if ('add' === this.panelMode || 'clone' === this.panelMode) {
					this.cloneInfo = parseFlattenObj(cloneDeep(result.data.object));
				}
			}

			if (`delete` !== type) this.panelMode = 'done';

			this.$alert({ type: 'normal', message: this.$t(message) });

			this.$emit('search');

			if (successCallback) successCallback();
		}
	}
};
</script>
