<template>
	<Modal :settingInfo="settingInfo" @hideModal="hideModal">
		<template slot="modal-header">
			<h2
				class="modal-title"
				@click="modalInfo.titleClickEvt ? modalInfo.titleClickEvt() : null"
				:style="modalInfo.titleClickEvt ? { cursor: 'pointer', 'text-decoration': 'underline', color: '#007bff' } : null"
			>
				{{ modalInfo.title }}
			</h2>
			<button type="button" class="btn-icon" aria-label="Close" @click="hideModal">
				<i class="icon-close"></i>
			</button>
		</template>
		<template slot="modal-body">
			<component :is="content" ref="content" v-for="(content, idx) in contentList" :key="idx" />
		</template>
		<template slot="modal-footer">
			<component :is="button" ref="button" v-for="(button, idx) in buttonList" :key="idx" />
			<button type="button" class="btn" @click="hideModal">{{ $t('label.close') }}</button>
		</template>
	</Modal>
</template>

<script>
import Modal from '@/components/Modal.vue';
import { MenuClickEventBus } from '@/utils/common';

export default {
	name: 'CommonModal',
	components: {
		Modal
	},
	data() {
		return {
			showModal: false,
			modalInfo: null
		};
	},
	computed: {
		contentList() {
			return this.modalInfo.contentList;
		},
		buttonList() {
			return this.modalInfo.buttonList;
		},
		settingInfo() {
			return {
				isHeader: true,
				showModal: this.showModal,
				backdropMode: this.modalInfo.backdropMode,
				modalWidth: this.modalInfo.modalWidth,
				modalHeight: this.modalInfo.modalHeight,
				classObject: {
					'modal-dialog': true,
					'modal-fullscreen': this.modalInfo.fullscreen,
					'modal-dialog-centered': true,
					'modal-dialog-scrollable': true
				}
			};
		}
	},
	methods: {
		hideModal() {
			this.showModal = false;
			this.backdropMode = null;
			this.$destroy();
		}
	},
	created() {
		MenuClickEventBus.$on('ModalDelete', this.hideModal);
	},
	destroyed() {
		document.body.removeChild(this.$el);
	}
};
</script>
<style lang="scss">
.modal-fullscreen {
	.panel-body {
		height: 100% !important;
	}
}
</style>
