import Vue from 'vue';
import i18n from '@/i18n';
import Confirm from '@/components/modal/Confirm.vue';

Vue.use(Confirm);

export default {
	install(Vue) {
		const modalConstructor = Vue.extend(Confirm);

		Vue.prototype.$confirm = window.$confirm = function (modalInfo = {}) {
			let modalData = {
				showModal: true,
				modalInfo
			};

			const modalInstane = new modalConstructor({
				el: document.createElement('div'),
				i18n,
				data() {
					return modalData;
				}
			});

			document.body.appendChild(modalInstane.$el);
		};
	}
};
