<template>
	<div id="spinnerDiv" v-show="showSpinner" :style="{ width: spinnerWidth }">
		<div class="spinnerBg"></div>
		<div class="spinner"></div>
	</div>
</template>

<script>
import { WindowResizeEventBus, SideMenuToggleEventBus } from '@/utils/common.js';
import commonMixin from '@/mixins/commonMixin.js';

export default {
	name: 'Spinner',
	mixins: [commonMixin],
	data() {
		return {
			spinnerWidth: null,
			showSpinner: false,
			backdropMode: ''
		};
	},
	watch: {
		backdropMode() {
			if (null === this.backdropMode) return;
			this.spinnerWidth = this.getContentWidth(this.backdropMode);
		}
	},
	mounted() {
		this.spinnerWidth = this.getContentWidth(this.backdropMode);

		WindowResizeEventBus.$on('MainResize', () => {
			this.spinnerWidth = this.getContentWidth();
		});

		SideMenuToggleEventBus.$on('MainResize', () => {
			let isSidebar = document.body.classList.contains('sidebar-toggled');
			this.backdropMode = isSidebar ? 'full' : '';
			this.spinnerWidth = this.getContentWidth(isSidebar);
		});
	}
};
</script>
