<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/mappingFunction/Detail.vue';
import { HttpReq, getPrivilege } from '@/utils/common.js';

export default {
	name: 'MappingFunction',
	extends: Basic,
	components: {
		Detail
	},
	async created() {
		const httpReq = new HttpReq(`/api/page/columns`);

		const result = await httpReq.read();

		if (`ok` !== result.data.result) return;

		const config = JSON.parse(result.data.object);

		const extendColumnInfo = config.Configuration.Record.GroupHeaders.GroupHeader;

		const availableValue = extendColumnInfo[0].Column.find(column => `fieldType` === column.name).availableValue;

		availableValue.split(',').forEach(info => {
			this.fieldTypeList.push({
				key: info.split('=')[0],
				value: info.split('=')[1]
			});
		});

		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			fieldTypeList: [],

			cudUrl: `/api/entity/mappingFunction/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'text', vModel: 'id', label: this.$t('label.id'), placeholder: this.$t('msg.enterId'), regExpType: 'searchId' },
				{ type: 'text', vModel: 'name', label: this.$t('label.name'), placeholder: this.$t('msg.enterName'), regExpType: 'name' },
				{
					type: 'select',
					vModel: 'useYn',
					label: this.$t('label.useyn'),
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'List.Yn'
							},
							orderByKey: true
						},
						optionListName: 'info',
						optionFor: 'option in info',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/mappingFunction/search`,
				paging: {
					isUse: false
				},
				options: {
					columns: [
						{
							header: this.$t('label.id'),
							name: 'id',
							width: '33%'
						},
						{
							header: this.$t('label.name'),
							name: 'name',
							width: '33%'
						},
						{
							header: `Return Type`,
							name: 'returnType',
							width: '3e%',
							align: 'center',
							formatter: info => {
								return this.fieldTypeList.find(type => info.value === type.key)
									? this.fieldTypeList.find(type => info.value === type.key).value
									: info.value;
							}
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['id'],
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true }
			],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('label.basicInfo'),
					url: '/mappingFunction/BasicInfo'
				},
				{
					type: 'bundle',
					label: 'Parameter',
					url: '/mappingFunction/Parameter',
					dataKey: 'functionParameters'
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	}
};
</script>
