<template>
	<div id="serviceModal">
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item" v-for="(tabInfo, index) in detailTabList" :key="`menu_${index}`">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: index === selectedIdx }"
					@click="changeTab(index)"
					v-text="tabInfo.label"
				>
				</a>
			</li>
		</ul>
		<div class="panel-body tab-content">
			<div :class="{ 'tab-pane': true, active: index === selectedIdx }" v-for="(tabInfo, index) in detailTabList" :key="`content_${index}`">
				<component
					ref="component"
					:is="require(`@/views/detail${tabInfo.url}.vue`).default"
					:detailInfo="tabInfo"
					:detailData="detailData"
					:mode="mode"
					:existRequestMapping="existRequestMapping"
					:existResponseMapping="existResponseMapping"
					v-if="'bundle' === tabInfo.type"
				/>
				<DetailBasic ref="component" :detailInfo="tabInfo" :detailData="detailData" :mode="mode" v-else-if="'basic' === tabInfo.type" />
			</div>
		</div>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import { changeDateFormat } from '@/utils/common.js';
import DetailBasic from '@/components/detail/DetailBasic';

export default {
	name: `AddService`,
	props: {
		serviceObject: Object,
		mode: String,
		panelMode: String,
		existRequestMapping: Boolean,
		existResponseMapping: Boolean
	},
	components: {
		DetailBasic
	},
	data() {
		return {
			detailData: {},
			selectedIdx: 0,
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('label.basicInfo'),
					url: '/service/BasicInfo'
				},
				{
					type: 'bundle',
					url: '/service/ExtendInfo',
					label: this.$t('label.extendInfo')
				},
				{
					type: 'basic',
					label: this.$t('label.useInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'updateVersion',
									label: 'Version',
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('label.writer'),
									readonly: true
								}
							],
							[
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('label.created.date'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			]
		};
	},
	mounted() {
		this.detailData = cloneDeep(this.serviceObject);
	},
	methods: {
		async changeTab(idx) {
			if (`add` === this.panelMode) {
				if (this.$refs.component[idx] && `ServiceExtendInfo` === this.$refs.component[idx].$options.name) {
					await this.$refs.component[idx].generateProperty({
						serviceId: this.detailData.id,
						serverId: this.detailData.serverId,
						targetSystemId: this.detailData.targetSystemId,
						interfaceType: this.detailData.interfaceType
					});
				}
			}

			this.selectedIdx = idx;
		},
		async getData() {
			if (`add` === this.panelMode) {
				const serviceExtendInfo = this.$refs.component.find(info => `ServiceExtendInfo` === info.$options.name);

				await serviceExtendInfo.generateProperty({
					serviceId: this.detailData.id,
					serverId: this.detailData.serverId,
					targetSystemId: this.detailData.targetSystemId,
					interfaceType: this.detailData.interfaceType
				});
			}

			let tabAllData = {};

			this.$refs.component.forEach(component => {
				if (!component.getData) return;
				tabAllData = merge(tabAllData, component.getData());
			});

			return tabAllData;
		}
	}
};
</script>
