import { getNumFromStr } from '@/utils/common.js';

export default {
	methods: {
		getContentWidth(backdropMode) {
			if (0 === document.getElementsByClassName('wrap').length) return;

			if ('full' === backdropMode) return '100%';

			let adjustWidth = 0;

			const wrap = document.getElementsByClassName('wrap')[0];
			const wrapComputedStyle = getComputedStyle(wrap);

			const wrapPaddingLeft = getNumFromStr(wrapComputedStyle.getPropertyValue('padding-left'));
			const wrapPaddingRight = getNumFromStr(wrapComputedStyle.getPropertyValue('padding-right'));

			adjustWidth += wrap.clientWidth - (wrapPaddingLeft + wrapPaddingRight);

			if (992 <= window.innerWidth && document.getElementById('sidebar') && !document.body.classList.contains('sidebar-toggled')) {
				const sidebar = document.getElementById('sidebar');
				const sidebarComputedStyle = getComputedStyle(sidebar);
				const sidebarMarginLeft = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-left'));
				const sidebarMarginRight = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-right'));

				adjustWidth -= sidebar.offsetWidth + sidebarMarginLeft + sidebarMarginRight;
			}

			return `${adjustWidth}px`;
		}
	}
};
