<template>
	<div ref="login" class="loginLayout"></div>
</template>

<script>
import { mapMutations } from 'vuex';
import { getHttpUrl } from '@/utils/common.js';

export default {
	name: 'Login',
	computed: {
		loginSrc() {
			return getHttpUrl(`/api/page/login`);
		},
		passwordSrc() {
			return getHttpUrl(`/api/page/password`);
		}
	},
	created() {
		this.setUserInfo(null);
	},
	mounted() {
		const goPasswordPage = evt => {
			$(this.$refs.login)
				.empty()
				.load(`${this.passwordSrc}?userId=${evt.detail.userId}`, () => {
					document.querySelector('#password').addEventListener('goLoginPage', goLoginPage);
				});
		};

		const goLoginPage = () => {
			$(this.$refs.login)
				.empty()
				.load(this.loginSrc, () => {
					this.$refs.login.querySelector('[data-ready]').addEventListener('goPasswordPage', goPasswordPage);
					this.$refs.login.querySelector('[data-ready]').addEventListener('loginSuccess', loginSuccess);
				});
		};

		const loginSuccess = () => {
			this.$router.push({ name: 'app' }).catch(() => {});
		};

		goLoginPage();
	},
	methods: {
		...mapMutations([`setUserInfo`])
	}
};
</script>
<style lang="scss">
html {
	width: 100%;
	height: 100%;
	body {
		width: 100%;
		height: 100%;
	}
}
</style>
