<template>
	<div>
		<div class="col-lg-12">
			<div class="form-group">
				<label class="control-label">
					<span>{{ $t(`label.function`) }}</span>
				</label>
				<div class="detail-content-common" style="padding-left: 0px">
					<select class="form-control view-disabled" v-model="selectedMappingFuncInfo" @change="selectedActivityChange">
						<option
							v-for="(mappingFuncInfo, idx) in mappingFuncList"
							:key="idx"
							:value="mappingFuncInfo"
							v-text="`${mappingFuncInfo.id} - ${mappingFuncInfo.name}`"
						></option>
					</select>
				</div>
			</div>
			<div class="form-group">
				<label class="control-label">
					<span>{{ $t(`label.list`) }}</span>
				</label>
				<TuiGrid ref="tuiGrid" v-bind="options" />
			</div>
			<div class="form-group">
				<label class="control-label">
					<span>{{ $t(`label.detail`) }}</span>
				</label>
				<div class="detail-content-common" style="padding-left: 0px">
					<span>
						<input type="text" readonly="readonly" class="form-control view-disabled" v-model="selectedDetail" />
					</span>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import { Grid } from '@toast-ui/vue-grid';
import { WindowResizeEventBus, makeGridOptions, escapeHtml, HttpReq } from '@/utils/common.js';
import { convertClassToType } from '@/views/modal/mapping/ruleDetail/utils.js';

export default {
	name: 'function',
	props: {
		pageType: String,
		className: String,
		sourceRecordIdArr: Array,
		targetRecordIdArr: Object,
		rawValueInfo: Object
	},
	components: {
		TuiGrid: Grid
	},
	data() {
		return {
			mappingFuncList: [],
			activityInfo: null,
			selectedMappingFuncInfo: null,
			selectedDetail: null,
			fieldTypeList: [],
			options: {
				columns: [
					{
						header: 'Argument Type',
						name: 'parameterType',
						formatter: info => {
							return this.fieldTypeList.find(type => info.value === type.key)
								? this.fieldTypeList.find(type => info.value === type.key).value
								: info.value;
						}
					},
					{
						header: 'Argument Value',
						name: `argumentValue`,
						formatter: ({ row }) => {
							return `<div>
										<button type="button" class="btn-icon" style="width: 16px;height: 16px">
							 				<i class="icon-plus-circle"></i>
										</button>
										<span style="display: inline-block; height: 16px; vertical-align: top;">
											${escapeHtml(row.argumentValue)}
										</span>
									</div>`;
						}
					}
				],
				data: [],
				onGridMounted: evt => {
					evt.instance.on(`click`, evt => {
						if (!evt.nativeEvent.target.classList.contains(`icon-plus-circle`)) return;

						const pageType = this.pageType;

						const { parameterType, argumentValue } = this.$refs.tuiGrid.invoke(`getRow`, evt.rowKey);

						const className = this.fieldTypeList.find(type => parameterType === type.key)
							? convertClassToType(this.fieldTypeList.find(type => parameterType === type.key).value)
							: parameterType;

						const mappingExpression = argumentValue ? argumentValue : null;

						const sourceRecordIdArr = this.sourceRecordIdArr;
						const targetRecordIdArr = this.targetRecordIdArr;

						const _this = this;

						const modalInstance = this.$openModal({
							title: `Mapping Expression`,
							fullscreen: true,
							contentList: [
								{
									template: `<reference :pageType="pageType" :mappingExpression="mappingExpression" :className="className" :sourceRecordIdArr="sourceRecordIdArr" :targetRecordIdArr="targetRecordIdArr" />`,
									components: {
										reference: require('@/views/modal/mapping/ruleDetail/reference.vue').default
									},
									data() {
										return {
											pageType,
											mappingExpression,
											className,
											sourceRecordIdArr,
											targetRecordIdArr
										};
									}
								}
							],
							buttonList: [
								{
									template: `<button type="button" class="btn btn-primary" @click="click"> ${this.$t(`label.ok`)} </button>`,
									methods: {
										async click() {
											const { expression, isValidation } = await modalInstance.$refs.content[0].$children[0].getData();

											if (!isValidation) return;

											_this.$refs.tuiGrid.invoke(`setValue`, evt.rowKey, `argumentValue`, expression);

											const dataList = _this.$refs.tuiGrid.invoke(`getData`);

											_this.selectedDetail = _this.selectedMappingFuncInfo.id + `(`;

											const argumentValueArr = [];

											dataList.forEach(data => {
												argumentValueArr.push(data.argumentValue);
											});

											_this.selectedDetail += `${argumentValueArr.join(`,`)}`;

											_this.selectedDetail += `)`;

											modalInstance.hideModal();
										}
									}
								}
							]
						});
					});
				}
			}
		};
	},
	async created() {
		const httpReq = new HttpReq(`/api/page/columns`);

		const result = await httpReq.read();

		if (`ok` !== result.data.result) return;

		const config = JSON.parse(result.data.object);

		const extendColumnInfo = config.Configuration.Record.GroupHeaders.GroupHeader;

		const availableValue = extendColumnInfo[0].Column.find(column => `fieldType` === column.name).availableValue;

		availableValue.split(',').forEach(info => {
			this.fieldTypeList.push({
				key: info.split('=')[0],
				value: info.split('=')[1]
			});
		});

		this.options = makeGridOptions(this.options);
	},
	async mounted() {
		const res = await new HttpReq(`/api/mapping/expression/functions`).read({
			targetType: convertClassToType(this.className) ? convertClassToType(this.className) : this.className
		});

		this.mappingFuncList = res.data.object;

		WindowResizeEventBus.$on('MainResize', this.resize);

		this.resize();

		this.setData();
	},
	watch: {
		selectedMappingFuncInfo() {
			this.$refs.tuiGrid.invoke(`clear`);

			if (`object` === typeof this.selectedMappingFuncInfo) {
				this.$refs.tuiGrid.invoke(`resetData`, this.selectedMappingFuncInfo.functionParameters);
			}

			this.$nextTick(this.resize);
		},
		rawValueInfo: {
			immediate: true,
			deep: true,
			handler() {
				this.setData();
			}
		}
	},
	methods: {
		resize() {
			if (!this.$refs.tuiGrid) return;

			this.$nextTick(() => {
				const modalBody = document.querySelector(`#referenceModal`).parentNode;

				const adjustWidth = modalBody.clientWidth - 48 - 76;
				const adjustHeight = modalBody.clientHeight - 32 - 48 - 48 - 56 - 16 - 24 - 72;

				this.$refs.tuiGrid.invoke('setWidth', adjustWidth);
				this.$refs.tuiGrid.invoke('setHeight', adjustHeight);
			});
		},
		selectedActivityChange() {
			this.selectedDetail = null;
		},
		setData() {
			if (!this.rawValueInfo) return;

			if (0 === this.mappingFuncList.length) return;

			const { type, name, args } = this.rawValueInfo;

			if (`FunctionExpression` !== type) return;

			this.selectedDetail = name;

			for (const mappingFuncInfo of this.mappingFuncList) {
				if (name === mappingFuncInfo.id) {
					this.selectedMappingFuncInfo = mappingFuncInfo;
					break;
				}
			}

			this.$nextTick(() => {
				if (!args || 0 === args.length) return;

				args.forEach(({ expressionString }, idx) => {
					this.$refs.tuiGrid.invoke(`setValue`, idx, `argumentValue`, expressionString);
				});

				const dataList = this.$refs.tuiGrid.invoke(`getData`);
				const argumentValueArr = [];

				dataList.forEach(data => {
					argumentValueArr.push(data.argumentValue);
				});

				this.selectedDetail += `(`;
				this.selectedDetail += argumentValueArr.join(`,`);
				this.selectedDetail += `)`;
			});
		},
		getData() {
			return null === this.selectedDetail ? `` : this.selectedDetail;
		}
	}
};
</script>
