<template>
	<div class="sub-bar">
		<span v-if="null !== currentCnt && currentCnt !== totalCnt && viewer" style="margin-right: 5px">
			{{ $t('label.currentCount', { 0: currentCnt }) }} /
		</span>
		<span v-if="null !== totalCnt && viewer">{{ $t('label.totalCount', { 0: totalCnt }) }}</span>

		<div class="ml-auto form-inline m-full">
			<a href="javascript:void(0);" class="btn btn-m" @click="newTab" v-if="newTabBtn && viewer" :title="$t('label.newTab')">
				<i class="icon-info"></i><span class="hide">{{ $t('label.newTab') }}</span>
			</a>
			<span v-if="refreshBtn && viewer">
				<span v-if="!isStartRefresh">
					<select v-model="timerSeconds" class="form-control mainRefreshSelect" style="min-width: 0px; width: 80px">
						<option v-for="seconds in timerSecondsList" :value="seconds" :key="seconds">{{ seconds }}</option>
					</select>
					{{ $t('label.seconds') }}
				</span>
				<span v-else>
					<input type="text" v-model="displayTimerSeconds" class="form-control" style="min-width: 0px; width: 80px" readonly="readonly" />
					{{ $t('label.seconds') }}
				</span>
				<a href="javascript:void(0);" class="btn btn-m mainRefreshBtn" @click="refresh" :title="$t('label.refresh')">
					<i :class="{ 'icon-play': !isStartRefresh, 'icon-pause': isStartRefresh }"></i>
					<span class="hide">{{ $t('label.refresh') }}</span>
				</a>
			</span>
			<a href="javascript:void(0);" class="btn btn-m" @click="download" v-if="downloadBtn && viewer" :title="$t('label.list.output')">
				<i class="icon-result"></i><span class="hide">{{ $t('label.list.output') }}</span>
			</a>
			<a
				href="javascript:void(0);"
				class="btn btn-m"
				@click="importExcel"
				v-if="importExcelBtn && viewer"
				:title="$t('label.upload.statement')"
			>
				<i class="icon-result"></i><span class="hide">{{ $t('label.upload.statement') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn btn-m" @click="initialize" v-if="initializeBtn && viewer" :title="$t('label.initialize')">
				<i class="icon-reset"></i><span class="hide">{{ $t('label.initialize') }}</span>
			</a>
			<a
				href="javascript:void(0);"
				class="btn btn-m btn-primary"
				@click="migration"
				v-if="migrationBtn && editor"
				:title="$t('label.migration')"
			>
				<i class="icon-disk"></i>
				<span class="hide">{{ $t('label.migration') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn btn-m btn-primary" @click="add" v-if="addBtn && editor" :title="$t('label.insert')">
				<i class="icon-plus"></i><span class="hide">{{ $t('label.insert') }}</span>
			</a>
		</div>
	</div>
</template>

<script>
export default {
	name: 'btn',
	props: {
		list: Array,
		viewer: Boolean,
		editor: Boolean,
		totalCnt: String,
		currentCnt: String,
		gridInstanceInfo: Object,
		startTimerSeconds: Number
	},
	data() {
		return {
			addBtn: false,
			initializeBtn: false,
			newTabBtn: false,
			importExcelBtn: false,
			migrationBtn: false,
			refreshBtn: false,
			downloadBtn: false,
			timerSeconds: 60,
			displayTimerSeconds: 60,
			timerSecondsList: [10, 20, 30, 40, 50, 60],
			isStartRefresh: false,
			refreshIntervalId: null
		};
	},
	created() {
		this.useBtn();
	},
	beforeDestroy() {
		clearInterval(this.refreshIntervalId);
	},
	watch: {
		list: {
			deep: true,
			handler() {
				this.useBtn();
			}
		},
		gridInstanceInfo() {
			this.useBtn();
		},
		startTimerSeconds() {
			clearInterval(this.refreshIntervalId);

			this.isStartRefresh = false;

			this.timerSeconds = this.startTimerSeconds;

			this.refresh();
		}
	},
	methods: {
		initialize() {
			this.$emit('initialize');
		},
		add() {
			this.$emit('add');
		},
		newTab() {
			this.$emit('newTab');
		},
		importExcel() {
			this.$emit('importExcel');
		},
		migration() {
			this.$emit('migration');
		},
		useBtn() {
			this.list.forEach(info => {
				this[`${info.id}Btn`] = `function` === typeof info.isUse ? info.isUse(this.gridInstanceInfo) : info.isUse;
			});
		},
		refresh() {
			this.isStartRefresh = !this.isStartRefresh;

			if (this.isStartRefresh) {
				this.displayTimerSeconds = this.timerSeconds;

				this.refreshIntervalId = setInterval(() => {
					if (0 >= this.displayTimerSeconds) {
						this.displayTimerSeconds = this.timerSeconds;
						this.$emit(`refresh`);
					} else {
						--this.displayTimerSeconds;
					}
				}, 1000);
			} else {
				clearInterval(this.refreshIntervalId);
			}
		},
		download() {
			this.$emit(`download`);
		}
	}
};
</script>
