<template>
	<div class="basic">
		<Search :list="searchList" @search="search" searchType="mainBasic" ref="search" />
		<Main
			:buttonList="buttonList"
			:gridInfo="gridInfo"
			:gridSearchObj="gridSearchObj"
			:gridType="gridType"
			:viewer="viewer"
			:editor="editor"
			@rowClicked="rowClicked"
			@initialize="initialize"
			@add="add"
			@newTab="newTab"
			ref="main"
		/>
		<Detail
			v-show="isDetailView"
			:mode="detailMode"
			:pk="detailPk"
			:buttonList="detailButtonList"
			:info="detailInfo"
			:cudUrl="cudUrl"
			:viewer="viewer"
			:editor="editor"
			@search="$refs.search.search()"
			ref="detail"
		/>
	</div>
</template>

<script>
import Basic from '@/components/main/Basic.vue';
import Search from '@/components/Search.vue';
import Main from '@/components/Main.vue';
import Detail from '@/views/detail/mapping/Detail.vue';
import { getPrivilege } from '@/utils/common.js';

export default {
	name: 'Mapping',
	extends: Basic,
	components: {
		Search,
		Main,
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	data() {
		return {
			cudUrl: `/api/entity/mapping/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'text', vModel: 'id', label: this.$t('label.mapping.id'), placeholder: this.$t('msg.enterId') },
				{ type: 'text', vModel: 'name', label: this.$t('label.mapping.name'), placeholder: this.$t('msg.enterName') },
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/mapping/search`,
				totalCntUrl: `/api/entity/mapping/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'id',
							header: this.$t('label.mapping.id'),
							width: '20%'
						},
						{
							name: 'name',
							header: this.$t('label.mapping.name'),
							width: '20%'
						},
						{
							name: 'serverId',
							header: this.$t('label.privilege'),
							width: '20%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//detail
			detailPk: ['mappingId'],
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true }
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	},
	methods: {
		initDetail(mode, info) {
			if (this.isDetailView) {
				this.isDetailView = false;
				this.detailInfo = null;
				this.detailMode = null;
			}

			this.$nextTick(() => {
				this.isDetailView = true;
				this.detailInfo = info;
				this.detailMode = mode;
			});
		}
	}
};
</script>
