<script>
import Basic from '@/components/main/Basic.vue';
import { getPrivilege } from '@/utils/common.js';

export default {
	name: 'Property',
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			cudUrl: `/api/entity/property/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'dataList',
					vModel: 'pk.propertyId',
					label: this.$t('label.id'),
					regExpType: 'searchId',
					placeholder: this.$t('msg.enterId'),
					optionInfo: {
						url: '/api/entity/property/group/search',
						optionListName: 'propertyIdList',
						optionFor: 'option in propertyIdList',
						optionValue: 'option',
						optionText: 'option'
					}
				},
				{ type: 'text', vModel: 'pk.propertyKey', label: this.$t('label.property.key'), placeholder: this.$t('msg.enterData') },
				{
					type: 'text',
					vModel: 'propertyValue',
					label: this.$t('label.property.value'),
					placeholder: this.$t('msg.enterData')
				},
				{
					type: 'select',
					refs: 'cacheYn',
					vModel: 'cacheYn',
					label: this.$t('label.cacheYn'),
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'List.Yn'
							},
							orderByKey: true
						},
						optionListName: 'cacheYnList',
						optionFor: 'option in cacheYnList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'select',
					vModel: 'requireYn',
					label: this.$t('label.requiredYn'),
					optionInfo: {
						url: `/api/page/properties`,
						params: {
							pk: {
								propertyId: 'List.Yn'
							},
							orderByKey: true
						},
						optionListName: 'requireYnList',
						optionFor: 'option in requireYnList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'text',
					vModel: 'propertyDesc',
					label: this.$t('label.desc'),
					placeholder: this.$t('msg.enterDesc'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/property/search`,
				totalCntUrl: `/api/entity/property/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('label.id'),
							name: 'pk.propertyId',
							width: '15%'
						},
						{
							header: this.$t('label.property.key'),
							name: 'pk.propertyKey',
							width: '22%'
						},
						{
							header: this.$t('label.property.value'),
							name: 'propertyValue',
							width: '17%',
							formatter: info => {
								let rtnValue = '';

								if ('Y' === info.row.cipherYn && 'Y' === info.row.cacheYn) {
									for (let i = 0; i < info.value.length; i++) {
										rtnValue += '*';
									}
								} else {
									rtnValue = info.value;
								}

								return rtnValue;
							}
						},
						{
							header: this.$t('label.cacheYn'),
							name: 'cacheYn',
							width: '10%',
							align: `center`,
							formatter: ({ row }) => (`Y` === row.cacheYn ? `Yes` : `No`)
						},
						{
							header: this.$t('label.requiredYn'),
							name: 'requireYn',
							width: '10%',
							align: `center`,
							formatter: ({ row }) => (`Y` === row.requireYn ? `Yes` : `No`)
						},
						{
							header: this.$t('label.desc'),
							name: 'propertyDesc',
							width: '26%'
						},
						{ name: 'cipherYn', hidden: true }
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['pk.propertyId', 'pk.propertyKey'],
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'dump', isUse: true }
			],
			detailTabList: [
				{
					type: 'bundle',
					url: '/property/BasicInfo',
					label: this.$t('label.basicInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'pk.propertyId',
									label: this.$t('label.id'),
									isPK: true,
									regExpType: 'id'
								},
								{
									type: 'text',
									vModel: 'pk.propertyKey',
									label: this.$t('label.property.key'),
									isPK: true
								},
								{
									type: 'select',
									vModel: 'cipherYn',
									label: this.$t('label.cipherYn'),
									isRequired: true,
									val: 'N',
									optionInfo: {
										url: `/api/page/properties`,
										params: {
											pk: {
												propertyId: 'List.Yn'
											},
											orderByKey: true
										},
										optionListName: 'cipherYnList',
										optionFor: 'option in cipherYnList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								}
							],
							[
								{
									type: 'select',
									vModel: 'requireYn',
									label: this.$t('label.requiredYn'),
									isRequired: true,
									val: 'N',
									optionInfo: {
										url: `/api/page/properties`,
										params: {
											pk: {
												propertyId: 'List.Yn'
											},
											orderByKey: true
										},
										optionListName: 'requireYnList',
										optionFor: 'option in requireYnList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'select',
									refs: 'cacheYn',
									vModel: 'cacheYn',
									label: this.$t('label.cacheYn'),
									isRequired: true,
									val: 'N',
									optionInfo: {
										url: `/api/page/properties`,
										params: {
											pk: {
												propertyId: 'List.Yn'
											},
											orderByKey: true
										},
										optionListName: 'cacheYnList',
										optionFor: 'option in cacheYnList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'password',
									vModel: 'propertyValue',
									label: this.$t('label.property.value'),
									isRequired: true
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'propertyDesc',
									label: this.$t('label.desc'),
									regExpType: 'desc'
								}
							]
						]
					]
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	}
};
</script>
