<template>
	<date-range-picker class="input-daterange" v-model="obj[vModel]" v-bind="options" v-if="isShow">
		<template #input="picker"> {{ getDisplayDate(picker.startDate) }} </template>
	</date-range-picker>
</template>

<script>
import { mapState } from 'vuex';
import DateRangePicker from 'vue2-daterange-picker';
import moment from 'moment';
import merge from 'lodash/merge';

export default {
	name: 'dateRange',
	components: {
		DateRangePicker
	},
	props: {
		vModel: String,
		readonly: {
			type: Boolean,
			default: false
		},
		val: {
			type: String,
			default: null
		},
		dateRangeType: String,
		customOptions: Object
	},
	data() {
		return {
			obj: {},
			isShow: false,
			options: {},
			fixDateFormat: 'YYYY-MM-DD HH:mm:ss'
		};
	},
	created() {
		for (const key in this.$data.obj) {
			this.$delete(this.$data.obj, key);
		}

		this.$set(this.obj, this.vModel, { startDate: null, endDate: null });

		this.setData(this.val);

		this.options = {
			'locale-data': this.dateLocaleData,
			opens: 'right',
			singleDatePicker: true,
			'show-dropdowns': false,
			'auto-apply': false,
			'time-picker': true,
			'time-picker-seconds': true,
			appendToBody: false,
			disabled: this.readonly
		};

		if (this.customOptions) {
			this.options = merge(this.options, this.customOptions);
		}
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	computed: {
		...mapState(['dateLocaleData'])
	},
	methods: {
		getDate(val) {
			if (val instanceof Date) {
				return val;
			} else if (val) {
				let dateStr = val.match(/[0-9]{4}-[0-9]{2}-[0-9]{2}\s[0-9]{2}:[0-9]{2}:[0-9]{2}/)[0];
				dateStr = dateStr.replace(/-/g, '/');

				return new Date(dateStr);
			} else {
				const date = new Date();

				const time = 'to' === this.dateRangeType ? 59 : 0;

				date.setHours('to' === this.dateRangeType ? 23 : 0);
				date.setMinutes(time);
				date.setSeconds(time);
				date.setMilliseconds(time);

				return date;
			}
		},
		getDisplayDate(date) {
			return moment(date).format(this.fixDateFormat);
		},
		setData(val) {
			this.isShow = false;

			this.obj[this.vModel].startDate = this.getDate(val);
			this.obj[this.vModel].endDate = this.getDate(val);
			this.$nextTick(() => {
				this.isShow = true;
			});
		},
		getData() {
			return {
				[this.vModel]: moment(this.obj[this.vModel].startDate).format(this.fixDateFormat)
			};
		}
	}
};
</script>
