<template>
	<div class="userPrivileges">
		<div class="btn-group" role="group" style="margin-bottom: 10px" v-if="!isReadOnlyMode">
			<a class="btn admin" @click="allAdmin" style="font-size: 0.8rem; min-width: auto">
				{{ $t('label.privilege.admin') }}
			</a>
			<a class="btn member" @click="allMember" style="font-size: 0.8rem; min-width: auto">
				{{ $t('label.privilege.member') }}
			</a>
			<a class="btn none" @click="allNone" style="font-size: 0.8rem; min-width: auto">
				{{ $t('label.privilege.none') }}
			</a>
		</div>
		<table class="table">
			<colgroup>
				<col width="5%" />
				<col width="5%" />
				<col width="45%" />
			</colgroup>
			<thead>
				<tr>
					<th scope="col">{{ $t('label.user.privilege.grade') }}</th>
					<th scope="col">{{ $t(`label.privilege.type`) }}</th>
					<th scope="col">{{ $t('label.privilege') }}</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(elm, idx) in totalUserPrivileges" :key="idx">
					<td class="text-center view-disabled align-middle authGrade">
						<select
							:class="{ 'form-control': true, 'view-disabled': true, [`${elm.privilegeName.toLowerCase()}`]: true }"
							v-model="elm.privilegeName"
							:readonly="isReadOnlyMode || elm.readonly"
						>
							<option value="None">{{ $t('label.privilege.none') }}</option>
							<option value="Admin">{{ $t('label.privilege.admin') }}</option>
							<option value="Member">{{ $t('label.privilege.member') }}</option>
						</select>
					</td>
					<td class="px-1">
						<input
							type="text"
							class="form-control view-disabled"
							disabled="disabled"
							:value="
								elm.privilegeType === isSystem
									? $t(`label.privilege.type.system`)
									: elm.privilegeType === isBusiness
									? $t(`label.privilege.type.business`)
									: $t('label.role')
							"
						/>
					</td>
					<td class="px-1"><input type="text" class="form-control view-disabled" v-model="elm.privilegeId" disabled="disabled" /></td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import { mapState, mapGetters } from 'vuex';

export default {
	name: 'UserDetailPrivileges',
	props: {
		detailData: Object,
		mode: String
	},
	data() {
		return {
			totalUserPrivileges: [],
			isSystem: 'S',
			isBusiness: 'b'
		};
	},
	watch: {
		detailData() {
			this.setTotalUserPrivileges();
		}
	},
	computed: {
		...mapState(['userInfo']),
		...mapGetters(['isUserAdmin']),
		isReadOnlyMode() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	methods: {
		getAuthFix(authInfo) {
			if (this.isUserAdmin) {
				return false;
			} else if (
				this.userInfo.totalUserPrivileges.find(userPrivilege => {
					return `UserEditor` === userPrivilege.privilegeId && `undefined` === typeof userPrivilege.onlyFrontPrivilege;
				})
			) {
				return false;
			} else {
				const authId = authInfo.privilegeId;
				const isFix = !authInfo.role
					? this.userInfo.userPrivileges.find(privilegeInfo => privilegeInfo.admin && privilegeInfo.pk.privilegeId === authId)
					: this.userInfo.userRoles.find(roleInfo => roleInfo.admin && roleInfo.pk.roleId === authId);

				return isFix ? false : true;
			}
		},
		setTotalUserPrivileges() {
			if (!this.detailData.totalUserPrivileges) return;

			this.totalUserPrivileges = cloneDeep(this.detailData.totalUserPrivileges).map(info => {
				info.privilegeName = 'None';
				return info;
			});

			if ('add' === this.mode) {
				this.totalUserPrivileges.forEach(info => {
					info.admin = false;
					info.member = false;
					info.privilegeName = 'None';
					info.readonly = this.getAuthFix(info);
				});
			} else {
				this.totalUserPrivileges.forEach(info => {
					info.privilegeName = info.admin ? 'Admin' : info.member ? 'Member' : 'None';
					info.readonly = this.getAuthFix(info);
				});
			}
		},
		allAdmin() {
			this.totalUserPrivileges.forEach(info => {
				if (this.getAuthFix(info)) return;
				info.privilegeName = 'Admin';
			});
		},
		allMember() {
			this.totalUserPrivileges.forEach(info => {
				if (this.getAuthFix(info)) return;
				info.privilegeName = 'Member';
			});
		},
		allNone() {
			this.totalUserPrivileges.forEach(info => {
				if (this.getAuthFix(info)) return;
				info.privilegeName = 'None';
			});
		},
		getData() {
			return {
				totalUserPrivileges: this.totalUserPrivileges.map(info => {
					if (`None` === info.privilegeName) {
						info.admin = false;
						info.member = false;
					} else if (`Member` === info.privilegeName) {
						info.admin = false;
						info.member = true;
					} else {
						info.admin = true;
						info.member = false;
					}

					return info;
				})
			};
		}
	}
};
</script>
