<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-wrap">
				<div class="form-table-head">
					<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('label.insert')">
						<i class="icon-plus-circle"></i>
					</button>
					<label class="col" @click="goPropertyPage">
						<span class="link-new-tab">{{ this.$t('label.id') }}</span>
					</label>
					<label class="col" @click="goPropertyPage">
						<span class="link-new-tab">{{ this.$t('label.system.property') }}</span>
					</label>
					<label class="col" @click="goPropertyPage">
						<span class="link-new-tab">{{ this.$t('label.desc') }}</span>
					</label>
				</div>
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('label.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<dataList
								:ref="`component${bodyIndex}`"
								vModel="pk.keyId"
								:classObject="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj['pk.keyId']"
								:readonly="readonly"
								:optionInfo="keyIdOptionInfo"
								@changeEvt="changeEvt($event, bodyIndex)"
								regExpType="id"
								@setLengthCnt="info => setLengthCnt({ [`${`pk.keyId`}_${bodyIndex}`]: info[`pk.keyId`] })"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ curLengthCnt[`${`pk.keyId`}_${bodyIndex}`] }} / {{ maxLengthCnt[`pk.keyId`] }})
							</span>
						</div>
					</div>

					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="property"
								:classObject="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj['property']"
								:readonly="readonly"
								regExpType="value"
								@setLengthCnt="info => setLengthCnt({ [`property_${bodyIndex}`]: info[`property`] })"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ curLengthCnt[`property_${bodyIndex}`] }} / {{ maxLengthCnt[`property`] }})
							</span>
						</div>
					</div>

					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="description"
								:classObject="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj['description']"
								:readonly="readonly"
								regExpType="desc"
								@setLengthCnt="info => setLengthCnt({ [`description_${bodyIndex}`]: info[`description`] })"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ curLengthCnt[`description_${bodyIndex}`] }} / {{ maxLengthCnt[`description`] }})
							</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import isNil from 'lodash/isNil';
import isEmpty from 'lodash/isEmpty';
import { parseHierarchyObj, parseFlattenObj, getUUID, HttpReq, getRegExpInfo, openNewTab } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: 'ServiceExtendInfo',
	extends: DetailProperty,
	data() {
		return {
			serviceId: null,
			isInitProperties: false,
			servicePropertyList: [],
			keyIdOptionInfo: {
				optionFor: 'option in []',
				optionValue: 'option.pk.propertyKey',
				optionText: 'option.pk.propertyKey'
			},
			regExpInfo: {
				'pk.keyId': 'id',
				description: 'desc',
				property: 'value'
			},
			curLengthCnt: {},
			maxLengthCnt: {}
		};
	},
	async created() {
		const httpReq = new HttpReq(`/api/page/properties`);

		const result = await httpReq.read({ pk: { propertyId: 'Property.Service' }, orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.servicePropertyList = result.data.object;

		const servicePropertyList = JSON.stringify(this.servicePropertyList).replace(/"/g, `'`);

		this.keyIdOptionInfo.optionFor = `option in ${servicePropertyList}`;

		for (const [key, value] of Object.entries(this.regExpInfo)) {
			this.maxLengthCnt[key] = getRegExpInfo(value).maxLength;
		}

		if (this.detailData.id) this.setPropertyList();
	},
	methods: {
		async generateProperty({ serviceId, serverId, targetSystemId, interfaceType }) {
			if (null === this.serviceId) {
				this.propertyList = [];
			}

			if (null !== this.serviceId && serviceId !== this.serviceId) {
				this.propertyList = this.propertyList.filter(info => `undefined` === typeof info.isGenerate);
				this.isInitProperties = false;
			}

			if (this.isInitProperties) return;

			this.isInitProperties = true;

			this.serviceId = serviceId;

			const httpReq = new HttpReq(`/api/entity/service/generateProperties`);

			const result = await httpReq.read({ id: serviceId, serverId, targetSystemId, interfaceType });

			if (`ok` !== result.data.result) return;

			const serviceProperties = result.data.object.serviceProperties;

			serviceProperties.forEach((info, index) => {
				const cloneInfo = parseFlattenObj(info);

				for (const key in this.regExpInfo) {
					this.setLengthCnt({ [`${key}_${index}`]: cloneInfo[key] ? cloneInfo[key].length : 0 });
				}

				this.propertyList.unshift({
					idx: getUUID(),
					'pk.keyId': info.pk.keyId,
					name: info.name,
					property: info.property,
					description: info.description,
					isGenerate: true
				});
			});
		},
		getData() {
			const rtnArr = [];

			for (let i = 0; i < this.propertyList.length; i++) {
				let rtnObj = {};

				this.$refs[`component${i}`].forEach(comp => {
					rtnObj = merge(rtnObj, comp.getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				serviceProperties: rtnArr
			};
		},
		addProperty() {
			for (const key in this.regExpInfo) {
				this.setLengthCnt({ [`${key}_${this.propertyList.length}`]: 0 });
			}

			this.propertyList.push({
				idx: getUUID(),
				'pk.keyId': '',
				name: '',
				property: '',
				description: ''
			});
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);

			this.$nextTick(() => {
				this.curLengthCnt = {};

				this.propertyList.forEach((property, bodyIndex) => {
					let rtnObj = {};

					this.$refs[`component${bodyIndex}`].forEach(comp => {
						rtnObj = merge(rtnObj, comp.getData());
					});

					for (const key in rtnObj) {
						this.setLengthCnt({ [`${key}_${bodyIndex}`]: isNil(rtnObj[key]) ? 0 : String(rtnObj[key]).length });
					}
				});
			});
		},
		async setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			if ('undefined' !== typeof this.detailData.serviceProperties) {
				this.propertyList = [...this.detailData.serviceProperties];

				this.propertyList.forEach((propertyInfo, index) => {
					const clonePropertyInfo = parseFlattenObj(propertyInfo);
					propertyInfo.idx = getUUID();

					for (const key in this.regExpInfo) {
						this.setLengthCnt({ [`${key}_${index}`]: clonePropertyInfo[key] ? clonePropertyInfo[key].length : 0 });
					}
				});
			} else {
				this.propertyList = [];
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		changeEvt(info, index) {
			const filterServicePropertyList = this.servicePropertyList.filter(serviceInfo => info.value === serviceInfo.pk.propertyKey);

			if (0 === filterServicePropertyList.length) return;

			const serviceInfo = filterServicePropertyList[0];

			this.propertyList[index]['pk.keyId'] = serviceInfo.pk.propertyKey;
			this.propertyList[index]['property'] = serviceInfo.propertyValue;
			this.propertyList[index]['description'] = serviceInfo.propertyDesc;
		},
		goPropertyPage() {
			openNewTab(`205000`);
		}
	}
};
</script>
