import Vue from 'vue';
import Spinner from '@/components/Spinner.vue';

Vue.use(Spinner);

export default {
	install(Vue) {
		let spinnerConstructor = Vue.extend(Spinner);
		let spinnerInstane = null;

		Vue.prototype.$startSpinner = window.$startSpinner = function (backdropMode) {
			let spinnerData = {
				backdropMode: backdropMode,
				showSpinner: true
			};

			if (spinnerInstane) {
				for (let key in spinnerData) {
					spinnerInstane[key] = spinnerData[key];
				}
				return;
			}

			spinnerInstane = new spinnerConstructor({
				el: document.createElement('div'),
				data() {
					return spinnerData;
				}
			});

			document.body.appendChild(spinnerInstane.$el);
		};

		Vue.prototype.$stopSpinner = window.$stopSpinner = function (callbackFunc) {
			if (callbackFunc) callbackFunc();

			spinnerInstane.showSpinner = false;
			spinnerInstane.backdropMode = null;
		};
	}
};
