<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/user/Detail.vue';
import moment from 'moment';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'User',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	data() {
		return {
			cudUrl: '/api/entity/user/object',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'userId',
					label: this.$t('label.id'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'userName',
					label: this.$t('label.name'),
					placeholder: this.$t('msg.enterName')
				},
				{
					type: 'text',
					vModel: 'department',
					label: this.$t('label.user.department'),
					placeholder: this.$t('msg.enterData')
				},
				{
					type: 'text',
					vModel: 'telephone',
					label: this.$t('label.telephone.number'),
					placeholder: this.$t('msg.enterData')
				},
				{
					type: 'text',
					vModel: 'mobilephone',
					label: this.$t('label.mobile.phone.number'),
					placeholder: this.$t('msg.enterData')
				},
				{
					type: 'text',
					vModel: 'email',
					label: this.$t('label.email'),
					placeholder: this.$t('msg.enterData')
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/user/search`,
				totalCntUrl: `/api/entity/user/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('label.id'),
							name: 'userId',
							width: '20%'
						},
						{
							header: this.$t('label.name'),
							name: 'userName',
							width: '20%'
						},
						{
							header: this.$t('label.user.department'),
							name: 'department',
							width: '15%'
						},
						{
							header: this.$t('label.telephone.number'),
							name: 'telephone',
							width: '15%'
						},
						{
							header: this.$t('label.mobile.phone.number'),
							name: 'mobilephone',
							width: '15%'
						},
						{
							header: this.$t('label.email'),
							name: 'email',
							width: '15%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['userId'],
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true }
			],
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('label.basicInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'userId',
									label: this.$t('label.id'),
									isPK: true,
									regExpType: 'id'
								},
								{
									type: 'text',
									vModel: 'userName',
									label: this.$t('label.name'),
									isRequired: true
								},
								{
									type: 'text',
									vModel: 'department',
									label: this.$t('label.user.department')
								},
								{
									type: 'text',
									vModel: 'telephone',
									label: this.$t('label.telephone.number')
								}
							],
							[
								{
									type: 'text',
									vModel: 'mobilephone',
									label: this.$t('label.mobile.phone.number')
								},
								{
									type: 'text',
									vModel: 'email',
									label: this.$t('label.email')
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('label.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'updateUserId',
									label: `${this.$t(`label.update`)} ${this.$t(`label.user`)} ${this.$t(`label.id`)}`,
									readonly: true
								}
							]
						]
					]
				},
				{
					type: 'basic',
					label: this.$t('label.authenticationInfo'),
					content: [
						[
							[
								{
									type: 'select',
									vModel: 'userDisableYn',
									label: this.$t('label.disabled'),
									val: 'N',
									optionInfo: {
										url: '/api/page/properties',
										params: {
											pk: {
												propertyId: 'List.Yn'
											},
											orderByKey: true
										},
										optionListName: 'openWindowList',
										optionFor: 'option in openWindowList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'text',
									vModel: 'loginIp',
									label: this.$t('label.last.login.address'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'allowedIp',
									label: this.$t('label.user.allowed.ip')
								},
								{
									type: 'password',
									vModel: 'newPassword',
									label: this.$t('label.user.newPassword'),
									isRequired: true,
									warning: true,
									warningLabel: this.$t('msg.user.validationPassCheck'),
									regExpType: 'password'
								}
							],
							[
								{
									type: 'singleDaterange',
									vModel: 'passwordExpiration',
									label: this.$t('label.user.passwordExpiration'),
									val: moment(new Date().setMonth(new Date().getMonth() + 3)).format('YYYY-MM-DD HH:mm:ss')
								},
								{
									type: 'singleDaterange',
									vModel: 'userExpiration',
									label: this.$t('label.user.userExpiration'),
									val: moment(new Date().setMonth(new Date().getMonth() + 3)).format('YYYY-MM-DD HH:mm:ss')
								},
								{
									type: 'text',
									vModel: 'loginAttempts',
									val: '0',
									label: this.$t('label.login.count'),
									regExpType: 'num'
								},
								{
									type: 'text',
									vModel: 'loginTimestamp',
									label: this.$t('label.last.login.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				},
				{
					type: 'bundle',
					url: '/user/UserDetailPrivileges',
					label: this.$t(`label.user.privilege.info`)
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	},
	async mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	methods: {
		search(searchObj) {
			this.gridSearchObj = searchObj;
		}
	}
};
</script>
