<template>
	<component
		ref="component"
		:is="optionElement"
		:classObject="classObject"
		:readonly="readonly"
		:placeholder="placeholder"
		:regExpType="regExpType"
		@changeEvt="changeEvt"
		@setLengthCnt="setLengthCnt"
		@enter="enter"
		@blur="blur"
	></component>
</template>

<script>
import { HttpReq, getUUID, getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'dataList',
	props: {
		vModel: String,
		classObject: Object,
		readonly: {
			type: Boolean,
			default: false
		},
		optionInfo: Object,
		placeholder: String,
		isEvent: {
			type: Boolean,
			default: true
		},
		regExpType: {
			type: String,
			default: 'default'
		},
		val: {
			type: String,
			default: null
		}
	},
	data() {
		return {
			optionElement: {
				template: `
                    <span style="line-height: 0.9rem;">
                        <input
                            type="text"
                            :list="uuid"
                            :class="classObject"
							:readonly="readonly"
							:placeholder="placeholder"
							:maxlength="maxLength"
                            v-model="obj['${this.vModel}']"
							@input="input"
							@keyup.enter="enter"
							@blur="blur"
							${this.isEvent && this._events.changeEvt ? '@change="changeEvt"' : ''}
                        />
                        <datalist :id="uuid">
                            <option
                                v-for="${this.optionInfo.optionFor}"
                                :key="${this.optionInfo.optionValue}"
                                :value="${this.optionInfo.optionValue}"
                                v-text="${this.optionInfo.optionText}" >
                            </option>
                        </datalist>
                    </span>
				`,
				props: {
					classObject: Object,
					readonly: Boolean,
					placeholder: String,
					regExpType: String
				},
				data: (() => {
					const rtnDataObj = {
						vModelName: this.vModel,
						optionListName: this.optionInfo.optionListName,

						obj: {
							[this.vModel]: this.val
						},

						[this.optionInfo.optionListName]: [],

						url: this.optionInfo.url,
						params: this.optionInfo.params ? this.optionInfo.params : null,

						uuid: getUUID(),

						HttpReq: null,

						maxLength: null,
						regExp: null
					};

					return function () {
						return rtnDataObj;
					};
				})(),
				async created() {
					this.maxLength = getRegExpInfo(this.regExpType).maxLength;
					this.regExp = getRegExpInfo(this.regExpType).regExp;

					if (!this.url) return;

					this.HttpReq = new HttpReq(this.url);

					const result = await this.HttpReq.read(this.params);

					if (`ok` !== result.data.result) return;

					this[this.optionListName] = result.data.object;
				},
				methods: {
					changeEvt() {
						this.$emit('changeEvt', { key: this.vModelName, value: this.obj[this.vModelName] });
					},
					setData(val) {
						this.obj[this.vModelName] = val;
						this.setLengthCnt();
					},
					getData() {
						return {
							[this.vModelName]: this.obj[this.vModelName]
								? this.obj[this.vModelName]
								: `num` === this.regExpType
								? null
								: this.obj[this.vModelName]
						};
					},
					input(e) {
						this.obj[this.vModel] = e.target.value;
						this.setLengthCnt();
					},
					setLengthCnt() {
						if ('string' !== typeof this.obj[this.vModelName]) return;

						this.obj[this.vModelName] = this.obj[this.vModelName].replace(new RegExp(this.regExp, 'g'), '');

						const length = this.obj[this.vModelName].length;

						if (length > this.maxLength) return;

						this.$emit('setLengthCnt', { [this.vModelName]: length });
					},
					enter() {
						this.$emit('enter');
					},
					blur() {
						if (`pageSize` === this.vModelName) this.$emit('blur');
					}
				}
			}
		};
	},
	watch: {
		val() {
			this.setData(this.val);
		}
	},
	methods: {
		changeEvt(value) {
			this.$emit('changeEvt', value);
		},
		setLengthCnt(info) {
			this.$emit('setLengthCnt', info);
		},
		setData(val) {
			this.$refs.component.setData(val);
		},
		getData() {
			return this.$refs.component.getData();
		},
		enter() {
			this.$emit('enter');
		},
		blur() {
			this.$emit('blur');
		}
	}
};
</script>
